import { action, delay, AppConfig } from '../utils';
import { shopDetailS } from '../service';


// 
function replaceKey (str, word, color = '#999') {
  const reg = new RegExp("" + word + "", "gi");
  const matchArr = str?.match(reg);

  if (matchArr) {
    const uniArr = Array.from(new Set(matchArr));
    for (let i = 0; i < uniArr.length; i++) {
      str = str.replaceAll(matchArr[i], `<span style="color:${color}">${matchArr[i]}</span>`)
    }

    return str
  } else {
    return str
  }
}


const initState = {
  restaurantInfo: {},
  dishMenu: [],
  status: {},
  allTime: [],
  dishBriefList: null,
  selectedMenu: {},

  showSearch: false,
  searchKeyword: '',
  scrollTop: 0,// 
  // 4 loading
  restaurantInfoLoading: false,
  dishMenuListLoading: true,
  restaurantStatusLoading: false,
  dishBriefListLoading: false, //  dishBriefList 
  restaurantAllTimeLoading: false,
  showStoreInfo: false,
};

export default {
  namespace: 'shopDetail',
  state: initState,
  reducers: {
    save (state, { payload }) {
      return { ...state, ...payload };
    },
  },
  effects: {
    * getDishBriefList ({ payload: { rId, typeId, menuId } }, { call, put, select }) {
      try {
        yield put(action('save', { dishBriefListLoading: true }))
        if (typeId && menuId) {
          const res = yield call(shopDetailS.getDishBriefList, { restaurantId: rId, typeId: typeId, menuId: menuId });
          yield put(action('dealDishBriefList', { undealDishBriefList: { normal: res || [] } }))
        }
      } catch (e) {
        console.error('getDishBriefList', e);
      } finally {
        yield put(action('save', { dishBriefListLoading: false }))
      }
    },

    * reGetDishBriefList ({ payload }, { call, put, select }) {
      try {
        const { rId } = yield select(({ orderList }) => orderList);
        yield put(action('getDishMenuList', { rId: rId, type: 'reget' }));
      } catch (e) {
        console.error('reGetDishBriefList', e);
      }
    },

    // Pc menu
    * setSelectedMenu ({ payload: { menu, callBack } }, { call, put, select }) {
      try {
        const { selectedMenu } = yield select(({ shopDetail }) => shopDetail);
        // if (selectedMenu.id !== menu.id) {

        yield put(action('save', {
          selectedMenu: menu,
          showSearch: false,
        }));
        callBack();
        // }

      } catch (e) {
        console.error('setSelectedMenu', e);
      }
    },

    * getRestaurantInfo ({ payload: { rId } }, { call, put, select }) {
      try {
        yield put(action('save', {
          restaurantInfoLoading: true,
        }));
        const res = yield call(shopDetailS.getRestaurantInfo, { rId: rId });
        yield put(action('save', {
          restaurantInfo: res || {},
        }));
        //   restaurantInfo 
        if (res) {
          yield put(action('orderList/save', { rId: rId, rName: res.name, currency: res.currency, shopGeo: { lat: res.lat, lon: res.lon }, country: res.country }))
        }
      } catch (e) {
        console.error('getRestaurantInfo', e);
      } finally {
        yield put(action('save', {
          restaurantInfoLoading: false,
        }));
      }
    },

    *getDishMenuList ({ payload: { rId, type } }, { call, put, select }) {
      try {
        yield put(action('save', {
          dishMenuListLoading: true,
        }));

        const { landing } = yield select(({ webConfigModal }) => webConfigModal);

        const res = yield call(shopDetailS.getDishMenuList, { restaurantId: rId, menuIds: landing?.menuIds })

        yield put(action('save', {
          dishMenu: res || [],
        }));
        const { selectedMenu } = yield select(({ shopDetail }) => shopDetail);

        //  
        if (!selectedMenu.id || type === 'reget') {
          if (res && res.length > 0) {
            yield put(action('save', {
              selectedMenu: res.filter(item => item.isDefault)[0]?.dishTypes[0] || res[0]?.dishTypes[0] || [],
            }));
          }
          const { selectedMenu } = yield select(({ shopDetail }) => shopDetail);
          yield put(action('getDishBriefList', { rId, typeId: selectedMenu.id, menuId: selectedMenu.menuId }))
        }
      } catch (e) {
        console.error('getDishMenuList', e);
      } finally {
        yield put(action('save', { dishMenuListLoading: false }))
      }
    },

    // shopFoodsM  
    * getDishMenuInM ({ payload: { rId } }, { call, put, select }) {
      try {
        const res = yield call(shopDetailS.getDishMenuList, { restaurantId: rId })

        yield put(action('save', {
          dishMenu: res || [],
        }));

      } catch (e) {
        console.error('getDishMenuInM', e);
      }
    },


    * restaurantStatusCheck ({ payload: { rId } }, { call, put, select }) {
      try {
        yield put(action('save', {
          restaurantStatusLoading: true,
        }));
        const res = yield call(shopDetailS.restaurantStatusCheck, { rId: rId });
        yield put(action('save', {
          status: res || {},
        }));
      } catch (e) {
        console.error('restaurantStatusCheck', e);
      } finally {
        yield put(action('save', {
          restaurantStatusLoading: false,
        }));
      }
    },


    * restaurantStatusRefresh ({ payload }, { call, put, select }) {
      try {
        const { restaurantInfo } = yield select(({ shopDetail }) => shopDetail);
        const { status } = yield select(({ shopDetail }) => shopDetail);
        if (restaurantInfo.id) {
          const res = yield call(shopDetailS.restaurantStatusCheck, { rId: restaurantInfo.id });
          if (res !== status) {
            yield put(action('save', {
              status: res || {},
            }));
          }
        }
      } catch (e) {
        console.error('restaurantStatusRefresh', e);
      } finally {
      }
    },

    *getRestaurantAllTime ({ payload: { rId } }, { call, put, select }) {
      try {
        yield put(action('save', {
          restaurantAllTimeLoading: true,
        }));
        const res = yield call(shopDetailS.getRestaurantAllTime, { rId: rId });
        if (Object.keys(res)?.length > 0) {
          const newRes = Object.entries(res).map(([name, value], index) => {
            let lable;
            switch (name) {
              case 'businessTime':
                lable = 'Business Hours';
                break;
              case 'collectionTime':
                lable = 'Collection Hours';
                break;
              case 'deliveryTime':
                lable = 'Delivery Hours';
                break;
            }
            return {
              name, lable, value, choosed: index === 0,
            }
          })

          yield put(action('save', {
            allTime: newRes || [],
          }));
        }
      } catch (e) {
        console.error('getRestaurantAllTime', e);
      } finally {
        yield put(action('save', {
          restaurantAllTimeLoading: false,
        }));
      }
    },

    *chooseAllTime ({ payload: { selectedTime } }, { call, put, select }) {
      try {
        const { allTime } = yield select(({ shopDetail }) => shopDetail);

        const newAllTime = allTime.map(item => {

          return {
            ...item,
            choosed: selectedTime.name === item.name
          }
        })

        yield put(action('save', {
          allTime: newAllTime,
        }));
      } catch (e) {
        console.error('getRestaurantAllTime', e);
      }
    },

    getDishBriefListByKeyword: [
      function* ({ payload: { keyword, rId } }, { call, put, select }) {
        try {
          yield put(action('save', { dishBriefListLoading: true }))
          yield call(delay, 300);
          const { landing } = yield select(({ webConfigModal }) => webConfigModal);
          if (keyword && keyword.length > AppConfig.searchCount) {
            const res = yield call(shopDetailS.getDishBriefListByKeyword, { keyword: keyword, restaurantId: rId, menuIds: landing?.menuIds })

            const newRes = {};
            Object.keys(res).map(i => {
              let newKey = replaceKey(i, keyword);

              const newList = res[i].map(item => {

                const newItem = {
                  ...item,
                  name: replaceKey(item.name, keyword),
                  description: replaceKey(item.description, keyword, '#222'),
                };
                return newItem
              })
              newRes[newKey] = newList;
            })
            //  
            yield put(action('save', {
              dishBriefList: newRes || {},
            }));
            yield put(action('dealDishBriefList', { undealDishBriefList: newRes || {}, }))

          } else {
            yield put(action('save', {
              dishBriefList: {},
            }));
          }
        } catch (e) {
          console.error('restaurantStatusCheck', e);
        } finally {
          yield put(action('save', { dishBriefListLoading: false }))
        }
      }, { type: 'takeLatest' }],

    *onSearchBack ({ payload: { rId, typeId, menuId } }, { call, put, select }) {
      try {
        yield put(action('getDishBriefList', { rId, typeId, menuId }))
      } catch (e) {
        console.error('onSearchBack', e);
      }
    },

    *clear ({ payload }, { call, put, select }) {
      try {
        const { scrollTop } = yield select(({ shopDetail }) => shopDetail);

        yield put(action('save', { ...initState, scrollTop: scrollTop }))
      } catch (e) {
        console.error('clear', e);
      }
    },

    /**
     *  dishBriefList 
     */
    *dealDishBriefList ({ payload }, { call, put, select }) {
      try {
        const { dishList } = yield select(({ orderList }) => orderList);
        const dishBriefList = payload?.undealDishBriefList;
        if (dishBriefList) {
          const list = dishList.map(item => Object.values(item)[0]);
          const newDishBriefList = {};

          Object.keys(dishBriefList).map(i => {
            const newList = dishBriefList[i].map(item => {
              const newItem = { ...item, count: 0 };
              list?.map(ll => {
                if (item.isHalfPizza && item.isHalfPizza === ll.isHalfPizza) {
                  newItem.count = newItem.count + ll.count
                } else {
                  if (item.id === ll.id && item.menuId === ll.menuId && item.typeId === ll.typeId && item.isHalfPizza === ll.isHalfPizza) {
                    newItem.count = newItem.count + ll.count
                  }
                }
              })
              return newItem
            })
            newDishBriefList[i] = newList;
          })

          yield put(action('save', {
            dishBriefList: newDishBriefList, //  
          }));
        }

      } catch (e) {
        console.error('dealDishBriefList', e);
      }
    },
  },
};