import React, { useRef, useEffect, useMemo } from 'react';


import './index.less';
import './indexPc.less';
import ShopFoodsItem from './shopFoodsItem';
import ShopSearchBar from './shopSearchBar';
import DishDetail from './dishDetail';
import { Loader, Notice } from '../../../component';


import { action, connect, compose, getValueFromQuery, useLazyload, isMobile, getStatusBarHeight, inRN } from '../../../utils';
import { withRouter } from 'react-router-dom';
import ShopEmpty from '../shopEmpty';


const ShopFoods = (props) => {

  const { selectedMenu, dishBriefList, modelId, submodelId, from, dishBriefListLoading,
    search, showSearch, searchKeyword, dishDetailLoading, data, dishList, stickyTop } = props;

  // const rId = getValueFromQuery('rId', props.location.search);


  useEffect(() => {
    props.dispatch(action('shopDetail/dealDishBriefList', { undealDishBriefList: dishBriefList }));
  }, [dishList])


  function onFoodClick (item) {
    const params = {
      rId: item.shopId,
      dishId: item.id,
      typeId: item.typeId,
      categoryId: item.categoryId,
      menuId: item.menuId,
      from: 'foods',
    }
    props.dispatch(action('statistic/recordPointTag', { recordclick: 'onFoodClick', mark: JSON.stringify(params) }));

    props.dispatch(action('cart/getDishDetail', {
      modelId: item.id, params: params
    }));//  data   popModal    
  }

  // 
  function onClose () {
    props.dispatch(action('cart/close', { submodelId: null }))
  }

  function onReset (categoryId, onConChange) {
    if (categoryId === 8) {
      props.dispatch(action('cart/onResetDish', { modelId: modelId + '', callBack: onConChange && onConChange, from: 'foods' }));
    } else {
      props.dispatch(action('cart/onResetPizza', { modelId: modelId + '', }));
    }
  }

  // 
  function onSubClose () {
    props.dispatch(action('cart/close', { submodelId: submodelId + '' }))
  }

  function onResetSub () {
    props.dispatch(action('cart/onResetPizza', { modelId: submodelId + '', }));
  }

  //  
  function onSave () {
    if (submodelId) { //    merge 
      props.dispatch(action('cart/mergeSub', { submodelId: submodelId + '' }))
    } else { //   
      props.dispatch(action('orderList/addDishToOrder', {
        modelId: modelId + '', data: data, callBack: () => {
          props.dispatch(action('cart/close'))
        }
      }))

    }
  }

  const searchHeight = useMemo(() => {
    if (search === 'search') {
      if (inRN()) {
        return `calc(100% - ${(getStatusBarHeight() / document.documentElement.clientWidth * 7.5 + 1).toFixed(2) + 'rem'})`;
      } else if (isMobile()) {
        return `calc(100% - 1rem)`;
      } else {
        return 'unset'
      }
    }
  }, [search])


  function renderContent () {

    if (dishBriefListLoading) {
      return <Loader type='tran' loading />
    }


    if (dishBriefList && Object.keys(dishBriefList)) {

      return <div className='shop_foods_content_wrapper_container' style={{ height: searchHeight }}>
        {
          searchKeyword?.length > 1 && !dishDetailLoading && Object.keys(dishBriefList).length == 0 ?
            <section className='shop_foods_content_search_empty_container'>
              <ShopEmpty text={`Didn't find any results for "${searchKeyword}"`} />
            </section>
            : null
        }
        {
          Object.keys(dishBriefList).map(key => {
            return <div className='shop_foods_content_container_wrapper' key={key}>
              {key !== 'normal' && <h5 className='shop_foods_content_container_wrapper_title' style={{ top: stickyTop }}>
                <span dangerouslySetInnerHTML={{ __html: key }}></span>
              </h5>}
              {(search === 'search' && key === 'normal') ? null :
                dishBriefList[key]?.length > 0 ? dishBriefList[key]?.map((item, index) => {
                  return <ShopFoodsItem
                    showDetailDes={showSearch && searchKeyword}
                    data={item} key={item.id + '' + index} onFoodClick={() => onFoodClick(item)} />
                }) : <ShopEmpty text={`Couldn't find any dish under this category.`} />
              }
            </div>
          })
        }
      </div>
    }
  }

  useLazyload(dishBriefList, '.shop_foods_des_right');

  return <section className='shop_foods_container'>
    <ShopSearchBar />
    {selectedMenu?.description && !showSearch && <div className='shop_foods_notice'>
      <div className='shop_foods_notice_wapper'>
        <Notice text={selectedMenu?.description} />
      </div>
    </div>}
    {renderContent()}
    {(!!modelId || modelId === 0) && from === 'foods' && <DishDetail onClose={onClose} onReset={onReset} modelId={modelId} save={onSave} from='foods' />}
    {!!submodelId && from === 'foods' && <DishDetail onClose={onSubClose} onReset={onResetSub} modelId={submodelId} sub={true} save={onSave} from='foods' />}
    {(dishDetailLoading) && <Loader type='tran' loading />}
  </section>

}

export default compose(
  withRouter,
  connect(({ shopDetail: { dishBriefList, selectedMenu, showSearch, searchKeyword, dishBriefListLoading, }, cart: { modelId, submodelId, from, dishDetailLoading, data }, orderList: { dishList, } }) => ({
    dishBriefList, selectedMenu, modelId, submodelId, from, showSearch, searchKeyword, dishBriefListLoading, dishDetailLoading, data, dishList
  })))(ShopFoods)