import React, { useEffect, useRef } from 'react';

import ShopOrderList from './shopOrderList';
import { Icon, AdaptHeader } from '../../component';
import { connect, action, getValueFromQuery } from './../../utils'



import './index.less';

//   
const shopOrderListM = (props) => {

  const rId = getValueFromQuery('rId', props.location.search);
  const { rName } = props;

  useEffect(() => {
    if (rId) {
      props.dispatch(action('shopDetail/restaurantStatusCheck', { rId: rId }));
      props.dispatch(action('shopDetail/getRestaurantInfo', { rId: rId }));
      props.dispatch(action('orderList/judgeDishBelongShop', { rId: rId }));
    } else {
      props.history.replace('/')
    }
  }, [])

  const checkStatusTimer = useRef();

  useEffect(() => {
    if (process.env.NODE_ENV !== 'development') { //   log 
      checkStatusTimer.current = setInterval(() => {

        props.dispatch(action('shopDetail/restaurantStatusRefresh'));
      }, 30000)
    }
    return () => checkStatusTimer.current && clearInterval(checkStatusTimer.current)
  }, [])

  function onRightPress () {
    props.dispatch(action('shopDetail/save', { scrollTop: 0 }))
    props.history.push(`/shopDetail?rId=${rId}`)
  }

  return <section className='shop_order_list_page_m' >
    <AdaptHeader
      title={
        <>
          <h5>Your order</h5>
          <p className='one-line'>{rName}</p>
        </>
      }
      right='View Menu'
      onRightPress={onRightPress}
    />
    <ShopOrderList fromPage='shopOrderListM' />
  </section>
}

export default connect(({ orderList: { rName } }) => ({ rName }))(shopOrderListM);

