import React from 'react';
import { Popup, message } from '../../../component';
import ReviewPaymentChoose from './reviewPaymentChoose';

import { action, connect, compose, withRouter, getReturnPath, AppConfig, inRN, openRevolut } from '../../../utils';

import './index.less';
import './indexPc.less';
import { reviewPaymentS } from '../../../service';

const ReviewPaymentChoosePop = (props) => {


  const { uuid, } = props;

  function onPayChoose (type, cardType) {
    props.dispatch(action('reviewPayment/save', { showPayChoose: false }))

    props.dispatch(action('statistic/recordPointTag', {
      recordclick: 'onPayChoose',
      mark: ' ReviewPayment ' + type + ':' + cardType
    }));

    props.dispatch(action('reviewPayment/pay', {
      payType: type, cardType: cardType, callBack: (res) => {
        callBackWithRes(type, res, cardType)
      }
    }));
  }

  async function callBackWithRes (type, res, cardType) {
    if (type === 'cash') {
      if (res === 'success') { //  
        props.history.push(`/historyOrderDetail?uuid=${uuid}&from=payment`);
        props.dispatch(action('statistic/saveAdsPurchaseAndClear'));
      } else {
        message.base(res, 1500);
        props.dispatch(action('reviewPayment/save', { showOops: true, showPayChoose: false }))
      }
    } else {
      if (type === 'card') {
        if (cardType === AppConfig.REVOLUT) {

          // if (inRN()) {
          // const res = await reviewPaymentS.revolutPay({ uuid: uuid, host: window.location.origin, version: 'v2' });
          // openRevolut({ uuid: uuid, revolutId: res.revolutId })
          //   openRevolut({ uuid: uuid })
          // } else {
          props.history.push(`/revolutPay/pending`);
          // }
        }
      }
    }
  }

  return <section className="review_payment_choose_pop">
    <h5>Payment</h5>
    <ReviewPaymentChoose onPayChoose={onPayChoose} />
  </section>
}


export default compose(
  withRouter,
  connect(({ orderList: { uuid } }) => ({
    uuid,
  })))(Popup(ReviewPaymentChoosePop))

