import React, { useMemo, useState, useRef } from 'react';

import './index.less';
import './indexPc.less';

import DishItem from '../dishItem'

const DishCon = (props) => {

  const { list, onChooseDish, finishChoose, categoryId, onEditPizza, onNoteChange, from, isMinPOS, onToogleShow, onSkip, onNONE, totolPrice
  } = props;


  // pid   immutable 
  // subId   immutable 
  function onChoose (subitem, partId, pid, subId, type) {
    onChooseDish(subitem, partId, pid, subId, type)
  }

  function onEdit (index, subItem) {
    onEditPizza(index, subItem)
  }

  function onNote (index, subItem, v) {
    onNoteChange(index, subItem, v)
  }

  function onToogle (index, v) {
    onToogleShow(index, categoryId, v)
  }

  function renderItem () {

    let firstOpenNextIndex = 0;
    for (let index = 0; index < list.length; index++) {
      const element = list[index];
      if (!element.openNext) {
        firstOpenNextIndex = index;
        break
      }

    }

    const OpenedItems = list.filter((item, i) => i <= firstOpenNextIndex)



    return (finishChoose ? list : OpenedItems).map((item, index) => {
      return (
        <DishItem
          isMinPOS={isMinPOS} // 
          from={from}
          categoryId={categoryId}
          key={item.partId + 'con'}
          id={item.partId}
          index={index}
          itemData={item}
          finishChoose={finishChoose}
          itemPrice={totolPrice ? totolPrice[index] : 0}
          onEditPizza={(subItem) => onEdit(index, subItem)}
          onNote={(subItem, v) => onNote(index, subItem, v)}
          onToogleShow={(v) => onToogle(index, v)}
          onChooseDish={(subitem, partId, subId, type) => onChoose(subitem, partId, index, subId, type)}
          onSkip={() => onSkip(index)}
          onNONEPress={() => onNONE(index)}
        />
      )

    })
  }

  return (
    <div className='dish_con_container' >
      {renderItem()}
    </div>
  )
}

export default DishCon;