import React, { useMemo, useState, } from 'react';
import { AutoTextarea, Modal, Icon } from '../../../component';
import { usePrevious, connect, action } from '../../../utils';

import './index.less';
import './indexPc.less';

// 1 :"Takeaway"
// 2 :"Delivery"
// 3  :"Collection"
// 4  :"Quick food"
// 5 :"Eat in"
const DeliveryTime = (props) => {

  const { onClose, onChoose, time, currentType } = props;

  const timeList = useMemo(() => {
    const deliveryTime = ['A.S.A.P', '10 mins', '30 mins', '60 mins', '1.5 hours', '2 hours'];
    const collectionTime = ['A.S.A.P', '45 mins', '60 mins', '1.5 hours', '2 hours', '2.5 hours'];
    if (currentType === 3) {
      return deliveryTime;
    } else if (currentType === 2) {
      return collectionTime;
    }
  }, [currentType])

  function renderItem () {
    return timeList?.map(item => {
      return <div className='delivery_time_item' key={item}>
        <input id={item} type="radio" name="attributes" value={item} onClick={onChoose} checked={item === time} readOnly={true} />
        <label htmlFor={item} >
          {item === time ? <Icon type='iconicon_radio_pre_A20' /> : <Icon type='iconicon_radio_A20' />}
          {item}</label>
      </div>
    })
  }
  return <Modal
    className='delivery_time_contianer'
    onClose={onClose} title={currentType === 2 ? 'Delivery Time' : 'Collection Time'}>
    <section className='delivery_time_content'>
      {renderItem()}
    </section>
  </Modal>
}

export default connect()(DeliveryTime);