import React from 'react';
import { AdaptHeader, CurrencyPrice, Icon, TelCon, Basket } from '../../../component';
import { isPc } from '../../../utils';


import './index.less';
import './indexPc.less';

const tooFar = (props) => {
  const { orderDeliveryModel, restaurantInfo, onClose, onSwitch, onButtonPress } = props;


  return <section className='set_delivery_charge_too_far_container'>
    <div className='set_delivery_charge_too_far_container_wrapper'>
      <AdaptHeader title='Too far away' onLeftPress={onClose} right={!isPc() && <Basket withoutBorder canPress />} />
      <section className='set_delivery_charge_too_far_content'>
        <section className='set_delivery_charge_too_far_basic_info'>
          <h5>{restaurantInfo?.name}</h5>
          <p>{restaurantInfo?.address}</p>
          <div className='set_delivery_charge_too_far_basic_info_delivery'>
            <Icon type='iconicon_location_b' />
            <p>{orderDeliveryModel?.distance} km | Delivery charge(Rounded) <span><CurrencyPrice price={orderDeliveryModel?.fee} /></span></p>
          </div>
          <div className='set_delivery_charge_too_far_basic_info_notice'>
            <Icon type='iconicon_tips_o_A19' />
            <p> You are too far away from the restaurant. The restaurant may not deliver to your location.</p>
          </div>
          <div className='set_delivery_charge_too_far_basic_info_contact'>
            <p>Please contact the store before you place the order.</p>
            <div className='set_delivery_charge_too_far_basic_info_contact_phone'>
              <Icon type='iconicon_phone_a19' />
              <TelCon tel={restaurantInfo?.phone}>
                <p>{restaurantInfo?.phone}</p>
              </TelCon>
            </div>
          </div>
        </section>

        <div className='set_delivery_charge_too_far_switch_wrapper'>
          <section className='set_delivery_charge_too_far_switch' onClick={onSwitch}>
            <Icon type='iconicon_collect_o_a19' />
            <div className='set_delivery_charge_too_far_switch_text'>
              <p>Switch to collection</p>
              <p>I will collect the order from the store.</p>
            </div>
            <Icon type='iconicon_more_A1' />
          </section>
        </div>

        <section className='set_delivery_charge_too_far_buttom'>
          <p>I have contacted the store, the store will deliver to my location.</p>
          <button onClick={onButtonPress}>Place order for delivery</button>
        </section>
      </section>
    </div>
  </section>
}

export default tooFar;