import React, { useEffect, useMemo, useRef, useState } from 'react';
import './indexPc.less';
import './index.less';
import { connect, action, AppConfig, inRN, getUpdateMetadata, transFormTel } from '../../utils';
import Icon from '../icon';
import MenuItem from './menuItem'
import MenuHeader from './menuHeader';
import AutoTextarea from '../autoTextarea';
import { useHistory } from 'react-router';

const MyMenu = (props) => {

  const { user } = props;

  const history = useHistory()

  const userInfo = user?.principal?.user

  const userName = userInfo?.userName;
  const phone = userInfo?.phone;

  const [showDevelop, setShowDevelop] = useState(false);
  const [code, setCode] = useState('');// 8
  const [site, setSite] = useState('');// 8

  const clickTime = useRef(0);
  const timer = useRef();

  // 
  useEffect(() => {
    props.dispatch(action('home/pendingCheck'));
    return () => {
      timer.current && clearTimeout(timer.current)
    }
  }, [])

  function redirect () {
    if (!inRN()) {
      return
    }
    if (timer.current) {
      clearTimeout(timer.current);
    }
    clickTime.current = clickTime.current + 1;
    timer.current = setTimeout(() => {
      clickTime.current = 0
    }, 1000)
    if (clickTime.current > 5) {
      setShowDevelop(true)
    }
  }

  function onAddress () {
    props.onAddress && props.onAddress();
  }

  function onHistory () {
    props.onHistory && props.onHistory();
  }

  function logout () {
    props.dispatch(action('user/logout'))
    props.onLogout && props.onLogout();
  }

  function onChange (e) {
    setCode(e.target.value);
  }

  function onChangeSite (e) {
    setSite(e.target.value);
  }

  const [showSite, setShowSite] = useState(false);
  useEffect(() => {
    if (code.length === 6 && code === '111222') {
      setShowSite(true);
      setShowDevelop(false);
    }
  }, [code]);

  function goSite () {
    window.open('https://' + site)
  }

  const version = useMemo(() => {
    const RNupdate = getUpdateMetadata();
    if (RNupdate) {
      return `Version ${AppConfig.APP_VERSION} ${RNupdate.appVersion}(${RNupdate.label})`
    } else {
      return `Version ${AppConfig.APP_VERSION}`
    }
  }, [])


  function onHelp () {
    history.push('/help')
  }

  function onDelete () {
    history.push('/deleteAccount')
  }


  return (<section className={'my_menu_container'}>
    <section className='base_info_container'>
      <MenuHeader />
      <div className='base_info'>
        <h5>{userName}</h5>
        <p>{transFormTel(phone)}</p>
      </div>
      {/* <Icon type='icongengduo' /> */}
    </section>
    <section className='base_item_container'>
      <MenuItem type='My address' onClick={onAddress} icon='iconicon_location_b' iconColor='#FF9D0B' />
      <MenuItem type='History order' onClick={onHistory} pending={props.pending} icon='iconhistory-order'
        iconColor='#457AE5' />
      <MenuItem type='Help' onClick={onHelp} icon='icon5' iconColor='#FF9D0B' />
      <MenuItem type='Account' onClick={onDelete} icon='iconicon_me' iconColor='#457AE5' />
    </section>
    <div className='menu_vsersion' onClick={redirect}> {version}</div>
    {showDevelop && <AutoTextarea onChange={onChange} placeholder='Enter developer code' value={code} maxLength={6} />}
    {showSite && <AutoTextarea onChange={onChangeSite} placeholder='Enter your site' value={site} type='url' autocapitalize="off" autocorrect="off" />}
    {showSite && <div className='gosite' onClick={goSite}>goSite</div>}
    <button onClick={logout}>Log out</button>

  </section>)
}
export default connect(
  ({ user: { isLogin, user }, home: { pending } }) => ({
    isLogin, user, pending
  })
)(MyMenu);
