
import { http } from '../utils';
import { API } from '../config';


// /getData/order/getOrders4Pend?pending=0
const getOrders4Pend = (query) => {
  return http.get(API.getOrders4Pend, query)
};

const getOrderDetail = (query) => {
  return http.get(API.getOrderDetail, query)
}

const getOrderDetailByNotice = (query) => {
  return http.get(API.getOrderDetailByNotice, query)
}

const removeCustomer = (query) => {
  return http.post(API.removeCustomer, query)
}

const checkOrderAgain = (query) => {
  return http.get(API.checkOrderAgain, query)
}


export default {
  getOrders4Pend, getOrderDetail, checkOrderAgain, getOrderDetailByNotice,removeCustomer
}
