import React, { useEffect, useState, } from 'react';
import { CloseIcon, AutoTextarea, Popup } from '../../../../component';

import './index.less';
import './indexPc.less';

import { connect, action } from '../../../../utils';

import CouponCardItem from '../couponCardItem';


const EnterCode = (props) => {

  const { errorCouponText, searchCode, searchCoupon } = props;

  function onChange (e) {
    const value = e.target.value;
    props.dispatch(action('reviewPayment/save', { searchCode: value }))

  }

  function onCouponSearch () {
    props.dispatch(action('reviewPayment/couponSearch'))
  }

  function clearText () {
    props.dispatch(action('reviewPayment/save', { searchCode: '', errorCouponText: '', searchCoupon: null }))
  }

  function onCloseEnterCode () {
    props.dispatch(action('reviewPayment/save', { showEnterCode: false }))
  }

  function useCoupon () {
    props.dispatch(action('reviewPayment/useCoupon'))
  }

  return <section className='enter_code_wrapper'>
    <h5>Enter code</h5>
    <section className='conpon_search_header'>
      <AutoTextarea onChange={onChange} placeholder='Please enter coupon code' value={searchCode} />
      <CloseIcon onClick={clearText} text={searchCode} />
      <button onClick={onCouponSearch} >REDEEM</button>
    </section>

    <section className='conpon_search_result'>
      {errorCouponText && <section className='conpon_search_result_contet'>
        <p className='error_text'>{errorCouponText}</p>
        <button onClick={onCloseEnterCode}>OK</button>
      </section>}
      {searchCoupon && !errorCouponText && <section className='conpon_search_result_contet'>
        <CouponCardItem item={searchCoupon} />
        <button onClick={useCoupon}>Use it</button>
      </section>
      }
    </section>
  </section>
}

export default connect(({ reviewPayment: { errorCouponText, searchCode, searchCoupon } }) => ({
  errorCouponText, searchCode, searchCoupon
}))(Popup(EnterCode))