import React, { useEffect, useMemo } from 'react';
import './index.less';
import './indexPc.less';

import { useHistory } from 'react-router-dom';
import { action, connect, openWeb, hex2Rgba, scrollToAnchor } from '../../utils';


const HeaderItem = (props) => {
  const { item, feedbackShopId, selectedHeader, landing } = props;
  const { name, url } = item;

  const history = useHistory();

  const formatUrl = useMemo(() => {
    if (feedbackShopId) {
      switch (url) {
        case '/MenuView':
          return `MenuView?rId=${feedbackShopId}`
        default:
          return url;
      }

    } else {
      return url
    }
  }, [url, feedbackShopId])

  function onItemPress (item) {
    return () => {
      props.dispatch(action('home/save', { selectedHeader: item.name }))
      if (item.url.indexOf('http') > -1) {
        openWeb(item.url);
      } else {
        history.push(formatUrl);
        scrollToAnchor(formatUrl.replace('/#', ''))
      }
    }
  }

  const style = useMemo(() => {
    const color = document.body.style.getPropertyValue('--themeColor');
    const themeColor = hex2Rgba(color, .7);

    if (selectedHeader === name) {
      return {
        background: `linear-gradient(to right,${themeColor}, rgba(255, 255, 255, .2))`,
      }
    } else {
      return {}
    }
  }, [landing, selectedHeader])

  return <h1 onClick={onItemPress(item)}
    className='header_item_container'
    style={style}>
    {name}</h1>
}

export default connect(({ home: { feedbackShopId, selectedHeader }, webConfigModal: { landing } }) => ({
  feedbackShopId, selectedHeader, landing
}))(HeaderItem);