function getHost () {
  let domainStr = window.location.host;
  //  url 
  const suffix = ['.com', '.ie', '.co.uk', '.net'];

  suffix.map(item => {
    const reg = new RegExp(item + "\$");

    if (reg.test(domainStr)) {
      domainStr = domainStr.replace(reg, '')

      const hostArr = domainStr.split('.');
      if (hostArr.length === 1) {
        domainStr = "www." + domainStr;
      }
      domainStr = domainStr + item
    }
  });

  return domainStr;
}

function baseENVkey (dev, pro) {
  if (process.env.NODE_ENV === 'development') {
    return dev
  } else {
    if (window.location.host === 'rebuildtest.easydish.com' ||
      window.location.host === 'slandingtest.easydish.com' ||
      window.location.host === 'ulandingtest.easydish.com') {
      return dev
    } else {
      return pro
    }
  }
}

