import React, { useEffect } from 'react';
import { connect, action, compose } from '../../utils';
import { Icon, Modal, AdaptHeader } from '../../component';

import './index.less';
import './indexPc.less';
import ResultDishView from '../resultDishView';
import { withRouter } from 'react-router-dom';


const ErrOrderList = (props) => {

  const { errorList, shopId, currency } = props;

  function onClose () {
    props.dispatch(action('historyOrder/closeErrorList'))
  }

  function onConfirm () {
    props.dispatch(action('historyOrder/closeErrorList'))
    props.dispatch(action('historyOrder/confirmOrderAgain'))
    props.history.push(`shopDetail?rId=${shopId}`)
    //  orderList 
    errorList?.filter(item => item.isValid).map(item => {
      props.dispatch(action('cart/getDishDetailFromOrder', {
        rId: item.shopId,
        dishId: item.id,
        typeId: item.typeId,
        categoryId: item.categoryId,
        menuId: item.menuId,
        dish: item,
      }));
    })
  }

  function renderDishList (item, index) {

    return <div className="unavailable_removed_items">
      {/* <div className="unavailable_item_header">
        <Icon type='iconicon_tips_o_A19' />
        <p >
          {item.text}
        </p>
      </div> */}
      {<ResultDishView dishList={item.list} currency={currency} key={index} />}
    </div>

  }

  function filterList () {
    return [
      {
        text: 'The items have been removed from the menu.',
        list: errorList?.filter(item => !item.isValid),
      },
      // {
      //   text: 'The items are unavailable online.',
      //   list: errorList?.filter(item => item.isValid),
      // },
      // {text:'The items are unavailable for Collection.'},
      // {text:'The items are unavailable on Monday.'},
    ].map((item, index) => {
      if (item?.list?.length > 0) {
        return renderDishList(item, index)
      }
    })

  }

  return <Modal title=''
    onClose={onClose}
    className="unavailable_page">
    <AdaptHeader title='Order again' onLeftPress={onClose} />
    <section className='unavailable_page_wrapper'>
      <div className="unavailable_alert">
        <img src={require('../../assets/unavailable_item.png')} className='iconUnavailableitem' />
        <h5 >Unavailable item</h5>
        <small >
          The following item will be removed from the basket.
        </small>
      </div>
      {filterList()}
    </section>
    <div className="unavailable_btn_div">
      <button className=" unavailable_btn_return"
        onClick={onClose}>Return</button>

      <button className=" unavailable_btn_confirm"
        onClick={onConfirm}>
        Confirm
      </button>

    </div>
  </Modal>
}

export default compose(withRouter, connect(({ historyOrder: { errorList, shopId, currency } }) => ({
  errorList, shopId, currency
})))(ErrOrderList);

