import React, { useState } from 'react';

const PreImg = (props) => {

  const [loaded, setLoaded] = useState(false);

  function onLoad () {
    setLoaded(true)
  }

  return <>
    <img {...props} onLoad={onLoad} style={{ display: loaded ? undefined : 'none' }} />
    <div {...props} style={{ display: !loaded ? undefined : 'none' }} />
  </>
}

export default PreImg;