import React, { useEffect, useMemo, useState } from 'react';
import './index.less';
import './indexPc.less';

const ToppingC = (props) => {

  const { topping, className } = props;


  return (!!topping?.count) ? <p key={topping.id} className={`toppings_c  ${className}`}>
    {topping.count > 1 && `${topping.count} x `}
    {topping.name}
    {topping.isFree && <span className='toppings_free'>Free</span>}
    {topping.isPremium && <span className='toppings_premium'>Premium</span>}

  </p> : null
}

const NoToppingC = (props) => {

  const { topping, className } = props;

  const name = useMemo(() => {

    if (topping.name.indexOf('NO') > -1||topping.name.indexOf('No') > -1) {
      return topping.name
    } else {
      return 'NO ' + topping.name
    }

  }, [topping])


  return (!!topping?.def) ? <p className={`toppings_c  ${className}`}>
    <span className='line-through'>{name}</span>
    {topping.isFree && <span className='toppings_free'>Free</span>}
    {topping.isPremium && <span className='toppings_premium'>Premium</span>}
  </p> : null
}

export { NoToppingC, ToppingC }