import React from 'react';
import { withRouter } from 'react-router-dom';

import './indexPc.less';

import ShopCategoryItem from './ShopCategoryItem/indexPc';
import { action, connect, compose, getValueFromQuery } from '../../../utils';


const ShopCategory = (props) => {

  const { dishMenu, selectedMenu, rId } = props;

  function onCategoryItemPress (item) {
    props.dispatch(action('shopDetail/setSelectedMenu', {
      menu: item, callBack: () => {
        props.dispatch(action('shopDetail/getDishBriefList', { rId: rId, typeId: item.id, menuId: item.menuId }));
      }
    }));
  }

  function renderContent () {
    return dishMenu?.map(item => {
      return (
        <ShopCategoryItem
          key={item.name}
          selectedMenu={selectedMenu}
          content={item}
          onCategoryItemPress={onCategoryItemPress}
          hideTitle={dishMenu.length <= 1}
        />
      )
    })
  }

  return <section className='shop_category_container_pc'>
    <div className='shop_category_container_pc_wrapper'>
      {renderContent()}

    </div>
  </section>

}

export default compose(
  withRouter,
  connect(({ shopDetail: { dishMenu, selectedMenu }, orderList: { rId } }) => ({
    dishMenu, selectedMenu, rId
  })))(ShopCategory)