import React from 'react';
import { ShopMap } from '../../../../component';
import { connect, } from '../../../../utils';

import './index.less'
import './indexPc.less';

const MapC = (props) => {

  const { shopList } = props;
  if (shopList?.length > 0)
    return (<section className="map_c" >
      <ShopMap shopList={shopList} />
    </section>)
  else return null
}

export default connect(({ home: { shopList, } }) => ({
  shopList,
}))(MapC);