import React, { useEffect, useRef, useState, useMemo } from 'react';
import { Icon, AddressModal, Swiper, DirectionC } from '../../../../component';
import { connect, compose, withRouter, inRN, useThrottle, action, h5ToRn, useSize } from '../../../../utils';

import './index.less'
import './indexPc.less';
import AddressC from '../addressC';
import addEventListener from "add-dom-event-listener";


const TopC = (props) => {

  const { landing, showAddress, restaurantInfo, rId } = props;


  function onOrderNow () {
    props.dispatch(action('statistic/recordPointTag', { recordclick: 'onOrderNow' }));

    if (landing.webSiteType === 2) {
      props.dispatch(action('home/save', { showAddress: true }))
    } else {
      if (rId) {
        props.history.push(`/shopDetail?rId=${landing.shopId}`);
      } else {
        props.history.push(`/selectOrderType?rId=${landing.shopId}`);
      }
    }
  }

  function onBookTable () {
    props.dispatch(action('statistic/recordPointTag', { recordclick: 'onBookTable' }));
    if (landing.webSiteType === 2) {
      props.history.push(`/booking?unionId=${landing.unionId}`);
    } else {
      props.history.push(`/booking?rId=${landing.shopId}`);
    }
  }

  const [bannerR, setBanerR] = useState([]);


  const size = useSize(() => {
    setBanerR(landing.bannerWapImages)
  }, () => {
    setBanerR(landing.bannerPCImages)
  }, [landing], 750)

  function onAddressClosePress () {
    props.dispatch(action('home/save', { showAddress: false }));
    //
    h5ToRn({ type: 2, statusBar: 'light-content' });
  }

  // 
  function onAddressSelect (ad) {
    props.dispatch(action('home/save', { showAddress: false }))
    props.history.push('/shopList')
  }

  // 
  function getAddress (geo) {
    props.dispatch(action('address/queryLocalAddrsByGeo', {
      lat: geo.lat, lng: geo.lon, callBack: (res) => {
        props.dispatch(action('user/saveAddress', {
          lat: geo.lat, lng: geo.lon, ad: res.formatted_address, callBack: () => {
            props.history.push('/shopList');
            props.dispatch(action('home/save', { showAddress: false }));
          }
        }));
      }
    }));
  }

  const showAddressC = useMemo(() => {
    if (landing) {
      if (landing?.webSiteType && landing?.bannerType) {
        return landing.webSiteType === 2 && landing.bannerType === 2
      }
    }
  }, [landing])

  const showBook = useMemo(() => {
    if (landing) {
      return (landing?.navList || []).some(item => {
        return item.lingType === 'booking'
      })
    }

  }, [landing])

  return (<section className="home_top_c">
    {bannerR && <Swiper images={bannerR} />}

    <div className="home_top_c_wapper">
      {landing?.bannerContent &&
        <>
          {
            landing?.unionId === 1053 &&
            <img src={require('./img/mizzoniBigLogo.png')} className="home_top_c_welcome_img" />
          }
          <div className="home_top_c_welcome">
            <div className="home_top_c_welcome_border" />
            <h1>{landing?.bannerContent}</h1>
            <div className="home_top_c_welcome_border" />
          </div>
        </>
      }

      {!showAddressC && <div className="home_top_c_order_now" onClick={onOrderNow}>
        <span>Order Now</span>
        <Icon type='icondianji' />
      </div>

      }

      {!showAddressC && showBook && <div className="home_top_c_book_table" onClick={onBookTable}>Book Table</div>}
      {showAddressC && <AddressC getAddress={getAddress} />}
      {(landing?.bannerType === 3 || landing?.webSiteType === 3) &&
        <DirectionC
          restaurantInfo={restaurantInfo}
          className='home_top_c_address' />
      }
      {!inRN() && <div className='home_top_c_download'>
        <a className='home_top_c_download_item' href={landing?.appStoreUrl} target="_blank" >
          {/* <Icon type='iconpingguo' />
          <p>Apple Download</p> */}
          <img src={require('../../../../assets/AppStore.png')} />
        </a>
        <a className='home_top_c_download_item' href={landing?.googlePlayUrl} target="_blank" >
          {/* <Icon type='iconandroid' />
          <p>Android Download</p> */}
          <img src={require('../../../../assets/google_play.png')} />

        </a>
      </div>}
      <div className='powered'>Powered by <a href="https://www.easydish.ie" alt="Easy Dish Takeaway">Easy Dish</a></div>
    </div>
    {showAddress && <AddressModal onClose={onAddressClosePress} onAddressSelect={onAddressSelect} getAddress={getAddress} />}

  </section>)
}

export default compose(
  connect(({ address: { allRensentAddress, }, user: { isLogin }, webConfigModal: { landing }, home: { loading, showAddress, restaurantInfo }, orderList: { rId } }) => ({
    allRensentAddress, isLogin, landing, loading, showAddress, restaurantInfo, rId
  })),
  withRouter
)(TopC);