import React, { useMemo, useState } from 'react';
import NavBlock from '../navBlock';
import MyMenu from '../myMenu';
import { withRouter } from 'react-router-dom';

import './indexPc.less'
import { isMobile, compose, connect, action } from '../../utils';
import { INORDERDISH } from '../../route';

const MenuBlock = (props) => {

  const [menuStatus, toggleMenuStatus] = useState(false);

  const user = props.user;
  const name = useMemo(() => {
    const userName = user?.principal?.user?.userName
    if (isMobile()) {

      return userName ? (userName?.substring(0, 6) + '...') : ''
    }
    else {
      return userName || ''
    }
  }, [user])

  function toggleMenu () {
    if (isMobile()) {
      props.history.push('/menu')
    } else {
      toggleMenuStatus(!menuStatus)
    }
  }

  function onLogout () {
    if (isMobile()) {

    } else {
      toggleMenuStatus(false)
      if (INORDERDISH.includes(props.location.pathname?.toLowerCase())) {
        props.history.replace(`/shopDetail?rId=${props?.rId}`)
      } else {
        props.history.push('/')
      }
    }

    props.dispatch(action('user/logout'));
  }

  function onAddress () {
    if (isMobile()) {

    } else {
      toggleMenuStatus(false)
    }
    props.history.push('/myAddress')
  }

  function onHistory () {
    if (isMobile()) {
    } else {
      toggleMenuStatus(false)
    }
    props.history.push('/historyOrder')

  }

  function onMaskPress () {
    toggleMenuStatus(false)
  }

  return (<div className={'menu_container_pc'} >
    <NavBlock
      type="iconAccount"
      onClick={toggleMenu}
      pending={props.pending}
      text={name}
    />
    {menuStatus && <section className={`menu_container ${menuStatus ? 'fadeIn' : 'fadeOut'}`}>
      <div className='mask' onClick={onMaskPress} />
      <MyMenu
        onAddress={onAddress}
        onHistory={onHistory}
        onLogout={onLogout} />
    </section>
    }
  </div>)
}

export default compose(
  withRouter,
  connect(({ home: { pending }, orderList: { rId }, user: { user } }) => ({
    pending, rId, user
  })))(MenuBlock)
