import React, { useMemo, useEffect } from 'react';

import { connect, action, inRN, getValueFromQuery } from '../../utils';
import NormalHome from './normalhome';
import LandingHome from './landingHome';
import { Loader, Modal } from '../../component';



// const NormalHome = lazy(() => import('./normalhome'));
// const LandingHome = lazy(() => import('./landingHome'));


const Home = (props) => {

  const { landing, isLogin } = props;

  const MID = getValueFromQuery('MID', props.location.search);

  useEffect(() => {
    if (MID) {
      props.dispatch(action('statistic/saveAdsView', { adsCode: MID }));
    }
  }, [MID])

  useEffect(() => {
    props.dispatch(action('webConfigModal/clearChunkLoadError'));

    const time = inRN() ? 1200 : 500;
    Promise.all([
      //  0.5 
      new Promise((resolve, reject) => {
        setTimeout(() => {
          resolve();
        }, time)
      }),
      new Promise((resolve, reject) => {
        props.dispatch(action('webConfigModal/init', {
          callBack: () => {
            resolve();
          }
        }));
      }),
    ]).then(res => {
      document.getElementById('app_loading').style.display = 'none'
    })


  }, [])

  useEffect(() => {
    if (isLogin) {
      props.dispatch(action('home/pendingCheck'));
      props.dispatch(action('address/getAllAddr'));

    }
    props.dispatch(action('home/displayActivity'))
    props.dispatch(action('home/showAppDownload'))

  }, [isLogin]);

  const type = useMemo(() => {
    if (landing?.webSiteType) {
      return landing.webSiteType
    } else {
      return 0
    }

  }, [landing?.webSiteType])


  // 1:easydish, 2:union, 3:store
  const app = useMemo(() => {
    switch (type) {
      case 1:
        return <NormalHome />
      case 2:
      case 3:
        return <LandingHome />
      case 0:
      default:
        return <Loader loading />
    }
  }, [type])


  return <>
    {app}
  </>
}

export default connect(({ user: { isLogin }, webConfigModal: { landing }, }) => ({
  isLogin, landing
}))(Home);