import React from 'react';

import './index.less';
import './indexPc.less';

const AboutUsCard = (props) => {
  const { data, reserve } = props;

  return <div className={`about_us_item ${reserve ? 'about_us_item_reseve' : undefined}`} >
    <div className='about_us_img_wrapper' >
      <img className="about_us_img" src={data.img} alt={'about-us:'+data?.des} />
      <h5 className='about_us_title'>{data.title}</h5>
    </div>
    <div className='about_us_text'>
      <h5 className='about_us_title'>{data.title}</h5>
      {data.des}
    </div>
  </div>
}

export default AboutUsCard