import React, { useMemo } from 'react';

import './index.less';
import './indexPc.less';
import Icon from '../icon';
import { Link, withRouter } from 'react-router-dom';

import { connect, compose, openWeb } from '../../utils'

const MyFooter = (props) => {

  const isShopList = props.location.pathname === '/shopList';
  const isHome = props.location.pathname === '/' || props.location.pathname === '/home'


  const { landing } = props;

  const footerNavList = useMemo(() => {
    if (landing?.navList?.length > 0) {
      return landing?.navList?.filter(item => item.type === 2)

    } else {
      return []
    }
  }, [landing?.navList])

  function renderNavList () {
    return footerNavList.map(item => {
      return <Link
        key={item.name}
        className="footer_fonts" to={item.url}>{item.name}</Link>
    })
  }

  function onPress (url) {
    return () => openWeb(url)
  }

  return (
    <footer className={`my_footer_box ${false ? 'my_footer_box_fixed' : undefined} ${isHome ? '' : 'footer_my_nolandinghome'}`}>
      <div className='my_footer_box_wrapper'>
        <div className="my_footer_icon" >
          <div className='my_footer_icon_logo'>
            {landing?.logoUrl ?
              <img src={landing?.logoUrl} className="pc_logo" alt="logo" /> : <Icon className="pc_logo" type="iconlogo_o" />}
          </div>
          <div className='my_footer_links_container'>
            {renderNavList()}
            <a className="footer_fonts" href="/help">Help</a>
          </div>

        </div>
        <div className='my_footer_box_right'>
          <div className="footer_fonts_share_icon">
            {landing?.facebookUrl && <a onClick={onPress(landing?.facebookUrl)} target="_blank" rel="noopener noreferrer">
              <Icon type='iconfb' />
            </a>}
            {landing?.twitterUrl && <a onClick={onPress(landing?.twitterUrl)} target="_blank" rel="noopener noreferrer">
              <Icon type='icontw' />
            </a>}

            {landing?.youtubeUrl && <a onClick={onPress(landing?.youtubeUrl)} target="_blank" rel="noopener noreferrer">
              <Icon type='iconytb' />
            </a>}
            {landing?.instagramUrl && <a onClick={onPress(landing?.instagramUrl)} target="_blank" rel="noopener noreferrer">
              <Icon type='iconins' />
            </a>}
            {landing?.linkedInUrl && <a onClick={onPress(landing?.linkedInUrl)} target="_blank" rel="noopener noreferrer">
              <Icon type='iconlinkedin' />
            </a>}
            {landing?.tiktokUrl && <a onClick={onPress(landing?.tiktokUrl)} target="_blank" rel="noopener noreferrer">
              <Icon type='icontiktok' />
            </a>}
          </div>
          <p>
            Powered by
            <a href="https://www.easydish.ie" alt="Easy Dish Takeaway">
              Easy Dish
            </a>
          </p>
        </div>
      </div>
    </footer>
  )
}

export default compose(
  connect(({ webConfigModal: { landing } }) => ({
    landing
  })),
  withRouter
)(MyFooter);

