import React, { useEffect, useState, } from 'react';

import './index.less';
import './indexPc.less';

import { action, connect, compose, usePrevious } from '../../../utils';
import { withRouter } from 'react-router-dom';
import { Icon, DishView, ModalAlert } from '../../../component';
import ShopTotlePrice from '../shopTotlePrice';
import DishDetail from '../shopFoods/dishDetail';
import UnavailableModal from '../unavailableModal';

const ShopOrderList = (props) => {

  // fromPage ='shopOrderListM' 
  const { dishList, currentType, modelId, submodelId, from, data, showUnavailableModal, fromPage, status } = props;

  useEffect(() => {
    // ,save 
    props.dispatch(action('orderList/save', { saveFlag: 0 }))
  }, [])

  // shopdetail restaurantStatusCheck 
  useEffect(() => {
    if (!status.isDelivery && status.isCollection) {
      changeType(3)()
    } else if (status.isDelivery && !status.isCollection) {
      changeType(2)()
    }
  }, [status?.isDelivery, status?.isCollection])


  //  tab
  // 1 :"Takeaway"
  // 2 :"Delivery"
  // 3  :"Collection"
  // 4  :"Quick food"
  // 5 :"Eat in"
  function renderTab () {
    let typeList = [];
    if (status.isDelivery)
      typeList.push({ name: 'Delivery', type: 2 });
    if (status.isCollection)
      typeList.push({ name: 'Collection', type: 3 });
    return typeList.map((item, index) => {
      return <div
        key={index}
        className={`order_list_tab_item ${item.type === currentType ? 'order_list_tab_item_active' : undefined}`}>
        <h5
          key={index}
          onClick={changeType(item.type)}
          className={item.type === currentType ? `active_title` : undefined}>
          {item.name}
        </h5>
      </div>
    })
  }

  function changeType (changeType) {
    return () => {
      props.dispatch(action('orderList/changeType', { currentType: changeType }))
      props.dispatch(action('orderList/judgeDishBelongCurrentType'))
      if (fromPage !== 'shopOrderListM') {
        props.dispatch(action('shopDetail/reGetDishBriefList'))
      }
    }
  }

  function onDishClick (dish, index) {
    props.dispatch(action('cart/reopen', { data: dish, reopenId: index, from: 'order', }))
  }


  function deleteDish (index) {
    ModalAlert({
      msg: 'Are you sure you want to delete this dish?', onCancel: () => { }, onSure: () => {
        props.dispatch(action('orderList/deleteDish', { keylist: [index] }))
      }
    })
  }

  //   dishDetail
  function onClose () {
    props.dispatch(action('cart/close', { modelId: modelId }))
  }

  //  
  function onSave () {
    if (submodelId) { //    merge 
      props.dispatch(action('cart/mergeSub', { submodelId: submodelId + '' }))
    } else { //   
      props.dispatch(action('orderList/addDishToOrder', {
        modelId: modelId + '', data: data, callBack: () => {
          props.dispatch(action('cart/close'));
        }
      }))
    }
  }

  function onReset (categoryId, onConChange) {
    if (categoryId === 8) {
      props.dispatch(action('cart/onResetDish', {
        modelId: modelId + '',
        callBack: onConChange && onConChange,
        from: 'order'
      }));
    } else {
      props.dispatch(action('cart/onResetPizza', { modelId: modelId + '', }));
    }

  }

  // 
  function onSubClose () {
    props.dispatch(action('cart/close', { submodelId: submodelId + '' }))
  }


  function onResetSub () {
    if (submodelId) {
      props.dispatch(action('cart/onResetPizza', { modelId: submodelId + '', }));
    }
  }

  return (
    <>
      <section className='shop_tab_container'>
        <section className='shop_tab_tabbar' >
          {renderTab()}
        </section>
        <section className='shop_tab_content'  >
          <DishView
            onDishClick={(item, index) => onDishClick(item, index)}
            deleteDish={(index) => deleteDish(index)}
            dishList={dishList} />
          <ShopTotlePrice />
        </section>
      </section>
      {showUnavailableModal && <UnavailableModal />}
      {!!modelId && from === 'order' && <DishDetail onClose={onClose} onReset={onReset} modelId={modelId} save={onSave} from='order' />}
      {!!submodelId && from === 'order' && <DishDetail onClose={onSubClose} onReset={onResetSub} modelId={submodelId} sub={true} save={onSave} from='order' />}
    </>
  )
}


export default compose(
  withRouter,
  connect(({ orderList: { dishList, currentType, showUnavailableModal }, user: { isLogin }, cart: { modelId, submodelId, from, data }, shopDetail: { status }, }) => ({
    dishList, currentType, isLogin, modelId, submodelId, from, data, showUnavailableModal, status
  })))(ShopOrderList)


