import React, { useEffect } from 'react';
import NavBlock from '../navBlock'

import { connect, compose, isMobile, withRouter, action } from '../../utils';
import { getCPrice } from '../currencyPrice';



const DishpriceIcon = (props) => {

  const { dishListPrice, rId, style } = props;

  const dishPrice = getCPrice(dishListPrice);

  useEffect(() => {
    if(rId){
      props.dispatch(action('orderList/judgeDishBelongShop', { rId: rId }));
    }
  }, [])

  function onCartClick () {
    if (isMobile()) {
      props.history.push(`/shopOrderListM?rId=${rId}`);
    } else {
      props.history.push(`/shopDetail?rId=${rId}`);
    }
  }

  return rId ? <NavBlock
    style={style}
    type="iconicon_cart"
    text={dishPrice}
    onClick={onCartClick}
  /> : null
}


export default compose(
  withRouter,
  connect(({ orderList: { dishListPrice, rId, currentType } }) => ({
    dishListPrice, rId, currentType
  })))(DishpriceIcon)