import React, { useMemo } from 'react';
import { connect } from '../../utils';


export function getCPrice (price,hiddenZero=false) {

  const { currency } = window.dvaStore?.getState()?.orderList

  if(hiddenZero&&!price){
    return ''
  }else{
    if(hiddenZero){
      if(price>0){
        return '+'+currency + '' + (price || 0).toFixed(2)
      }else{
        return '-'+currency + '' + (Math.abs(price)|| 0).toFixed(2)
      }
    }else{
      return currency + '' + (price || 0).toFixed(2)
    }
  }
}

const CurrencyPrice = (props) => {

  const { price, currency ,hiddenZero=false,displayPlus=true} = props;
  if(hiddenZero&&!price){
    return null
  }else{
    if(hiddenZero){
      if(price>0){
        return <>{displayPlus?'+':''}{currency}{((price || 0).toFixed(2))}</>
      }else{
        return <>-{currency}{((Math.abs(price) || 0).toFixed(2))}</>
      }
    }else{
      return <>{currency}{((price || 0).toFixed(2))}</>
    }
  }

}

export default connect(({ orderList: { currency } }) => ({ currency }))(CurrencyPrice)





