import { action } from '../utils';
import { shopDetailS } from '../service';

const initState = {
  restaurantInfo: {},
  restaurantStatus: {
    deliveryStatus: true,
    collectionStatus: true,
    isDelivery: true,
    isCollection: true,
  },
  loading: false,

};

export default {
  namespace: 'selectOrderType',
  state: initState,
  reducers: {
    save (state, { payload }) {
      return { ...state, ...payload };
    },
  },
  effects: {
    * getRestaurantInfo ({ payload: { rId } }, { call, put, select }) {
      try {
        yield put(action('save', { loading: true }))

        const res = yield call(shopDetailS.getRestaurantInfo, { rId: rId });
        yield put(action('save', {
          restaurantInfo: res || {},
        }));
      } catch (e) {
        console.error('getRestaurantInfo', e);
      } finally {
        yield put(action('save', { loading: false }))

      }
    },

    * restaurantStatusCheck ({ payload: { rId } }, { call, put, select }) {
      try {
        yield put(action('save', { loading: true }))
        const res = yield call(shopDetailS.restaurantStatusCheck, { rId: rId });
        yield put(action('save', {
          restaurantStatus: res || {},
        }));
      } catch (e) {
        console.error('restaurantStatusCheck', e);
      } finally {
        yield put(action('save', { loading: false }))

      }
    },

    * checkRestaurantOrder ({ payload: { restaurantId, callBack } }, { call, put, select }) {
      try {
        const { rId } = yield select(({ orderList }) => orderList);
        callBack && callBack(rId === restaurantId)
      } catch (e) {
        console.error('checkRestaurantOrder', e);
      } finally {
      }
    },

    * clear ({ payload }, { call, put, select }) {
      try {
        yield put(action('save', { ...initState }))
      } catch (e) {
        console.error('getRestaurantInfo', e);
      }
    },


  },
};