import { getScroll } from "./index";
import raf from "raf";

/**
 * 
 * @param {*} ele 
 * @param {*} q 
 */
export function scrollEle (ele, q = 10) {

  const offsetWidth = ele.offsetWidth;
  const scrollWidth = ele.scrollWidth;

  this.ele = ele;
  this.start = getScroll(ele, false);
  this.end = scrollWidth - offsetWidth;


  const totalLength = scrollWidth - offsetWidth

  this.duration = (totalLength - this.start) * q;


  this.startTime = Date.now();

  this.running = false;
  // 
  this.canRun = scrollWidth - offsetWidth > 0;
  this.timer = null;


  const frameFunc = () => {
    if (this.running) {
      const timestamp = Date.now();
      const time = timestamp - this.startTime;

      const nextScroll = move(time > this.duration ? this.duration : time, this.start, this.end, this.duration);
      this.ele && this.ele.scrollTo(nextScroll, 0);
      if (time < this.duration) {
        raf(frameFunc);
      } else {
        this.timer = setTimeout(() => {
          this.start = this.end;
          this.end = 0;
          this.startTime = Date.now();

          raf(frameFunc);
        }, 1000)

      }
    }
  };

  this.startScroll = function () {
    if (this.canRun) {
      this.running = true;
      raf(frameFunc);
    }
  }

  this.stopScroll = function () {
    if (this.canRun) {
      this.running = false
    }
  }

  this.clear = function () {
    if (this.timer) {
      clearTimeout(this.timer)
    }
  }
}

function move (t, c0, c, d) {
  if (c > c0) {
    return c0 + (c - c0) / d * t
  } else {
    return c0 - (c0 - c) / d * t
  }
}