
import { http } from '../utils';
import { API } from '../config';

const getWebPageConfig = (query) => {
  return http.get(API.getWebPageConfig, query)
};


const pendingCheck = (query) => {
  return http.get(API.pendingCheck, query)
}

const feedback = (query) => {
  return http.post(API.feedback, query)
}

const getBriefListByUnionId = (query) => {
  return http.get(API.getBriefListByUnionId, query)
}

const getMenuPaper = (query) => {
  return http.get(API.getMenuPaper, query)
}

const getShopIdByName = (query) => {
  return http.get(API.getShopIdByName, query)
}

export default {
  pendingCheck, getWebPageConfig, feedback, getBriefListByUnionId, getMenuPaper, getShopIdByName
}