import React, { useEffect, useState, useMemo, useRef } from "react";
import { useLocation } from "react-router-dom";
import { getStatusBarHeight, inRN, isMobile, getValueFromQuery } from "../../utils";
import PreImg from "../preImg";
import './index.less';
import './indexPc.less';


const Loader = (props) => {
  const { type, loading, text, fixTop } = props;

  const [show, setShow] = useState(loading);

  const timmer = useRef();

  useEffect(() => {
    if (loading) {
      if (timmer.current) {
        timmer.current && clearTimeout(timmer.current)
      }
      setShow(true);
      timmer.current = setTimeout(() => {
        setShow(false);
      }, 500)
    }

    return () => {
      setShow(false);
      timmer.current && clearTimeout(timmer.current)
    }
  }, [loading])

  const className = useMemo(() => {
    switch (type) {
      case 'tran':
        return 'loading_container_fixed loading_container_tran'
      case 'normal':
        return 'loading_container_normal'
      default:
        return 'loading_container_fixed'
    }

  }, [type])

  const height = useMemo(() => {
    switch (window.Platform) {
      case 'iphoneX':
        return 44;
      case 'ios':
        return 35;
      case 'android':
        return 20;
      case 'pad':
        return 20;
      default:
        return 0;
    }
  }, [])


  const top = useMemo(() => {
    if (fixTop) {

      if (inRN()) {
        return (height / document.documentElement.clientWidth * 7.5 + 1).toFixed(2) + 'rem';
      } else if (isMobile()) {
        return '1rem'
      } else {
        return '0';
      }
    } else {
      return '0';
    }
  }, [fixTop])

  const loaderCom = useMemo(() => {
    return <div className={className} style={{ top: top }}>
      <PreImg src={require('./loader.gif')} />
      {text && <p className="loading_text">{text}</p>}
    </div>

  }, [])

  if (show) {
    return loaderCom
  }

  if (loading) {
    return loaderCom
  }

  return null
}


export default Loader;
