import React, { useEffect, useMemo } from 'react';
import './indexPc.less';
import './index.less'

import TopC from './topC';

import { compose, connect, withRouter, scrollToAnchor, action, isMobile, isPc } from '../../../utils';
import AddressC from './addressC';
import AboutUs from './aboutUs';
import AlbumC from './albumC';

import FeedbackC from './feedbackC';
import ContactUsC from './contactUsC';
import IntroductionC from './introductionC';


const LandingHome = (props) => {
  const { landing } = props;


  // 
  useEffect(() => {
    let hash = props.history.location.hash;
    hash = hash.replace('#', '')
    setTimeout(() => {
      scrollToAnchor(hash, { offsetTop: isPc() ? 80 : 0 });
    })
  }, [props.history.location.hash])

  const showAddress = useMemo(() => {
    if (landing) {
      if (landing?.webSiteType && landing?.bannerType) {
        return landing.webSiteType === 2 && landing.bannerType === 2
      }
    }

  }, [landing])

  const content = useMemo(() => {
    if (landing?.navList) {
      return landing.navList
        .map(item => {
          const regText = item.url.match(/\/home#(.+)$/) || item.url.match(/(\/introduction)/)
          const name = regText && regText[1]
          const lingType = item.lingType

          switch (lingType) {
            case 'aboutus':
              return <AboutUs id={name} key={name} />
            case 'gallery':
              return <AlbumC id={name} key={name} />
            case 'feedback':
              return <FeedbackC id={name} key={name} />
            case 'contactus':
              return <ContactUsC id={name} key={name} />
            case 'introduction':
              return <IntroductionC id={name} key={name} />
            default:
              return null
          }
        })
    }
  }, [landing])

  return (
    <section className='landinghome_page'>
      {showAddress && <AddressC />}
      {landing.unionId === 1087 &&
        <div className='landinghome_page_pizza_mia_com'>
          <div className='landinghome_page_pizza_mia' />
        </div>
      }
      {landing.unionId === 1024 &&
        <div className='landinghome_page_peking' />
      }
      <TopC />
      {content}
    </section>
  )
}

export default compose(
  connect(({ user: { isLogin }, webConfigModal: { landing } }) => ({
    landing, isLogin
  })),
  withRouter,
)(LandingHome);
