import { action, delay, AppConfig } from '../utils';
import { myDetailS, addressS, } from '../service'
import { message } from '../component';


//1 :"Takeaway"
// 2 :"Delivery"
// 3  :"Collection"
// 4  :"Quick food"
// 5 :"Eat in"
const initState = {
  name: '',
  number: '',
  email: '',
  ad: {},
  time: 'A.S.A.P',
  note: '',

  showTime: false,
  buttonLoading: false,

  allRensentAddress: [],
  predictions: [],// 

  showAddress: false,// 
  inputvalue: '', // 
  chooseGeo: {},
  nameValue: '',
  getUserLoaidng: false,
  nameError: '',
  phoneError: '',
  addressError: '',
  emailError: '',

  // 
  showRelocation: false,
};

export default {
  namespace: 'myDetail',
  state: initState,
  reducers: {
    save (state, { payload }) {
      return { ...state, ...payload };
    },
  },
  effects: {
    *getUserInfo ({ payload }, { call, put, select }) {
      try {
        yield put(action('save', { getUserLoaidng: true }))
        const { ad, name, number, email, time } = yield select(({ myDetail }) => myDetail);
        if (!ad?.address) {
          const res = yield call(myDetailS.getUserInfo);
          if (res && res?.code) {
            const { data } = res;
            if (data) {
              yield put(action('save', {
                time: time || 'A.S.A.P',
                name: name ? name : data.orderName,
                number: number ? number : data.orderPhone ? data.orderPhone : data.phone,
                email: email ? email : data.orderEmail,
                ad: {
                  address: data.address,
                  lat: data.lat,
                  lon: data.lon,
                }
              }))
            }
          }
        }
      } catch (e) {
        console.error('getUserInfo', e)
      } finally {
        yield put(action('save', { getUserLoaidng: false }))
      }
    },

    //  address
    *onAddressChange ({ payload }, { call, put, select }) {
      try {
        const { ad, name } = yield select(({ myDetail }) => myDetail);
        yield put(action('save', {
          showAddress: true,
          inputvalue: ad.address,
          nameValue: name,
          chooseGeo: {},
        }))
      } catch (e) {
        console.error('getUserInfo', e)
      }
    },

    //    
    *saveDetailGeo ({ payload: { cuslat, cuslon } }, { call, put, select }) {
      try {
        const { ad } = yield select(({ myDetail }) => myDetail);
        yield put(action('save', {
          ad: {
            address: ad.address,
            lat: cuslat,
            lon: cuslon,
          }
        }))
      } catch (e) {
        console.error('getUserInfo', e)
      }
    },

    // 2.
    *initStateFromOrder ({ payload: { order } }, { call, put, select }) {
      try {
        yield put(action('save', {
          ...initState,
          name: order?.userName,
          number: order?.userPhone,
          email: order?.userEmail,
          ad: {
            address: order?.address,
            lat: order?.lat,
            lon: order?.lon,
          },
          time: order?.deliveryTime || 'A.S.A.P',
        }))

        yield put(action('orderList/save', { currentType: order.type }))
      } catch (e) {
        console.error('initStateFromOrder', e)
      }
    },



    *clear ({ payload }, { call, put, select }) {
      try {
        yield put(action('save', { ...initState }))
      } catch (e) {
        console.error('myDetail/clear', e)
      }
    },

    *testInfo ({ payload: { callBack } }, { call, put, select }) {
      try {
        const { dishList, currentType, currency, dishListPrice, uuid, } = yield select(({ orderList }) => orderList);
        const { isLogin } = yield select(({ user }) => user);
        if (!isLogin) { // 1  2   3 
          callBack(1);
          return
        }
        if (!dishList || dishList.length === 0 || !currentType || !currency || dishListPrice < 0) {
          callBack(2);
          //message.base('You have some problem about your dish')
          return
        }
        callBack(3);

      } catch (e) {
        console.error('myDetail/testInfo', e)
      }
    },

    *saveAddress ({ payload: { lat, lng, address, name, callBack } }, { call, put, select }) {
      try {
        yield put(action('save', {
          // ad: {
          //   address: address,
          //   lat: lat,
          //   lon: lng,
          // },
          chooseGeo: {
            lat: lat,
            lon: lng,
          },
          inputvalue: address,
          nameValue: name,
        }));
        callBack && callBack()
      } catch (e) {
        console.error("saveAddress", e);
      }
    },
    // 
    * getAllAddr ({ payload }, { call, put, select }) {
      try {
        const { isLogin, } = yield select(({ user }) => user);
        if (isLogin) {
          const res = yield call(addressS.getAllAddr);
          yield put(action('save', {
            allRensentAddress: res.data || [],
          }));
        }

      } catch (e) {
        console.error("getAllAddr", e);
      }
    },

    //   
    * getFullAddress ({ payload: { input, callBack } }, { call, put, select }) {
      try {

        const params = {
          input: input,
          inputtype: 'textquery',
          fields: 'formatted_address,name,rating,opening_hours,geometry',
        };


        const res = yield call(addressS.getFullAddress, params);
        if (res.status === 'OK') {
          callBack(res.candidates)
        }

      } catch (e) {
        console.error("getAllAddr", e);
      }
    },

    // 
    * onSavePress ({ payload }, { call, put, select }) {
      try {

        const { inputvalue, chooseGeo } = yield select(({ myDetail }) => myDetail);
        if (inputvalue.length <= 3) {
          message.base('pleaser tap more than 3 letter')
          return
        }
        if (chooseGeo.lat && chooseGeo.lon) { //   
          yield put(action('save', {
            showAddress: false,
            ad: {
              address: inputvalue,
              lat: chooseGeo.lat,
              lon: chooseGeo.lon,
            },
            addressError: '',
          }))
        } else {
          const params = {//   
            input: inputvalue,
            inputtype: 'textquery',
            fields: 'formatted_address,name,rating,opening_hours,geometry',
          };

          const res = yield call(addressS.getFullAddress, params);
          if (res.status === 'OK') {
            const data = res.candidates;
            const { geometry: { location: { lat, lng } } } = data[0];

            // 
            yield put(action('statistic/recordPointTag', {
              recordclick: '',
              mark: JSON.stringify({
                lat: lat,
                lon: lng,
              })
            }));

            yield put(action('save', {
              showAddress: false,
              ad: {
                address: inputvalue,
                lat: lat,
                lon: lng,
              },
              addressError: '',
            }))
          } else { //   
            yield put(action('save', {
              ad: {
                address: inputvalue,
                lat: null,
                lon: null,
              },
              showAddress: false,
              showRelocation: true,
            }))
          }
        }
      } catch (e) {
        console.error("getAllAddr", e);
      }
    },

    // 
    * onRelocationChoose ({ payload: { latlng } }, { call, put, select }) {
      const { ad } = yield select(({ myDetail }) => myDetail);

      yield put(action('save', {
        ad: {
          address: ad.address,
          lat: latlng.lat,
          lon: latlng.lng,
        },
        showAddress: false,
        showRelocation: false,
      }))
    },

    // 
    * queryLocalAddrsByGeo ({ payload: { lat, lng, callBack } }, { call, put, select }) {
      try {
        const latlng = lat + "," + lng + "";
        const params = {
          latlng: latlng
        }
        const resData = yield call(addressS.queryLocalAddrsByGeo, params);
        if (resData) {
          const results = resData.results[0];
          callBack && callBack(results)
        }
      } catch (e) {
        console.error(e);
      }
    },


    *queryAddress ({ payload: { input } }, { call, put, select }) {
      if (input.length < 2) {
        return
      }
      const { country } = yield select(({ orderList }) => orderList);
      if (country === 'IE') {
        yield put(action('queryAddr', { input: input }))
      } else { //   google api
        yield put(action('autocomplete', { input: input }))
      }
    },

    //   
    queryAddr: [
      function* ({ payload: { input } }, { call, put, select }) {
        try {
          yield call(delay, 300);
          const { shopGeo } = yield select(({ orderList }) => orderList);
          const { ad } = yield select(({ myDetail }) => myDetail);

          // keyword=&gate=eircode&country=IE &lat=&lon=
          const res = yield call(addressS.queryAddr, {
            keyword: input,
            gate: 'eircode', country: 'IE', lat: shopGeo.lat, lon: shopGeo.lon
          });
          if (res && res.length === 0) {
            yield put(action('save', {
              predictions: []
            }));
          } else {
            const predictions = (res || []).map(item => {
              return {
                id: item.id,
                description: item.address,
                lat: item.lat,
                lng: item.lon,
              }
            })
            yield put(action('save', {
              predictions: predictions,
            }));
          }

        } catch (e) {
          console.error('queryByKeyword', e);
        }
      },
      { type: 'takeLatest' }
    ],

    // mydetail    
    autocomplete: [
      function* ({ payload: { input } }, { call, put, select }) {
        try {
          yield call(delay, 300);
          const { country } = yield select(({ orderList }) => orderList);
          const { ad } = yield select(({ myDetail }) => myDetail);

          if (input.length > AppConfig.searchCount) {
            const params = {
              input: input,
              components: `country:${country}`,
              language: 'en',
            }
            const res = yield call(addressS.autocomplete, params);

            if (res.predictions && res.predictions.length === 0) {
              yield put(action('save', {
                predictions: res.predictions,
                ad: {
                  address: input,
                  lat: ad.lat,
                  lon: ad.lon,
                }
              }));
            } else {
              yield put(action('save', {
                predictions: res.predictions,
              }));
            }
          }

        } catch (e) {
          console.error("autocomplete", e);
        }
      },
      { type: 'takeLatest' }
    ],

  },
};