import React, { useEffect, useRef, useState, } from 'react';
import { AdaptHeader, CloseIcon, Loader, Modal, ModalAlert } from '../../../component';
import { Link } from 'react-router-dom'

import './index.less';
import './indexPc.less';

import { connect, compose, action, getPayIframeUrl } from '../../../utils';
import { withRouter } from 'react-router-dom';



const CardPay = (props) => {
  const { uuid, cardPayLoading } = props;


  const timer = useRef();

  function onClose () {
    props.dispatch(action('reviewPayment/save', { showCard: false }))
    props.dispatch(action('reviewPayment/testPayStatus', {
      uuid: uuid, callBack: (res) => {
        if (res.error) { // 
          props.dispatch(action('reviewPayment/save', { showCard: false, showOops: true }))
        } else {
          if (res.isSubmit) {
            props.dispatch(action('reviewPayment/save', { showCard: false }))
            props.dispatch(action('statistic/saveAdsPurchaseAndClear'));
            props.history.push(`/historyOrderDetail?uuid=${uuid}&from=payment`);

          }
          // else {
          //   if (res.payStart) {
          //     props.dispatch(action('reviewPayment/save', { cardPayLoading: true }))

          //     timer.current = setTimeout(() => {
          //       onClose()
          //     }, 5000)
          //   } else {
          //     props.dispatch(action('reviewPayment/save', { showCard: false, cardPayLoading: false }))
          //   }
          // }
        }

      }
    }))
  }

  useEffect(() => {
    props.dispatch(action('reviewPayment/save', { cardPayLoading: false }))

    // ModalAlert({
    //   comheader:<div className='pay_notice_img'><img src={require('./pay_notice_img.png')}/></div>,
    //   msg: 'Due to a technical issue, Easy Dish is unable to save any new card information. Please do not tick the save card details option.'
    // })

    // window.appBack = (uid) => {
    //   if (uid.toString() === "0") {
    //     // props.dispatch(action('reviewPayment/save', { showOops: true, showCard: false }))
    //   } else {
    //     props.dispatch(action('orderList/clear'));
    //     props.history.push(`/historyOrderDetail?uuid=${uuid}&from=payment`);
    //   }
    //   props.dispatch(action('reviewPayment/save', { showCard: false }))
    // }

    // window.onClose = () => {
    //   props.dispatch(action('reviewPayment/save', { showCard: false }))
    // }

    function onMessage (event) {
      switch (event.data) {
        case 'failed':
          props.dispatch(action('reviewPayment/save', { showOops: true, showCard: false }))
          break;
        case 'success':
          props.dispatch(action('statistic/saveAdsPurchaseAndClear'));
          props.history.push(`/historyOrderDetail?uuid=${uuid}&from=payment`);
          props.dispatch(action('reviewPayment/save', { showCard: false }))
          break;
        case 'close':
          props.dispatch(action('reviewPayment/save', { showCard: false }))
          break;
      }
    }
    window.addEventListener('message', onMessage)

    return () => {
      timer.current && clearTimeout(timer.current)
      props.dispatch(action('reviewPayment/save', { errCount: 0 }))
      window.removeEventListener('message', onMessage, false)
    }


  }, [])

  const [loading, setLoading] = useState(true);

  function onLoad () {
    setLoading(false)
  }

  const [show, setShow] = useState(true)

  if (!uuid)
    return null;

  return <Modal className='card_pay_modal' >
    <AdaptHeader title='Online payment' onLeftPress={onClose} />
    {show && <div className='card_pay_modal_warning'>
      Please don't close or refresh this page until the payment process is finished.
      <CloseIcon text={show} onClick={() => setShow(false)} />
    </div>}
    <iframe id="targetIframe" title="Topup" className="pc_pay" src={getPayIframeUrl(uuid)} frameBorder="0" onLoad={onLoad}></iframe>
    {/* <iframe id="targetIframe" title="Topup" className="pc_pay" src={'http://172.20.10.2:9100/opayo.html'} frameBorder="0"></iframe> */}
    {/* <iframe id="targetIframe" title="Topup" className="pc_pay" src={'http://localhost:9100/opayo.html'} frameBorder="0" onLoad={onLoad}></iframe> */}
    {/* <iframe id="targetIframe" title="Topup" className="pc_pay" src={'http://localhost:9100/paySuccess.html'} frameBorder="0"></iframe> */}
    <Loader loading={loading} type='tran' />
  </Modal>

}

export default compose(
  withRouter, connect(({ orderList: { uuid }, reviewPayment: { cardPayLoading } }) => ({
    uuid, cardPayLoading
  })))(CardPay)