import React, { useState, useEffect, useRef } from 'react';
import { createRoot } from 'react-dom/client';

import './index.less';
import './indexPc.less';
import { stopScroll, stayScroll, useMessage, AppConfig } from '../../../utils'


const LocationService = (props) => {
  const { onOk, onCancel, msg, title, img, okText = 'Ok', cancelText } = props;

  useEffect(() => {
    stopScroll();
  }, [])

  //  RN 
  useMessage(onRNMessage)

  function onRNMessage (event) {
    if (event.data === AppConfig.hardwareBackPress) {
      onCancel()
    }
  }

  return (
    <section className="location_service_modal">
      <div className="mask"></div>
      <div className={"d-alert-modal"}>
        {img && <img src={img} />}
        {title && <h5 >{title}</h5>}
        {msg && <p>{msg}</p>}

        <div className='alert_button_container'>
          <button onClick={onOk}>{okText}</button>
          {cancelText && <button onClick={onCancel}>{cancelText}</button>}
        </div>
      </div>
    </section>
  )
};


const LocationServiceAlert = ({ msg, title, onCancel, onOk, img, okText, cancelText }) => {

  let msgBox = document.createElement('div');
  document.body.appendChild(msgBox);

  function cancel () {
    onCancel && onCancel();
    close();
  }

  function sure () {
    onOk && onOk();
    close();
  }

  function close () {
    stayScroll()
    if (msgBox && document.body.contains(msgBox)) {
      document.body.removeChild(msgBox);
    }
  }

  createRoot(msgBox).render(<LocationService
    okText={okText}
    cancelText={cancelText}
    img={img}
    msg={msg}
    title={title}
    onCancel={cancel}
    onOk={sure}
  />);
}

export default LocationServiceAlert
