import React, { useRef, useEffect, useCallback, useState } from 'react';

import addEventListener from "add-dom-event-listener";
import { getOs, isMobile } from '../utils';

export function useThrottle (fn, delay, dep = []) {
  const { current } = useRef({ fn, timer: null });
  useEffect(function () {
    current.fn = fn;
  }, [fn]);

  return useCallback(function f (...args) {
    if (!current.timer) {
      current.timer = setTimeout(() => {
        delete current.timer;
      }, delay);
      current.fn.call(this, ...args);
    }
  }, dep);
}

export function useDebounce (fn, delay, dep = []) {
  const { current } = useRef({ fn, timer: null });
  useEffect(() => {
    current.fn = fn;
  }, [fn]);

  return useCallback(function f (...args) {
    if (current.timer) {
      clearTimeout(current.timer);
    }
    current.timer = setTimeout(() => {
      current.fn.call(this, ...args);
    }, delay);
  }, dep)
}

export function usePrevious (value) {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  });
  return ref.current;
}


export function useKeyboardType () {
  const ref = useRef();
  useEffect(() => {
    if (isMobile()) {
      if (getOs() === 'ios') {
        ref.current = 'tel'
      } else {
        ref.current = 'number'
      }
    } else {
      ref.current = undefined
    }
  });
  return ref.current;
}

/**
 * 
 * @param {*} dep 
 * @param {*} selector  img  class 
 */
export function useLazyload (dep, selector) {

  const scrollEvent = useRef();

  useEffect(() => {
    scrollEvent.current = addEventListener(window, "scroll", handleScroll);
    if (dep) {
      setTimeout(() => {
        handleScroll();
      }, 500)
    }
    return () => {
      if (scrollEvent.current) {
        scrollEvent.current.remove();
      }
    }
  }, [dep])

  const handleScroll = useThrottle((e) => {
    checkAllImags()
  }, 500, [])

  const isInClietn = (el) => {
    const eldom = el;
    const { top, right, bottom, left } = eldom.getBoundingClientRect();
    const clientHeight = window.innerHeight;
    const clientWidth = window.innerWidth;
    return !(top > clientHeight + 300 || bottom < 0 || left > clientWidth || right < 0);
  };

  let loadImageIndex = [];

  const checkAllImags = () => {
    const imgs = document.querySelectorAll(selector);
    let index = 0;
    Array.from(imgs).map((item, inx) => {
      if (isInClietn(item) && inx >= index) {
        if (!loadImageIndex.includes(index)) {
          const eldom = item;
          eldom.src = eldom.getAttribute('data-src');
          loadImageIndex.push(index);
        }
        index = inx + 1;
      }
    });
  };

}

export function useMessage (func, deep) {
  useEffect(() => {
    const os = getOs();
    switch (os) {
      case 'android':
        document.addEventListener('message', func);
        break;
      case 'ios':
        window.addEventListener('message', func);
        break;
      default:
        window.addEventListener('message', func);
        break;
    }
    return () => {
      document.removeEventListener('message', func);
      window.removeEventListener('message', func);
    }
  }, [deep]);
}

export function useSize (func1, func2, deep = [], offsetWidth = 768) {
  const reizeEvent = useRef();
  const { current } = useRef({ func1, func1 });

  useEffect(() => {
    current.func1 = func1;
    current.func2 = func2;
  }, [func1, func2]);

  const [size, setSize] = useState(isMobile() ? 'WAP' : 'PC')

  const handleResize = useThrottle((e) => {
    const offsent = document.body.offsetWidth;
    if (offsent < offsetWidth) {
      setSize('WAP')
    } else {
      setSize('PC')
    }
  }, [])

  useEffect(() => {
    reizeEvent.current = addEventListener(window, "resize", handleResize);
    handleResize()
    return () => {
      reizeEvent.current && reizeEvent.current.remove();
    }
  }, [], 100);

  useEffect(() => {
    switch (size) {
      case 'WAP':
        current.func1 && current.func1()
        break;
      case 'PC':
        current.func2 && current.func2()
        break;
    }
  }, [size, ...deep])


  return size
}
