import React, { useState } from 'react';
import './index.less';
import './indexPc.less';

import Icon from '../icon'
import Spin from '../spin';
import { inRN, h5ToRn, useMessage } from '../../utils';
import message from '../message';
import LocationServiceAlert from './locationServiceAlert';

const LocationIcon = (props) => {

  const { className, returnGeo } = props

  const [loading, setLoading] = useState(false);

  //  RN 
  useMessage(onRNMessage)

  function onRNMessage (event) {
    const res = event.data;
    queryGEORN(res);
  }

  // RN 
  function queryGEORN (permission) {
    switch (permission) {
      case 'prompt':
        LocationServiceAlert({
          okText: 'Turn on',
          cancelText: 'Not now',
          img: require('./locationServiceAlert/img_location.png'),
          title: 'Location Service',
          msg: 'We use your location to show nearby restaurants.',
          onOk: () => h5ToRn({ type: 5 }),
          onCancel: () => {
            LocationServiceAlert({
              title: 'Are you sure?',
              msg: 'We need your location to display the restaurant nearby. Tap the location icon and then select Turn on when you are asked.',
            })
          }
        })
        break;
      case 'granted':
        onGetCurrentLocation();
        break;
      case 'denied': // 
      case 'unavailable': //   ios  
      case 'limited':  // 
      case 'blocked': // 
        LocationServiceAlert({
          title: 'We are unable to find you.',
          msg: `Location services of your device are turned off. Please open the device setting and switch them on by going to Privacy > Location Services.`,
          onOk: () => h5ToRn({ type: 6 }),
        })
        break;
    }
  }

  function getLocationBefore (event) {
    event.stopPropagation();
    if (inRN()) {
      h5ToRn({ type: 1 });
    } else {
      queryGEO();
    }
  }

  //  web
  function queryGEO () { // EASYDISHDEV-507  andorid"A14. "

    onGetCurrentLocation();
  }

  function onGetCurrentLocation () {
    setLoading(!loading);
    const time = 3000;
    Promise.all([
      //  3 
      new Promise((resolve, reject) => {
        setTimeout(() => {
          resolve();
          setLoading(false)
        }, time)
      }),

      new Promise((resolve, reject) => {
        navigator.geolocation.getCurrentPosition((data) => {
          if (data && data.coords && data.coords.latitude) {
            returnGeo && returnGeo({
              lat: data.coords.latitude,
              lon: data.coords.longitude,
            })
            resolve()
          }
        }, (error) => {

          resolve(error)
        }, {
          enableHighAccuracy: true,
          timeout: 5000,
          maximumAge: 0
        })
      }),
    ]).then((res) => {
      const error = res[1];
      if (!error) {
        return
      }
      switch (error?.code) {
        case error.PERMISSION_DENIED:
          LocationServiceAlert({
            title: 'We need your location permission.',
            msg: ` Oops! We couldn't find your location. you can see this message because:
            You refused to give our app permission to see your location.
            Your mobile internet browser doesn't have permission to see your location.
            Your location is turned off on your phone or tablet.
            `,
          })
          break;
        case error.POSITION_UNAVAILABLE:
          message.base("Oops! We couldn't find your location. Please enter your address manually.")
          break;
        case error.TIMEOUT:
          message.base("The request to get user location timed out.")
          break;
        case error.UNKNOWN_ERROR:
          message.base("An unknown error occurred.")
          break;
      }
    })
  }

  return <div className={`location_icon_container ${className}`} onClick={getLocationBefore}>{
    !loading ? <Icon
      type='iconicon_location2'
      className='location_icon_search' />
      : <Spin />
  }
  </div>
}

export default LocationIcon