import { action, } from '../utils';
import { setDeliveryChargeS, } from '../service';
import { message } from '../component';


const initState = {
  area: [],
  selectedAreaItem: null,//  
  customerAd: {}, // 
  shopAd: {},
  finishChooseAd: false,
  distance: {},// 
  deliveryCipher: 0, // 1  0  3   
  orderDeliveryModel: {},
  goToPaymentType: 0, // 
  showTofarModal: false,
  restaurantInfo: {},
  buttonLoading: false,
  loadingDate: false,
};

export default {
  namespace: 'setDeliveryCharge',
  state: initState,
  reducers: {
    save (state, { payload }) {
      return { ...state, ...payload };
    },
  },
  effects: {

    * getRestaurantInfo ({ payload }, { call, put, select }) {
      try {
        yield put(action('save', { loadingDate: true }))
        const { rId } = yield select(({ orderList }) => orderList);
        const { ad } = yield select(({ myDetail }) => myDetail);

        const res = yield call(setDeliveryChargeS.getRestaurantInfo, { rId: rId });
        if (res) {
          const deliveryCipher = res?.deliveryModel?.deliveryCipher;

          yield put(action('save', {
            area: res?.deliveryModel?.area || [],
            deliveryCipher: deliveryCipher === 1 ? (ad.lat ? deliveryCipher : 3) : deliveryCipher,
            customerAd: ad,
            shopAd: {
              lat: res.lat,
              lon: res.lon,
            },
            finishChooseAd: !!ad.lat,
            restaurantInfo: res,
          }));

          const { selectedAreaItem } = yield select(({ setDeliveryCharge }) => setDeliveryCharge);

          if (selectedAreaItem) {
            yield put(action('setChooseItem', { item: selectedAreaItem }))
          }
        }


      } catch (e) {
        console.error('getRestaurantInfo', e);
      } finally {
        yield put(action('save', { loadingDate: false }))

      }
    },


    *saveDistanceAndgetDeliveryFee ({ payload: { distance } }, { call, put, select }) {
      try {
        yield put(action('save', { buttonLoading: true }))

        const { rId, uuid, dishListPrice } = yield select(({ orderList }) => orderList);
        const { customerAd: { lat, lon } } = yield select(({ setDeliveryCharge }) => setDeliveryCharge);

        const disValue = (distance.value <= 100 ? 0.1 : (distance.value / 1000).toFixed(1));

        const orderDeliveryModel = yield call(setDeliveryChargeS.getDeliveryFee, { lat, lon, shopId: rId, distance: disValue, orderAmount: dishListPrice, orderUUID: uuid });

        if (orderDeliveryModel) {
          let goToPaymentType = 0;
          if (orderDeliveryModel.maxRange > 0) {
            if (disValue > orderDeliveryModel.maxRange) {
              if (disValue > orderDeliveryModel.maxRange * 2) {
                //32
                goToPaymentType = 3;
              } else {
                //22
                goToPaymentType = 2;
              }
            } else {
              goToPaymentType = 1;
            }
          } else {
            goToPaymentType = 1;
          }


          yield put(action('save', {
            distance: distance,
            orderDeliveryModel: orderDeliveryModel,
            goToPaymentType: goToPaymentType
          }))
        }

        yield put(action('statistic/saveDeliveryfee', { deliveryFee: orderDeliveryModel?.fee, success: !!orderDeliveryModel }));

      } catch (e) {
        console.error('saveDistanceAndgetDeliveryFee', e);
      } finally {
        yield put(action('save', { buttonLoading: false }))
      }
    },

    *setChooseItem ({ payload: { item } }, { call, put, select }) {
      try {
        const { area } = yield select(({ setDeliveryCharge }) => setDeliveryCharge);
        const newArea = area.map(a => {
          if (a.id === item.id) {

            return {
              ...a,
              choose: true
            }
          } else {
            return {
              ...a,
              choose: false,
            }
          }
        })


        yield put(action('statistic/saveAreaDeliveryfee', { ...item }));

        yield put(action('save', {
          selectedAreaItem: item,
          area: newArea,
        }));
      } catch (e) {
        console.error('setChooseItem', e);
      }
    },

    *chooseCusAd ({ payload: { lat, lng } }, { call, put, select }) {
      try {
        yield put(action('save', {
          customerAd: {
            lat: lat,
            lon: lng,
          },
        }));
      } catch (e) {
        console.error('chooseCusAd', e);
      }
    },

    *saveDeliveryFeeAndToPay ({ payload: { callBack } }, { call, put, select }) {
      try {
        yield put(action('save', { buttonLoading: true }))

        const { deliveryCipher, area, orderDeliveryModel, customerAd, distance } = yield select(({ setDeliveryCharge }) => setDeliveryCharge);

        let deliveryFee = 0;
        if (deliveryCipher === 1) {
          if (area.some(item => item.choose)) {
            deliveryFee = area.filter(item => item.choose)[0]?.price;
          } else {
            return
          }

        } else {
          if (!distance?.value) {
            message.base('error fetching distance!')
            return
          }
          deliveryFee = orderDeliveryModel.fee
        }
        //  mydetail 
        yield put(action('myDetail/saveDetailGeo', {
          cuslat: customerAd.lat,
          cuslon: customerAd.lon,
        }));


        // updateorder 
        yield put(action('orderList/updateOrder', {
          params: {
            deliveryFee: deliveryFee,
            lat: customerAd.lat,
            lon: customerAd.lon,
          },
          callBack: (res) => {
            if (res) {
              callBack()
            } else {
              message.base('update delivery error,please try again.')
            }
          }
        }))

      } catch (e) {
        console.error('saveDeliveryFeeAndToPay', e);
      } finally {
        yield put(action('save', { buttonLoading: false }))
      }
    },

    *clear ({ payload }, { call, put, select }) {
      try {
        yield put(action('save', {
          ...initState
        }));
      } catch (e) {
        console.error('setDeliveryCharge/clear', e);
      }
    },

    *testInfo ({ payload: { callBack } }, { call, put, select }) {
      try {
        const { isLogin } = yield select(({ user }) => user);
        if (!isLogin) { // 1  2   3  4. Delivery
          callBack(1);
          return
        }
        const { dishList, currentType, currency, dishListPrice, uuid, } = yield select(({ orderList }) => orderList);
        if (!dishList || dishList.length === 0 || !currentType || !currency || dishListPrice < 0 || !uuid) {
          callBack(2);
          //message.base('You have some problem about your dish')
          return
        }
        const { name, number, email, time, } = yield select(({ myDetail }) => myDetail);
        if (!name || !number || !email || !time) {
          callBack(2);
          //message.base('You have some problem about your info')
          return
        }
        if (currentType !== 2) {
          callBack(4);
          //message.base('You have some problem about your info')
          return
        }
        callBack(3);

      } catch (e) {
        console.error('setDeliveryCharge/testInfo', e)
      }
    },
  },
};