import React, { forwardRef, useImperativeHandle, useMemo, useState } from "react";
import { AutoTextarea } from "../../../component";
import { checkEmail, checkPhoneNumber } from "../../../utils";

import './index.less';
import './indexPc.less';

const BookingInput = (props, ref) => {

  const { placeholder, onChange, value, require = true, mutiline = 0, inputType, maxLength, type } = props;

  const [errText, setErrText] = useState(null);

  useImperativeHandle(ref, () => ({
    // changeVal 
    checkValue: checkValue,
  }));

  function checkValue () {
    if (!value && require) {
      switch (inputType) {
        case 'email':
          setErrText('Please enter your email')
          return false
        case 'mobile':
          setErrText('Please enter your mobile number')
          return false
        default:
          setErrText('Please enter your name')
          return false
      }

    }
    switch (inputType) {
      case 'email':
        if (!checkEmail(value)) {
          setErrText('Invalid email address')
          return false
        }

        break;
      case 'mobile':
        if (!value) {
          setErrText('Invalid mobile number')
          return false
        }
        break;
    }
    return true
  }

  function onValueChange (e) {
    setErrText(null)
    onChange(e)
  }

  const propsObj = useMemo(() => {

    return {
      type: type,
      mutiline: mutiline,
      maxLength: maxLength,
      className: inputType !== 'mobile'
        ? `table_input ${errText ? 'table_input_container_err' : undefined} ${mutiline ? 'table_textarea' : undefined}`
        : `table_input ${errText ? 'table_input_container_err' : undefined}`,
      placeholder: placeholder,
      onChange: onValueChange,
      value: value,
      onBlur: checkValue,
    }
  }, [props])


  return <div className={`table_input_container ${mutiline ? 'table_input_container_mutiline' : undefined}`} >
    {inputType !== 'mobile'
      ? <AutoTextarea
        {...propsObj} />
      : <input   {...propsObj} />}
    <div className="table_input_err_text">{errText}</div>
  </div>


}

export default forwardRef(BookingInput)