import React, { useState, useEffect, useRef } from 'react';
import { createRoot } from 'react-dom/client';

import { stayScroll, stopScroll, useMessage, AppConfig } from '../../utils';
import './index.less';
import './indexPc.less';

const Alert = (props) => {
  const { onSure, onCancel, msg, title, com, comheader, className, showCancel, sureText, cancelText } = props;

  const contentRef = useRef();
  const container = useRef();

  useEffect(() => {
    stopScroll();
  }, [])

  //  RN 
  useMessage(onRNMessage)

  function onRNMessage (event) {
    if (event.data === AppConfig.hardwareBackPress) {
      animateOnCancel()
    }
  }

  function animateLeave () {
    contentRef.current.className = `d-alert-modal flyOut ${className ? className : undefined}`;
    container.current.className = "mask-out com_alert_modal";
  }

  function animateOnSure () {
    animateLeave()
    contentRef.current.addEventListener("animationend", onSure);
  }

  function animateOnCancel () {
    animateLeave()
    contentRef.current.addEventListener("animationend", onCancel);
  }



  return (
    <section className="mask com_alert_modal" ref={container}>
      <div className={`d-alert-modal flyIn ${className ? className : undefined}`} ref={contentRef}>
        <div className='com_alert_modal_content '>
          {title && <h5 >{title}</h5>}
          {comheader && comheader}
          {msg && <p>{msg}</p>}
          {com && com}
        </div>

        <div className='alert_button_container'>
          {showCancel && <button onClick={animateOnCancel}>{cancelText || 'Cancel'}</button>}
          <button onClick={animateOnSure}>{sureText || (showCancel ? 'Sure' : 'OK')}</button>
        </div>
      </div>
    </section>
  )
};


const ModalAlert = ({ title, msg, onCancel, onSure, com, comheader, className, sureText, cancelText }) => {

  let msgBox = document.createElement('div');
  document.body.appendChild(msgBox);

  function cancel () {
    onCancel && onCancel();
    close();
  }

  function sure () {
    onSure && onSure();
    close();
  }

  function close () {
    stayScroll();
    if (msgBox && document.body.contains(msgBox)) {
      document.body.removeChild(msgBox);
    }
  }

  createRoot(msgBox).render(
    <Alert
      title={title}
      msg={msg}
      comheader={comheader}
      showCancel={!!onCancel}
      onCancel={cancel}
      onSure={sure}
      com={com}
      sureText={sureText}
      cancelText={cancelText}
      className={className}
    />);
}

export default ModalAlert
