import React, { createRef, useEffect, useRef } from 'react';
import { Icon } from '../../../component';
import addEventListener from "add-dom-event-listener";
import { getScroll, h5ToRn, isPc } from '../../../utils'

import './index.less';
import './indexPc.less';

import { withRouter } from 'react-router-dom';

import { action, connect, getStatusBarHeight, stopScroll, stayScroll, getValueFromQuery, compose } from '../../../utils';


const ShopFoodHeader = (props) => {

  const { restaurantInfo, showSearch, landing } = props;

  const scrollEvent = useRef();
  const headerRef = useRef();

  useEffect(() => {
    scrollEvent.current = addEventListener(window, "scroll", handleScroller);

    return () => {
      scrollEvent.current && scrollEvent.current.remove();
    }
  }, [])

  function handleScroller () {
    const top = getScroll(window, true);
    if (headerRef.current) {
      headerRef.current.style.display = 'unset';
      headerRef.current.style.opacity = (top - 200) / 50;
      if (top > 160) {
        h5ToRn({ type: 2, statusBar: 'dark-content' });
      } else {
        h5ToRn({ type: 2, statusBar: 'light-content' });
      }
    }
  }


  function goBack (e) {
    e.stopPropagation();
    stayScroll()
    if (isPc()) {
      if (landing.webSiteType === 3) {
        props.history.replace('/')
      } else {
        props.history.replace('/shopList')
      }
    } else {
      if (showSearch) {
        props.dispatch(action('shopDetail/save', { showSearch: false }))
      } else {
        if (landing.webSiteType === 3) {
          props.history.replace('/')
        } else {
          props.history.replace('/shopList')
        }
      }
    }
  }

  function onSearchClick () {
    props.dispatch(action('shopDetail/save', { showSearch: true }))
    stopScroll()
  }

  // ShopFoods   
  return <section className='shop_detail_header' style={{ top: getStatusBarHeight() }} >
    <div className='shop_detail_header_name' ref={headerRef} style={{ paddingTop: getStatusBarHeight() }}>
      <span className='one-line'> {restaurantInfo?.name}</span>
    </div>

    <div className='shop_back_icon_wrapper'>
      <Icon type='iconicon_more_A1' className='shop_back_icon' onClick={goBack} />

    </div>

    <div className='shop_search_wrapper'>
      <Icon type='iconicon_search_a9' className='shop_search' onClick={onSearchClick} />
    </div>

  </section>

}


export default compose(
  connect(({ shopDetail: { restaurantInfo, showSearch }, webConfigModal: { landing } }) => ({ restaurantInfo, showSearch, landing })),
  withRouter
)(ShopFoodHeader);