import React, { useEffect, useMemo } from 'react';
import { AdaptHeader, Icon, Loader, ErrOrderList } from '../../component';
import { action, connect, getValueFromQuery, getReturnPath, cookie, h5ToRn, formatOrderType } from '../../utils';

import './index.less';
import './indexPc.less';


const HistoryOrder = (props) => {

  const { pendingList, completedList, currentIndex, loading, showErrorList, isLogin } = props;

  const fromPlat = getValueFromQuery('fromPlat', props.location.search);

  useEffect(() => {
    if (isLogin || fromPlat === 'RN') {
      props.dispatch(action('historyOrder/init'))
    } else {
      props.history.push(`/login?returnUrl=${getReturnPath(props.location)}`)

    }
  }, [])

  const headerList = useMemo(() => {
    return [
      `Pending  (${pendingList.length})`,
      `Completed  (${completedList.length})`,
    ]
  }, [pendingList, completedList]);

  function changeTab (index) {
    return () => {
      props.dispatch(action('historyOrder/save', { currentIndex: index }))
    }
  }

  function goShopDetail (item) {
    return (event) => {
      event.stopPropagation();
      if (fromPlat === 'RN') {
        h5ToRn({ type: 103, data: { uuid: item.uuid, shopId: item.shopId } })
      } else {
        props.dispatch(action('historyOrder/checkOrderAgain', {
          uuid: item.uuid, shopId: item.shopId, callBack: ((isValid, list) => {
            if (isValid) {
              props.history.push(`shopDetail?rId=${item.shopId}`);
              //  orderList 
              list.map(item => {

                props.dispatch(action('cart/getDishDetailFromOrder', {
                  rId: item.shopId,
                  dishId: item.id,
                  typeId: item.typeId,
                  categoryId: item.categoryId,
                  menuId: item.menuId,
                  dish: item,
                }));
              })
            }
          })
        }))
      }

    }
  }

  function goOrderDetail (uuid) {
    return () => {

      if (fromPlat === 'RN') {
        h5ToRn({ type: 101, data: { url: `/hisOrderDetail?uuid=${uuid}` } })
      } else {
        props.history.push(`/hisOrderDetail?uuid=${uuid}`);
      }
    }
  }

  function getCompletedText (orderType) {
    let result = "Order Completed";
    switch (orderType) {
      case 1:
        result = "Picked up";
        break;
      case 2:
        result = "Delivered";
        break;
      case 3:
        result = "Collected";
        break;
      case 4:
        result = "Sent to table";
        break;
      case 5:
        result = "Sent to table";
        break;
      default:
        break;
    }
    return result;
  }

  function renderHeader () {
    return <div className='history_order_header'>
      {headerList.map((item, index) => {
        return <div
          key={index}
          onClick={changeTab(index)}
          className={`history_order_header_item ${currentIndex === index ? 'history_order_header_item_active' : undefined}`}>
          {item}
          {currentIndex === index && <div className='history_order_header_item_active_triangle' />}
        </div>
      })}
    </div>
  }

  function formatTime (str) {
    if (str) {
      // .match(/((\d{4})\-(\d{2})\-(\d{2})\s(\d{2})\:(\d{2}))/) 
      return str.replace(/(.+?)(\:\d{2})$/, '$1')  //  
    } return str
  }

  function renderContent () {
    return <div className='history_order_content'>
      {
        [pendingList, completedList]?.map((content, i) => {
          if (currentIndex === i) {
            return content.map(item => {
              let top = { text: 'Awaiting confirmation', color: '#F45D1D', background: '#F7E7E0' };
              switch (item.status) {
                case 0:
                  break;
                case 1:
                  top = { text: 'Order confirmed', color: '#3DCBC9', background: '#D8F6F6' };
                  break;
                case 2:
                  top = { text: 'Order completed', color: '#FFA900', background: '#FFF3DB' };
                  break;
                case 3:
                  top = { text: 'Order failed', color: '#6E6E6E', background: '#E4E4E4' };
                  break;
              }
              return <div className='history_order_item' key={item.uuid} onClick={goOrderDetail(item.uuid)} >
                <div className='history_order_item_heander'>
                  <img src={item.shopLogo ? item.shopLogo : require('../../assets/shop_logo.svg')} alt='shop-logo' />
                  <div className='history_order_item_info'>
                    <h5>{item.shopName}</h5>
                    <p><span>{item.currency}{item.due?.toFixed(2)}</span><span>{formatTime(item.createTime)}</span></p>
                    <p>{formatOrderType(item.type)}</p>
                  </div>
                  <Icon type='iconicon_more_A1' />
                </div>
                <div className='history_order_item_footer'>
                  <div>Placed Online</div>
                  <div className='history_order_again_text' onClick={goShopDetail(item)} >
                    <Icon type='iconicon_cart' />
                    Order it again
                  </div>
                </div>
                <div className='history_order_item_status' style={{ background: top.background, color: top.color }}
                >{top.text}</div>
              </div>
            })
          }
        })
      }
    </div>
  }

  return (
    <section className='history_order_page'>
      {loading && <Loader type='tran' loading={loading} fixTop />}
      <AdaptHeader title={'History order'} />
      <section className='history_order_container'>
        {renderHeader()}
        {renderContent()}
      </section>
      {showErrorList && <ErrOrderList />}

    </section>
  )
}


export default connect(({ historyOrder: { pendingList, completedList, currentIndex, loading, showErrorList, }, user: { isLogin } }) => ({
  pendingList, completedList, currentIndex, loading, showErrorList, isLogin
}))(HistoryOrder);