import React, { useState, useEffect } from 'react';
import { getMonth } from '../../utils';
import Icon from '../icon';
import './index.less';
import './indexPc.less';

const DatePicker = (props) => {

  const { onOk, onClose, } = props;

  const init = props.value ? new Date(props.value) : new Date();
  const [year, setYear] = useState(init.getFullYear());
  const [month, setMonth] = useState(init.getMonth());
  const [date, setDate] = useState(init.getDate());

  const drawDates = () => {
    const currentTime = new Date(`${new Date().getFullYear()}/${new Date().getMonth() + 1}/${new Date().getDate()}  00:00:00`);

    const days = month === 1 ? (isLunarYear(year) ? 29 : 28) : (isSolarMonth(month) ? 31 : 30);
    const firstDayWeek = new Date(`${year}/${month + 1}/1`).getDay();
    const dates = [];
    for (let i = 0; i < firstDayWeek; i += 1) {
      dates.push(
        <li key={i + 1}></li>
      );
    }
    for (let i = 0; i < days; i += 1) {
      const i1 = i + 1;
      const eleDate = date === i1 ? <span>{i1}</span> : i1;

      const canPress = new Date(`${year}/${month + 1}/${i1}`).getTime() >= currentTime.getTime();

      dates.push(
        <li key={i * 10}
          className={canPress ? undefined : 'l_date_disable'}
          onClick={() => {
            if (canPress) {
              setDate(i1)
            }

          }}>{eleDate}</li>
      );
    }
    return dates;
  };

  /**
   *  
   *  @param month number 
   *  0
   *  @return true false
   */
  const isSolarMonth = month => {
    return [0, 2, 4, 6, 7, 9, 11].includes(month);
  };

  /**
   *  
   *  @param year number 
   *  
   *  14002000
   *  24001001900
   *  342016
   *  4
   *  @return true false
   */
  const isLunarYear = year => {
    if (year % 400 === 0) {
      return true;
    } else {
      if (year % 100 === 0) {
        return false;
      } else {
        if (year % 4 === 0) {
          return true;
        }
      }
    }
    return false;
  };

  const OnChangeYear = offset => {
    return () => {
      setYear(year + offset);
    };
  };

  const OnChangeMonth = offset => {
    return () => {
      if (month === 11 && offset === 1) {
        setMonth(0);
        setYear(year + 1);
      } else if (month === 0 && offset === -1) {
        setMonth(11);
        setYear(year - 1);
      } else {
        setMonth(month + offset);
      }
    };
  };

  const OnCancel = () => {
    onClose();
  };

  const OnEmpty = () => {
    onOk('');
    onClose();
  };

  const OnOk = () => {
    const currentTime = new Date(`${new Date().getFullYear()}/${new Date().getMonth() + 1}/${new Date().getDate()}`);

    const selectedTime = new Date(`${year}/${month + 1}/${date}`);
    if (selectedTime >= currentTime) {
      onOk(new Date(`${year}/${String(month + 1).padStart(2, '0')}/${String(date).padStart(2, '0')}`).getTime());
      onClose();
    }
  };

  return (
    <div className="com_datepicker">
      <div className="mask"></div>
      <div className="datepicker_pan">
        <div className="datepicker_ym">
          <div className="datepicker_y">
            <Icon type='iconicon_more_A1' className="datepicker_btn_left" onClick={OnChangeYear(-1)} />
            <span>{year}</span>
            <Icon type='iconicon_more_A1' className="datepicker_btn_right" onClick={OnChangeYear(1)} />
          </div>
          <div className="datepicker_y">
            <Icon type='iconicon_more_A1' className="datepicker_btn_left" onClick={OnChangeMonth(-1)} />
            <span>{getMonth(month + 1)}</span>
            <Icon type='iconicon_more_A1' className="datepicker_btn_right" onClick={OnChangeMonth(1)} />
          </div>
        </div>
        <div className="datepicker_calendar">
          <ul className="l_weekstitle">
            <li>Sun</li>
            <li>Mon</li>
            <li>Tue</li>
            <li>Wed</li>
            <li>Thu</li>
            <li>Fri</li>
            <li>Sat</li>
          </ul>
          <ul className="l_dates">
            {drawDates()}
          </ul>
        </div>
        <div className="datepicker_btns">
          {/* <button type="button" className="btn0" onClick={OnEmpty}></button> */}
          <button type="button" className='datepicker_btn' onClick={OnCancel}>Cancel</button>
          <button type="button" className="datepicker_btn btnok" onClick={OnOk}>Sure</button>
        </div>
      </div>
    </div>
  );
};

export default DatePicker;
