import React, { useState, useEffect, useRef } from 'react';
import './index.less';
import './indexPc.less';
import Icon from '../icon';


import { stayScroll, stopScroll } from '../../utils'


const Viewer = (props) => {

  const { start, list } = props;
  const [index, setIndex] = useState(start);

  useEffect(() => {
    stopScroll()
    return () => stayScroll();
  }, [])

  const getList = () => {
    return list.map((item, i) => {
      return index === i ? (
        <li key={i} className="i_img">
          <img className="img" src={item.url} alt={item.alt} />
        </li>
      ) : null;
    });
  };

  const getDot = () => {
    return list.map((item, i) => {
      return <div key={i} className={`dot_item ${index === i ? 'dot_item_active' : undefined}`}></div>
    });
  };

  const OnMove = offset => {
    return event => {
      event.stopPropagation();
      setIndex(index + offset);
    };
  };

  const onMaskPress = () => {
    props.onMaskPress && props.onMaskPress();
  }

  const onViewerPress = () => {
    const width = document.body.offsetWidth;
    if (width > 768) {
      props.onMaskPress && props.onMaskPress();
    }
  }

  const touchList = useRef([]);

  function handleTouchStart (event) {

  }

  function handleTouchMove (event) {
    const _x = event.touches[0].pageX
    touchList.current.push(_x)

  }

  function handleTouchEnd (event) {
    const _startX = touchList.current[0]
    const _endX = touchList.current[touchList.current.length - 1];

    if (_startX - _endX >= 20) {
      if (index + 1 < list.length) {
        setIndex(index + 1)

      }
    } else if (_startX - _endX < -20) {

      if ((index) > 0) {
        setIndex(index - 1)
      }
    }
    touchList.current = [];
  }



  return (
    <div className="com_viewer">
      <div className="mask" onClick={onMaskPress}></div>
      <div className="d_viewer " onClick={onViewerPress}>
        <ul className="l_img" onTouchStart={handleTouchStart}
          onTouchMove={handleTouchMove}
          onTouchEnd={handleTouchEnd}>
          {getList()}
        </ul>
        {index > 0 &&
          <Icon type='iconxiangyou' className="btn_op btn_left" onClick={OnMove(-1)} />
        }
        {index < list.length - 1 &&
          <Icon type='iconxiangyou' className="btn_op btn_right" onClick={OnMove(1)} />
        }
      </div>
      <div className='dot_container'>
        {getDot()}
      </div>
    </div>
  );
};

export default Viewer;
