import React, { useState, useRef, useEffect, useMemo } from 'react';

import { isMobile, useDebounce, stopScroll, stayScroll, useThrottle, scrollToAnchor, getStatusBarHeight } from '../../utils'
import Icon from '../icon';
import addEventListener from "add-dom-event-listener";


import './index.less';
import './indexPc.less';

const Popup = (WrapperComponent) => (props) => {

  //  -23530
  const topValue = useMemo(() => {
    if (props.data &&
      (props.modelId || props.modelId === 0) &&
      props.data[props.modelId]?.picUrl) {
      return -235
    } else {
      return 30
    }
  }, [props.data])

  const contentRef = useRef(); //  ref

  const viewPortHeihgt = useRef(); //  ref

  const ref = useRef();

  const [type, setType] = useState('normal')

  const [tHeight, setTHeight] = useState();

  const [contentH, setContentH] = useState();

  const fixedHeader = useRef();
  function getFixedHeaderRef (ref) {
    fixedHeader.current = ref.current
  }

  const bindHandleScroll = useDebounce((event) => {
    // 
    const top = contentRef.current && contentRef.current.getBoundingClientRect().top;
    if (top <= topValue) {
      setType('whole')

      if (fixedHeader.current) {
        const totalLength = 20;
        const currentPersent = (topValue - top) / totalLength;
        if (currentPersent < 0) {
          fixedHeader.current.style.opacity = 0
        } else if (currentPersent > 1) {
          fixedHeader.current.style.opacity = 1

        } else {
          fixedHeader.current.style.opacity = currentPersent
        }
      }

    } else if (top > topValue && type === 'whole') {
      setType('normal')
    }
  }, [])

  //  
  const threshold = useRef(150);

  useEffect(() => {
    return () => {
      stayScroll();
      ref.current && ref.current.removeEventListener('scroll', bindHandleScroll)
    }
  }, [])

  // 
  const reizeEvent = useRef();
  useEffect(() => {
    reizeEvent.current = addEventListener(window, "resize", handleResize);
    handleResize()
    return () => {
      reizeEvent.current && reizeEvent.current.remove();
    }
  }, [], 100);

  const handleResize = useThrottle((anchorName) => {
    //  scroll 
    ref.current && ref.current.removeEventListener('scroll', bindHandleScroll)

    // 
    const contentHeight = contentRef.current && contentRef.current.getBoundingClientRect().height;
    //
    viewPortHeihgt.current = window.innerHeight;

    //  - ,
    const t = viewPortHeihgt.current - contentHeight

    if (t <= threshold.current) { //  
      setTHeight(100); //  0
      // setType('whole'); //  

    } else {
      setTHeight(t);
    }
    ref.current?.addEventListener('scroll', bindHandleScroll)

    //   
    setContentH(Math.min(contentHeight + 1, viewPortHeihgt.current))

    // 
    if (anchorName) {
      if (isMobile()) {
        scrollToAnchor(anchorName, { getContainer: () => ref.current, offsetTop: getStatusBarHeight() + document.documentElement.clientWidth / 7.5 * 1.2 })
      } else {
        scrollToAnchor(anchorName, { getContainer: () => contentRef.current })
      }
    }

    //  body
    stopScroll();

  }, [])

  function onClose () {
    props.onClose && props.onClose()
  }

  /**
   * 
   */
  function onConChange (anchorName) {


    setTimeout(() => {
      handleResize(anchorName);
    }, 500)
  }


  return (
    <div className='popup_modal_container' ref={ref}>
      <div className='mask' />
      <div className='popup_modal_top' style={{ height: tHeight }}>
      </div>
      <div
        className='popup_modal_content'

        style={{ top: tHeight, height: contentH }}>
        {

          <div className='popup_modal_close' onClick={onClose}>
            <Icon type='iconicon_close' />
          </div>
        }
        <div ref={contentRef} className='popup_modal_content_wrapperr'>
          <WrapperComponent
            {...props}
            onConChange={onConChange}
            type={type}
            getFixedHeaderRef={getFixedHeaderRef}
          />
        </div>
      </div>

    </div>
  )
}


export default Popup

