import React from 'react';
import './index.less';
import './indexPc.less';


const NavSkeleton = (props) => {

  const { className } = props;

  return <div className={`nav_skeleton ${className}`}>
    {Array.from({ length: 12 }).map((item, index) => {
      return <div
        key={index}
        className="nav_item ">
        <div className='nav_item_top skeleton_loading'></div>
        <div className='nav_item_bottom skeleton_loading'></div>
      </div>
    })}


  </div>
}

export default NavSkeleton;