import React, { useState, useRef, useEffect, useMemo, useLayoutEffect } from 'react';
import ShowMoreText from 'react-show-more-text';


import './index.less';
import './indexPc.less';

const MyNotice = (props) => {

  const { text = '', className, } = props;


  function executeOnClick () {
    props.executeOnClick && props.executeOnClick()
  }

  return <section className={`notice_container ${className} `} >
    <ShowMoreText
      lines={3}
      more='more'
      less='less'
      onClick={executeOnClick}
      expanded={false}
      keepNewLines={true}
    >
      {text}
    </ShowMoreText>
  </section>

}

export default MyNotice;

MyNotice.defaultProps = {
  className: undefined,
}