import React, { useMemo } from 'react';
import { connect, compose, withRouter, inRN, getOs, action, openAppStore } from '../../../utils';
import Icon from '../../icon'

import './index.less'
import './indexPc.less'

import Downloadbg from './downloadbg';

const DownloadTop = props => {

  const { showDown, landing } = props;

  const isHome = props.location.pathname === '/' || props.location.pathname === '/home'

  function closeShowDown () {
    props.dispatch(action('home/save', { showDown: false }));
  }

  const downloadConfig = useMemo(() => {
    if (getOs() && getOs() !== 'other') {

      return {
        name: landing?.webSiteAppName + ' for ' + (getOs() === 'ios' ? 'iOS' : 'Android'),
        icon: landing?.webSiteAppIcon
      }
    } else {
      return { name: '' };
    }
  }, [landing])

  const link = useMemo(() => {
    switch (getOs()) {
      case 'android':
        return landing?.googlePlayUrl
      case 'ios':
        return landing?.appStoreUrl
      default:
        return ''
    }
  }, []);

  return !inRN() && showDown && isHome ? <section className='header_my_down_load'>
    <Downloadbg />
    <div className='header_my_down_load_content'>
      <Icon type='iconicon_close' className='header_my_down_load_close' onClick={closeShowDown} />
      <img src={downloadConfig?.icon} alt="logo" />
      <div className='header_my_shop_name'>
        <h5>{downloadConfig?.name}</h5>
        <div className='header_my_shop_name_rate'>
          {/* <Icon type='iconicon_star_g' />
        <Icon type='iconicon_star_g' />
        <Icon type='iconicon_star_g' />
        <Icon type='iconicon_star_g' />
        <Icon type='iconicon_star_g' />
        <span className='header_my_down_count'>(482,088)</span> */}
        </div>
      </div>
      <a className='header_my_down_button' onClick={() => openAppStore(link)}>
        Install
      </a>

    </div>
  </section> : null
}


export default compose(
  withRouter,
  connect(({ webConfigModal: { landing }, home: { showDown } }) => ({ landing, showDown }))
)(DownloadTop)