
import React, { useEffect, useMemo, useState } from 'react';
import './index.less'
import './indexPc.less';

import HeaderItem from '../../headerItem';
import Icon from '../../icon';


import { connect, getStatusBarHeight, inRN, stayScroll, stopScroll, useThrottle, useSize } from '../../../utils'
import { useLocation } from 'react-router';

function HeaderItems (props) {

  const { landing } = props;

  const headerNavList = useMemo(() => {
    if (landing?.navList?.length > 0) {
      return landing?.navList?.filter(item => item.type === 1)

    } else {
      return []
    }
  }, [landing?.navList])

  return <>
    {headerNavList.map(item => {
      return <HeaderItem item={item} key={item.name} />
    })}
  </>

}

function MenuPc (props) {

  const { landing } = props;

  const [showToogleMenu, setShowToogleMenu] = useState(false);

  useSize(
    () => {
      setShowToogleMenu(true)
    }, () => {
      setShowToogleMenu(false)
    }, [], 1000);

  return (<div className='my_header_menu_pc fadeIn' >
    {showToogleMenu ? <MyHeaderMenuM className='my_header_menu_from_pc' /> : <>
      <HeaderItems landing={landing} />
    </>}
  </div>)
}

const MyHeaderMenuPc = connect(({ webConfigModal: { landing } }) => ({
  landing
}))(MenuPc);

function MenuM (props) {
  const [show, setShow] = useState(false);

  const location = useLocation();
  const isShopList = location.pathname === '/shopList';


  const { className, landing, showDown } = props;

  function toggleMenu () {
    setShow(!show)
    if (!show) {
      stopScroll();
    } else {
      stayScroll();
    }
  }

  useEffect(() => {
    return () => {
      setShow(false);
      stayScroll()
    }
  }, [])

  const top = useMemo(() => {
    const offsent = document.body.offsetWidth;
    if (offsent < 750) {
      if (isShopList) {
        return '1.2rem'
      }
      if (inRN()) {
        return '1.2rem'
      }
      if (showDown) {
        return '2.8rem'
      } else {
        return '1.2rem'
      }
    } else {
      return '105px'
    }
  }, [showDown, isShopList])


  return (<div className={`my_header_menu_m ${className}`} onClick={toggleMenu}>
    <Icon type='iconmenu' />
    {show && <section className="my_header_menu_m_modal" onClick={toggleMenu}>
      <section className="my_header_menu_m_container" style={{ marginTop: getStatusBarHeight(), top: top }}>
        <HeaderItems landing={landing} />
      </section>
    </section>}
  </div>
  )
}

const MyHeaderMenuM = connect(({ webConfigModal: { landing }, home: { showDown } }) => ({
  landing, showDown
}))(MenuM);


export { MyHeaderMenuPc, MyHeaderMenuM };