import React, { useEffect, useMemo } from 'react';
import { connect, action, compose } from '../../../utils';
import { Icon, Modal, AdaptHeader, DishView } from '../../../component';

import './index.less';
import './indexPc.less';
import { withRouter } from 'react-router-dom';


const UnavailableModal = (props) => {

  const { dishList, currentType, } = props;

  function onClose () {
    props.dispatch(action('orderList/save', { showUnavailableModal: false, currentType: currentType === 2 ? 3 : 2 }));
  }

  function onConfirm () {

    props.dispatch(action('orderList/deleteDish', { keylist: errorList.keylist }))

  }

  const errorList = useMemo(() => {
    if (dishList) {
      const list = [];
      const keylist = [];
      dishList.map((item, index) => {
        const a = Object.values(item)[0];
        let res = true
        if (a.subDishListView && a.subDishListView.length > 0) {
          res = a.subDishListView.every(b => {
            return b.subDishes.filter((c => c.choose)).every(f => f.isDelivery)
          })
        }
        if (currentType === 2 && (!a.isDelivery || !res)) {
          list.push({ [index]: a });
          keylist.push(index);
        }

      })

      dishList.map((item, index) => {
        const a = Object.values(item)[0];
        let res = true
        if (a.subDishListView && a.subDishListView.length > 0) {
          res = a.subDishListView.every(b => {
            return b.subDishes.filter((c => c.choose)).every(f => f.isCollection)
          })
        }
        if (currentType === 3 && (!a.isCollection || !res)) {
          list.push({ [index]: a });
          keylist.push(index);
        }

      })

      return {
        keylist: keylist,
        list: list
      };
    }
    return {
      keylist: {},
      list: []
    }

  }, [dishList])

  function renderDishList () {

    return <div className="unavailable_removed_items">
      {/* <div className="unavailable_item_header">
        <Icon type='iconicon_tips_o_A19' />
        <p >
          The items are unavailable for {currentType === 3 ? 'collection' : 'delivery'}.
        </p>
      </div> */}
      <DishView dishList={errorList.list} />
    </div>

  }

  return <Modal title=''
    onClose={onClose}
    className="unavailable_modal">
    <AdaptHeader title={`Unavailable item(s) for ${currentType === 3 ? 'Collection' : 'Delivery'}`} onLeftPress={onClose} />
    <section className='unavailable_page_wrapper'>
      <div className="unavailable_alert">
        <img src={require('../../../assets/unavailable_item.png')} className='iconUnavailableitem' />
        <small >
          The following item(s) will be removed from the basket.
        </small>
      </div>
      {renderDishList()}
    </section>
    <div className="unavailable_btn_div">
      <button className=" unavailable_btn_return"
        onClick={onClose}>Return</button>

      <button className=" unavailable_btn_confirm"
        onClick={onConfirm}>
        Confirm and Remove them
      </button>

    </div>
  </Modal>
}

export default compose(withRouter, connect(({ orderList: { dishList, currentType } }) => ({
  dishList, currentType
})))(UnavailableModal);

