import React, { useEffect, useState } from 'react';
import { AutoTextarea, Icon, CloseIcon } from '../../../../component';
import { isMobile, getStatusBarHeight, compose, connect, action, getValueFromQuery, stayScroll } from '../../../../utils';
import { withRouter } from 'react-router-dom';


import './index.less';
import './indexPc.less';


const ShopSearchBar = (props) => {

  const { selectedMenu, showSearch, searchKeyword, rId } = props;


  const typeId = selectedMenu?.id;

  const menuId = selectedMenu?.menuId;


  function goBack () {
    props.history.goBack();
    props.dispatch(action('shopDetail/save', { dishBriefList: null }))

  }

  // 
  function onChange (e) {
    const keyword = e.target.value;
    props.dispatch(action('shopDetail/save', { searchKeyword: keyword }))
    props.dispatch(action('shopDetail/getDishBriefListByKeyword', { keyword: keyword, rId: rId }))
  }



  function onSearchBack () {
    stayScroll();
    props.dispatch(action('shopDetail/save', { showSearch: false, searchKeyword: '' }))
    props.dispatch(action('shopDetail/onSearchBack', { rId: rId, typeId: typeId, menuId: menuId }))
  }

  function onSearch () {
    props.dispatch(action('shopDetail/save', { showSearch: true }))
  }

  function clearSearch () {
    props.dispatch(action('shopDetail/save', { searchKeyword: '', dishBriefList: null }))
  }

  useEffect(() => {
    if (!showSearch) {
      props.dispatch(action('shopDetail/save', { searchKeyword: '', dishBriefList: null }))
    }
  }, [showSearch])

  useEffect(() => {
    return () => {
      // props.dispatch(action('shopDetail/save', { showSearch: false, searchKeyword: '', dishBriefList: null }))
      stayScroll();
    }
  }, [])

  return <section className='shop_search_bar' style={{ paddingTop: getStatusBarHeight() }}>
    {!showSearch ?
      <div className='shop_search_bar_title' >
        {isMobile() ? <Icon type='iconicon_more_A1' className='search_go_back_icon' onClick={goBack} /> : <span></span>}

        <h1>{selectedMenu?.name}</h1>
        <Icon type='iconicon_search_a9' className='search_icon' onClick={onSearch} />
      </div> :
      <div className='shop_search_bar_container' >
        <Icon type='iconicon_more_A1' className='search_back_icon' onClick={onSearchBack} />
        <div className='search_container'>
          <Icon type='iconicon_search_a9' className='search_icon' />
          <AutoTextarea className='search_input' onChange={onChange} value={searchKeyword} placeholder='Searching dishes' />
          <CloseIcon onClick={clearSearch} text={searchKeyword} />
        </div>
      </div>}
  </section>

}


export default compose(
  withRouter,
  connect(({ shopDetail: { selectedMenu, showSearch, searchKeyword }, orderList: { rId } }) => ({
    selectedMenu, showSearch, searchKeyword, rId
  }))
)(ShopSearchBar)
