import axios from 'axios';
import { AppConfig } from '../config';
import { getUnionId, getOrderType } from '../utils';


axios.interceptors.request.use(
  config => {
    config.timeout = 300000;
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);



export function getConfigParams () {
  return {
    appId: AppConfig.APP_ID,
    website: getUnionId(),
    orderType: getOrderType(),
    unionId: getUnionId(),
  }
}

const http = {
  checkLogin (data) {
    return true;
  },


  async get (url, params = {}) {
    const pa = { ...getConfigParams(), ...params, };
    const { data } = await this.request({ url, method: 'get', params: pa });
    if (data && data.code !== 500) {
      return data;
    }
    return null;
  },

  async post (url, params = {}) {
    const pa = { ...getConfigParams(), ...params, };
    const { data } = (await this.request({ url, method: 'post', data: pa }));
    if (data && data.code !== 500) {
      return data;
    }
    return null;
  },

  async request (config) {
    let result = await Promise.all([
      new Promise((resolve, reject) => {
        setTimeout(() => {
          resolve();
        }, 500)
      }),
      axios({
        ...config
      }).catch(e => {
        if (e.toString().indexOf('timeout')) {
          window.dvaStore.dispatch({ type: 'user/onError' });
        }
      })

    ]).then(res => {
      return res[1]
    })

    if (result?.status == 200) {
      if (result.data) {

        if (result.data.code === '401') {
          //   
          window.dvaStore.dispatch({ type: 'user/logout401' });
        } else if (result.data.code === 500) {
          // nullnull
          // console.error("api 500 error", config.method, config.url, JSON.stringify(config.params));
          window.dvaStore.dispatch({ type: 'user/onError' });
        }
      }
    } else {
      // console.error('', config)
    }
    return result || {};
  },
};

export {
  http,
};
