import React, { useState,useEffect } from 'react';
import { Icon } from '../../../component';

import './index.less';
import './indexPc.less';

import { connect, compose, action, AppConfig } from '../../../utils';
import { withRouter,useParams } from 'react-router-dom';



const OrderFailedOops = (props) => {

  const { uuid } = props
  let { result } = useParams();

  function onOkClick () {
    props.dispatch(action('orderList/addErrorCount', { callBack:({count,rId})=>{
      if(count > AppConfig.orderErrorCount){ // 15OKEASYDISHDEV-1236
        props.history.push(`/shopDetail?rId=${rId}`);
      }
    } }));
    props.dispatch(action('reviewPayment/save', { showOops: false }));
    if(window.parent && window.parent.appBack && result){
      window.parent.appBack(result);
    }
  }

  useEffect(() => {
    if(window.parent && window.parent.appBack && result){
      window.parent.appBack(result);
    }
  }, [])

  return <section className='order_failed_oops_wrapper'>
    <section className='order_failed_oops'>
      <Icon type='iconlogo_o' />
      {
        result && result.toString() !== "0" ?
        <>
          <h5>Thank You!</h5>
          <p>We will send you an order</p>
          <p>confirmation when the store</p>
          <p>accept your order.</p>
        </>
        : 
        <>
          <h5>Oops!</h5>
          <p>Something went wrong.</p>
          <p>Your order failed.</p>
        </>
      }
      <p className='order_failed_oops_uuid'>{uuid}</p>
      <button onClick={onOkClick}>OK</button>
    </section>
  </section>

}

export default compose(
  withRouter, connect(({ orderList: { uuid }, }) => ({
    uuid
  })))(OrderFailedOops)