
import { http } from '../utils';
import { API } from '../config';

const saveAdsView = (query) => {
  return http.post(API.saveAdsView, query)
};

const saveAdsPurchase = (query) => {
  return http.post(API.saveAdsPurchase, query)
};

const recordAction = (query) => {
  return http.post(API.recordAction, query)
};

const updatePointTag = (query) => {
  return http.post(API.updatePointTag, query)
};



export default {
  saveAdsView, saveAdsPurchase, recordAction, updatePointTag
}
