import React, { useEffect, useMemo } from 'react';
import { AdaptHeader, Modal, Icon, ShopMap, DirectionC, TelCon, ShopTel } from '../../../component';
import { action, connect, isMobile, compose, withRouter, getValueFromQuery } from '../../../utils';

import './indexPc.less';
import './index.less';
import { useParams } from 'react-router-dom';

const StoreInfoModal = (props) => {

  const { restaurantInfo, allTime } = props;

  useEffect(() => {
    if (restaurantInfo?.id) {
      props.dispatch(action('shopDetail/getRestaurantAllTime', { rId: restaurantInfo.id }))
    }
  }, [restaurantInfo])

  let { urlName, menuUrl } = useParams();

  function onClose () {
    props.dispatch(action('shopDetail/save', { showStoreInfo: false }))
    if (urlName) {
      props.history.replace(`/${restaurantInfo.menuUrl}`);
    }
  }

  const shopList = useMemo(() => {
    if (restaurantInfo?.lat && restaurantInfo?.lon) {
      return [{
        id: 'id',
        lat: restaurantInfo.lat,
        lon: restaurantInfo?.lon
      }]
    } else {
      return []
    }
  }, [restaurantInfo])

  const title = useMemo(() => {
    return <AdaptHeader title={<span className={'one-line'}>{isMobile() ? 'Store info' : restaurantInfo?.name}</span>} onLeftPress={onClose} left={isMobile() ? 'Menu' : undefined} />
  }, [restaurantInfo])


  function chooseTime (item) {
    return () => {
      props.dispatch(action('shopDetail/chooseAllTime', { selectedTime: item }))

    }
  }

  function renderTab () {
    return allTime.map((item, index) => {
      return <li key={item.lable}
        onClick={chooseTime(item, index)}
        className={`store_info_time_tab ${item.choosed ? 'store_info_time_tab_active' : undefined}`}>
        <p>{item.lable}</p>
        <div className='store_info_time_tab_bg' />
      </li>
    })
  }

  function renderWeek () {
    const choosedItem = allTime.filter(item => item.choosed)[0];
    return choosedItem?.value?.map(item => {
      return <li key={item.day} className='store_info_week_item'>
        <p>{item.name}</p>
        <p>{item.timeStr + ''}</p>
      </li>
    })

  }

  return <Modal className="store_info_modal"
    title={title}
    onClose={onClose}
  >
    <section className="store_info_modal_wrapper">
      <h6 className={'one-line  store_name'}>{restaurantInfo?.name}</h6>
      <div className='store_info_map_c'>
        <ShopMap shopList={shopList} mapOptions={{ defaultZoom: 15, zoomControl: false, fullscreenControl: false }} />
      </div>
      <div className='store_info_base_c'>
        <DirectionC
          type='shop'
          reverse
          restaurantInfo={restaurantInfo}
          className='home_top_c_address' />
        <ShopTel restaurantInfo={restaurantInfo} />
      </div>
      <section className='store_info_times'>
        <ul className='store_info_time'>
          {renderTab()}
        </ul>
        <ul className='store_info_week'>
          {renderWeek()}
        </ul>
      </section>

    </section>



  </Modal>
}


export default compose(
  withRouter,
  connect(({ shopDetail: { restaurantInfo, allTime, selectedTime }, }) => ({
    restaurantInfo, allTime, selectedTime
  })))(StoreInfoModal);