import React, { useEffect, useMemo, useState, useRef } from 'react';
import { Icon, Viewer, Swiper } from '../../../../component';
import { connect, compose, withRouter, useThrottle } from '../../../../utils';
import addEventListener from "add-dom-event-listener";

import './index.less'
import './indexPc.less';

const AlbumC = (props) => {


  const { landing, id } = props;

  const [showViewer, toogleShowViewer] = useState(false);
  const [start, setStart] = useState(0);

  const [bannerR, setBanerR] = useState([]);
  const [galleryImages, setGalleryImages] = useState([]);

  const reizeEvent = useRef();

  useEffect(() => {
    reizeEvent.current = addEventListener(window, "resize", handleResize);
    handleResize()
    return () => {
      reizeEvent.current && reizeEvent.current.remove();
    }
  }, [landing]);


  const handleResize = useThrottle((e) => {
    const width = document.body.offsetWidth;
    if (width > 768) {
      setBanerR(landing.galleryBannerBgPCUrl);
      setGalleryImages(landing.galleryPC)
    } else {
      setBanerR(landing.galleryBannerBgPhoneUrl)
      setGalleryImages(landing.galleryImages)
    }
  }, 100, [])




  const list = useMemo(() => {

    return galleryImages?.map(item => ({
      url: item.url,
      alt: item.imageText
    }))

  }, [galleryImages]);

  function onMaskPress () {
    toogleShowViewer(false)
  }

  function onImgClick (index) {
    return () => {
      toogleShowViewer(true);
      setStart(index)
    }
  }

  function renderImgs () {
    return galleryImages.map((item, index) => {
      let className = index % 2 === 0 ? 'item' : 'item_sapn';
      if (index === 2 || index === 3) {
        className = index % 2 === 0 ? 'item_sapn' : 'item';
      }
      if (index < 6) {
        return <div className={className} onClick={onImgClick(index)} key={index}>
          <img src={item.url} alt={item.imageText} />
          <div className="item_top_search">
            <Icon type='iconfangda' />
          </div>
        </div>
      } else {
        return null
      }

    })
  }

  const galleryType = useMemo(() => {
    return landing?.galleryType === 2;
  }, [landing?.galleryType])

  function renderGalleryImages () {
    if (galleryImages?.length >= 6) {
      return (
        <section className={`album_c_wapper ${galleryType ? 'album_c_wapper_2' : undefined}`}>
          <div className="album_c_left_top" />
          <div className="album_c_right_bottom" />
          <div className="album_c_grid">
            {renderImgs()}
          </div>

        </section>
      )
    } else {
      return null
    }
  }

  function renderGalleryBanner () {
    return bannerR?.length > 0 ? <section className='album_banner'>
      <Swiper images={bannerR} />
    </section> : null
  }

  return <section className="album_c" id={id}>
    {renderGalleryImages()}
    {renderGalleryBanner()}

    {showViewer && <Viewer list={list} start={start}
      onMaskPress={onMaskPress}
    />}
  </section>
}

export default compose(
  connect(({ webConfigModal: { landing, } }) => ({
    landing,
  })),
  withRouter
)(AlbumC);