import React, { useEffect, useState, useRef } from 'react';
import { connect, action, useThrottle } from '../../../../utils';

import './index.less'
import './indexPc.less';

import addEventListener from "add-dom-event-listener";


import { ListSelect, SubmitModal } from '../../../../component';


const FeedbackC = (props) => {
  const { showFeedback, feedbackName, feedbackPhone, feedbackEmail, feedbackText, landing, id, shopList, feedbackShopName } = props;

  const reizeEvent = useRef();



  useEffect(() => {
    if (landing) {
      reizeEvent.current = addEventListener(window, "resize", handleResize);
      handleResize()
      return () => {
        reizeEvent.current && reizeEvent.current.remove();
      }
    }
  }, []);


  const [bannerR, setBanerR] = useState([]);

  const handleResize = useThrottle((e) => {
    const width = document.body.offsetWidth;
    if (width > 768) {
      if (landing?.webSiteFeedbackBgPc) {
        setBanerR(`url(${landing?.webSiteFeedbackBgPc})`)
      } else {
        setBanerR(`url(${landing?.bannerPCImages[0].url})`)
      }
    } else {
      if (landing?.webSiteFeedbackBgPhone) {
        setBanerR(`url(${landing?.webSiteFeedbackBgPhone})`)
      } else {
        setBanerR(`url(${landing?.bannerWapImages[0].url})`)
      }
    }
  }, 100, [])



  function onSubmit () {
    props.dispatch(action('home/feedback'))
  }

  function onChangeName (e) {
    const value = e.target.value;
    props.dispatch(action('home/save', { feedbackName: value }))
  }

  function onChangePhone (e) {
    const value = e.target.value;
    props.dispatch(action('home/save', { feedbackPhone: value }))
  }

  function onChangeEmail (e) {
    const value = e.target.value;
    props.dispatch(action('home/save', { feedbackEmail: value }))
  }

  function onChangeText (e) {
    const value = e.target.value;
    props.dispatch(action('home/save', { feedbackText: value }))
  }

  function chooseShop (item) {
    props.dispatch(action('home/save', { feedbackShopId: item.id, feedbackShopName: item.name }))
  }

  function onClose () {
    props.dispatch(action('home/save', { showFeedback: false }))
  }

  return (<section className="feedback_c" id={id} style={{ backgroundImage: bannerR }}>
    <section className="feedback_c_wapper">
      <section className="feedback_c_container">
        <h4>Feedback</h4>
        <div className='feedback_c_line' />
        <div className='feedback_c_content'>
          <div className='feedback_c_left'>
            <input placeholder='Name' onChange={onChangeName} value={feedbackName} />
            <input placeholder='Phone' onChange={onChangePhone} value={feedbackPhone} />
            <input placeholder='Email' onChange={onChangeEmail} value={feedbackEmail} />
          </div>
          <div className='feedback_c_right'>
            {landing?.webSiteType === 2
              && <ListSelect
                chooseItem={chooseShop}
                showList={shopList}
                showName={feedbackShopName} />}
            <textarea placeholder='Please input the message' onChange={onChangeText} value={feedbackText} />
          </div>

        </div>
        <button className='feedback_c_submit'
          onClick={onSubmit}
        >Submit</button>
      </section>

    </section>
    {showFeedback && <SubmitModal
      type='icontijiaochenggong'
      title='Feedback Submitted'
      msg='Thank You!'
      onClose={onClose} />}
  </section>)
}

export default
  connect(({ webConfigModal: { landing },
    home: { showFeedback, feedbackName, feedbackPhone, feedbackEmail, feedbackText, shopList, feedbackShopName } }
  ) => ({
    landing, showFeedback, feedbackName, feedbackPhone, feedbackEmail, feedbackText, shopList, feedbackShopName
  })
  )(FeedbackC);