
import React, { useMemo } from 'react';
import './index.less'
import './indexPc.less'
import { connect, compose, getStatusBarHeight, inRN, getOs, action } from '../../utils';
import { withRouter } from 'react-router-dom';
import Icon from '../icon';
import MenuBlock from '../menuBlock';
import { MyHeaderMenuPc, MyHeaderMenuM } from './myHeaderMenu';
import DishpriceIcon from '../dishpriceIcon';
import LoginIcon from '../loginIcon';
import DownloadTop from './downloadTop';



function MyHeader (props) {

  const { landing, showDown, isFocus } = props;

  const isHome = props.location.pathname === '/' || props.location.pathname === '/home'
  const showMHeader = props.location.pathname === '/' || props.location.pathname === '/home' || props.location.pathname === '/shopList'


  function goHome () {
    props.history.push("/");
    props.dispatch(action('home/save', { selectedHeader: 'Home' }))
  }

  if (!isFocus) {
    return (<header className={`header_my ${showMHeader ? '' : 'header_my_nolandinghome'} ${isHome ? 'header_my_index' : ''}`} style={{ paddingTop: getStatusBarHeight() }}>
      <DownloadTop />
      <section className='header_my_container'>
        <div className='header_my_left' onClick={goHome}>
          {landing?.logoUrl ?
            <img src={landing?.logoUrl} className="pc_logo" alt="logo" /> : <Icon className="pc_logo" type="iconlogo_o" />}
        </div>
        <div className="header_my_right">
          <MyHeaderMenuPc />
          {/*  */}
          <DishpriceIcon />

          {/* Menu */}
          {props.isLogin && <MenuBlock />}

          {/*  */}
          <LoginIcon />
          <MyHeaderMenuM />
        </div>
      </section>
    </header>)
  }
  else {
    return null;
  }
}

export default compose(
  withRouter,
  connect(({ webConfigModal: { landing }, user: { isLogin }, home: { showDown }, shopList: { isFocus } }) => ({
    landing, isLogin, showDown, isFocus
  })))(MyHeader)