import React, { useEffect, useMemo, useState } from 'react';
import { getBrowserInfo } from '../../bugreport/tools';
import { stopScroll, stayScroll, isMobile, inRN, h5ToRn, getOs, openWeb } from '../../utils';
import Icon from '../icon';
import message from '../message';
import LocationServiceAlert from '../locationIcon/locationServiceAlert';

import './index.less';
import './indexPc.less';
import Loader from '../loader';
import Spin from '../spin';

const DirectionC = (props) => {
  const { className, restaurantInfo, type = 'daohang', reverse = false } = props;

  const [show, setShow] = useState(false);

  const [loading, setLoading] = useState(false);


  function onCancel () {
    setShow(false)
  }



  function showModal () {
    if (inRN()) {
      setShow(true)
    } else {
      if (loading) {
        return;
      }
      setLoading(true);
      const time = 3000;
      let ll;
      Promise.all([
        //  3 
        new Promise((resolve, reject) => {
          setTimeout(() => {
            resolve();
          }, time)
        }),

        new Promise((resolve, reject) => {
          navigator.geolocation.getCurrentPosition((data) => {
            if (data && data.coords && data.coords.latitude) {
              ll = ({
                lat: data.coords.latitude,
                lon: data.coords.longitude,
              })
              resolve()
            }
          }, (error) => {

            resolve(error)
          }, {
            enableHighAccuracy: true,
            timeout: 5000,
            maximumAge: 0
          })
        }),
      ]).then((res) => {
        const error = res[1];
        const { lat, lon, address } = restaurantInfo
        const latLng = `${lat},${lon}`;
        const saddr = !!ll ? `${ll.lat},${ll.lon}` : 'Current+Location'
        if (!error) {

          window.open(`https://maps.google.com?saddr=${saddr}&daddr=${latLng}&destination=${address}`)
          return
        }

        switch (error.code) {
          case error.PERMISSION_DENIED:
            LocationServiceAlert({
              title: 'We need your location permission.',
              msg: ` Oops! We couldn't find your location. you can see this message because:
            You refused to give our app permission to see your location.
            Your mobile internet browser doesn't have permission to see your location.
            Your location is turned off on your phone or tablet.
            `,
              onOk: () => window.open(`https://www.google.com/maps/dir/?api=1${restaurantInfo?.addressPalceId ? '&destination_place_id=' + addressPalceId : ''}&destination=${address}&travelmode=driving`)
            })
            break;
          case error.POSITION_UNAVAILABLE:
            message.base("Oops! We couldn't find your location. Please enter your address manually.")
            break;
          case error.TIMEOUT:
            message.base("The request to get user location timed out.")
            break;
          case error.UNKNOWN_ERROR:
            message.base("An unknown error occurred.")
            break;
        }
      }).finally(() => {
        setLoading(false);
      })

    }
  }

  return <div className={`direction_c_address ${className}`} style={{ flexDirection: reverse ? 'row-reverse' : 'unset' }}>
    <div className="direction_c_address_left" onClick={showModal}>
      {
        loading ? <Spin className='direction_c_address_loading' /> :
          <Icon
            type={type === 'daohang' ? 'icondaohang' : 'icondianpudaohang'}
            style={{ fill: type === 'daohang' && '#fff' }}
          />
      }

      <p>Direction</p>
    </div>
    <div className='direction_c_drive'></div>
    <div className='direction_c_address_right'>{restaurantInfo?.address}</div>
    {show && <DirectionModal onCancel={onCancel} restaurantInfo={restaurantInfo} />}
  </div>
}


