import React from 'react';
import PropTypes from 'prop-types';
import Icon from '../icon';
import './indexPc.less';
import './index.less';


const NavBlock = (props) => {

  const { onClick, text, type, pending, style } = props;

  return (
    <section
      onClick={onClick}
      className="nav_block_container"
      style={style}
    >
      <Icon type={type} />
      {text && <span >&nbsp;{text}</span>}
      {!!pending && <div className='pending_dot' />}
    </section>
  )
}

export default NavBlock

NavBlock.propTypes = {
  text: PropTypes.string,
  type: PropTypes.string,
  onClick: PropTypes.func,
}

NavBlock.defaultProps = {
  onClick: () => { },
  text: '',
  type: ''
}