import React, { useMemo, useState, } from 'react';
import { connect, compose, action, isMobile, withRouter } from '../../../utils';
import { Icon, MyButton, getCPrice, CurrencyPrice } from '../../../component'

import './index.less';
import './indexPc.less';


const ReviewPaymentNext = (props) => {

  const { restaurantInfo, dishListPrice, currentType, dishList, payType, onPayNextClick, buttonLoading, } = props;

  // 
  const isMinOrder = restaurantInfo.isMinOrder;
  const minOrder = getCPrice(restaurantInfo.minOrder);

  const shopStatus = useMemo(() => {
    const count = dishList?.length || 0

    if (buttonLoading) {
      return { text: 'loading', dishable: true, }
    }
    if (currentType === 2) { // devilery

      if (isMinOrder && minOrder > dishListPrice && count > 0) {
        return { text: `Minimum Order ${minOrder} for delivery`, dishable: true, }
      } else {
        return { text: 'Next', dishable: false, }
      }
    }
    return { text: 'Next', dishable: false, }
  }, [dishListPrice, currentType, payType, buttonLoading])


  function onNextClick () {
    props.dispatch(action('statistic/recordPointTag', {
      recordclick: 'onNextClick',
      mark: ' ReviewPayment Next'
    }));

    if (!shopStatus.dishable) {
      onPayNextClick && onPayNextClick();
    }
  }


  return <div className={`review_payment_price_container ${shopStatus.dishable ? 'review_payment_price_uneough_container' : undefined}`}>
    <MyButton loading={buttonLoading} className={`review_payment_price_btn ${buttonLoading ? 'review_payment_price_btn_loading' : undefined}`} onClick={onNextClick}>
      <span>{shopStatus.text} </span>
    </MyButton>
  </div>
}

export default compose(
  withRouter,
  connect(({ orderList: { currentType, dishListPrice, dishList, }, shopDetail: { restaurantInfo },
    user: { isLogin }, reviewPayment: { payType, buttonLoading } }) => ({
      restaurantInfo, currentType, isLogin, dishListPrice, dishList, payType, buttonLoading,
    })))(ReviewPaymentNext)