import React, { useEffect, useMemo, useRef, } from 'react';


import { AdaptHeader, CurrencyPrice, message, ModalAlert, Notice, Popup } from '../../../../component';
import FoodTasteItem from './../shopFoodsItem/foodTasteItem';
import ShopCounter from './shopCounter';
import ShopOrderPrice from './shopOrderPrice';
import DishCon from './dishCon';
import DishPizza from './dishPizza';

import { getValueFromQuery, compose, connect, action, formatHtml, } from '../../../../utils';
import { withRouter } from 'react-router-dom';

import './index.less';
import './indexPc.less';
import DishNote from './dishNote';
import ShopEmpty from '../../shopEmpty';

/**
 *    
 * @param {*} props 
 * @returns 
 */
const DishDetail = (props) => {

  const { data, style, type, modelId, sub, from, getFixedHeaderRef, restaurantInfo, rId } = props;
  const detail = data[modelId] || {};

  function renderFoodTast () {
    if (!detail.hotRank && !detail.isVegetarian && !detail.calorie && !detail.isNut) {
      return null
    }
    return <div className='shop_dish_foods_taste'>
      {!!detail.hotRank && <FoodTasteItem type='spicy' number={detail.hotRank} />}
      {!!detail.isVegetarian && <FoodTasteItem type='vegetarian' />}
      {!!detail.calorie && <FoodTasteItem type='cal' number={detail.calorie} />}
      {!!detail.isNut && <FoodTasteItem type='nuts' />}
    </div>
  }


  function onReset () {
    ModalAlert({
      title: 'Are you sure',
      msg: 'This will reset your customized selections.',
      onSure: () => {
        props.onReset && props.onReset(detail.categoryId, props.onConChange)
      },
      onCancel: () => { },
    })
  }



  // 
  function onNumChange (n) {
    props.dispatch(action('cart/changeCount', { modelId: modelId + '', params: { count: n } }))
  }


  function onChooseDish (item, partId, pid, subId, type) {
    // item 
    if (detail.categoryId !== 8) {
      props.onConChange && props.onConChange(partId + '');

    }
    switch (detail.categoryId) {
      case 1:
        props.dispatch(action('cart/onChoose14', { modelId: modelId + '', params: { rId, item, partId, pid, subId, type } }))
        break;
      case 4:
      case 8:
        const subDishListViewItem = detail.subDishListView[pid];
        if (subDishListViewItem.showType === 'single' ||
          (((subDishListViewItem.totalSize + 1) === subDishListViewItem.maxSize) && type === 'add')
        ) {
          props.onConChange && props.onConChange(partId + '');
        }
        props.dispatch(action('cart/onChoose14', { modelId: modelId + '', params: { rId, item, partId, pid, subId, type } }))
        break;
      case 6:
        props.dispatch(action('cart/onChoose14', { modelId: modelId + '', params: { rId, item, partId, pid, subId, type } }))
        break;
    }
  }

  function onSkip (pid) {
    props.dispatch(action('cart/onSkip', { modelId: modelId + '', params: { rId, pid, } }))
    props.onConChange();

  }

  function onNONE (pid) {
    props.dispatch(action('cart/onNONE', { modelId: modelId + '', params: { rId, pid: pid + '', } }))
    props.onConChange();
  }

  //  pizza
  function onEditPizza (index, subItem) {
    if (subItem && subItem.categoryId === 5) {
      props.dispatch(action('cart/openSubDish', { modelId: modelId + 'd' + index, params: { subItem: subItem, rId: rId } }))
    }
  }


  //  pizza  note
  function onNoteChange (index, subItem, v) {
    props.dispatch(action('cart/changeSubNote', { modelId: modelId + '', params: { index, categoryId: subItem.categoryId, value: v } }))

  }

  //  pizza
  function onChoosePizza (id, partId, pid, subId, type) {
    props.onConChange && props.onConChange();
    props.dispatch(action('cart/onChoose5', { modelId: modelId + '', params: { rId, id, partId, pid, subId, type } }))
  }

  function toogleChangeHalf (checked) {
    props.dispatch(action('cart/changeHalf', { modelId: modelId + '', params: { rId, checked } }))
  }

  function changeCurrentSide (side) {
    props.dispatch(action('cart/changeCurrentSide', { modelId: modelId + '', params: { side } }))
  }

  function onClose () {
    props.onClose && props.onClose()
  }

  function toCustomize () {
    props.dispatch(action('cart/changeCustomize', { modelId: modelId + '', }))
  }


  function addOrder () {
    if (detail.categoryId === 5) {
      let tips = ''
      const sizesname = detail.subPizzaDishView.sizesname;
      const haveno = ['selectedbase', 'selectedsauce', 'selectedcheese'].some(item => {
        const havenoItem = detail.subPizzaDishView['haveno' + item];
        if (havenoItem) {
          const itemname = detail.subPizzaDishView[item + 'name'];
          tips = tips + `<div>${itemname} is unavailable for ${sizesname} size.</div>`
        }
        return havenoItem
      })

      if (haveno) {
        message.base(`<div class='wrapper_msg'>${tips}</div>`)
        return
      }
      const selectedtoppingsCount = detail?.subPizzaDishView?.selectedtoppingsCount

      const limitNum = detail?.subPizzaDishView?.sizes //  size 
        .filter(item => item.id === detail.subPizzaDishView.sizesid)[0]?.toppingLimit


      if (selectedtoppingsCount > limitNum) {

        //   topping
        const allDefault = detail.subPizzaDishView.selectedtoppings["All over"]
          .filter(item => item.count > 0).every(item => item.def)

        if (!allDefault) {
          message.base(`Maximum ${limitNum} toppings for size \n ${sizesname}`)
          return
        }
      }

      props.save && props.save();
    } else {
      props.save && props.save();
    }
  }

  // fixed Header 
  const fixedHeaderRef = useRef();


  const fixedHeader = useMemo(() => {
    if (type === 'whole') {
      return <div className='shop_dish_detail_fixed_header' ref={fixedHeaderRef}>
        <AdaptHeader
          onLeftPress={onClose}
          right={(detail?.categoryId === 5 || detail?.categoryId === 8) && renderReset()}
          title={detail.name} />
      </div>
    } else {
      return null;
    }
  }, [type])

  useEffect(() => {
    if (type === 'whole') {
      if (fixedHeaderRef.current) {
        getFixedHeaderRef(fixedHeaderRef)
      }
    }
  }, [type])

  const className = useMemo(() => {

    if ((detail && detail.subDishListView && detail.subDishListView.length > 0) || (detail && detail.subPizzaDishView)) {
      return undefined
    } else {
      return 'shop_dish_detail_container_white'
    }
  }, [detail])

  const decriptionRef = useRef();
  function executeOnClick () {
    decriptionRef.current.style.maxHeight = "unset";
    props.onConChange();
  }

  function renderReset () {
    if (detail?.categoryId === 8) {
      return <span onClick={onReset} className='shop_dish_detail_reset'>Reset</span>

    }
    if ((detail.showCustomiz === undefined || detail.showCustomiz) && !sub) {
      if (detail.isHalfPizza && detail.finishChoose) {
        return <span onClick={onReset} className='shop_dish_detail_reset'>Reset</span>
      }
      return null
    } else {
      return <span onClick={onReset} className='shop_dish_detail_reset'>Reset</span>
    }
  }

  function onToogleShow (index, categoryId, v) {
    props.dispatch(
      action('cart/changeToogleShow', {
        modelId: modelId + '',
        params: { index, categoryId: categoryId, value: v },
      }),
    );
  }

  //   
  const isEmpty = useMemo(() => {
    return (detail.categoryId === 4 || detail.categoryId === 6 || detail.categoryId === 8) && (!detail.subDishListView || detail.subDishListView.length === 0)
  }, [detail])

  return (
    <section
      className={`shop_dish_detail_container ${className} ${type === 'whole' ? 'shop_dish_detail_container_whole' : undefined}`}
      style={style}>
      {fixedHeader}
      <div className='shop_dish_detail_top_container'>

        {
          detail.picUrl &&
          <img src={detail.picUrl} alt={'DetailName:' + detail.name} />}
        <div >
          <div className={`shop_dish_detail_top ${sub ? 'shop_dish_detail_top_sub' : undefined}`}>
            <div className={`shop_dish_detail_top_title ${detail?.categoryId === 5 && 'shop_dish_detail_top_title_center'}`}>{detail.name}</div>
            {detail?.categoryId !== 5 ? <span>
              <CurrencyPrice price={detail?.price} />
            </span> : renderReset()}
          </div>
          {
            detail.description && <div className='shop_dish_detail_notice' ref={decriptionRef}>
              <Notice text={detail.description}
                executeOnClick={executeOnClick} />

            </div>
          }
          {
            renderFoodTast()}
          {
            (detail.categoryId !== 5 || detail.partId) && !sub &&
            <DishNote modelId={modelId} detail={detail} onReset={onReset} />
          }
        </div>
      </div>
      {
        isEmpty && <ShopEmpty text={`Couldn't find any further details of this dish.`} />
      }
      { //     
        detail && detail.subDishListView && detail.subDishListView.length > 0 &&
        <DishCon
          from={from}
          categoryId={detail.categoryId}
          list={detail.subDishListView}
          onChooseDish={onChooseDish}
          onSkip={onSkip}
          onNONE={onNONE}
          onEditPizza={onEditPizza}
          onNoteChange={onNoteChange}
          onToogleShow={onToogleShow}
          finishChoose={detail.finishChoose}
          isMinPOS={restaurantInfo.isMinPOS} // 
          totolPrice={detail.totalvPrice}
        />
      }
      { // pizza  
        detail && detail.subPizzaDishView &&
        <DishPizza
          sizePrice={detail.price}
          from={from}
          isHalfPizza={detail.isHalfPizza}
          sub={sub}
          showCustomiz={detail.showCustomiz}//  customiz
          customiz={detail.customiz} // 
          toCustomize={toCustomize}  // 
          categoryId={detail.categoryId}
          pizza={detail.subPizzaDishView}
          totolPrice={detail.totalvPrice}
          onChooseDish={onChoosePizza}
          onToogleShow={onToogleShow}
          toogleChangeHalf={toogleChangeHalf}
          changeCurrentSide={changeCurrentSide}
          finishChoose={detail.finishChoose}
          showKeys={detail.showKeys}
        />
      }
      <ShopCounter className='shop_dish_counter' onChange={onNumChange} sub={sub} count={detail.count || 1} />
      <ShopOrderPrice
        from={from}
        sub={sub}
        fromPrice={detail.fromPrice}
        totolPrice={detail.totalPrice}
        finishChoose={detail.finishChoose}
        addOrder={addOrder}
      />

    </section>

  )
}

export default compose(
  withRouter,
  connect(({ cart: { data }, shopDetail: { restaurantInfo }, orderList: { rId } }) => ({
    data: data.toJS(), restaurantInfo, rId
  })),

)(Popup(DishDetail))
