
import React, { useEffect, useMemo } from 'react';
import './index.less'
import { connect, compose, getStatusBarHeight, inRN, usePrevious, } from '../../utils';
import { withRouter } from 'react-router-dom';
import Icon from '../icon';
import MenuBlock from '../menuBlock';
import DishpriceIcon from '../dishpriceIcon';
import LoginIcon from '../loginIcon';
import BgTop from './bgTop';
import DownloadTop from '../myHeader/downloadTop';

function MHeader (props) {

  const { show = true, landing, onLogoClick } = props;

  const showExpand = props.location.pathname.toString().toLowerCase() === '/' || props.location.pathname.toString().toLowerCase() === '/home' || props.location.pathname.toString().toLowerCase() === '/help';

  const expand = useMemo(() => {
    if (showExpand) {
      switch (props.location.pathname.toString().toLowerCase()) {
        case '/':
        case '/home':
          return {
            text: 'Find your favorite restaurants near you',
          };

        case '/help':
          return {
            text: 'Frequently Asked',
            text2: 'Questions',
          };
      }
    }
  }, [])


  const isHelpPage = useMemo(() => {
    return props.location.pathname.toString().toLowerCase() === '/help'
  }, [])

  function goHome () {
    if (isHelpPage) {
      props.history.goBack();
    } else {
      props.history.push("/");
    }
  }

  const proviousShow = usePrevious(show);

  const wrapperShowClassName = useMemo(() => {
    if (proviousShow !== undefined && proviousShow !== show) {
      if (show) {
        return 'toHFull'
      } else {
        return 'toHZero'
      }
    } else {
      return undefined
    }

  }, [show])

  const title = useMemo(() => {
    return <section className={`header_m_container_line ${wrapperShowClassName} ${isHelpPage ? 'header_m_container_line_help' : undefined}`}
      style={{ paddingTop: show ? getStatusBarHeight() : 0 }}>
      {show ? <>
        <div className='header_m_left' onClick={goHome}>
          {landing?.logoUrl ?
            <img src={landing?.logoUrl} className="pc_logo" alt="logo" /> :
            <Icon className='m_logo' type={"iconlogo_o"}
              style={{ color: showExpand && !isHelpPage && '#fff' }} />}
        </div>
        <div className="header_m_right">
          {/*  */}
          <DishpriceIcon />
          {/* Menu */}
          {props.isLogin && <MenuBlock />}

          {/*  */}
          <LoginIcon />
        </div>
      </> : null}

    </section>
  }, [props.isLogin, showExpand, show, getStatusBarHeight()])


  return (<div>
    <DownloadTop />
    <section className={"header_m"} >

      {showExpand ? <section className={`header_m_container `} >
        <BgTop />
        {title}
        <section className={`header_m_container_line_two ${inRN() ? 'header_m_container_line_two_rn' : undefined}`}>
          <p>
            {expand.text}
          </p>
          {
            expand.text2 && <p>{expand.text2}</p>
          }
        </section>
      </section> : title}
    </section>
  </div>
  )
}

export default compose(
  withRouter,
  connect(({ webConfigModal: { landing }, user: { isLogin } }) => ({
    landing, isLogin
  })))(MHeader)