import React, { useEffect, useMemo, useRef } from 'react'
import { AdaptHeader, message, Modal } from '../../../component';

import { connect, toJSON, action, getStatusBarHeight, inRN, isMobile } from '../../../utils';

import './index.less'
import './indexPc.less'

const Relocation = (props) => {

  const { shopGeo } = props;
  const latlngRef = useRef();

  useEffect(() => {
    function onMessage (event) {
      const data = toJSON(event.data);
      if (data.signal === 'map_relocation') {
        latlngRef.current = data;
      }
    }
    window.addEventListener('message', onMessage)
    return () => {
      window.removeEventListener('message', onMessage, false)
    }
  }, [])

  function onChoose () {
    props.dispatch(action('myDetail/onRelocationChoose', { latlng: latlngRef.current }))

  }


  function getReloctionIframeUrl () {
    if (shopGeo.lat && shopGeo.lon) {
      return `/hereMapRelocation.html?center=[${shopGeo.lat},${shopGeo.lon}]`
    } else {
      message.base(`The address have no lat or lon.`, 4000)

      return null
    }
  }

  function onBackPress () {
    props.dispatch(action('myDetail/save', { showRelocation: false }))
  }

  const height = useMemo(() => {
    if (inRN()) {
      return `calc(100% - ${(getStatusBarHeight() / document.documentElement.clientWidth * 7.5 + 1).toFixed(2) + 'rem'})`;
    } else if (isMobile()) {
      return `calc(100% - 1rem)`;
    }
  }, [])

  const bottom = useMemo(() => {
    if (inRN()) {
      return ` ${(40 / document.documentElement.clientWidth * 7.5 + 1.5).toFixed(2) + 'rem'}`;
    }
  })

  return <Modal paddingTop={false} className="relocation_modal" title={<AdaptHeader title='Pin Your Location For The Driver' onLeftPress={onBackPress} />}>
    <section className="relocation_modal_content">
      {getReloctionIframeUrl() && <iframe src={getReloctionIframeUrl()} frameBorder="0" style={{ height: height }} />}
      <button onClick={onChoose} style={{ bottom: bottom }}>Confirm</button>
    </section>
  </Modal>

}

export default connect(({
  orderList: { shopGeo } }) => ({
    shopGeo
  }))(Relocation);