import React, { useEffect } from 'react';
import {Link} from 'react-router-dom'
import AdaptHeader from '../../component/adaptHeader';
import './indexPc.less';
import './index.less';

import { action, connect, getValueFromQuery } from '../../utils';
import PDFView from './pdfView';

// 
const MenuView = (props) => {

  const rId = getValueFromQuery('rId', props.location.search);
  const hiddenHeader = getValueFromQuery('hiddenHeader', props.location.search) || false;
  
  const {menuPaper} = props;

  
  useEffect(() => {
    props.dispatch(action('home/getMenuPaper',{rId:rId}));

  }, []);

  function renderMenuPaper(){
    return menuPaper?.map((item,index)=>{
      return  <PDFView src={item.url} key={index}/>
    })
    
  }

  return <section className='menu_view_page'>
    {!hiddenHeader ?  <AdaptHeader title='Menu'/>:null}
    <div className='menu_view_page_wrapper'>
    {renderMenuPaper()}
    </div>
  </section>

}

export default connect(({ home: { menuPaper, } }) => ({
  menuPaper, 
}))(MenuView);

