import React, { useCallback, useEffect, useRef, useState } from 'react';

import './index.less'
import './indexPc.less';
import Icon from '../icon';
import addEventListener from "add-dom-event-listener";
import PropTypes from 'prop-types';
import { getScroll, scrollTo } from '../../utils';



function getDefaultTarget () {
  return window;
}

const ScrollTop = (props) => {

  const { target = getDefaultTarget } = props;

  const [visible, setVisible] = useState(false);

  const scrollEvent = useRef();

  useEffect(() => {
    const getTarget = target
    scrollEvent.current = addEventListener(getTarget(), "scroll", handleScroll);
    handleScroll();
    return () => {
      if (scrollEvent.current) {
        scrollEvent.current.remove();
      }
    }
  }, [])

  function scrollToTop (e) {
    const { target = getDefaultTarget, onClick } = props;
    scrollTo(0, {
      getContainer: target
    });
    if (typeof onClick === "function") {
      onClick(e);
    }
  };

  function handleScroll () {
    const { visibilityHeight, target = getDefaultTarget } = props;
    const scrollTop = getScroll(target(), true);
    if (scrollTop > visibilityHeight) {
      setVisible(true)
    } else {
      setVisible(false)
    }

  };

  return visible ? <Icon type='iconicon_ReturnToTop' className='return_to_top_container' onClick={scrollToTop} /> : null

}

export default ScrollTop;

ScrollTop.propTypes = {
  visibilityHeight: PropTypes.number,
}

ScrollTop.defaultProps = {
  visibilityHeight: 400,
}