import React, { useEffect, useMemo } from 'react';
import './indexPc.less';
import './index.less';
import { AdaptHeader, AutoTextarea, Icon, Modal, LocationIcon, CloseIcon } from '../../../component';
import { connect, action } from '../../../utils';

const DetailAddressModal = (props) => {

  const { isLogin, inputvalue, nameValue } = props;

  useEffect(() => {
    props.dispatch(action('myDetail/getAllAddr'));
  }, []);

  // 
  function onBackPress () {
    // props.dispatch(action('myDetail/save', { showAddress: false }))
    props.dispatch(action('myDetail/save', { showAddress: false }))
    // props.dispatch(action('myDetail/getUserInfo'))
  }

  // 
  function onSavePress () {
    props.dispatch(action('myDetail/onSavePress'))
  }

  function onChange (e) {
    const value = e.target.value?.substring(0, 255);
    props.dispatch(action('myDetail/save', { inputvalue: value }))
    if (!value) {
      props.dispatch(action('myDetail/save', { ad: {} }))

    }
    props.dispatch(action('myDetail/queryAddress', { input: value }))
  }

  function onChangeName (e) {
    const value = e.target.value?.substring(0, 50);
    props.dispatch(action('myDetail/save', { nameValue: value }))
  }


  //  address 
  function onChooseAd (item, t) {
    return () => {
      switch (t) {
        case 1://  
          if (item.lat && item.lng) {
            props.dispatch(action('myDetail/saveAddress', {
              lat: item.lat, lng: item.lng, address: item.description
            })); // 
          } else {
            props.dispatch(action('myDetail/getFullAddress', {
              input: item.description, callBack: (data) => {
                const { geometry: { location: { lat, lng } } } = data[0];
                props.dispatch(action('myDetail/saveAddress', {
                  lat: lat, lng: lng, address: item.description
                })); // 
              }
            }))
          }

          break;
        case 2:
          props.dispatch(action('myDetail/saveAddress', {
            lat: item.lat, lng: item.lon, address: item.address, name: item.name
          })); // 

          break;
      }
    }
  }

  // 
  function getAddress (geo) {
    props.dispatch(action('myDetail/queryLocalAddrsByGeo', {
      lat: geo.lat, lng: geo.lon, callBack: (res) => {
        props.dispatch(action('myDetail/saveAddress', {
          lat: geo.lat, lng: geo.lon, address: res.formatted_address
        }));
      }
    }));
  }

  function clearText () {
    props.dispatch(action('myDetail/save', { inputvalue: '', predictions: [], }))
  }


  return <Modal className="detail_address_page" title={<AdaptHeader title='Address' right='save' onRightPress={onSavePress} onLeftPress={onBackPress} />} >
    <section className='detail_address_content'>
      {/* <section className='detail_address_input'>
        <AutoTextarea onChange={onChangeName} placeholder='home,work, etc' value={nameValue} maxLength={50} />
      </section> */}
      <section className='detail_address_input'>
        <AutoTextarea onChange={onChange} placeholder='Enter your address' value={inputvalue} maxLength={255} />
        <CloseIcon text={inputvalue} onClick={clearText} />
        <LocationIcon returnGeo={getAddress} />
      </section>
      <section className='detail_address_content_container'>
        <section className='predictions_container'>
          {props.predictions.map((item, index) => {
            return <div key={item.id} className='resent_detail_address_item' onClick={onChooseAd(item, 1)}>
              <Icon type='iconicon_location_b' />
              <p>{item.description}</p>
            </div>
          }
          )}
        </section>

        {isLogin && <section className='resent_detail_address_container'>
          <div className='or_select'>{props.allRensentAddress && props.allRensentAddress.length > 0 ?
            'Or, select a recent address' : 'You have no recent addresses.'}</div>
          {
            props.allRensentAddress?.map((item, index) => {
              if (index > 4) {
                return
              }
              return (
                <div key={item.id} className='resent_detail_address_item' onClick={onChooseAd(item, 2)}>
                  <Icon type='iconicon_location_b' />
                  <div>
                    <h5> {item.name}</h5>
                    <p> {item.address}</p>
                  </div>
                </div>
              )
            })
          }
        </section>}
      </section>
    </section>
  </Modal>
}

export default connect(({ myDetail: { allRensentAddress, predictions, inputvalue, nameValue }, user: { isLogin } }) => ({
  allRensentAddress, predictions, isLogin, inputvalue, nameValue
}))(DetailAddressModal);
