import React from 'react';
import PropTypes from 'prop-types';
import './index.less';

const Icon = (props) => {
  const { style, type, className, onClick } = props;
  return (
    <svg className={`icon ${className}`} aria-hidden="true" style={style} onClick={onClick}>
      <use xlinkHref={`#${type}`} />
    </svg>
  );
  // return (<i className={`iconfont ${type} ${className}`} style={style} onClick={onClick} />)

};

Icon.propTypes = {
  style: PropTypes.object,
  type: PropTypes.string,
  className: PropTypes.string,
  onClick: PropTypes.func
};
Icon.defaultProps = {
  onClick: () => { }
}

export default Icon;
