import React, { useEffect, useMemo, useRef } from 'react';
import ShopFoods from './shopFoods';
import ShopTotlePrice from './shopTotlePrice';

import { action, connect, getStatusBarHeight, getValueFromQuery, inRN, isMobile } from '../../utils';
import './index.less';

const ShopFoodsM = (props) => {

  const rId = getValueFromQuery('rId', props.location.search);
  const typeId = Number(getValueFromQuery('typeId', props.location.search));

  const menuId = Number(getValueFromQuery('menuId', props.location.search));

  const { dishMenu, restaurantInfo, dishList, status, dishListPrice, showSearch, searchKeyword } = props;

  useEffect(() => {
    dishMenu.map(a => {
      if (a?.dishTypes) {
        a.dishTypes.map(b => {
          if (b.menuId === menuId && b.id === typeId) {
            props.dispatch(action('shopDetail/save', { selectedMenu: b, }));
          }
        });
      }
    })
  }, [dishMenu])


  useEffect(() => {
    // 
    if (showSearch && searchKeyword) {
      return
    }
    props.dispatch(action('shopDetail/getDishBriefList', { rId: rId, typeId: typeId, menuId: menuId }));
    props.dispatch(action('shopDetail/getDishMenuInM', { rId: rId }));
    props.dispatch(action('shopDetail/getRestaurantInfo', { rId: rId })); //   
    props.dispatch(action('shopDetail/restaurantStatusCheck', { rId: rId }));
    props.dispatch(action('orderList/judgeDishBelongShop', { rId: rId }));
  }, [])

  const checkStatusTimer = useRef();

  useEffect(() => {
    if (process.env.NODE_ENV !== 'development') { //   log 
      checkStatusTimer.current = setInterval(() => {

        props.dispatch(action('shopDetail/restaurantStatusRefresh'));
      }, 30000)
    }
    return () => checkStatusTimer.current && clearInterval(checkStatusTimer.current)
  }, [])


  const stickyTop = useMemo(() => {
    if (inRN()) {
      return (getStatusBarHeight() / document.documentElement.clientWidth * 7.5 + 1).toFixed(2) + 'rem';
    } else if (isMobile()) {
      return '1rem'
    } else {
      return '0';
    }
  }, [])


  return <section className='shop_food_page'>
    <ShopFoods stickyTop={stickyTop} />
    <ShopTotlePrice
      dishList={dishList} status={status}
      restaurantInfo={restaurantInfo} dishListPrice={dishListPrice} />
  </section>
}

export default connect(({ shopDetail: { dishMenu, restaurantInfo, status, showSearch, searchKeyword }, orderList: { dishList, dishListPrice, } }) => ({
  restaurantInfo, dishMenu, dishList, restaurantInfo, status, dishListPrice, showSearch, searchKeyword
}))(ShopFoodsM);