
import {http} from '../utils';
import {API} from '../config';


const getStoreListByGeo = (query) => {
    return http.get(API.getStoreListByGeo,query)
};

const queryByKeyword = (query) => {
    return http.get(API.queryByKeyword, query)
};


export default{
    getStoreListByGeo,
    queryByKeyword,
}
