import React, { useMemo } from 'react';
import { Icon, getCPrice, DirectionC } from '../../../component';
import ShopBasicInfoItem from './shopBasicInfoItem';

import './index.less';
import './indexPc.less';

import { action, caculateLL, connect, isMobile } from '../../../utils';


const ShopBasicInfo = (props) => {

  const { restaurantInfo, status, selectedAddress, restaurantStatusLoading, restaurantInfoLoading } = props;

  // 
  function getCuisines () {
    if (restaurantInfo.cuisines) {
      return restaurantInfo.cuisines.split(',').map(item => {
        if (!!item) {
          return <p className='country_tag'
            key={item}>{String(item)
              .replace("Fish&Chips", "Fish & Chips")
              .replace("AsianStreetFood", "Asian Street Food")}
          </p>
        }
      })
    }
  }

  //   
  function getTimeText () {
    let timeList = [];
    let deliverTime, collectionTime;
    if (status.isDelivery && restaurantInfo.deliverTime) {
      deliverTime = restaurantInfo.deliverTime;
      timeList.push({
        type: 'Delivery',
        time: status.deliveryStatus ? deliverTime : 'closed',
      });
    }
    if (status.isCollection && restaurantInfo.collectionTime) {
      collectionTime = restaurantInfo.collectionTime;
      timeList.push({
        type: 'Collection',
        time: status.collectionStatus ? collectionTime : 'closed',
      });
    }
    return timeList;
  }



  const deliveryFee = restaurantInfo.deliveryFee;
  const minOrder = restaurantInfo.minOrder;

  function text1 () {
    if (restaurantInfo.lat && selectedAddress.lat) {
      const mile = caculateLL(restaurantInfo.lat, restaurantInfo.lon, selectedAddress.lat, selectedAddress.lng)
      return (mile / 1000).toFixed(2) + ' km'
    }
    return ''
  }

  function text2 () {
    let result = '';
    if (restaurantStatusLoading || restaurantInfoLoading) {
      return 'loading...';
    }
    if (status?.isDelivery) {
      if (deliveryFee === '0.00' || deliveryFee === 0) {
        result += ' | Free Delivery';
      } else {
        result += ' | Delivery fee ' + getCPrice(deliveryFee)
      }
      if (!minOrder && (minOrder === '0.00' || minOrder === 0)) {
        result += '  No minimum spend';
      } else {
        result += '  Min order ' + getCPrice(minOrder);
      }
    } else {
      result = ' | Delivery service is not available';
    }
    return result;
  }

  function onStoreInfoClick () {
    props.dispatch(action('shopDetail/save', { showStoreInfo: true }))
  }

  return <section className='shop_basic_info'>
    <section className='shop_basic_info_top'>
      <div className='shop_basic_info_top_left' style={restaurantInfo.logo && { backgroundImage: 'url(' + restaurantInfo.logo + ')' }} />
      <section className='shop_basic_info_top_right'>
        <div className='shop_basic_info_top_right_name'>
          <h1 className="two-line">{restaurantInfo.name}</h1>
          <Icon type='iconicon-into' onClick={onStoreInfoClick} />
        </div>
        <div className='tag_container'>
          {getCuisines()}
        </div>
      </section>
    </section>
    <div className='tag_container'>
      {getCuisines()}
    </div>
    <div className='shop_drive_line' />
    <DirectionC
      type='shop'
      restaurantInfo={restaurantInfo}
      className='shop_basic_direction_c' />
    <div className='shop_basic_info_buttom'>
      <ShopBasicInfoItem
        type='address'
        text1={text1()}
        text2={text2()} />
      {restaurantInfo?.couponList?.length > 0 && <ShopBasicInfoItem type='discount' text1={restaurantInfo.couponList} />}
      <ShopBasicInfoItem type='time' text3={getTimeText()} />
    </div>
  </section>

}


export default connect(({ shopDetail: { restaurantInfo, status, restaurantStatusLoading, restaurantInfoLoading }, user: { selectedAddress } }) => ({
  restaurantInfo, status, selectedAddress, restaurantStatusLoading, restaurantInfoLoading
}))(ShopBasicInfo);