import React, { useEffect, useMemo } from 'react';
import './indexPc.less';
import './index.less';
import { AdaptHeader, AutoTextarea, Icon, CloseIcon, Modal, LocationIcon } from '../../component';
import { connect, action, AppConfig, useMessage } from '../../utils';

const AddressModal = (props) => {

  const { isLogin, onClose, onAddressSelect, type, onBack, inputvalue, nameValue } = props;

  useEffect(() => {
    props.dispatch(action('address/getAllAddr'));
  }, []);

  //  RN 
  useMessage(onRNMessage)

  function onRNMessage (event) {
    if (event.data === AppConfig.hardwareBackPress) {
      onClose()
    }
  }

  function onBackPress () {
    onBack()

  }

  function onSavePress () {
    props.dispatch(action('address/getFullAddressFromInput', {
      input: undefined, callBack: (inputvalue, data) => {
        const ad = { address: inputvalue }
        if (data && data[0]) {
          const { geometry: { location: { lat, lng } } } = data[0];
          ad.lat = lat;
          ad.lng = lng;
        }
        onAddressSelect({ lat: ad.lat, lng: ad.lng, ad: inputvalue })
      }
    }))
  }

  const title = useMemo(() => {
    if (type === 'save') {
      return <AdaptHeader title='Address' right='save' onRightPress={onSavePress} onLeftPress={onBackPress} />
    } else {
      return <AdaptHeader title='Address' onLeftPress={onClose} />;
    }
  }, [])



  function onChange (e) {
    const value = e.target.value?.substring(0, 255);
    props.dispatch(action('address/save', { inputvalue: value }))
    props.dispatch(action('address/autocomplete', { input: value }))
  }

  function onChangeName (e) {
    const value = e.target.value?.substring(0, 50);
    props.dispatch(action('address/save', { nameValue: value }))
  }


  //  address 
  function onChooseAd (item, t) {
    return () => {
      switch (t) {
        case 1://  
          props.dispatch(action('address/getFullAddress', {
            input: item.description, callBack: (data) => {
              const { geometry: { location: { lat, lng } } } = data[0];
              props.dispatch(action('user/saveAddress', {
                lat: lat, lng: lng, ad: item.description, callBack: () => {
                  onAddressSelect({ lat: lat, lng: lng, ad: item.description })
                }
              })); // 
            }
          }))
          break;
        case 2:
          props.dispatch(action('user/saveAddress', {
            lat: item.lat, lng: item.lon, ad: item.address, callBack: () => {
              onAddressSelect({ lat: item.lat, lng: item.lon, ad: item.address, })
            }
          })); // 

          break;
      }
    }
  }

  function getAddress (geo) {
    props.getAddress && props.getAddress(geo)
  }

  function clearText () {
    props.dispatch(action('address/save', { inputvalue: '', predictions: [] }))
  }


  return <Modal className="address_page" title={title} >
    <section className='address_content'>
      {type === 'save' && <section className='address_input'>
        <AutoTextarea onChange={onChangeName} placeholder='home,work, etc' value={nameValue} maxLength={50} />
      </section>}
      <section className='address_input'>
        <AutoTextarea onChange={onChange} placeholder='Enter your address' value={inputvalue} maxLength={255} autoFocus />
        <CloseIcon onClick={clearText} text={inputvalue} />
        <LocationIcon returnGeo={getAddress} />
      </section>
      <section className='address_content_container'>
        <section className='predictions_container'>
          {props.predictions.map((item, index) => {
            return <div key={item.id} className='resent_address_item' onClick={onChooseAd(item, 1)}>
              <Icon type='iconicon_location_b' className='resent_address_item_icon'/>
              <p>{item.description}</p>
            </div>
          }
            // <p key={item.description} onClick={onChooseAd(item, 1)}>{item.description}</p>
          )}
        </section>

        {isLogin && <section className='resent_address_container'>
          <div className='or_select'>{props.allRensentAddress && props.allRensentAddress.length > 0 ?
            'Or, select a recent address' : 'You have no recent addresses.'}</div>
          {
            props.allRensentAddress?.map((item, index) => {
              if (index > 4) {
                return
              }
              return (
                <div key={item.id} className='resent_address_item' onClick={onChooseAd(item, 2)}>
                  <Icon type='iconicon_location_b' className='resent_address_item_icon'/>
                  <div>
                    <h5> {item.name}</h5>
                    <p> {item.address}</p>
                  </div>
                </div>
              )
            })
          }
        </section>}
      </section>
    </section>
  </Modal>
}

export default connect(({ address: { allRensentAddress, predictions, inputvalue, nameValue }, user: { isLogin } }) => ({
  allRensentAddress, predictions, isLogin, inputvalue, nameValue
}))(AddressModal);
