import React, { useMemo } from 'react';
import { inRN, h5ToRn, openWeb } from '../../utils'

const TelCon = (props) => {

  const { tel, className, callType = 'tel' } = props;

  const hrefprefix = useMemo(() => {
    switch (callType) {
      case 'tel':
        return "tel:"
      case 'mail':
        return "mailto:"

    }
  }, [])

  function onClick (event) {
    event.stopPropagation();
    if (inRN()) {
      switch (callType) {
        case 'tel':
          h5ToRn({ type: 3, phone: tel })
          break
        case 'mail':
          openWeb(`mailto:${tel}`);
          break
      }

    } else {
      window.location.href = hrefprefix + tel;
    }
  }

  return <div onClick={onClick} className={className} style={{cursor:'pointer'}}>
    {props.children}
  </div>
}

export default TelCon;