
import { http } from '../utils';
import { API } from '../config';


const sendCode = (query) => {
  return http.post(API.sendCode, query)
};

const login = (query) => {
  return http.post(API.login, query)
};

const logout = (query) => {
  return http.post(API.logout, query)
};

const deleteUser = (query) => {
  return http.get(API.deleteUser, query)
};


const smsStaff = (query) => {
  return http.post(API.smsStaff, query)
};


const saveUserDevice = (query) => {
  return http.post(API.saveUserDevice, query)
};

const saveDeviceToken = (query) => {
  return http.post(API.saveDeviceToken, query)
}

const updateUserName = (query) => {
  return http.post(API.updateUserName, query)
}

const getSession = (query)=>{
  return http.get(API.getSession, query)
}

export default {
  sendCode, login, logout, smsStaff, saveUserDevice, deleteUser, saveDeviceToken, 
  updateUserName,getSession
}
