import React, { useEffect, } from 'react';

import './index.less';
import './indexPc.less';

import { Icon } from '../../../../component';
import { AppConfig, connect, inRN } from '../../../../utils';



const ReviewPaymentChoose = (props) => {
  const { onPayChoose, restaurantInfo } = props;

  function onPay (type, cardType) {
    return () => {
      onPayChoose && onPayChoose(type, cardType)
    }
  }

  return <section className='review_payment_choose'>
    {restaurantInfo?.isCardPay && <div className='review_payment_choose_item' onClick={onPay("card")}>
      <label htmlFor="card" >Pay with card</label>
      <Icon type='iconicon_more_A1' />
      <div className='card_container'>
        <img src={require('./icon_visa_A21.png')} />
        <img src={require('./icon_master_A21.png')} />
      </div>
    </div>}

    {restaurantInfo?.isCashPay && <div className='review_payment_choose_item' onClick={onPay("cash")}>
      <label htmlFor="cash" >Pay with cash</label>
      <Icon type='iconicon_more_A1' />
    </div>}

    {!inRN() && restaurantInfo?.isCardPay && <div className='review_payment_choose_item'
      onClick={onPay("card", AppConfig.REVOLUT)}
    >
      <label htmlFor="card" >Pay with revolut</label>
      <Icon type='iconicon_more_A1' />
    </div>}

  </section>
}



export default connect(({ shopDetail: { restaurantInfo } }) => ({
  restaurantInfo
}))(ReviewPaymentChoose)
