import { action, cookie, delay, AppConfig, moment } from '../utils';
import { tableS } from '../service';
import { message } from '../component';

const initState = {

  bookingConfig: {},

  showDatePicker: false,

  shopItem: null,

  name: '', // booking 
  email: '',
  mobile: '',
  remark: '',// 

  showsubmit: false,
  submitLoading: false,

  // result 
  detail: {},
  rejectReason: '',// 

  detailLoading: false,
};

export default {
  namespace: 'booking',
  state: initState,
  reducers: {
    save (state, { payload }) {
      return { ...state, ...payload };
    },
  },
  effects: {



    *getBookingConfig ({ payload: { from } }, { call, put, select }) {
      try {
        const params = {
          domain: AppConfig.WEB_SITE,
        }

        const res = yield call(tableS.getBookingConfig, params)
        if (res) {
          if (from === 'single') {
            const item = res.bookingInfos[0]
            item.bookingDate = moment.format(new Date());
            const index = new Date().getDay();
            item.selectBookingDayTime = item.bookingDayTime[index === 0 ? 7 : index];
            item.bookingTime = null;
            yield put(action('save', { bookingConfig: res, shopItem: item }))
          } else {
            yield put(action('save', { bookingConfig: res }))
          }
        }

      } catch (e) {
        console.error("booking/getBookingConfig", e);
      }
    },

    *acceptBooking ({ payload: { id } }, { call, put }) {
      try {
        const res = yield call(tableS.acceptBooking, { id: id })
        if (res) {
          message.base('Table Booking Confirmed.')
          yield put(action('getBookingDetails', { id: id }))
        } else {
          message.base('something wrong with your accept booking.')
        }
      } catch (e) {
        console.error("booking/acceptBooking", e);
      }
    },

    *rejectBooking ({ payload: { id } }, { put, call, select }) {
      try {
        const { rejectReason } = yield select(({ booking }) => booking)
        const res = yield call(tableS.rejectBooking, { id: id, rejectReason: rejectReason })
        if (res) {
          message.base('Table Booking Rejected.')

          yield put(action('getBookingDetails', { id: id }))
        } else {
          message.base('something wrong with your reject booking.')
        }
      } catch (e) {
        console.error("rejectBooking", e);
      }
    },

    *fillUserInfo ({ payload }, { put, select }) {
      try {
        const { user } = yield select(({ user }) => user);
        yield put(action('save', {
          name: user?.principal?.user?.userName || '',
          mobile: user?.principal?.user?.phone || '',
        }))

      } catch (e) {
        console.error("fillUserInfo", e);
      }
    },

    onBook: [
      function* ({ payload }, { call, put, select }) {
        try {
          yield call(delay, 300);
          yield put(action('save', { submitLoading: true }))

          const { shopItem, name, email, mobile, remark, } = yield select(({ booking }) => booking);
          if (!shopItem?.shopId) {
            message.base('Please select store first.')
            return
          }
          if (!shopItem?.bookingDate) {
            message.base('Please select date.')
            return
          }
          if (!shopItem?.bookingTime) {
            message.base('Please select time.')
            return
          }
          if (!shopItem?.bookingPeople) {
            message.base('Please select number of people .')
            return
          }

          const current = new Date();
          const selectTime = new Date(`${shopItem.bookingDate} ${shopItem.bookingTime}:00`)
          if (current.getTime() > selectTime.getTime()) {
            message.base('Your selected time is invalid.')
            return
          }

          const res = yield call(tableS.submitBooking, {
            domain: AppConfig.WEB_SITE,
            shopId: shopItem.shopId,
            bookingDate: shopItem.bookingDate + ' 00:00:01', //
            bookingTime: shopItem.bookingTime,
            bookingPeople: shopItem.bookingPeople,
            bookingName: name,
            bookingEmail: email,
            bookingPhone: mobile,
            bookingRequest: remark,
          })

          if (res?.id) { //6267b9caf76b25076921a519
            yield put(action('save', { showsubmit: true }))
          }
        } catch (e) {
          console.error('onBook', e);
        } finally {
          yield put(action('save', { submitLoading: false }))
        }
      }, { type: 'takeLatest' }
    ],

    *getBookingDetails ({ payload: { id } }, { call, put }) {
      try {
        yield put(action('save', { detailLoading: true }))

        const res = yield call(tableS.getBookingDetails, { id: id })
        if (res) {
          yield put(action('save', { detail: res }))
        }

      } catch (e) {
        console.error("booking/getBookingDetails", e);
      } finally {
        yield put(action('save', { detailLoading: false }))
      }
    },

    *clear ({ payload }, { call, put }) {
      try {
        yield put(action('save', { ...initState }))
      } catch (e) {
        console.error("booking/clear", e);
      }
    }
  },
};