import React, {
  useEffect, useMemo, useRef, useState,
} from 'react';
import PropTypes from 'prop-types';
import './index.less';
import PreImg from '../preImg';

const Swiper = (props) => {
  const { images } = props;
  const [show, setShow] = useState(0);

  const timer = useRef();
  const reserve = useRef(false);

  const count = useMemo(() => {
    return images.length
  }, [images])

  useEffect(() => {
    timer.current = setTimeout(() => {
      if (count > 1) {
        if (reserve.current) {
          setShow(show - 1);
          if (show === 1) {
            reserve.current = false
          }
        } else {
          setShow(show + 1);
          if ((show + 2) >= count) {
            reserve.current = true
          }
        }
      }
    }, 5000)
    return () => {
      clearTimeout(timer.current);
    };
  }, [show, images]);




  const getItems = () => images.map((image, index) => {
    const { url } = image;

    const alt = image?.imageText || `banner-${index}`
    return <li key={index}
      style={{
        transform: `translate3d(${(index - show) * 100}%,0,0)`,
      }}
    >
      <PreImg src={url} alt={alt} />
    </li>

  });


  return (
    <div className="widget_swiper">
      <ul>
        {getItems()}
      </ul>
    </div>
  );
};

Swiper.propTypes = {
  images: PropTypes.arrayOf(
    PropTypes.object
  ).isRequired,
};

export default Swiper;
