import React, { useMemo } from 'react';
import { Icon, TelCon } from '../../../component';

import './index.less';
import './indexPc.less';

import { connect } from '../../../utils';

const FoodAllergy = (props) => {
  const { restaurantInfo, restaurantInfoLoading } = props;

  const phone = useMemo(() => {
    if (restaurantInfoLoading) {
      return 'loading ...'
    }
    if (restaurantInfo?.phone) {
      return restaurantInfo?.phone
    }
    return null
  }, [restaurantInfoLoading, restaurantInfo])

  return <TelCon tel={restaurantInfo.phone}>
    <section className='food_allergy_container'>
      <div>
        <Icon type='iconicon_guomin' />
        Food allergy? Call the restaurant.
      </div>
      <div>
        <Icon type='iconicon_phone_A6' />
        {phone}
      </div>

    </section>
  </TelCon>

}

export default connect(({ shopDetail: { restaurantInfo, restaurantInfoLoading } }) => ({
  restaurantInfo, restaurantInfoLoading
}))(FoodAllergy);