import React, { useMemo } from 'react';
import { hex2Rgba } from '../../../../utils';
import './index.less';

const ShopCategoryItem = (props) => {

  const { content, onCategoryItemPress, selectedMenu } = props;

  function onItemPress (item) {
    return () => {
      onCategoryItemPress(item)
    }
  }

  const style = useMemo(() => {
    const color = getComputedStyle(document.body).getPropertyValue('--themeColor').trim();
    const themeColor = hex2Rgba(color, .2);
    return themeColor
  }, [])

  function renderContent () {
    if (content?.dishTypes?.length > 0) {

      return content.dishTypes.map(item => {
        const active = selectedMenu?.id === item.id && selectedMenu?.menuId === item.menuId

        return <div key={item.id} onClick={onItemPress(item)}

          className={`shop_category_item_des border-top`}>
          {item.name}
          <p className='one-line'>{item.description}</p>
        </div>
      })
    }
  }

  return <div
    className='shop_category_item_content'
  >
    {renderContent()}
  </div>

}

export default ShopCategoryItem;