import React from 'react';

import './index.less';
import './indexPc.less';

import DishPizzaItem from '../dishPizzaItem'

const DishPizza = (props) => {

  //  showKeys  index 
  const { pizza, onChooseDish, totolPrice, categoryId, customiz, showCustomiz, sub, 
    isHalfPizza, from, showKeys, finishChoose, sizePrice = 0, changeCurrentSide,onToogleShow } = props;



  // pid   immutable 
  // subId   immutable 
  // function onChoose (id, partId,pid,subId) {
  //   onChooseDish(id, partId,pid,subId)
  // }

  function onChoose (id, partId, pid, subId, type) {
    onChooseDish(id, partId, pid, subId, type)
  }

  function toCustomize () {
    props.toCustomize && props.toCustomize(true)
  }

  function toogleChangeHalf (checked) {
    props.toogleChangeHalf && props.toogleChangeHalf(checked);
  }

  function onToogle(index, v){
    onToogleShow(index, categoryId, v)
  }


  function renderItem (itemKey, index) {
    return pizza[itemKey] &&
      <DishPizzaItem
        sizePrice={sizePrice}
        finishChoose={finishChoose}
        from={from}
        isHalfPizza={isHalfPizza}
        showCustomiz={showCustomiz}
        sub={sub}
        key={itemKey}
        pizza={pizza}
        name={pizza?.pizzaNames[index]?.name}
        show = {pizza?.pizzaNames[index]?.show}
        itemKey={itemKey}
        index={index}
        categoryId={categoryId}
        totolPrice={totolPrice}
        toogleChangeHalf={toogleChangeHalf} //   half
        onToogleShow={(v)=>onToogle(index, v)}
        changeCurrentSide={changeCurrentSide}
        customiz={customiz} //  
        onChooseDish={(id, partId, pid, subId, type) => onChoose(id, partId, pid, subId, type)}
      />
  }


  function renderSize () {
    return pizza?.pizzaKeys.map((itemKey, index) => {
      if (itemKey === 'sizes') {
        if (sub) {
          return null
        } else {
          return renderItem(itemKey, index)
        }
      } else if (customiz && (showKeys.includes(itemKey)) || from === 'order') {
        return renderItem(itemKey, index)
      }
    }
    )
  }

  return (
    <div className='dish_pizza_container' >
      {renderSize()}
      {showCustomiz && <section className='dish_pizza_container_start'>
        <img src={require('./pizza.png')} />
        <button onClick={toCustomize}>Customize your pizza</button>
      </section>}
    </div>
  )
}

export default DishPizza;