import React, { useEffect, useMemo } from 'react';
import { AdaptHeader, Loader } from '../../component';
import { connect, getValueFromQuery } from '../../utils';
import './index.less';
import './indexPc.less';

const NotFoundPage = (props) => {

  const { loading404 } = props;

  const name = getValueFromQuery('name', props.location.search);

  useEffect(() => {
    let pathname = props.location.pathname;
    if (pathname === '/notFoundPage') {
      pathname = name;
    }
    if (pathname.indexOf('category/holiday') >= 0) {
      props.history.replace('/');
    } else if (pathname.indexOf('/Contact') >= 0) {
      props.history.replace('/help');
    } else if (pathname.indexOf('/contact-us') >= 0) {
      props.history.replace('/help');
    } else if (pathname.indexOf('/Term') >= 0) {
      props.history.replace('/linkPage?page=terms');
    } else if (pathname.indexOf('/Privacy') >= 0) {
      props.history.replace('/linkPage?page=privacy');
    } else {
      props.history.replace('/');
    }
  }, [props.location.pathname, name]);

  function onClose () {
    props.history.replace('/')
  }

  if (loading404) {
    return <Loader loading type='tran' />
  } else {
    return <div className='not_found_page_wrapper'>
      <AdaptHeader title='' left='Home' onLeftPress={onClose} />
      <div className='not_found_page'>
        <p>404</p>
      </div>
    </div>

  }
}

export default connect(({ webConfigModal: { loading404, } }) => ({
  loading404
}))(NotFoundPage);