import React, { useState } from 'react';
import './index.less';
import './indexPc.less';

import { connect, action } from '../../../../../utils';
import { AutoTextarea, CloseIcon, Icon } from '../../../../../component';

const DishNote = (props) => {
  const { detail, modelId, onReset } = props;
  const length = (detail?.note?.length || 0);

  const categoryId = detail.categoryId;

  const [showNote, setShowNote] = useState(length > 0);


  // 
  function onChange (v) {
    const value = v.target.value?.substring(0, 50)
    props.dispatch(action('cart/changeNote', { modelId: modelId + '', params: { note: value } }))
  }

  function onNotePress () {
    setShowNote(true)
  }

  function clearText () {
    setShowNote(false)
    props.dispatch(action('cart/changeNote', { modelId: modelId + '', params: { note: '' } }))
  }

  return <div className='shop_dish_detail_note'>
    <div className='shop_dish_detail_note_wrapper'>
      {!showNote ? <div className='shop_dish_detail_note_edit_container' onClick={onNotePress}>
        <Icon type='iconicon_add_notice' />
        <span>Notes for chef</span>
      </div> : <div className='leave_message'>
        <AutoTextarea
          autoFocus={true}
          placeholder='Notes for the chef...'
          maxLength={50}
          value={detail.note || ''}
          onChange={onChange} />
        <CloseIcon onClick={clearText} text={true} />
      </div>}

      {
        categoryId === 8 && <div onClick={onReset} className='shop_dish_detail_note_reset'>
          <p>Reset</p>
        </div>
      }
    </div>


    {length === 50 && <div className='remaining_text'>Maximum 50 Characters</div>}
  </div>
}


export default connect()(DishNote)