import React, { useState } from 'react';

import './index.less';
import './indexPc.less';

import { Icon } from '../../../../../component'

const ShopCounter = (props) => {

  const { className, onChange, sub, count } = props;

  const [num, setNum] = useState(count || 1);

  function changeNum (type) {
    return () => {
      let n = num;
      if (type === 'sub') {
        if (num !== 1) {
          n = num - 1
          setNum(n)
        }
      } else if (type === 'add') {
        n = num + 1
        setNum(n)
      }
      onChange(n);
    }
  }

  return sub ? <div className={`shop_counter_detail ${className}`}></div> : (
    <div className={`shop_counter_detail ${className}`}>
      <Icon type='iconicon_reduce' onClick={changeNum('sub')} />
      <span className='shop_counter_num'>{num}</span>
      <Icon type='iconicon_add' onClick={changeNum('add')} />
    </div>
  )
}

export default ShopCounter;