import React, { useEffect } from 'react';
import PropTypes from 'prop-types';

import './index.less';
import './indexPc.less';
import Icon from '../icon';
import { getStatusBarHeight, h5ToRn, stopScroll, stayScroll } from '../../utils'

const Modal = (props) => {

  useEffect(() => {
    h5ToRn({ type: 2, statusBar: 'dark-content' });
    return () => { };
  }, [])

  useEffect(() => {
    stopScroll();
    return () => stayScroll();
  }, [])

  const { children, className, onClose, title, paddingTop = true } = props;
  return (
    <section className="com_modal">
      <div className="mask"></div>
      <div className={`d_modal  ${className}`} style={{ paddingTop: paddingTop ? getStatusBarHeight() : 0 }}>
        {onClose && <Icon className='d_close' type='iconicon_close' onClick={onClose} style={{ top: getStatusBarHeight() }} />}
        {typeof title === 'string' ? <h5 className='d_title'>{title}</h5> : title}
        {children}
      </div>

    </section>
  );
};

export default Modal;

Modal.defaultProps = {
  title: '',
}
Modal.propTypes = {
  onClose: PropTypes.func,
  title: PropTypes.any,
}
