import React, { useMemo } from 'react';
import { Notice } from '../../../component';

import './index.less';
import './indexPc.less';

import { connect } from '../../../utils';

const ShopDishDes = (props) => {
  const { dishMenu, selectTab } = props;

  const selectedDishMenu = useMemo(() => {
    if (selectTab && dishMenu?.length > 0) {
      return dishMenu?.filter(item => item.name === selectTab)[0] || null
    }
    return null
  }, [selectTab])

  return selectedDishMenu ? <Notice text={selectedDishMenu?.desc} /> : null

}

export default connect(({ shopDetail: { dishMenu, } }) => ({
  dishMenu,
}))(ShopDishDes);