import { action, AppConfig, delay } from '../utils';
import { homeS } from '../service'

//  landing menu pending 
const initState = {
  landing: {},
  errorCount: 0,
  lastErrorTime: 0,
  loading404: false,
  isNewCustomer: 0, // 0 : 1

};

export default {
  namespace: 'webConfigModal',
  state: initState,
  reducers: {
    save (state, { payload }) {
      return { ...state, ...payload };
    },
  },
  effects: {
    // home 
    * init ({ payload: { callBack } }, { call, put, select, take }) {
      try {
        const { selectedAddress } = yield select(({ user }) => user);
        const isNewCustomer = !selectedAddress.ad ? 1 : 0;

        yield put(action('save', { isNewCustomer: isNewCustomer }))

        const params = {
          domain: AppConfig.WEB_SITE,
          unionId: AppConfig.UNION_ID,
        }
        const landingRes = yield call(homeS.getWebPageConfig, params);

        if (!landingRes) {
          console.error(' landingRes ')
          return
        }
        yield put(action('save', {
          landing: landingRes,
        }));

        if (landingRes?.webSiteType) {

          switch (landingRes.webSiteType) { // 1:easydish, 2:union, 3:store
            case 1:
              break;
            case 2:
              const shopList = yield call(homeS.getBriefListByUnionId, { unionId: landingRes.unionId });
              yield put(action('home/save', { shopList: shopList || [] }))
              yield put(action('home/fillUserInfo'))

              break;
            case 3:
              if (!landingRes?.shopId) {
                console.error(' shopId', landingRes)
              }
              yield put(action('home/save', {
                feedbackShopId: landingRes.shopId,
              }));
              yield put(action('home/getRestaurantInfo', { rId: landingRes.shopId, }))
              yield take('home/getRestaurantInfo/@@end');
              const { restaurantInfo } = yield select(({ home }) => home);

              yield put(action('home/save', {
                shopList: [{
                  address: restaurantInfo.address,
                  id: restaurantInfo.id,
                  lat: restaurantInfo.lat,
                  lon: restaurantInfo.lon,
                  name: restaurantInfo.name,
                  phone: restaurantInfo.phone,
                }]
              }))

              yield put(action('home/fillUserInfo'))
              break;
          }

          callBack && callBack();
        } else {
          console.error(' webSiteType', landingRes)
        }
      } catch (e) {
        console.error("init", e);
      }
    },

    // loading 
    *getWebPageConfig ({ payload: { callBack } }, { call, put, select }) {
      try {
        const params = {
          domain: AppConfig.WEB_SITE,
        }
        const landingRes = yield call(homeS.getWebPageConfig, params);
        if (landingRes) {

          yield put(action('save', {
            landing: landingRes,
          }));
        }

        callBack && callBack();
      } catch (e) {
        console.error('getWebPageConfig', e);
      }
    },

    // 404  shopName
    *getShopIdByName ({ payload: { name, callBack } }, { call, put, select, take }) {
      try {
        const { landing } = yield select(({ webConfigModal }) => webConfigModal);
        if (!landing?.webSiteType) {
          yield put(action('save', { loading404: true }));
          yield put(action('getWebPageConfig', {}))
          yield take('getWebPageConfig/@@end');
        }
        const res = yield call(homeS.getShopIdByName, { name: name })
        if (res?.shopId) {
          yield put(action('orderList/save', { rId: res.shopId, rName: res.shopName }));

          //urlType: 1StoreInfo, 2StoreMenu
          callBack && callBack(res.shopId, res.urlType)
        } else {
          callBack && callBack(0, 0)
        }
      } catch (e) {
        console.error('getShopIdByName', e);
      } finally {
        yield put(action('save', { loading404: false }));

      }
    },

    *initPage ({ payload: { callBack } }, { call, put, select }) {
      try {
        const { landing } = yield select(({ webConfigModal }) => webConfigModal);
        if (landing?.unionId) {
          callBack && callBack()
        } else {
          yield put(action('getWebPageConfig', {
            callBack: () => callBack && callBack()
          }
          ));
        }

      } catch (e) {
        console.error('initPage', e);
      } finally {
      }
    },

    //   
    *clearChunkLoadError ({ payload }, { call, put, select }) {
      try {
        const { errorCount, lastErrorTime } = yield select(({ webConfigModal }) => webConfigModal);
        const diffTime = new Date().getTime() - lastErrorTime;
        if (errorCount > AppConfig.loadChunkErrorCount && ((diffTime / 1000 / 60 / 60 / 24) > 1)) {
          yield put(action('save', {
            errorCount: 0,
            lastErrorTime: 0
          }))
        }
      } catch (e) {
        console.error('loadStorage', e);
      }
    },

    onChunkLoadError: [
      function* ({ payload }, { call, put, select }) {
        try {
          const { errorCount } = yield select(({ webConfigModal }) => webConfigModal);
          if (errorCount > AppConfig.loadChunkErrorCount) {
            return
          }
          yield call(delay, 3000);
          yield put(action('save', {
            errorCount: errorCount + 1,
            lastErrorTime: new Date().getTime()
          }))
          window.location.reload();
        } catch (e) {
          console.error("autocomplete", e);
        }
      },
      { type: 'takeLatest' }
    ],
  },

};