import React, { useEffect, useState, useRef } from 'react';

const TimeCounter = (props) => {
  const [count, setCount] = useState(60);
  const timer = useRef();

  useEffect(() => {
    if (count > 0) {
      timer.current = setTimeout(() => {
        setCount(count - 1);
      }, 1000)
    } else {
      timer.current && clearTimeout(timer.current);
    }
    return ()=>timer.current && clearTimeout(timer.current)
  }, 
  [count])

  function resend(){
    setCount(60);
    props.onClick();
  }

  return <div className="login_code_text">
    {count > 0 ?
      <span >{`If you do not receive the code, you can resend it in ${count}s`}</span> :
      <h4 onClick={resend}>Resend login code</h4>}
  </div>
}

export default TimeCounter;