import { action, checkPhoneNumber, checkEmail, AppConfig, inRN } from '../utils';
import { homeS, shopDetailS } from '../service'
import { message, ModalAlert } from '../component';

const initState = {
  pending: null,
  showAddress: false,// 

  showFeedback: false,//  feedback
  feedbackName: '',
  feedbackPhone: '',
  feedbackEmail: '',
  feedbackText: '',
  feedbackShopId: '',
  feedbackShopName: '',
  shopList: [], // 

  restaurantInfo: '',

  menuPaper: undefined,// 
  selectedHeader: 'Home',
  showDown: true,// home down

  activity: {
    show: false,
    title: '',
    content: '',
    showdownload: false,
    showsocial: false,
    showlogin: false,
  },// 

  appDownload: {
    visiable: false,
    couponCode: 'EASYDISH05',
  }
};

export default {
  namespace: 'home',
  state: initState,
  reducers: {
    save (state, { payload }) {
      return { ...state, ...payload };
    },
  },
  effects: {

    //  
    *pendingCheck ({ payload }, { call, put, select }) {
      try {
        const { isLogin } = yield select(({ user }) => user);

        if (isLogin) {
          const { landing } = yield select(({ webConfigModal }) => webConfigModal);
          const params = { pend: 0 };
          if (landing.webSiteType === 3) {
            params.shopId = landing.shopId;
          }
          const pendingRes = yield call(homeS.pendingCheck, params);
          yield put(action('save', {
            pending: typeof pendingRes === 'number' ? pendingRes : 0,
          }));
        }
      } catch (e) {
        console.error('pendingCheck', e);
      }
    },



    //    
    * getRestaurantInfo ({ payload: { rId } }, { call, put, select }) {
      try {
        const res = yield call(shopDetailS.getRestaurantInfo, { rId: rId });
        yield put(action('save', {
          restaurantInfo: res || {},
        }));
      } catch (e) {
        console.error('getRestaurantInfo', e);
      }
    },

    // 
    * getMenuPaper ({ payload: { rId } }, { call, put, select }) {
      try {
        const res = yield call(homeS.getMenuPaper, { shopId: rId });
        yield put(action('save', {
          menuPaper: res || [],
        }));
      } catch (e) {
        console.error('getMenuPaper', e);
      }
    },



    *fillUserInfo ({ payload }, { call, put, select }) {
      try {
        const { user } = yield select(({ user }) => user);
        yield put(action('save', {
          feedbackName: user?.principal?.user?.userName || '',
          feedbackPhone: user?.principal?.user?.phone || '',
        }))

      } catch (e) {
        console.error("fillUserInfo", e);
      }
    },

    // 
    *displayActivity ({ payload }, { call, put, select }) {

      try {
        const current = Date.now();
        const startDate = new Date('2022/12/14').getTime();
        // const endDate = new Date('2022/12/15').getTime();
        const endDate = new Date('2023/1/3').getTime();

        if (current > endDate) {
          return
        }

        const { landing } = yield select(({ webConfigModal }) => webConfigModal);
        if (!['test.easydish.ie', 'localhost:2000', 'rebuildtest.easydish.com', 'www.easydish.ie'].includes(window.location.host)) {
          return
        }



        if (current > startDate && current < endDate) {
          // if (true) {

          if (!inRN()) {
            yield put(action('save', {
              activity: {
                show: true,
                title: 'WIN 10 GIFT VOUCHER',
                content: `  Simply download and register for the Easy Dish app to participate. \n  We will randomly select 50 winners on the 3rd of Jan, 2023.`,
                showdownload: true,
              }
            }))
          } else {
            // const { isLogin } = yield select(({ user }) => user);
            // const { isNewCustomer } = yield select(({ webConfigModal }) => webConfigModal);

            // if (isNewCustomer === 1 // 
            //   && landing.webSiteType === 1
            //   && !isLogin) {
            //   yield put(action('save', {
            //     activity: {
            //       show: true,
            //       title: 'WIN 10 GIFT VOUCHER',
            //       content: `  Simply download and register for the Easy Dish app to participate. \n  We will randomly select 50 winners on the 3rd of Jan, 2023.`,
            //       showsocial: true,
            //       showlogin: true,
            //     }
            //   }))
            // }

          }
        }
        // else if (current > endDate) {
        //   yield put(action('save', {
        //     activity: {
        //       show: true,
        //       title: `See if You've Won 100 Gift vouchers`,
        //       content: `We will randomly select 50 winners on the 3rd of Jan, 2023 from new registers of the Easy Dish App in the Easter Campaign!
        //       Follow our social media @EasyDishIreland to See if You've won.`,
        //       showsocial: true,
        //     }
        //   }))
        // }
      } catch (e) {
        console.error('displayActivity', e);
      }
    },


    *showAppDownload ({ payload }, { put, select }) {
      const { landing } = yield select(({ webConfigModal }) => webConfigModal);

      if (new Date().getTime() > (new Date(landing?.popupEndTime?.replaceAll('-', '/')).getTime() + 24 * 60 * 60 * 1000)) {
        return
      }

      if (new Date() < new Date(landing?.popupStartTime?.replaceAll('-', '/'))) {
        return
      }

      yield put(action('save', {
        appDownload: {
          visiable: landing?.popupOpen === "1",
          couponCode: landing?.popupCouponCode,
          couponValue: landing?.popupCouponValue,
          headerColor: landing?.popupHeaderColor,
          textBackColor: landing?.popupTextBackColor,
          logoUrl: landing?.popupLogoUrl,
          condition: landing?.popupCondition,
          installUrl: landing?.popupInstallUrl,
          title: landing?.popupTitle,
        }
      }))
    },

    *displayActivityResult ({ payload }, { call, put, select }) {
      try {
        const { isNewCustomer } = yield select(({ webConfigModal }) => webConfigModal);

        if (isNewCustomer === 1 && inRN()) {
          yield put(action('save', {
            activity: {
              show: true,
              title: 'CONGRATULATIONS!',
              content: `You have successfully joined the Easter Campaign to win a 100 gift voucher!`,
              showsocial: false,
              showlogin: false,
            }
          }))
        }
      } catch (e) {
        console.error('displayActivityResult', e);
      }
    },



    //    
    *feedback ({ payload }, { call, put, select }) {
      try {
        const { feedbackName, feedbackPhone, feedbackEmail, feedbackText, feedbackShopId } = yield select(({ home }) => home);
        if (!feedbackName) {
          message.base('please tap your name')
          return
        }
        if (!feedbackPhone) {
          message.base('please tap your phone')
          return
        } else {
          if (!checkPhoneNumber(feedbackPhone)) {
            message.base('please tap right phone')
            return
          }
        }
        if (!feedbackEmail) {
          message.base('please tap your email')
          return
        } else {
          if (!checkEmail(feedbackEmail)) {
            message.base('please tap right email')
            return
          }
        }
        if (!feedbackText) {
          message.base('please tap your feedback')
          return
        }
        if (!feedbackShopId) {
          message.base('please select store')
          return
        }

        const feedbackRes = yield call(homeS.feedback, {
          "content": feedbackText,
          "email": feedbackEmail,
          "name": feedbackName,
          "phone": feedbackPhone,
          "shopId": feedbackShopId,
          domain: AppConfig.WEB_SITE,
        });

        yield put(action('save', { showFeedback: true, feedbackText: '' }))

      } catch (e) {
        console.error("feedback", e);
      }
    },

  },

};