import React, { useState, } from 'react';

import './index.less';
import './indexPc.less';

import { action, connect } from '../../../utils'
import { AutoTextarea } from '../../../component';


const ReviewPaymentNote = (props) => {
  const { note } = props;

  function onNoteChange (e) {
    props.dispatch(action('myDetail/save', { note: e.target.value?.substring(0, 300) }))
  }

  return <section className='review_payment_note'>
    <h5>Notes for the store</h5>
    <AutoTextarea
          value={note}
          onChange={onNoteChange}
          placeholder='e.g extra onion or instructions for the driver.'
          maxLength={300} />
    {note?.length === 300 && <div className='remaining_text'>Maximum 300 Characters</div>}

  </section>

}

export default connect(({ myDetail: { note } }) => ({
  note
}))(ReviewPaymentNote)