
import { http } from '../utils';
import { API } from '../config';


const getBookingDetails = (query) => {
  return http.get(API.getBookingDetails, query)
};

const acceptBooking = (query) => {
  return http.post(API.acceptBooking, query)
}

const rejectBooking = (query) => {
  return http.post(API.rejectBooking, query)
}

const submitBooking = (query) => {
  return http.post(API.submitBooking, query)
}

const getBookingConfig = (query) => {
  return http.get(API.getBookingConfig, query)
}



export default {
  getBookingDetails, acceptBooking, rejectBooking, submitBooking,getBookingConfig
}
