import React, { useEffect, useMemo } from 'react';
import { ModalNotification, MyFooter, MyHeader, PcFooter, PcHeader } from '../component';
import AppDownload from '../page/home/components/appDownload';
import { connect, usePrevious, h5ToRn, getValueFromQuery, AppConfig, action, cookie, isPc, toJSON, inRN, stayScroll, useMessage } from '../utils'

import { changeHtmlHeader, addGoogleTag, changeHtmlHeaderTitle } from '../utils/loadJS';

import ActivityModal from '../page/home/components/activiyModal'


const BaseLayout = (props) => {

  const { landing, restaurantInfo } = props;


  const fromPlat = getValueFromQuery('fromPlat', props.location.search);
  const SESSION = getValueFromQuery('SESSION', props.location.search);

  //  RNApp
  useEffect(() => {
    if (SESSION) {
      cookie.set('SESSION', SESSION)
    }
    if (fromPlat === 'RN' && !landing?.webSiteType) {
      props.dispatch(action('webConfigModal/getWebPageConfig', {}))
    }
  }, [])

  const proviousPahtname = usePrevious(props.location.pathname)

  // 
  useEffect(() => {
    const path = props.location.pathname;
    if (proviousPahtname !== props.location.pathname) {
      switch (path) {
        case '/':
        case '/home':
        case '/menu':
          h5ToRn({ type: 2, statusBar: 'light-content' });
          break;
        case '/shopList':
          if (landing.webSiteType === 2) {
            h5ToRn({ type: 2, statusBar: 'light-content' });
          } else {
            h5ToRn({ type: 2, statusBar: 'dark-content' });
          }
          break
        default:
          h5ToRn({ type: 2, statusBar: 'dark-content' });

          break;
      }

      // 
      h5ToRn({ type: 8, url: path });

    }
  }, [props.location.pathname])

  //  header
  useEffect(() => {
    const path = props.location.pathname;

    switch (path) {
      case '/':
      case '/home':
      case '/menu':
      case '/shopList':
      case '/myDetail':
        if (landing.webSiteSeo) {
          const json = toJSON(landing.webSiteSeo);
          if (landing.webSiteType === 3 && !json.title && restaurantInfo?.name) {
            const headerTitle = restaurantInfo?.name + (restaurantInfo?.couponList?.length > 0 ? ` | ${restaurantInfo?.couponList.join(',')}` : '')
            changeHtmlHeaderTitle(headerTitle);
          } else if (landing.webSiteType === 1 || landing.webSiteType === 2) {
            changeHtmlHeader(json, landing.logoUrl);
          } else if (landing.webSiteType === 3 && json.title) {
            changeHtmlHeader(json, landing.logoUrl);
          }
        }
        break;
      default:
        break;
    }
  }, [props.location.pathname, restaurantInfo, landing.webSiteSeo])

  //  
  function changeThemeColor () {
    if (landing.webSiteType) {
      document.body.style.setProperty('--themeColor', landing.webSiteThemeColor);
      document.body.style.setProperty('--themeBgColor', landing.webSiteThemeBgColor);
      document.body.style.setProperty('--themeDownloadBgColor', landing.webSiteDownloadBgColor);
    }
  }
  useEffect(() => {
    if (landing?.websiteTheme) {
      changeThemeColor()
    }
  }, [landing])

  //   googleTag
  useEffect(() => {
    setTimeout(() => {
      addGoogleTag(landing.webSiteGoogleTagId)
    }, 0);

  }, [landing.webSiteGoogleTagId])

  // 
  const header = useMemo(() => {
    const path = props.location.pathname;
    switch (landing?.webSiteType) {
      case 1:
        return <PcHeader />
      case 2:
      case 3:
        if (path.toString().toLowerCase() === '/help')
          return <PcHeader />
        else
          return <MyHeader />
      case 0:
      default:
        return null
    }
  }, [landing?.webSiteType])


  //   notification RN 
  useMessage(onRNMessage)
  function onRNMessage (event) {
    // 
    // props.dispatch(action('user/saveDeviceToken', { fcmToken: 'eM46WcWCp0Cdk53z2JQLVy:APA91bFgE0Y3lyCvJHiYbi6IMS5zjtyX7TXcga2aoY5Uv5n9FInjHBDDywW0y-iN6JRh6uPRsHBBsOeRDXLXZpt9nweDpJomYDd83uuc0KkAr8VNkxcHLNWB6to7-ksyWc94WvEb3iu3' }));

    // const remoteMessage = { data: { "btntext": "test", "page": "/historyOrderDetailDirect", "url": "/o/r?m=1340" }, "from": "542586976614", "messageId": "1682053426697831", "mutableContent": true, "notification": { "body": "test", "title": "test" }, "sentTime": "1682053265", "type": "inApp" }



    // // // 
    // ModalNotification({
    //   title: 'test',
    //   msg: 'test',
    //   btntext: 'test',
    //   click: () => {
    //     openNotification(remoteMessage)
    //   }
    // })

    if (!inRN()) {
      return
    }
    const data = toJSON(event.data);
    if (data?.signal === 'notification') {
      const remoteMessage = data.remoteMessage;
      ModalNotification({
        title: remoteMessage.notification.title,
        msg: remoteMessage.notification.body,
        click: () => {
          openNotification(remoteMessage)
        }
      })
    } else if (data?.signal === 'fcmToken') {
      const fcmToken = data.fcmToken;

      props.dispatch(action('user/saveDeviceToken', { fcmToken: fcmToken }));
    } else if (data?.signal === 'openNotification') {
      const remoteMessage = data.remoteMessage;
      openNotification(remoteMessage)
    }
  }

  function openNotification (remoteMessage) {
    const page = remoteMessage.data.page;
    switch (page) {
      case '/historyOrderDetailDirect':
        const query = remoteMessage.data.url.substring(4)
        const path = `${page}${query}`
        props.history.push(path);
        break;
      default:
        props.history.push(page);
        break;
    }
  }

  const footer = useMemo(() => {
    switch (landing?.webSiteType) {
      case 1:
        return <PcFooter />
      case 2:
      case 3:
        if (isPc()) {
          setTimeout(() => {
            if (document.querySelector('main')) {
              document.querySelector('main').style.minHeight = 'calc(100vh - 326px)';
            }
          }, 100);
        }
        return <MyFooter />
      case 0:
      default:
        return null
    }
  }, [landing?.webSiteType])

  //  saveAdsView
  const MID = getValueFromQuery('MID', props.location.search);

  useEffect(() => {

    if (MID) {
      props.dispatch(action('statistic/saveAdsView', { adsCode: MID }));
    }
  }, [MID])


  //  
  const orderIntention = getValueFromQuery('re', props.location.search);
  useEffect(() => {

    if (orderIntention) {
      const path = props.location.pathname;
      switch (path) {
        case '/selectOrderType':
        case '/shopDetail':
          props.dispatch(action('statistic/save', { orderIntention: orderIntention }));
          break;
        default:
          break;
      }
      props.dispatch(action('statistic/saveAdsView', { adsCode: MID }));
    }
  }, [MID])

  // 
  useEffect(() => {
    props.dispatch(action('statistic/recordPointTag'));
    return () => {
      const path = props.location.pathname;
      switch (path) {
        case '/':
        case '/home':
        case '/menu':
        case '/myDetail':
        case '/reviewPayment':
          props.dispatch(action('statistic/updatePointTag'));
          break;
        default:
          break;
      }
    }
  }, [])

  //  
  useEffect(() => {
    stayScroll()
    return () => {
      stayScroll()
    }
  }, [])

  //  section mobile 

  return <section>
    {header}
    <main>
      {props.children}
      <ActivityModal />
      <AppDownload />

    </main>
    {footer}
  </section>
}

export default connect(({ webConfigModal: { landing }, home: { restaurantInfo } }) => ({
  landing, restaurantInfo
}))(BaseLayout);