import { http } from '../utils';
import { API } from '../config';


// ?uuid=
const payByCash = (query) => {
  return http.get(API.payByCash, query)
};

const revolutPay = (query) => {
  return http.get(API.revolutPay, query)
};

const getCouponListByUser = (query) => {
  return http.get(API.getCouponListByUser, query)
};

// entityId  uuid
const checkCouponByOrderUUID = (query) => {
  return http.get(API.checkCouponByOrderUUID, query)
};

const redeem = (query) => {
  return http.get(API.redeem, query)
};

const getTermsByCode = (query) => {
  return http.get(API.getTermsByCode, query)
};


export default {
  payByCash, checkCouponByOrderUUID, getCouponListByUser, redeem, getTermsByCode, revolutPay
}