
import { http } from '../utils';
import { API } from '../config';

const getAllAddr = (query) => {
  return http.post(API.getAllAddr, query)
};

const autocomplete = (query) => {
  return http.get(API.autocomplete, query)
};

const queryAddr = (query) => {
  return http.get(API.queryAddr, query)
};

const queryLocalAddrsByGeo = (query) => {
  return http.get(API.queryLocalAddrsByGeo, query)
}

const getFullAddress = (query) => {
  return http.get(API.getFullAddress, query)
}

const editAddr = (query) => {
  return http.post(API.editAddr, query)
}

const deleteAddr = (query) => {
  return http.post(API.deleteAddr, query)
}

const addAddr  = (query) => {
  return http.post(API.addAddr, query)
}





export default {
  getAllAddr, autocomplete, queryLocalAddrsByGeo,
    getFullAddress,
   editAddr,deleteAddr,addAddr,queryAddr
}
