import React, { useEffect } from 'react';
import { Icon } from '../../../../component';
import AraleQRCode from 'arale-qrcode'

import { connect, AppConfig, action, getOs, } from '../../../../utils'
import './index.less'
import './indexPc.less'

const AppDownload = props => {

  const { appDownload } = props;

  useEffect(() => {
    if (appDownload?.installUrl && appDownload?.visiable) {
      generateQRCode(appDownload?.installUrl)

    }
  }, [appDownload?.installUrl, appDownload?.visiable])


  function generateQRCode (url) {
    const codeFigure = new AraleQRCode({
      "render": "svg",  //  'svg' or 'table'
      "text": url, // 
      "size": 120,// 
      "correctLevel": 0,
    });
    const qrcode_svg = document.getElementById('qrcode_svg');
    qrcode_svg.appendChild(codeFigure);
  }

  function closeActivity () {
    props.dispatch(action('home/save', {
      appDownload: {
        visiable: false
      }
    }))
  }


  function installApp () {
    window.open(appDownload?.installUrl, "_blank")
  }

  return appDownload?.visiable ? <section className='app_download_modal '>
    <div className="mask" onClick={closeActivity}></div>
    <section className='app_download_content flyIn'>
      <div className='app_download_header' style={{ backgroundColor: appDownload.headerColor }}>
        {appDownload.logoUrl && <img src={appDownload.logoUrl} alt='logo' />}

      </div>
      <h1>{appDownload.couponValue}</h1>
      <pre className='app_download_sub_title'>{appDownload?.title}  </pre>
      <div className='app_download_couponCode_c' >
        <div className='app_download_couponCode' style={{ backgroundColor: appDownload.textBackColor }}>{appDownload.couponCode}</div>
      </div>
      <div id='qrcode_svg' className='qrcode_svg_con'></div>

      <div className='button_groups'>
        <button className='ok_button' onClick={installApp} style={{ backgroundColor: appDownload.textBackColor }}>INSTALL NOW</button>
        <button onClick={closeActivity}>LATER</button>
      </div>

      {appDownload.condition && <pre>{appDownload.condition}</pre>}

      <div className='app_download_bottom'>
        Powered by
        <Icon type='iconlogo_o' />
      </div>
    </section>
  </section > : null
}


export default connect(({ home: { appDownload }, }) => ({
  appDownload
}))(AppDownload);