import React, { useRef, useMemo, useLayoutEffect, useEffect, useState } from 'react';
import ShopCard from '../components/shopCard';
import addEventListener from "add-dom-event-listener";
import { getScroll, h5ToRn, inRN, usePrevious, useLazyload, getStatusBarHeight, isMobile, useDebounce, connect, action, useThrottle } from '../../../utils';
import NoMore from '../noMore';

const List = (props) => {
  const { shops, goShopDetail, landing, showFixedHeader } = props;


  const scrollEvent = useRef();

  useEffect(() => {
    scrollEvent.current = addEventListener(window, "scroll", updateHeader);

    updateHeader();

    return () => {
      if (scrollEvent.current) {
        scrollEvent.current.remove();
      }
    }
  }, [])

  const moveOffsent = useMemo(() => {
    if (inRN()) {
      return (document.documentElement.clientWidth / 7.5 + getStatusBarHeight()).toFixed(2);
    } else if (isMobile()) {
      return (document.documentElement.clientWidth / 7.5).toFixed(2);
    } else {
      return (document.documentElement.clientWidth / 7.5);
    }
  }, [])

  const proviousShowFixedHeader = usePrevious(showFixedHeader)


  const updateHeader = () => {
    const currentTopOffsent = getScroll(window, true)

    if (currentTopOffsent < moveOffsent) {
      props.dispatch(action('shopList/save', { showFixedHeader: false }))
    } else if (currentTopOffsent > moveOffsent) {
      props.dispatch(action('shopList/save', { showFixedHeader: true }))
    }
  }

  useLazyload(shops, '.shop_card_content_img');



  useEffect(() => {
    if (inRN()) {
      if (landing?.webSiteType === 2 || landing?.webSiteType === 3) {
        if (proviousShowFixedHeader !== showFixedHeader) {
          if (showFixedHeader) {
            h5ToRn({ type: 2, statusBar: 'dark-content' });
          } else {
            h5ToRn({ type: 2, statusBar: 'light-content' });
          }
        }
      }
    }

  }, [landing?.webSiteType, showFixedHeader])

  return <>
    <section className="store_list_container"  >
      {shops.map((item, index) => {
        return <ShopCard cardData={item} key={item.id}
          onItemPress={goShopDetail(item)}
        />
      })}
    </section>
    <NoMore />
  </>

}


export default connect(({ shopList: { showFixedHeader } }) => ({
  showFixedHeader
}))(List);