import React, { useEffect, useMemo, useState, } from 'react';

import { AdaptHeader, AutoTextarea, Icon, message, MyButton, Basket, Loader } from '../../component';
import { connect, action, checkEmail, checkNumber, getReturnPath, isPc } from '../../utils';

import BasicInput from './basicInput';
import DeliveryTime from './deliveryTime';
import DetailAddressModal from './detailAddressModal';

import './index.less';
import './indexPc.less';
import Relocaiton from './relocaiton';

// 1 :"Takeaway"
// 2 :"Delivery"
// 3  :"Collection"
// 4  :"Quick food"
// 5 :"Eat in"
const MyDetail = (props) => {

  const { name, number, email, time, note, showTime, currentType, ad, showAddress, buttonLoading, rId, getUserLoaidng,
    nameError, phoneError, addressError, emailError, showRelocation } = props;


  useEffect(() => {
    props.dispatch(action('myDetail/testInfo', {
      callBack: (valide) => {
        switch (valide) { // 1  2   3 
          case 1:
            props.history.push(`/login?returnUrl=${getReturnPath(props.location)}`)
            break;
          case 2:
            if (rId) {
              props.history.replace(`/shopDetail?rId=${rId}`);
            } else {
              props.history.replace('/');
            }
            break;
          case 3:
            props.dispatch(action('myDetail/getUserInfo'))
            break;
        }
      }
    }))
    return () => {
      props.dispatch(action('statistic/saveMyDetail', {
        name, number, email, time, currentType, ad
      }))

      props.dispatch(action('myDetail/save', { nameError: '', phoneError: '', addressError: '', emailError: '', showRelocation: false, buttonLoading: false }))
    }
  }, [])

  const hiddenTime = useMemo(() => {
    return rId === '1208';
  }, [rId])

  function onNameChange (e) {
    const v = e.target.value?.substring(0, 50);
    props.dispatch(action('myDetail/save', { name: v }))
    checkInputName(v);
  }

  function onPhoneChange (e) {
    const v = e.target.value.replace(/[^\d]/g, '')?.substring(0, 20);
    props.dispatch(action('myDetail/save', { number: v }))
    if (phoneError.length > 0)
      checkInputPhone(v);
  }

  function onPhoneBlur () {
    checkInputPhone(number);
  }

  function onAddressChange () {
    props.dispatch(action('myDetail/onAddressChange'))
  }

  function onEmailChange (e) {
    const v = e.target.value?.trim();
    props.dispatch(action('myDetail/save', { email: v }))
    // if (emailError.length > 0)
    //   checkInputEmail(v);
  }

  function onEmailBlur () {
    checkInputEmail(email);
  }

  function onNoteChange (e) {
    const v = e.target.value?.substring(0, 300);
    props.dispatch(action('myDetail/save', { note: v }))
  }

  function closeDeliveryTime () {
    props.dispatch(action('myDetail/save', { showTime: false }))
  }

  function onTimeChoose (e) {
    const v = e.target.value;
    props.dispatch(action('myDetail/save', { time: v, showTime: false }))
  }

  function showDeliveryTime () {
    props.dispatch(action('myDetail/save', { showTime: true }))
  }

  function checkInputName (value) {
    let result = true;
    if (!value || value.length === 0) {
      props.dispatch(action('myDetail/save', { nameError: 'Please enter your name' }))
      result = false;
    } else if (value.length > 50) {
      props.dispatch(action('myDetail/save', { nameError: 'Maximum 50 characters' }))
      result = false;
    } else {
      props.dispatch(action('myDetail/save', { nameError: '' }))
      result = true;
    }
    return result;
  }

  function checkInputPhone (value) {
    let result = true;
    if (!value || value.length < 3) {
      props.dispatch(action('myDetail/save', { phoneError: 'Please enter your phone number' }))
      result = false;
    } else if (value.length > 20) {
      props.dispatch(action('myDetail/save', { phoneError: 'Maximum 20 characters' }))
      result = false;
    } else if (!checkNumber(value)) {
      props.dispatch(action('myDetail/save', { phoneError: 'Please enter your phone number' }))
      result = false;
    } else {
      props.dispatch(action('myDetail/save', { phoneError: '' }))
      result = true;
    }
    return result;
  }

  function checkInputEmail (value) {
    let result = true;
    if (!value) {
      props.dispatch(action('myDetail/save', { emailError: 'Please enter your email' }))
      result = false;
    } else if (value.length > 100) {
      props.dispatch(action('myDetail/save', { emailError: 'Maximum 100 characters' }))
      result = false;
    } else if (!checkEmail(value)) {
      props.dispatch(action('myDetail/save', { emailError: 'Invalid email address' }))
      result = false;
    } else {
      props.dispatch(action('myDetail/save', { emailError: '' }))
      result = true;
    }
    return result;
  }

  function checkInputAddress (value, type) {
    let result = true;
    if (!value?.address && type === 2) {
      props.dispatch(action('myDetail/save', { addressError: 'Please enter your address' }))
      result = false;
    }
    return result;
  }

  function goReviewPayMent () {
    if (!time) {
      message.base('Please tap your time');
      return
    }

    props.dispatch(action('statistic/recordPointTag', {
      recordclick: 'goReviewPayMent',
      mark: currentType === 3 ? 'reviewPayment' : 'setDeliveryCharge'
    }));


    if (checkInputName(name) && checkInputPhone(number) && checkInputEmail(email) && checkInputAddress(ad, currentType)) {
      props.dispatch(action('orderList/saveOrder', {
        callBack: () => {
          if (currentType === 3) {
            props.history.push('/reviewPayment')
          } else if (currentType === 2) {
            props.history.push('/setDeliveryCharge')
          }
        }
      }))
    } else {
      message.base('Please enter the required information.');
    }
  }

  const submitText = useMemo(() => {
    if (currentType === 3) {
      return 'Review & Payment'
    } else if (currentType === 2) {
      return 'Set Delivery Charge'
    }
  }, [currentType]);

  const timeText = useMemo(() => {
    if (currentType === 3) {
      return 'Collection Time'
    } else if (currentType === 2) {
      return 'Delivery Time'
    }
  }, [currentType]);

  return <section className='my_detail_page'>
    <AdaptHeader title='My details' right={!isPc() && <Basket withoutBorder canPress />} />
    {getUserLoaidng ? <Loader loading type='tran' /> : <section className='my_detail_container'>
      <section className='my_detail_basic_info'>
        <BasicInput placeholder='Name' type={'Name'} value={name} onChange={onNameChange} maxLength={50} errtext={nameError} />
        <BasicInput placeholder='Phone Number' type={'Phone'} value={number} onBlur={onPhoneBlur} onChange={onPhoneChange} maxLength={20} errtext={phoneError} />
        {currentType === 2 && <BasicInput placeholder='Please save your address' value={ad.address} onChange={onAddressChange} type={'Address'} errtext={addressError} />}
        <BasicInput placeholder='Email' type={'Email'} onBlur={onEmailBlur}
          value={email} onChange={onEmailChange} maxLength={100} errtext={emailError} />

      </section>
      <section className='my_detail_time_note'>
        <div className='my_detail_time_container' onClick={!hiddenTime ? showDeliveryTime : undefined}>
          <span>{timeText}</span>
          <div className='my_detail_time_right'>
            <p>{time}</p>
            {!hiddenTime && <Icon type='iconicon_more_A1' />}
          </div>
        </div>
        <div className='my_detail_note_container'>
          <p>Notes for the store</p>
          <AutoTextarea
            value={note}
            onChange={onNoteChange}
            placeholder='e.g extra onion or instructions for the driver.'
            maxLength={300} />
          {note?.length === 300 && <div className='remaining_text'>Maximum 300 Characters</div>}
        </div>
      </section>
      <MyButton loading={buttonLoading} onClick={goReviewPayMent}>{submitText}</MyButton>
    </section>}

    {showTime && <DeliveryTime
      currentType={currentType}
      time={time}
      onChoose={onTimeChoose}
      onClose={closeDeliveryTime} />}
    {showAddress && <DetailAddressModal
      type='save'
    />}
    {showRelocation && <Relocaiton />}

  </section>
}

export default connect(({ myDetail: { name, number, email, time, note, showTime, ad, showAddress, buttonLoading,
  getUserLoaidng, nameError, phoneError, addressError, emailError, showRelocation },
  orderList: { rId, currentType } }) => ({
    name, number, email, time, note, showTime, currentType, ad, showAddress, buttonLoading, rId, getUserLoaidng, nameError, phoneError, addressError, emailError, showRelocation
  }))(MyDetail);
