import React from 'react';
import { SearchEmpty, Loader } from '../../../component';

import './index.less';
import './indexPc.less';


const ShopEmpty = (props) => {
  const { text, loading = false } = props;




  return <div className='shop_empty_container'>
    {loading ? <Loader loading={loading} type='normal' /> :
      <>
        <SearchEmpty key='search' />
        <div key='text' className='shop_empty_container_text'>{text}</div>
      </>}
  </div>
}

export default ShopEmpty