import React from 'react';
import Icon from '../../icon';
import PropTypes from 'prop-types';

import './indexPc.less';
import './index.less';

const MenuItem = (props) => {

  //  History order Partner center Help
  const { type, onClick, pending, icon, iconColor } = props;


  return (
    <section className='menu_item' onClick={onClick}>
      <Icon type={icon} style={{ fill: iconColor }} />
      <h5>{type}</h5>
      <Icon type='icongengduo' />

      {type === 'History order' && pending ?
        <span className="notice_order_pending">Order pending
          {pending !== 0 ? <span className="notice_order_badg">{pending}</span> : ''}
        </span> : ''}

    </section>
  )
}

export default MenuItem;