import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useLocation, useHistory } from 'react-router-dom';
import { Icon } from '../../../../component';

import { connect, openWeb, action, getReturnPath, openAppStore } from '../../../../utils'
import './index.less'
import './indexPc.less'

const ActivityModal = props => {

  const { activity } = props;

  function closeActivity () {
    props.dispatch(action('home/save', {
      activity: {
        show: false,
        title: '',
        content: '',
        showdownload: false,
        showsocial: false,
        showlogin: false,
      }
    }))
  }

  function installApp () {
    window.open('https://www.easydish.ie/download.html', "_blank")
  }




  return activity.show ? <section className='activity_modal'>
    <div className="mask" onClick={closeActivity}></div>
    <section className='activity_content'>
      <div className='activity_header'>
        <img src={require('./logo.jpg')} className='logo' />
        <Icon type='iconicon_close' onClick={closeActivity} className='close' />
      </div>
      <h5>{activity.title}</h5>
      <div className='app_download_couponCode_c' >
        <div className='app_download_couponCode' >INSTALL THE EASY DISH APP</div>
      </div>
      <pre>{activity.content}</pre>

      <img src={require('./qcode.jpg')} className='qcode_img' />


      <div className='button_groups'>
        <button className='ok_button' onClick={installApp}>INSTALL NOW</button>
        <button onClick={closeActivity}>LATER</button>
      </div>
    </section>

  </section > : null

}


export default connect(({ home: { activity }, }) => ({
  activity
}))(ActivityModal);