import React, { useMemo, useEffect } from 'react';
import { AdaptHeader, Icon, Loader, ModalAlert } from '../../component';
import { action, AppConfig, connect, inRN, toJSON } from '../../utils';

import './index.less';
import './indexPc.less';
import EditAccountItem from './editAccountItem';

const DeleteAccount = (props) => {

  const { deleteUserLoading, landing, user } = props;

  const connectUrl = useMemo(() => {
    const phone = user?.principal?.user?.phone;
    const userId = user?.principal?.user?.userId;
    const unionId = landing.unionId;
    return `${AppConfig.loginUrl}?pageType=thirdlogin&userId=${userId}&bindphone=${phone}&unionId=${unionId}&t=${Date.now()}&host=${window.location.host}&from=${inRN()?'rn':'web'}`

  }, [landing])

  useEffect(() => {

    function onMessage (event) {
      const data = toJSON(event.data);

      switch (data.signal) {
        case 'login_loading':
          const { loading } = data.data;
          props.dispatch(action('user/save', { deleteUserLoading: loading }));
          break;
        case 'login_delete':
          props.dispatch(action('user/deleteUser', {
            callBack: () => {
              props.history.replace('/')
            }
          }))
      }

    }
    window.addEventListener('message', onMessage)
    return () => {
      window.removeEventListener('message', onMessage, false)
    }
  }, [])


  const [userName] = useMemo(() => {
    const userDetail = user?.principal?.user
    return [userDetail?.userName]
  }, [user])

  function onSave (data) {

    switch (data?.label) {
      case 'Name':
        props.dispatch(action('user/updateUserName', { username: data.value }));
        break;
      case 'Phone number':
        params.phoneNumber = data.value;
        break;
      case 'Email':
        params.email = data.value;
        break;
      default:
        break;
    }
  }

  return <section className='delete_account_page'>
    <section className='delete_account_page_wrapper'>
      <AdaptHeader title='Account' />
     {!inRN()&&<div className='edit_baseinfo'>
        <EditAccountItem label='Name' value={userName} onSave={(data) => onSave(data)} />
        {/* <EditAccountItem label='Phone number' value={phone} onSave={(data) => onSave(data)} />
        <EditAccountItem label='Email' value={email} onSave={(data) => onSave(data)} /> */}
      </div>} 
      <iframe src={connectUrl} frameBorder="0" className='delete_account_third' />


    </section>
    <Loader loading={deleteUserLoading} type={'tran'} />
  </section>
}



export default connect(({ user: { deleteUserLoading, user }, webConfigModal: { landing } }) => ({
  deleteUserLoading, landing, user
}))(DeleteAccount);