
import React, { useMemo } from 'react';
import './indexPc.less'
import { connect, compose } from '../../utils';
import { withRouter } from 'react-router-dom';
import Icon from '../icon';
import MenuBlock from '../menuBlock';
import DishpriceIcon from '../dishpriceIcon';
import LoginIcon from '../loginIcon';
import BgTop from './bgTop';


function PcHeader (props) {

  const { landing, onLogoClick } = props;

  const showExpand = props.location.pathname.toString().toLowerCase() === '/' || props.location.pathname.toString().toLowerCase() === '/home' || props.location.pathname.toString().toLowerCase() === '/help';

  const expand = useMemo(() => {
    if (showExpand) {
      switch (props.location.pathname.toString().toLowerCase()) {
        case '/':
        case '/home':
          return {
            text: 'Find your favorite restaurants near you',
          };
        case '/help':
          return {
            text: 'Frequently Asked',
            text2: 'Questions',
          };
      }
    }
  }, [])



  const hasNoButtom = props.location.pathname === '/shopList';

  const isHelpPage = useMemo(() => {
    return props.location.pathname.toString().toLowerCase() === '/help'
  }, [])

  function goHome () {
    if (isHelpPage) {
      props.history.goBack();
    } else {
      props.history.push("/");
    }
  }

  return (<section className={`header_pc ${showExpand && 'header_pc_index'} ${hasNoButtom ? 'header_pc_no_buttom' : undefined}`} >
    <section className={`header_pc_container`} >
      <BgTop />
      <section className={`header_pc_container_line ${isHelpPage ? 'header_pc_container_line_help' : undefined}`}>
        <div className='header_pc_left' onClick={goHome}>
          {landing?.logoUrl ?
            <img src={landing?.logoUrl} className="pc_logo" alt="logo" /> : <Icon className={showExpand ? "pc_logo" : "pc_logo_other_page"} type={"iconlogo_o"} />}
        </div>
        <div className="header_pc_right">
          {/*  */}
          <DishpriceIcon />
          {/* Menu */}
          {props.isLogin && <MenuBlock />}

          {/*  */}
          <LoginIcon />
        </div>
      </section>
      {showExpand && <section className='header_pc_container_line_two'>
        <p>
          {expand.text}
        </p>
        {
          expand.text2 && <p>{expand.text2}</p>
        }
      </section>}

    </section>
  </section>)

}

export default compose(
  withRouter,
  connect(({ webConfigModal: { landing }, user: { isLogin } }) => ({
    landing, isLogin,
  })))(PcHeader)