import React from 'react';

import { CurrencyPrice } from '../../../../component';
import { formatHtml } from '../../../../utils';
import FoodTasteItem from './foodTasteItem';

import './index.less';
import './indexPc.less';

const ShopFoodsItem = (props) => {

  const { data, onFoodClick, showDetailDes } = props;

  function renderFoodTast () {
    if (!!data.hotRank || !!data.isVegetarian || !!data.calorie || !!data.isNut) {
      return <div className='shop_foods_taste'>
        {!!data.hotRank && <FoodTasteItem type='spicy' number={data.hotRank} />}
        {!!data.isVegetarian && <FoodTasteItem type='vegetarian' />}
        {!!data.calorie && <FoodTasteItem type='cal' number={data.calorie} />}
        {!!data.isNut && <FoodTasteItem type='nuts' />}
      </div>
    }
  }

  return <section className='shop_foods_content_container' onClick={onFoodClick}>
    <section className='shop_foods_des'>
      <div className='shop_foods_des_left_wrapper'>
        <div className='shop_foods_des_left'>
          <h1>{!!data.count && <span className='shop_foods_des_left_count'>{data.count}</span>}
            <span dangerouslySetInnerHTML={{ __html: data.name }}></span>
          </h1>
          {!!data.description && <div
            dangerouslySetInnerHTML={{ __html: data.description }}
            className={`shop_foods_item_notice ${showDetailDes ? undefined : 'three-line'}`} />}
          {renderFoodTast()}
          <p className='shop_foods_des_price'>{data.categoryId !== 1 && 'from '}
            <CurrencyPrice price={data.categoryId === 8 ? data.fromPrice : data?.price} /></p>
        </div>

      </div>
      {data.picUrl && <img className='shop_foods_des_right' alt={'FoodItem:' + formatHtml(data.name)}
        data-src={data.picUrl + "&width=300"} src={require('../../../../assets/img_phone.svg')} />}
    </section>
    {renderFoodTast()}
    <p className='shop_foods_des_price'>{data.categoryId !== 1 && 'from '}
      <CurrencyPrice price={data.categoryId === 8 ? data.fromPrice : data?.price} />
    </p>

  </section >

}

export default ShopFoodsItem