import React, { useMemo } from 'react';
import { Icon } from '../../../../component';
import { hex2Rgba, scrollTo } from '../../../../utils';
import ShopDishDes from '../../shopDishDes';

import './indexPc.less';

const ShopCategoryItem = (props) => {

  const { content, onCategoryItemPress, selectedMenu, hideTitle } = props;

  function onItemPress (item) {
    return () => {
      onCategoryItemPress(item)
      scrollTo(700);
    }
  }

  const style = useMemo(() => {
    const color = getComputedStyle(document.body).getPropertyValue('--themeColor').trim();
    const themeColor = hex2Rgba(color, .2);
    return themeColor
  }, [])


  function renderContent () {
    if (content?.dishTypes?.length > 0) {
      return content?.dishTypes.map(item => {
        const active = selectedMenu?.id === item.id && selectedMenu?.menuId === item.menuId
        return <p
          className={`${active ? 'shop_category_active_menu' : undefined}`}
          style={{ background: active ? style : 'unset' }}
          key={item.id}
          onClick={onItemPress(item)}>{item.name}</p>
      })
    }
  }

  return <details className='shop_category_item' open>
    <summary style={hideTitle ? { display: "none" } : null}>{content.name} <Icon type='iconicon_more_A1' /></summary>
    <div
      className='shop_category_item_content'
    >
      <ShopDishDes selectTab={content.name} />
      {renderContent()}
    </div>
  </details>


}

export default ShopCategoryItem;