import React, { forwardRef, useRef, useEffect } from "react";
import "./indexPc.less";
import "./index.less"
import { CardContent, CardCoupon, CardLocal, CardStar, CardTag } from "./components";

import CardAddress from "./components/cardAddress";
import { MyMarquee } from "../../../../component";


const ShopCard = forwardRef((props, ref) => {

  const { cardData, onItemPress } = props;

  return (
    <div ref={ref} className="shop_card" onClick={onItemPress}>
      <div className="shop_card_header">
        <div className="card_header_logo" style={cardData.logo && { backgroundImage: 'url(' + cardData.logo + ')' }}>
        </div>
        <div className="card_header_content">
          <MyMarquee className='card_header_title_con'>
            <h1 className="card_header_title two-line"
              dangerouslySetInnerHTML={{ __html: cardData.name }} >
            </h1>
          </MyMarquee>
          {cardData.score && <CardStar score={cardData.score} />}
          <CardTag cuisines={cardData.cuisines} />
        </div>

      </div>
      <div className='shop_card_header_content'>
        <CardAddress address={cardData.address} />
        <CardLocal
          currency={cardData.currency}
          distance={cardData.distance}
          minOrder={cardData.minOrder}
          deliveryFee={cardData.deliveryFee}
          isDelivery={cardData.isDelivery}
        />
        <CardCoupon couponList={cardData.couponList} />
      </div>

      <CardContent indexPic={cardData.indexPic} status={cardData.status} alt={'ShopCard:' + cardData.name} />
    </div>
  )
});


export default ShopCard;
