import React, { useEffect, useMemo } from 'react';
import { ToppingC, NoToppingC } from '../toppingC';
import LineAdd from '../lineAdd';
import LineReduce from '../lineReduce';

import './index.less';
import './indexPc.less';
import './indexTheme.less';
import { caculteDipulteItem } from '../../utils';


const ResultDishView = (props) => {

  const { dishList, currency, dishTypeStyle = 'nomal' } = props;

  const dishStyle = useMemo(() => {
    switch (dishTypeStyle) {
      case 'theme':
      case 'added':
      case 'removed':
      case 'edited':
        return `result_dish_view_detail_${dishTypeStyle}`
      default:
        return undefined;
    }

  }, [dishTypeStyle])

  function renderDishDetail (subDish, subPizzaDishes, categoryId,) {
    if (categoryId === 8) return null
    if (categoryId === 4) { //   1356
      let name = '';
      const filterSubDish = subDish.filter(item => !item.isSide && !/^NONE$/i.test(item.name))

      const dishMap = {};
      filterSubDish.map(item => {
        if (Object.keys(dishMap).includes(item.partId + '')) {
          dishMap[item.partId] = (dishMap[item.partId] || []).concat(item)
        } else {
          dishMap[item.partId] = [item]
        }
      })


      if (categoryId === 4) {
        filterSubDish?.map((a, index) => {
          name = name + ' ' + (a?.count > 1 ? (a.count + ' x ') : '') + a.name + ((filterSubDish.length - 1) !== index ? ', ' : '');
        })
        return <div className='result_dish_view_detail_content'>
          <div className='result_dish_view_detail_item result_dish_view_detail_item_dish'>{name} </div>
        </div>
      }
    } else {
      let dishDetail = [];
      subPizzaDishes?.map(item => {
        const dishItem = { dishType: "subPizzaDishes", partId: item.partId, dishItem: item };
        dishDetail.push(dishItem);
      })
      subDish?.map(item => {
        if (!item.isSide) {
          const dishItem = { dishType: "subDish", partId: item.partId, dishItem: item };
          dishDetail.push(dishItem);
        }
      })
      return <div className='result_dish_view_detail_content'>
        {
          dishDetail?.sort((itemA, itemB) => itemA.partId - itemB.partId)?.map((item, index) => {
            let a = item.dishItem;
            if (item.dishType === "subDish" && !/^NONE$/i.test(a.name)) {
              return <div key={a.id + '' + index}
                className='result_dish_view_detail_item result_dish_view_detail_item_dish'>
                <div>{(a?.count > 1 ? (a.count + 'x') : '')} {a.name}</div>
                {renderNoteList(a.noteList)}
                {a.categoryId === 5 && renderPizzaDetail(a.subPizzaDishView, true)}
              </div>
            } else if (item.dishType === "subPizzaDishes") {
              return renderPizzaDetail(a, true)
            }
          })
        }
      </div>
    }
  }

  //  partName
  function generateParName (dish) {
    if (dish.isDisplayTitle === '1') {
      return <div className='result_dish_view_item_8_part_name'>{dish.partName}</div>
    }
  }

  function generateSubItem (sub = [], separate,) {

    if (sub?.length > 0) {
      if (separate.printSeparate !== '2') {
        return <div style={{ flex: 1 }}>
          {
            sub.map((item, index) =>
              <div key={item + index} className={`item_8_sub_item_spereate`} >{item}</div>)
          }
        </div>
      } else {
        return <div style={{ flex: 1 }}>{sub.join(separate.separateStr)}</div>
      }
    } else {
      return null
    }
  }

  function renderDishDetail8 (subDish, subPizzaDishes, categoryId, allSeparate = { printSeparate: 0, separateStr: ',' }) {
    if (categoryId === 8) { //   1356

      const filterNoneDish = subDish.filter(item => !/^NONE$/i.test(item.name))

      const dishMap = {};
      filterNoneDish.map(item => {
        if (Object.keys(dishMap).includes(item.partId + '')) {
          dishMap[item.partId] = (dishMap[item.partId] || []).concat(item)
        } else {
          dishMap[item.partId] = [item]
        }
      })

      let groupDish = {};
      if (allSeparate.printSeparate === 2) {
        groupDish.separate = [];
        groupDish.side = [];
        Object?.values(dishMap)?.map(item => {
          if (item && item[0]?.isSide) {
            groupDish["side"].push(item);
          } else {
            groupDish["separate"].push(item);
          }
        })

      } else {
        Object?.values(dishMap)?.map(item => {
          const key = item[0].dishCombinationId
          if (Object.keys(groupDish).includes(item[0].dishCombinationId + '')) {
            groupDish[key].push(item);
          } else {
            groupDish[key] = [item]
          }
        });
      }

      const sortGroupDish = Object.entries(groupDish).sort((a1, b1) => {

        if (a1 === undefined || b1 === undefined ||
          a1[1] === undefined || b1[1] === undefined ||
          a1[1][0] === undefined || b1[1][0] === undefined
        ) {
          return 1
        }
        const a = a1[1][0][0]?.groupSort1
        const b = b1[1][0][0]?.groupSort1

        if (a === undefined || b === undefined) {
          return 1
        }

        if (a > b) return 1;
        else if (a == b) return 0;
        else return -1;
      })


      return sortGroupDish.map(([key, group]) => <div className='result_dish_view_detail_content' key={key}>
        {
          renderDishDetailGroup(group, categoryId,)
        }
      </div>)
    } else return null
  }

  function renderDishDetailGroup (group, categoryId,) {
    if (Object.values(group)?.length > 0) {

      const groupGroup = [];

      group.map((item, index) => {
        const groupItem = {};
        for (let index = 0; index < item.length; index++) {
          const element = item[index];

          const key = element.id
          if (Object.keys(groupItem).includes(key + '')) {
            const ccc = { ...groupItem[key], count: groupItem[key].count + 1 }
            groupItem[key] = ccc
          } else {
            groupItem[key] = element
          }
        }
        groupGroup.push(Object.values(groupItem))
      })

      group = groupGroup

      group.sort((a1, b1) => {
        const a = a1[0]?.groupSort
        const b = b1[0]?.groupSort
        if (a > b) return 1;
        else if (a == b) return 0;
        else return -1;
      })
      const groupSeparate = Object.values(group)[0][0].printSeparate

      if (groupSeparate !== '2') {
        return group?.map((a, index) => {
          const add = [];
          const reduce = [];

          a.map((b, j) => {

            if (b.isDefault === 1) {
              if (b.count <= 1) {

              } else {
                const name = `${(b?.count > 2 ? ((b.count - 1) + ' x ') : '')} ${b.name}`
                add.push(name)
              }
            } else {
              const note = b.note ? `(${b.note})` : '';
              const name = `${(b?.count > 1 ? (b.count + ' x ') : '')} ${b.name}` + note
              add.push(name)
            }
            if (b.isDefault && b.defaultCount > 0 && b.count === 0) {
              reduce.push(b.name)
            }
          })
          const formatAdd = caculteDipulteItem(add)

          // 
          const separate = {
            printSeparate: a[0].printSeparatePart || '0',
            separateStr: ', ',
          }


          if (reduce?.length > 0) {
            return <div key={index} className={`result_dish_view_detail_item_8`}>
              {generateParName(a[0])}
              {formatAdd?.length > 0 && <LineAdd>{generateSubItem(formatAdd, separate,)}</LineAdd>}
              <LineReduce>
                <div style={{ textDecoration: 'line-through', flex: 1 }}>{generateSubItem(reduce, separate,)}</div>
              </LineReduce>
            </div>
          } else if (add?.length > 0) {

            return <div key={index} className={`result_dish_view_detail_item_8`}>
              {generateParName(a[0])}
              {generateSubItem(formatAdd, separate)}
            </div>
          } else {
            return null
          }
        })
      } else {
        const add = [];
        const reduce = [];
        group?.map((a, index) => {
          a.map((b, j) => {

            if (b.isDefault === 1) {
              if (b.count <= 1) {

              } else {
                const name = `${(b?.count > 2 ? ((b.count - 1) + ' x ') : '')} ${b.name}`
                add.push(name)
              }
            } else {
              const note = b.note ? `(${b.note})` : '';
              const name = `${(b?.count > 1 ? (b.count + ' x ') : '')} ${b.name}` + note

              add.push(name)
            }

            if (b.isDefault && b.defaultCount > 0 && b.count === 0) {
              reduce.push(b.name)
            }
          })
        })
        const formatAdd = caculteDipulteItem(add)
        // 
        const separate = { printSeparate: '2', separateStr: ', ' }

        if (reduce?.length > 0) {
          return <div className='result_dish_view_detail_item_8 result_dish_view_detail_item_8_no_border'>
            {formatAdd?.length > 0 && <LineAdd>{generateSubItem(formatAdd, separate,)}</LineAdd>}
            <LineReduce>
              <span style={{ textDecoration: 'line-through' }}>{generateSubItem(reduce, separate,)}</span>
            </LineReduce>
          </div>

        } else if (add?.length > 0) {
          // className={`result_dish_view_detail_item ${showBorderTop ? undefined : 'result_dish_view_detail_item_dish'}`}
          return <div className='result_dish_view_detail_item_8 result_dish_view_detail_item_8_no_border'>
            {generateSubItem(formatAdd, separate)}
          </div>
        } else {
          return null
        }
      }
    } else {
      return null
    }

  }

  function renderNoteList (noteList) {
    return noteList?.map(item => {
      return <div key={item.id} className='result_dish_view_detail_note'>
        <i>({item.comment?.trim()})</i>
        {!!item.price && <i>{currency}{item.price.toFixed(2)}</i>}
      </div>
    })
  }

  //  side side
  function renderDishDetailSide (subDish, categoryId) {
    if (subDish && subDish.length > 0 && categoryId !== 8) {
      return <div className='result_dish_view_detail_content'>
        {
          subDish?.map((a, index) => {
            if (a.isSide) {
              return <div key={a.id + '' + index}
                className='result_dish_view_detail_item result_dish_view_detail_item_dish'>
                <div>{a.name} </div>
                {renderNoteList(a.noteList)}
                {a.categoryId === 5 && renderPizzaDetail(a.subPizzaDishView, true, a.isHalfPizza)}
              </div>
            }
          })
        }
      </div>
    }
  }

  // pizza 
  function renderPizzaDetail (pizza, isSub, isHalfPizza) {
    if (pizza) {
      let key = pizza.id;
      if (pizza.sizeId) {
        key = pizza.id + '' + pizza.sizeId;
      }
      return <div className={!isSub ? 'result_dish_view_detail_content' : 'result_dish_view_detail_sub_content'} key={key}>
        {isSub && <div>{pizza.name}
          {pizza?.note && <i>({pizza.note})</i>}
        </div>}
        {renderPizzaDetailOrther(pizza)}
        {renderTipsHeaderOfHeader(pizza, isHalfPizza)}
      </div>
    }


  }

  // pizza 
  function renderPizzaDetailOrther (pizza) {
    // if (pizza && pizza.base && pizza.sauce && pizza.cheese) {
    //   return <div className='result_dish_view_detail_pizzaname '>{pizza.base} | {pizza.sauce} | {pizza.cheese}</div>
    // }

    function renderPizzaItem (key) {
      let pizzaItem;
      const pizzaItemData = pizza[key] || []
      if (pizzaItemData.length === 1 || pizzaItemData.length === 0) {
        pizzaItem = null
      } else if (pizzaItemData.length === 2) {
        const addName = pizzaItemData.filter(item => !item.default)[0]?.name
        const reduceName = pizzaItemData.filter(item => item.default)[0]?.name
        pizzaItem = <div className='result_dish_view_detail_pizzaname_item'>
          {addName && <LineAdd>{addName}</LineAdd>}
          {reduceName && <LineReduce><span className='line-through'>{reduceName}</span></LineReduce>}
        </div>
      }
      return pizzaItem
    }

    if (pizza?.basesView?.length > 0
      && pizza?.cheesesView?.length > 0
      && pizza?.saucesView?.length > 0) {
      const basesView = renderPizzaItem('basesView')
      const cheesesView = renderPizzaItem('cheesesView')
      const saucesView = renderPizzaItem('saucesView')
      if (!basesView && !cheesesView && !saucesView) {
        return null
      } else {
        return <div className='result_dish_view_detail_pizzaname'>
          {basesView}
          {saucesView}
          {cheesesView}
        </div>
      }
    }
  }

  //  pizza 
  function renderTipsHeaderOfHeader (pizza, isHalfPizza = false) {
    if (pizza) {
      let isExistsSideTopping = false;
      [{ keyName: 'lToppings', name: 'Left Side' },
      { keyName: 'rToppings', name: 'Right Side' }].map((a, j) => {
        if (pizza[a.keyName]) {
          const selectedtoppingsItem = Object.values(pizza[a.keyName]);
          if (!isExistsSideTopping) {
            selectedtoppingsItem.map((item) => {
              if (!isExistsSideTopping && item?.count > 0) {
                isExistsSideTopping = true;
                return;
              }
            });
          }
        }
      });
      return [{ keyName: 'toppings', name: 'All Over' },
      { keyName: 'lToppings', name: 'Left Side' },
      { keyName: 'rToppings', name: 'Right Side' }].map((a, i) => {
        return pizza[a.keyName] && pizza[a.keyName]
          .some(item => (a.keyName === 'toppings' && item.def && item?.count !== 1)
            || (a.keyName === 'toppings' && !item.def && item?.count > 0)
            || (a.keyName !== 'toppings' && item?.count > 0)
            || (isHalfPizza && item?.count > 0))
          && <div className='tips_container' key={a.keyName}>
            {isExistsSideTopping && <div className='tips_container_title'>{a.name}</div>}
            {renderAddToppings(pizza, a.keyName)}
            {renderDeleteToppings(pizza, a.keyName)}
          </div>

      })
    }
  }

  function renderAddToppings (pizza, a) {
    const addToppings = pizza[a]?.
      filter(item => (item.def && item?.count > 1)
        || (!item.def && item?.count > 0) || (a !== 'toppings' && item?.count > 0))
    if (addToppings?.length > 0)
      return <LineAdd wrapper>
        {
          addToppings.map((item, index) => {
            let newItem = { ...item };
            if (newItem.def && a === 'toppings') {
              newItem.count = item.count - 1;
            }
            return <ToppingC topping={newItem} key={index + '' + item.id} />
          })
        }
      </LineAdd>
    else
      return null;
  }

  //  pizza 
  function renderDeleteToppings (pizza, a) {
    const deleteToppings = pizza[a]?.filter(item => item.default && !item.count && a === 'toppings')
    if (deleteToppings?.length > 0) {
      return <div className='tips_container tips_no_container'>
        <LineReduce wrapper>
          {deleteToppings.map(((a, i) => {
            return <NoToppingC topping={a} key={a.id + '' + i} />
          }))}
        </LineReduce>
      </div>
    }

  }


  return dishList?.map((item, index) => {
    const dish = item;
    let dishName = dish.name;
    let oldTotalPrice = dish.oldTotalPrice;
    return <section className={dishStyle} key={index}>
      {(dishTypeStyle === 'added' || dishTypeStyle === 'removed' || dishTypeStyle === 'edited') && <div className='result_dish_view_detail_lef_type'>
        {dishTypeStyle.toLowerCase().replace(/( |^)[a-z]/g, (L) => L.toUpperCase())}
      </div>}
      {dish && <section className='result_dish_view_detail' key={index + ''} >
        <div className={`result_dish_view_detail_header ${oldTotalPrice && oldTotalPrice !== dish.totalPrice ? 'result_dish_view_detail_header_oldPrice' : undefined}`}>
          <div className='result_dish_view_detail_header_left'>{dish.count} x {dish.categoryId === 5 ? dish?.cnName : dishName}  </div>
          {oldTotalPrice && oldTotalPrice !== dish.totalPrice && <div className='result_dish_view_oldTotalPrice'>{currency}{dish.oldTotalPrice?.toFixed(2)}</div>}
          <div className='result_dish_view_totalPrice'>{currency}{dish.totalPrice.toFixed(2)}</div>
        </div>
        {renderNoteList(dish.noteList)}

        {renderDishDetail(dish.subDishes, dish.subPizzaDishes, dish.categoryId)}
        {renderDishDetail8(dish.subDishes, dish.subPizzaDishes, dish.categoryId,
          { printSeparate: dish.printSeparate, separateStr: dish.separateStr })}

        {renderDishDetailSide(dish.subDishes, dish.categoryId)}
        {renderPizzaDetail(dish.subPizzaDishView)}
      </section>}

    </section>
  })
}

export default ResultDishView;