import { action, delay, accAdd, testPizzaData, AppConfig } from '../utils';
import { orderListS } from '../service'
import { message } from '../component';


//1 :"Takeaway"
// 2 :"Delivery"
// 3  :"Collection"
// 4  :"Quick food"
// 5 :"Eat in"
const initState = {
  country: 'IE', //       
  shopGeo: {},// 
  dishList: [],  //  
  currentType: 2,  // 
  note: '',// 
  rId: '',//   rId
  rName: '',
  currency: '',
  dishListPrice: 0, // 
  uuid: '',
  errorCount: 0,//3
  showUnavailableModal: false,//   dishlist 
  deliveryFee: undefined,
  saveFlag: 0,// review paymentsave 0  saveOrder  1
};

function getPrice (list) {
  let dishListPrice = 0;
  list?.map((item, index) => {
    const dish = Object.values(item)[0];
    dishListPrice = accAdd(dishListPrice, dish.totalPrice)
  })
  return dishListPrice || 0;
}

export default {
  namespace: 'orderList',
  state: initState,
  reducers: {
    save (state, { payload }) {
      return { ...state, ...payload };
    },
  },
  effects: {
    //  currentType   dishList 
    *judgeDishBelongCurrentType ({ payload }, { call, put, select }) {
      try {
        const { dishList, currentType } = yield select(({ orderList }) => orderList);
        const deliveryEvery = dishList.every(item => {
          const a = Object.values(item)[0];
          let res = true
          if (a.subDishListView && a.subDishListView.length > 0) {
            res = a.subDishListView.every(b => {
              return b.subDishes.filter((c => c.choose)).every(f => f.isDelivery)
            })
          }
          return a.isDelivery && res;
        })
        const collectionEvery = dishList.every(item => {
          const a = Object.values(item)[0];
          let res = true
          if (a.subDishListView && a.subDishListView.length > 0) {
            res = a.subDishListView.every(b => {
              return b.subDishes.filter((c => c.choose)).every(f => f.isCollection)
            })
          }
          return a.isCollection && res;
        })

        if (currentType === 2 && !deliveryEvery) {
          yield put(action('save', { showUnavailableModal: true }))
        } else if (currentType === 3 && !collectionEvery) {
          yield put(action('save', { showUnavailableModal: true }))
        }
      } catch (e) {
        console.error('judgeDishBelongCurrentType', e);
      }
    },

    //  dishList 
    *judgeDishBelongShop ({ payload: { rId } }, { call, put, select }) {
      try {
        const { dishList } = yield select(({ orderList }) => orderList);
        if (!!rId) {  //  orderAgain  rId  orderAgain 
          const allBelongShop = dishList.every(item => {
            return Object.values(item)[0].shopId === Number(rId)
          })
          if (!allBelongShop) {
            yield put(action('save', { dishList: [], dishListPrice: 0, uuid: '', deliveryFee: undefined }))
          } else {
            yield put(action('removeExpireDateDish'))
          }
        }
      } catch (e) {
        console.error('judgeDishBelongShop', e);
      }
    },

    *removeExpireDateDish ({ payload }, { call, put, select }) {
      try {
        const { dishList } = yield select(({ orderList }) => orderList);
        const newDishList = dishList.filter(item => {
          return Object.values(item)[0].expireDate === undefined || Object.values(item)[0].expireDate > new Date().getTime()
        })
        if (newDishList.length !== dishList.length) {
          const dishListPrice = getPrice(newDishList);
          yield put(action('save', { dishList: newDishList, dishListPrice: dishListPrice }))

        }
      } catch (e) {
        console.error('removeExpireDateDish', e);
      }
    },

    //  dish  order
    * addDishToOrder ({ payload: { modelId, callBack, data } }, { call, put, select, take }) {
      try {
        let newData;
        data.map((item, key) => {
          if (!item.get('isHalfPizza')) {
            newData = data.updateIn([key, 'showCustomiz'], () => false)
          }
        })

        if (!testPizzaData(data, modelId)) {
          return
        }

        const normalData = (newData || data).toJS();
        normalData[modelId].expireDate = new Date().getTime() + AppConfig.DISH_EXPIRE_DATE

        const reopenId = normalData[modelId].reopenId

        const { dishList } = yield select(({ orderList }) => orderList);
        if (reopenId || reopenId === 0) {// 
          dishList[reopenId] = normalData
          const list = [...dishList];
          const dishListPrice = getPrice(list);

          yield put(action('save', { dishList: list, dishListPrice: dishListPrice }));
        } else {
          const list = [...dishList.concat([normalData])]
          const dishListPrice = getPrice(list);

          yield put(action('save', { dishList: list, dishListPrice: dishListPrice }));
        }
        callBack && callBack();

      } catch (e) {
        console.error('addDishToOrder', e);
      }
    },
    // 
    * deleteDish ({ payload: { keylist } }, { call, put, select }) {
      try {
        const { dishList } = yield select(({ orderList }) => orderList);

        const newDishList = dishList.filter((item, index) => !keylist.includes(index));
        const dishListPrice = getPrice(newDishList)

        yield put(action('save', { dishList: newDishList, dishListPrice: dishListPrice, showUnavailableModal: false }));


      } catch (e) {
        console.error("deleteDish", e);
      }
    },

    //1 :"Takeaway"
    // 2 :"Delivery"
    // 3  :"Collection"
    // 4  :"Quick food"
    // 5 :"Eat in"
    * changeType ({ payload: { currentType, callBack } }, { call, put, select }) {
      try {
        if (currentType !== 2) {
          yield put(action('save', { deliveryFee: undefined }));
        }

        yield put(action('save', { currentType: currentType }));
        yield put(action('myDetail/save', { time: 'A.S.A.P' }));


        yield put(action('statistic/saveOrderSwitch', { currentType }));
        callBack && callBack();
      } catch (e) {
        console.error("changeType", e);
      }
    },

    chageNote: [
      function* ({ payload: { note } }, { call, put, select }) {
        try {
          yield call(delay, 300);
          yield put(action('save', { note: note }))
        } catch (e) {
          console.error("chageNote", e);
        }
      }, [{ type: 'takeLatest' }]
    ],

    // 
    saveOrder: [
      function* ({ payload: { callBack } }, { call, put, select }) {
        try {
          yield call(delay, 100);
          yield put(action('myDetail/save', { buttonLoading: true, }))

          const { name, number, email, ad, time, note, } = yield select(({ myDetail }) => myDetail);
          const { dishList, currentType, dishListPrice, rId, uuid } = yield select(({ orderList }) => orderList);
          const { landing } = yield select(({ webConfigModal }) => webConfigModal);
          const { orderIntention } = yield select(({ statistic }) => statistic);


          const params = {
            userEmail: email,
            userName: name,
            userPhone: number,
            note: note,
            type: currentType,
            totalPrice: dishListPrice,
            payStatus: 0,
            shopId: rId,
            uuid: uuid,
            menuIds: landing?.menuIds,
            orderIntention: orderIntention,
            domainId: landing?.webSiteConfigId,// EASYDISHDEV-1793
          };

          const dishs = []; // 
          const dishListP = dishList.map((a, j) => {
            Object.keys(a).filter(item => {
              return item.indexOf('d') === -1
            }).map((b, j) => {

              const entry = a[b];

              const subPizzaDishes = [];
              const subDishes = [];

              const allDish = [];

              const subPizzaDishView = entry.categoryId === 5 ? {// pizza
                id: entry.id,
                note: entry.note,
                sizeId: entry.subPizzaDishView.sizesid,
                baseId: entry.subPizzaDishView.selectedbaseid,
                cheeseId: entry.subPizzaDishView.selectedcheeseid,
                sauceId: entry.subPizzaDishView.selectedsauceid,
                toppings: entry.subPizzaDishView.selectedtoppings['All over']?.filter(a => a.count > 0 || a.def).map(b => ({ id: b.id, count: b.count })),
                lToppings: entry.subPizzaDishView.selectedtoppings['Left side']?.filter(a => a.count > 0).map(b => ({ id: b.id, count: b.count })),
                rToppings: entry.subPizzaDishView.selectedtoppings['Right side']?.filter(a => a.count > 0).map(b => ({ id: b.id, count: b.count })),
              } : null;

              a[b]?.subDishListView?.filter((c, k) => {
                return c.choose
              }).map(item => {

                switch (entry.categoryId) {
                  case 6:
                    item.subDishes.filter(c => c.choose && c.categoryId === 5).map(d => {
                      const subPizzaDish = {
                        id: d.id,
                        partId: item.partId,// 
                        note: d.note,
                        isHalf: d.isHalfPizza,
                        sizeId: d.sizeId,
                      }
                      subPizzaDish.baseId = d.subPizzaDishView?.selectedbaseid;
                      subPizzaDish.cheeseId = d.subPizzaDishView?.selectedcheeseid;
                      subPizzaDish.sauceId = d.subPizzaDishView?.selectedsauceid;
                      subPizzaDish.toppings = d.subPizzaDishView?.selectedtoppings['All over']?.filter(a => a.count > 0 || a.def).map(b => ({ id: b.id, count: b.count }));
                      subPizzaDish.lToppings = d.subPizzaDishView?.selectedtoppings['Left side']?.filter(a => a.count > 0).map(b => ({ id: b.id, count: b.count }));
                      subPizzaDish.rToppings = d.subPizzaDishView?.selectedtoppings['Right side']?.filter(a => a.count > 0).map(b => ({ id: b.id, count: b.count }));

                      subPizzaDishes.push(subPizzaDish)
                    })

                    item.subDishes.filter(c => c.choose && c.categoryId !== 5).map(d => {
                      subDishes.push({
                        id: d.id,
                        partId: item.partId,
                        note: d.note,
                      })
                    })
                    break;
                  case 8:
                    const filterFreeSubDish = [];
                    const filterChargeableSubDish = [];
                    const deleteDefaultSubDish = [];
                    const arrayDish = [];

                    item?.subDishes?.map(c => {
                      item?.freeSubItem?.map(d => {
                        if (c.id === d.id) {
                          d.chargeType = 'free'
                          d.note = c.note
                          filterFreeSubDish.push(d)
                        }
                      })
                      item?.chargeableItem?.map(d => {
                        if (c.id === d.id) {
                          d.chargeType = 'charge'
                          d.note = c.note
                          filterChargeableSubDish.push(d)
                        }
                      })

                      if (c.count === 0 && c.isDefault === 1) {
                        deleteDefaultSubDish.push(c);
                      }
                      if (Array.isArray(c.id) && c.choose) {
                        arrayDish.push(c)

                      }
                    })


                    filterFreeSubDish.concat(filterChargeableSubDish).concat(deleteDefaultSubDish).concat(arrayDish)
                      .map(f => {
                        if (Array.isArray(f.id)) {
                          f.id.flat(15).map((g, k) => {
                            subDishes.push({
                              id: g,
                              partId: item.partId.flat(15)[k],
                              note: f.note,
                              count: f.count,
                              name: f.name
                            })
                          })
                        } else {
                          const subDish = {
                            id: f.id,
                            partId: item.partId,
                            note: f.note,
                            chargeType: f.chargeType,
                            count: f.count,
                            name: f.name
                          }

                          subDishes.push(subDish)
                        }
                      })
                    break;
                  default:

                    item.subDishes.filter(c => c.choose).map(d => {
                      if (Array.isArray(d.id)) {
                        d.id.flat(15).map((f, k) => {
                          subDishes.push({
                            id: f,
                            partId: item.partId.flat(15)[k],
                            note: d.note,
                          })
                        })
                      } else {
                        subDishes.push({
                          id: d.id,
                          partId: item.partId,
                          note: d.note,
                        })
                      }
                    })



                    break;
                }
              })


              const dish = {
                id: entry.id,
                typeId: entry.typeId,
                menuId: entry.menuId,
                categoryId: entry.categoryId,
                count: entry.count,
                note: entry.note,
                subDishes: subDishes,
                subPizzaDish: subPizzaDishView,
                subPizzaDishes: subPizzaDishes,
              }

              // left side name + right side name 
              if (entry.categoryId === 5 && entry.isHalfPizza) {
                dish.name = entry.name;
                dish.isHalfPizza = entry.isHalfPizza;
              }

              dishs.push(dish);
            })
          })

          params.dishList = dishs;

          const p = window.Platform;
          switch (p) {
            case 'iphoneX':
            case 'ios':
            case 'pad':
              params.origin = 'ios';
              break;
            case 'android':
              params.origin = 'android';
              break;
            default:
              params.origin = 'web';
              break
          }
          // 2 :"Delivery"
          // 3  :"Collection"
          if (currentType === 2) {
            params.address = ad.address;
            params.deliveryTime = time;
            if (!ad.lat || !ad.lon) {
              message.base(`The address have no lat or lon.`, 4000)
              return
            }
            params.lat = ad.lat;
            params.lon = ad.lon;

          } else {
            params.deliveryTime = time;
          }


          const res = yield call(orderListS.save, params);

          if (typeof res === 'string') {
            yield put(action('save', { uuid: res, saveFlag: 1 }))

            callBack && callBack()
          } else {
            yield put(action('addErrorCount', {
              callBack: ({ count, rId }) => {
                if (count > AppConfig.orderErrorCount) { // 3OK
                  message.base(`The basket will be cleared and start again.`, 4000)
                  callBack && callBack('error')
                } else {
                  message.base(`Oops! something went wrong. We are unable to process your order.`)
                }
              }
            }))
          }

          yield put(action('statistic/saveSave', { success: !!res, uuid: res }));

        } catch (e) {
          console.error("saveOrder", e);
        } finally {
          yield put(action('myDetail/save', { buttonLoading: false, }))
        }
      }, [{ type: 'takeLatest' }]
    ],

    * updateOrder ({ payload: { params, callBack } }, { call, put, select }) {
      try {
        const { uuid } = yield select(({ orderList }) => orderList);
        if (uuid) { // 
          const res = yield call(orderListS.update, { ...params, uuid });
          if (res && params?.deliveryFee) {
            //  updateorder deliveryFee  Review Payment 
            yield put(action('save', {
              deliveryFee: params.deliveryFee
            }))
          }

          yield put(action('statistic/saveUpdateInterface', { ...params, success: !!res }));

          callBack && callBack(res);
        }

      } catch (e) {
        console.error("updateOrder", e);
      }
    },

    //   
    * clear ({ payload }, { call, put, select }) {
      try {
        yield put(action('save', { ...initState }))
        yield put(action('myDetail/clear'))
        yield put(action('reviewPayment/clear'))
        yield put(action('setDeliveryCharge/clear'))

      } catch (e) {
        console.error("clear", e);
      }
    },

    // save
    * clearUUID ({ payload }, { call, put, select }) {
      try {
        yield put(action('save', { saveFlag: 0, uuid: '' }))
        yield put(action('myDetail/clear'))
        yield put(action('reviewPayment/clear'))
        yield put(action('setDeliveryCharge/clear'))

      } catch (e) {
        console.error("clear", e);
      }
    },

    // 
    * clearCart ({ payload }, { call, put, select }) {
      try {
        yield put(action('save', { saveFlag: 0, uuid: '', dishList: [], dishListPrice: 0, errorCount: 0 }))
        yield put(action('myDetail/clear'))
        yield put(action('reviewPayment/clear'))
        yield put(action('setDeliveryCharge/clear'))

      } catch (e) {
        console.error("clearCart", e);
      }
    },

    // errorCount: 0,//3
    * addErrorCount ({ payload: { callBack } }, { call, put, select }) {
      try {
        const { errorCount, rId } = yield select(({ orderList }) => orderList);
        const count = errorCount + 1;
        if (count > AppConfig.orderErrorCount) {
          yield put(action('clearCart'));
        } else {
          yield put(action('save', { errorCount: count }));
        }
        callBack && callBack({ count, rId })
      } catch (e) {
        console.error("addErrorCount", e);
      }
    },
  },


};