import React, { useEffect } from 'react';


import './index.less'
import './indexPc.less';
import { connect, stayScroll, stopScroll, action } from '../../utils'
import { Icon } from '..'



const SubmitModal = (props) => {

  const { type, title, msg, onClose } = props

  useEffect(() => {
    stopScroll()
    return () => stayScroll();
  }, [])

  return (<section className="feedback_modal" >
    <section className="feedback_modal_content">
      <Icon type={type} />
      <h5>{title}</h5>
      <p>{msg}</p>
      <button onClick={onClose}>Close</button>
    </section>
  </section>)
}

export default SubmitModal;