import React from 'react';

import './indexPc.less';
import './index.less'

import FoodCard from '../components/foodCard'

import { action, connect, inRN, compose, withRouter, getReturnPath, h5ToRn, openAppStore } from '../../../utils';
import { MFooter, Icon, LocationIcon, AddressModal, MHeader } from '../../../component';

const foodData = [
  {
    title: 'Chinese',
    img: require('./img/img_food_1.png'),
    icon: 'iconicon_chinese1',
    des: 'Order a Chinese feast from some of our best Chinese takeaway restaurants near you!'
  },
  {
    title: 'Thai',
    img: require('./img/img_food_2.png'),
    icon: 'iconicon_thai1',
    des: `Go on an adventure with our incredible selection of Thai restaurants and experience a unique combination of herbs and spices like never before!`,
    reserve: true,
  },
  {
    title: 'Japanese',
    img: require('./img/img_food_3.png'),
    icon: 'iconicon_japanese1',
    des: 'Bring the taste of Japan to your home by ordering from some of our best Japanese restaurants near you.'
  },
  {
    title: 'American',
    img: require('./img/img_food_4.png'),
    icon: 'iconicon_american',
    des: 'Enjoy some of Americas favourite dishes from restaurants near you!',
  }
]

const NormalHome = (props) => {

  const { showAddress } = props;

  function goAddress () {
    props.dispatch(action('home/save', { showAddress: true }))
  }

  function onAddressClosePress () {
    props.dispatch(action('home/save', { showAddress: false }))
    //
    h5ToRn({ type: 2, statusBar: 'light-content' });
  }

  // 
  function onAddressSelect (ad) {
    props.dispatch(action('home/save', { showAddress: false }))
    props.history.push('/shopList')
  }

  //  
  function goShopList (item) {
    return () => {
      props.dispatch(action('user/saveAddress', {
        lat: item.lat, lng: item.lon, ad: item.address, callBack: () => {
          props.history.push('/shopList')
        }
      })); // 
    }
  }

  function toLogin () {
    props.history.push(`/login?returnUrl=${getReturnPath(props.location)}`)
  }

  // 
  function getAddress (geo) {

    props.dispatch(action('address/queryLocalAddrsByGeo', {
      lat: geo.lat, lng: geo.lon, callBack: (res) => {
        props.dispatch(action('user/saveAddress', {
          lat: geo.lat, lng: geo.lon, ad: res.formatted_address, callBack: () => {
            props.history.push('/shopList')
            props.dispatch(action('home/save', { showAddress: false }));
          }
        }));
      }
    }));
  }

  return (
    <section className='normalhome_page'>
      <MHeader />
      <section className='home_address_container'>
        <section className='address_input' onClick={goAddress}>
          <div className='address_input_wrapper'>
            <span>
              Enter your address or area
            </span>
            {!showAddress && <LocationIcon className='address_icon' returnGeo={getAddress} />}
          </div>
          <button>Search</button>
        </section>
        {props.isLogin && <div className='or_select'>
          {props.allRensentAddress && props.allRensentAddress.length > 0 ?
            'Or, select a recent address' : 'You have no recent addresses.'}
        </div>}
        {/*  */}
        {
          props.isLogin && props.allRensentAddress && props.allRensentAddress.length > 0
          && props.allRensentAddress.map((item, i) => {
            if (i > 2) return null
            else if (item.lat && item.lon) return (
              <div key={item.id} className='rensent_address' onClick={goShopList(item)}>
                <Icon type='iconicon_location_b' />
                <p>{item.address}</p>
                <Icon type='iconicon_more_A1' />
              </div>
            )
          })
        }
        {/*  */}
        {
          (!props.isLogin) && <p className='rensent_address_no_login'><span onClick={toLogin}>Login</span> for your recent addresses</p>
        }
      </section>
      <section className='card_container'>
        <div className="card_title">Introduction to Cuisine</div>
        <section className='card_content'>
          {foodData.map((item, index) => {
            return <FoodCard data={item} key={index} reserve={index % 2} />
          })}
        </section>
      </section>
      {!inRN() && <div className="down_app_container_top">
        <div className='down_app_container_top_wrapper' />
      </div>}
      {!inRN() && <section className='down_app_container'>
        <div className='down_app_container_wapper'>
          <div className='down_app_left'>
            <div>
              <h5>Have you got the app?</h5>
              <p>Get it now - available on the iOS and Android app stores!</p>
            </div>
            <div className='down_link'>
              <a href={props.landing.googlePlayUrl} target="_blank" alt="Easy Dish for Android" >
                <img alt="Easy Dish for Android" src={require('../../../assets/google_play.png')} />
              </a>
              <a onClick={() => openAppStore(props.landing.appStoreUrl)} alt="Easy Dish for iOS" >
                <img alt="Easy Dish for iOS" src={require('../../../assets/AppStore.png')} />
              </a>
            </div>
          </div>
          <img src={require('./img_download_APP.png')} alt="Easy Dish App" />
        </div>

      </section>}
      {showAddress && <AddressModal onClose={onAddressClosePress} onAddressSelect={onAddressSelect} getAddress={getAddress} />}
      <MFooter />
    </section>
  )
}


export default compose(
  withRouter,
  connect(({ address: { allRensentAddress, }, user: { isLogin }, webConfigModal: { landing }, home: { showAddress } }) => ({
    allRensentAddress, isLogin, landing, showAddress
  })))(NormalHome);
