import React, { useEffect, useRef, useState } from 'react';
import { useMemo } from 'react';
import { AutoTextarea, CurrencyPrice, Icon, CloseIcon, ModalAlert, LineAdd, LineReduce } from '../../../../../component';
import { caculteDipulteItem, getStatusBarHeight, inRN, isMobile } from '../../../../../utils';

import './index.less';
import './indexPc.less';

const DishItem = (props) => {

  const { from, isMinPOS = false, id, finishChoose } = props;

  const [showNoteinput, setShowNoteinput] = useState(false);

  const conRef = useRef();
  const conHeight = useRef();

  useEffect(() => {
    if (conRef.current && !conHeight.current) {
      conHeight.current = conRef.current.getBoundingClientRect().height
    }
  })

  const { categoryId, itemData, onChooseDish, onEditPizza, onNote, onToogleShow, onSkip, onNONEPress, itemPrice } = props;
  const show = itemData.show;
  const choose = itemData.choose;
  const maxSize = itemData.maxSize;
  const minSize = itemData.minSize;
  const totalSize = itemData.totalSize || 0;
  const name = itemData.name;
  const freeSize = itemData.freeSize;
  const defaultFreeSize = itemData.defaultFreeSize;
  const replaceSize = itemData.replaceSize

  const freeSubItem = itemData.freeSubItem;
  const chargeableItem = itemData.chargeableItem;
  const removename = itemData.removename;
  const openNext = itemData.openNext;
  const showType = itemData.showType;

  const showPrice = itemData.showPrice;
  const isDefaultFree = itemData.isDefaultFree;

  const isNoneOption = itemData.isNoneOption;

  const selectedItem = useMemo(() => {
    for (const item of itemData.subDishes) {
      if (item.choose) {
        return item;
      }
    }
  }, [itemData.subDishes])

  // subId   immutable 
  function onChoose (item, partId, subId, type) {

    if (type === 'add' && showType !== 'single' && totalSize >= maxSize) {
      ModalAlert({
        className: 'limit_exceeded_modal',
        comheader: <div className='limit_exceeded_modal_header'>
          <Icon type='iconlimit_exceeded' />
        </div>,
        msg: 'You have reached the maximum number of items.'
      })
      return
    }

    onChooseDish(item, partId, subId, type);

    if (showType !== 'single') {//  

    } else {
      onToogleShow(false);
    }

  }

  function onSkipPress () {
    onSkip && onSkip();
  }




  function renderContent ({ subDishes, partId }) {
    return subDishes.map((item, index) => {
      let key = item.id + 'item';
      if (item.sizeId) {
        key = item.id + '' + item.sizeId
      }

      const showNoBorderTop = item.borderTop || index === 0;

      const nameCom = (conmType = 'normal') => (showType === 'itemMultiple' && categoryId === 8) ?
        <div className={`dish_detail_item_name ${conmType === 'reduce' ? 'dish_detail_item_name_reduce' : undefined}`}>{item.count > 0 &&
          <div className='item-count dish_detail_item_count'>{item.count}</div>}{item.name}
        </div>
        : <div className={`dish_detail_item_name ${conmType === 'reduce' ? 'dish_detail_item_name_reduce' : undefined}`}>
          {
            conmType === 'reduce' ? null : <Icon type={item.choose ? 'iconxuanzhong' : 'iconweixuanze'} />
          }{item.name}</div>

      return <div
        key={key}
        className={`fix_animate_transform dish_detail_item_container_wrapper
         ${item.borderTop ? 'dish_detail_item_container_border' : undefined}
         ${item.choose ? 'dish_detail_item_active' : undefined}`}

      >
        <div
          className={`dish_detail_item_container ${showNoBorderTop ? 'dish_detail_item_container_no_border' : undefined}`}
          onClick={() => onChoose(item, partId, index, 'add')}>
          {
            item.isDefault && item.count < 1 && categoryId === 8 ? <LineReduce>{nameCom('reduce')}</LineReduce> :
              nameCom()
          }
          {
            renderItemPrice(item, partId, index)
          }

        </div>


      </div>
    })
  }

  function renderItemPrice (item, partId, index) {

    if (categoryId !== 8) {
      return <div className='dish_detail_item_price'>
        <CurrencyPrice price={item?.vprice} hiddenZero displayPlus={item.isDisplayPlus} />
      </div>
    } else {
      let showItemPrice = (freeSize + defaultFreeSize) <= totalSize || showPrice;
      if (isDefaultFree === 1 && item.isDefault === 1 && item.count === 0) { // 0 
        showItemPrice = false;
      }
      return <div className='dish_detail_item_price'>
        {((showItemPrice || (freeSize + defaultFreeSize) <= totalSize) || item.isSide) && <CurrencyPrice price={item?.vprice} hiddenZero displayPlus={item.isDisplayPlus} />}
        {item.count > 0 && categoryId === 8 && showType !== 'single' &&
          <Icon type='iconicon_del' className='dish_detail_item_del' onClick={(e) => {
            e.stopPropagation();
            onChoose(item, partId, index, 'del')
          }
          } />}
      </div>
    }
  }





  function toogleShow () {
    onToogleShow(!show)
  }

  const finalName = useMemo(() => {
    if (itemData.partName) {
      if (itemData.partName === 'side') {
        return 'Choose side dish'
      }
      return itemData.partName
    }
    return 'Choose one'
  }, [itemData.partName]);

  function onEdit (event) {
    event.stopPropagation();

    onEditPizza(selectedItem)
  }

  function onNotePress () {
    setShowNoteinput(true)
  }

  function onNoteChange (e) {
    e.stopPropagation();
    const v = e.target.value?.substring(0, 50);
    onNote(selectedItem, v)
  }

  function onClearNoteText () {
    onNote(selectedItem, '')
    setShowNoteinput(false)
  }

  const note = useMemo(() => {
    if (selectedItem) {
      return selectedItem.note
    }
    return '';

  }, [selectedItem])

  useEffect(() => {
    if (note) {
      setShowNoteinput(true)
    }
  }, [note]);

  const stickyTop = useMemo(() => {
    if (inRN()) {
      return (getStatusBarHeight() / document.documentElement.clientWidth * 7.5 + 1) + 'rem';
    } else if (isMobile()) {
      return '1.2rem'
    } else {
      return 0;
    }
  }, [])

  const showEdit = useMemo(() => {
    switch (categoryId) {
      case 1:
      case 4:
      case 8:
        return selectedItem?.isSide && !isMinPOS
      case 5:
        return false;
      case 6:
        if (itemData.partName === 'side' && isMinPOS) {
          return false;
        }
        if (selectedItem?.categoryId === 5 || selectedItem?.name === 'NONE') {
          return false
        } else {
          return true
        }
    }

  }, [categoryId, selectedItem, isMinPOS])

  function renderSkipNext () {
    if (categoryId !== 8 || finishChoose) {
      return null
    }

    if (openNext) {
      return null
    } else {
      if ((showType !== 'single' && choose && totalSize >= minSize) || name === 'NONE') {// 
        return <div className='dish_skip' onClick={onSkipPress}>
          <button>Next</button>
        </div>
      }
      return null
    }
  }

  function renderNone () {
    if (categoryId !== 8) {
      return null
    }

    if (!isNoneOption) {
      return null
    }

    let noneCom;
    switch (showType) {
      case 'single':
      case 'multiple':
        noneCom = <div className={`dish_detail_item_container_none ${name === 'NONE' ? 'dish_detail_item_active' : undefined}`}>
          <Icon type={name === 'NONE' ? 'iconxuanzhong' : 'iconweixuanze'} />
          None
        </div>
        break;
      case 'itemMultiple': // 
        noneCom = <div className={`dish_detail_item_container_none `}>
          None
        </div>
        break;
      default:
        break;
    }

    return <div className={`fix_animate_transform dish_detail_item_container_wrapper
      dish_detail_item_container_border' }
     `} onClick={onNONEPress}
    >{noneCom}
    </div>
  }


  function generateFreeOrChargeableItem (chargetype = 'Free') {

    if (chargetype === 'Free' && freeSubItem && freeSubItem?.length <= 0) {
      return null
    }
    if (chargetype === 'Charged' && chargeableItem && chargeableItem?.length <= 0) {
      return null
    }
    const res = [];

    const elements = chargetype === 'Free' ? freeSubItem : chargeableItem
    for (let index = 0; index < elements?.length; index++) {
      const element = elements[index];
      let j;
      if (res.some((b, i) => {
        j = i
        return b.id === element.id
      })) {
        res[j] = { ...res[j], count: res[j].count + 1 }
      } else {
        res.push(element);
      }
    }

    return <div className='dish_con_free_or_chargeable_item'>

      <p >
        <span style={{ color: chargetype === 'Free' ? '#76C769' : '#E78B64' }}>{chargetype}</span>
        {/* {chargetype !== 'Free' && <span className='charge_price'><CurrencyPrice price={itemPrice} hiddenZero /></span>} */}
      </p>
      {
        res.map((item, i) => {
          const name = item.count > 1 ? `${item.count} x ${item.name}` : item.name
          return <span key={item.id} >
            {name}{(res?.length - 1) === i ? '' : ', '}
          </span>
        })
      }
    </div>

  }

  function renderFreeTips () {
    return ((freeSize > 0 || replaceSize > 0) && show) ? <div className='dish_con_item_free_tips'>
      {freeSize ? <p>
        {freeSize} selection(s) free
      </p> : null}
      {replaceSize ? <p>
        Swap {replaceSize} selection(s) for free
      </p> : null}
    </div> : null
  }

  function generateNormalCom (item) {
    let itemname = [];
    item.map(a => {
      itemname.push(a.name)
    })
    return caculteDipulteItem(itemname).join(', ')
  }

  function renderHeader () {

    if (categoryId === 8) {

      const showFreeOrCharged = showType !== 'single' && freeSubItem?.length > 0 && chargeableItem?.length > 0

      const freeCom = showFreeOrCharged ? generateFreeOrChargeableItem('Free') : generateNormalCom(freeSubItem)
      const chargeableCom = showFreeOrCharged ? generateFreeOrChargeableItem('Charged') : generateNormalCom(chargeableItem)
      if (name === 'NONE' || show) {
        return renderFreeTips()
      }
      if (removename) {

        return <div className='dish_con_item_line_two' onClick={toogleShow} >
          {renderFreeTips()}
          {(freeSubItem?.length > 0 || chargeableItem?.length > 0) && <LineAdd>
            <div style={{ flex: 1 }}>
              {freeCom}
              {chargeableCom}
            </div>
          </LineAdd>}
          <LineReduce>
            <div className='dish_con_item_line_two_removename'>
              {removename}
            </div>
          </LineReduce>
        </div>
      } else {
        return <div className='dish_con_item_line_two' onClick={toogleShow} >
          {renderFreeTips()}
          <div>
            {freeCom}
            {chargeableCom}
          </div>
        </div>
      }

    } else {
      return <div className='dish_con_item_line_two' onClick={toogleShow} >
        <Icon type='iconxuanzhong' className='dish_con_item_line_two_icon' />{name}
      </div>
    }
  }

  // 
  const lessThanMin = useMemo(() => {
    return itemData.choose && totalSize < minSize
  }, [itemData])


  function renderPrice () {
    if (categoryId === 8 && showType !== 'single' && name !== 'NONE') {
      let com;
      if (totalSize === 0) {
        com = <div className='dish_con_item_line_one_price_tips'>{minSize} required</div>
      } else if (totalSize < minSize) {
        com = <div className='dish_con_item_line_one_price_tips'>{minSize - totalSize} more</div>
      } else if (totalSize >= minSize && totalSize !== maxSize) {
        com = <div className='dish_con_item_line_one_price_tips'>pick up to {maxSize}</div>
      } else if (totalSize === maxSize) {
        com = null
      }
      return <div>
        <CurrencyPrice price={itemPrice} hiddenZero />
        {com}
      </div>
    } else {
      return <CurrencyPrice price={itemPrice} hiddenZero />
    }
  }

  return (
    <section
      id={id}
      className='dish_con_item_container' >
      <div className={`dish_item_header_container ${show ? 'dish_item_header_container_show' : undefined}`}
        style={{ top: stickyTop, background: lessThanMin ? '#F0DACF' : undefined }}>
        {
          (name || removename) ?
            <div className='dish_con_item_select'>
              <div className='dish_con_item_line_one' onClick={toogleShow}>
                <div className='dish_con_item_line_one_final_name'>
                  {finalName}
                  {categoryId === 8 && maxSize > 1 && <div className='item-count dish_con_item_line_two_count'>{totalSize}</div>}
                </div>
                <div className='dish_con_item_line_one_price'>
                  {renderPrice()}
                  {!!name && <Icon type='iconicon_more_A1' className={`${show ? 'choose_icon_show' : 'choose_icon'}`} />}
                </div>
              </div>
              {renderHeader()}
              {categoryId === 6 && selectedItem?.categoryId === 5 && <div className='dish_con_item_edit_container' onClick={onEdit}>
                <Icon type='iconicon_edit' />
                <span>Customize</span>
              </div>}
              {showEdit && (!showNoteinput ? <div className='dish_con_item_edit_container' onClick={onNotePress}>
                <Icon type='iconicon_add_notice' />
                <span>Notes</span>
              </div> : <div className='dish_con_item_input_container'>
                <AutoTextarea placeholder='Notes for chef'
                  maxLength={50}
                  onChange={onNoteChange} value={note} />
                <CloseIcon onClick={onClearNoteText} text={showNoteinput || note} />
              </div>)}
              {note?.length === 50 && <div className='remaining_text'>Maximum 50 Characters</div>}

            </div>
            : (
              categoryId === 8
                ? <div>
                  <div className='dish_con_item_unselect' onClick={() => {
                    if (categoryId === 8) {
                      toogleShow()
                    }
                  }}>   {finalName}{renderPrice()}</div>
                  {renderFreeTips()}
                </div>
                : <div className='dish_con_item_unselect' >   {finalName}{renderPrice()}</div>
            )
        }


      </div>
      {show ? <div
        className='dish_con_content' ref={conRef}
        style={{ background: lessThanMin ? '#F0DACF' : undefined }}
      >
        {renderNone()}
        {renderContent({ subDishes: itemData.subDishes, partId: itemData.partId })}
        {renderSkipNext()}
      </div> : <div className='toHZero' style={{ height: conHeight.current }}></div>}
    </section>
  )
}

export default DishItem;