import React, { useMemo } from 'react';
import { CurrencyPrice, getCPrice, Basket } from '../../../component';
import { connect, compose, action } from '../../../utils';
import { withRouter } from 'react-router-dom';

import './index.less';
import './indexPc.less';

const ShopTotlePrice = (props) => {
  const { dishList, restaurantInfo, status, dishListPrice, currentType, isLogin, rId, restaurantStatusLoading } = props;

  const count = useMemo(() => {
    if (dishList && dishList.length > 0) {
      let sum = 0;
      dishList.map(item => {
        sum = sum + Object.values(item)[0].count;
      })
      return sum;
    }
    return 0;

  }, [dishList])


  // 
  const shopStatus = useMemo(() => {
    const isMinOrder = restaurantInfo.isMinOrder;
    const minOrder = restaurantInfo.minOrder || 0;

    if (restaurantStatusLoading) {
      return { text: 'Loading...', dishable: true, }
    }
    if (!status.status) {
      return { text: 'Restaurant offline', dishable: true, }
    } else {
      if (currentType === 2) { // devilery
        if (!status.deliveryStatus || !status.isDelivery || !status.openStatus) {
          return { text: 'Delivery is unavailable', dishable: true, }
        }
        if (isMinOrder && minOrder > dishListPrice) {
          return { text: `Minimum Order ${getCPrice(minOrder)} for delivery`, dishable: true, }
        } else {
          if (count > 0) {
            return { text: 'Checkout', dishable: false, }
          } else {
            return { text: `Minimum Order ${getCPrice(minOrder)} for delivery`, dishable: true, }
          }
        }
      } else if (currentType === 3) {
        if (!status.collectionStatus || !status.isCollection || !status.openStatus) {
          return { text: 'Collection is unavailable', dishable: true, }
        }
        if (count === 0) {
          return { text: 'Checkout', dishable: true, }
        } else {
          return { text: 'Checkout', dishable: false, }
        }
      }
    }
    return {}
  }, [dishListPrice, currentType, status, restaurantInfo, dishList, restaurantStatusLoading])


  function onCheckout () {
    props.dispatch(action('statistic/recordPointTag', {
      recordclick: 'onCheckout',
      mark: JSON.stringify({
        shopStatus: !shopStatus.dishable,
      })
    }));

    if (document.body.offsetWidth < 1025) { //  pad
      if (props.location.pathname === '/shopOrderListM') {
        if (!shopStatus.dishable) {
          if (isLogin) {
            props.history.push('/myDetail')
          } else {
            props.history.push(`/login?returnUrl=${encodeURIComponent(`/myDetail`)}`)
          }
        }
      } else {
        props.history.push(`/shopOrderListM?rId=${rId}`)
      }
    } else {
      if (!shopStatus.dishable) {
        if (isLogin) {
          props.history.push('/myDetail')
        } else {
          props.history.push(`/login?returnUrl=${encodeURIComponent(`/myDetail`)}`)
        }
      }
    }
  }


  return (
    <div className={`shop_totle_price_container ${shopStatus?.dishable ? 'shop_totle_price_uneough_container' : undefined}`}>
      <button className='shop_totle_price_btn' onClick={onCheckout}>
        <span className='shop_totle_left_enough'>
          <Basket />
          <p className='shop_totle_dishlist_price'>
            <CurrencyPrice price={dishListPrice} />
          </p>
        </span>
        <span className='shop_totle_price_btn_text'>{shopStatus?.text} </span>
      </button>
    </div>
  )
}

export default compose(
  withRouter,
  connect(({ shopDetail: { restaurantInfo, status, restaurantStatusLoading }, orderList: { dishList, dishListPrice, currentType, rId }, user: { isLogin } }) => ({
    restaurantInfo, status, dishList, dishListPrice, currentType, isLogin, rId, restaurantStatusLoading
  })))(ShopTotlePrice);

