import React, { useEffect, useRef, useState, } from 'react';

import './index.less';
import './indexPc.less';

import { action, connect, compose, isPc, getReturnPath } from '../../utils';
import { withRouter } from 'react-router-dom';
import { AdaptHeader, Loader, Basket, message } from '../../component';
import ReviewPaymentContent from './reviewPaymentContent';
import ReviewPaymentDetailed from './reviewPaymentDetailed';
import ReviewPaymentNote from './reviewPaymentNote';
import ReviewPaymentNext from './reviewPaymentNext';

import OrderFailedOops from './orderFailedOops';
import CardPay from './cardPay';
import CouponCards from './couponCards';
import EnterCode from './couponCards/enterCode';
import ReviewPaymentChoosePop from './reviewPaymentChoosePop';
import ReviewPaymentShopStatus from './reviewPaymentShopStatus';

const ReviewPayment = (props) => {

  const { dishList, currentType, orderDetail,
    showOops, showEnterCode, showCard, buttonLoading, showPayChoose, status, showShopStatus, cardPayLoading } = props;

  const timer = useRef()

  useEffect(() => {
    props.dispatch(action('reviewPayment/testInfo', {
      callBack: (valide, shopId, errorCode) => {
        switch (valide) { // 1  2   3  4 rId
          case 1:
            props.history.push(`/login?returnUrl=${getReturnPath(props.location)}`)
            break;
          case 2:
            if (shopId) {
              if (errorCode === 1) {
                props.history.replace('/myDetail');
              } else {
                props.history.replace(`/shopDetail?rId=${shopId}`);
              }
            } else {
              props.history.replace(`/`);
            }
            break;
          case 3: // 
            props.dispatch(action('reviewPayment/checkOrderCoupon'));
            props.dispatch(action('reviewPayment/getRestaurantInfoAndStatus'));
            props.dispatch(action('reviewPayment/getCouponListByUser'));

            break;
          case 4:
            props.history.replace('/');
            break;
          case 5: //   review payment  save
            props.history.replace('/myDetail');
            break;
        }
      }
    }))
    return () => {
      timer.current && clearTimeout(timer.current)
      props.dispatch(action('reviewPayment/save', { cardPayLoading: false, showOops: false, showCard: false }))
    }
  }, [])


  useEffect(() => {
    if (orderDetail?.uuid) {
      if (orderDetail.isSubmit) {
        message.base('you order have been pay.')
        props.dispatch(action('orderList/clear'));
        props.history.push(`/historyOrderDetail?uuid=${orderDetail.uuid}&from=payment`);

      }
      // else {
      //   if (orderDetail.payStart && !orderDetail.payEnd) {
      //     props.dispatch(action('reviewPayment/save', { cardPayLoading: true }))

      //     timer.current = setTimeout(() => {
      //       testPayStatus(orderDetail.uuid)
      //     }, 5000)
      //   } else {
      //     props.dispatch(action('reviewPayment/save', { cardPayLoading: false }))
      //   }
      // }
    }

  }, [orderDetail?.uuid])

  function testPayStatus (uuid) {
    props.dispatch(action('reviewPayment/testPayStatus', {
      uuid: uuid, callBack: (res) => {
        if (res.error) { // 
          props.dispatch(action('reviewPayment/save', { showOops: true }))
        } else {
          if (res.isSubmit) {
            props.dispatch(action('reviewPayment/save', { showCard: false }))
            props.dispatch(action('orderList/clear'));
            props.history.push(`/historyOrderDetail?uuid=${uuid}&from=payment`);

          } else {
            if (res.payStart) {
              props.dispatch(action('reviewPayment/save', { cardPayLoading: true }))

              timer.current = setTimeout(() => {
                testPayStatus()
              }, 5000)
            } else {
              props.dispatch(action('reviewPayment/save', { cardPayLoading: false }))
            }
          }
        }

      }
    }))
  }


  //  enterCode popUp
  function onCloseEnterCode () {
    props.dispatch(action('reviewPayment/save', { showEnterCode: false }))
  }

  //  PayChoose popUp
  function onClosePayChoose () {
    props.dispatch(action('reviewPayment/save', { showPayChoose: false }))
  }

  function onPayNextClick () {
    props.dispatch(action('reviewPayment/checkRestaurantStatus'));
  }

  return (
    <section className='review_payment_page_wrapper'>
      <AdaptHeader title='Review & Payment' right={!isPc() && <Basket withoutBorder canPress />} />
      <section className='review_payment_page'>
        <section className='review_payment_page_container'>
          <ReviewPaymentContent
            dishList={dishList}
            currentType={currentType}
          />
          <aside>
            <ReviewPaymentDetailed />
            <CouponCards />
            <ReviewPaymentNote />
            <ReviewPaymentNext onPayNextClick={onPayNextClick} />
          </aside>
        </section>
      </section>
      {showOops && <OrderFailedOops />}
      {showEnterCode && <EnterCode onClose={onCloseEnterCode} />}
      {showPayChoose && <ReviewPaymentChoosePop onClose={onClosePayChoose} />}
      {showCard && <CardPay />}
      {buttonLoading && <Loader type='tran' loading={buttonLoading} />}
      {/* {cardPayLoading && <Loader loading={true} type='tran' text={`Please don't close or refresh this page util the payment process is finish.`} />} */}
      {showShopStatus && <ReviewPaymentShopStatus />}
    </section>
  )
}

export default compose(
  withRouter,
  connect(({ orderList: { dishList, currentType, uuid }, shopDetail: { restaurantInfo, status },
    reviewPayment: { orderDetail, payType, showOops, showEnterCode, showCard, buttonLoading, showPayChoose, showShopStatus, cardPayLoading } }) => ({
      dishList, currentType, orderDetail, uuid, payType,
      showOops, showEnterCode, showCard, buttonLoading, showPayChoose, restaurantInfo, status, showShopStatus, cardPayLoading
    })))(ReviewPayment)


