import React, { useState, useRef, useEffect, useMemo } from 'react';

import './index.less';
import './indexPc.less';

import Switch from '../switch'

import { CurrencyPrice, Icon, getCPrice, ToppingC, NoToppingC, ModalAlert, LineAdd, LineReduce } from '../../../../../component';
import { accAdd, hex2Rgba } from '../../../../../utils';

const DishPizzaItem = (props) => {

  const { pizza, onChooseDish, itemKey, totolPrice, categoryId, sub, isHalfPizza, from, finishChoose, sizePrice = 0, showCustomiz,
    onToogleShow, show,
  } = props;


  const sideType = useRef(['All over', 'Left side', 'Right side']);

  // const [show, setShow] = useState((sub || finishChoose || from === 'order') ? false : true);

  const currentSide = useMemo(() => {
    return pizza?.currentSide || 'All over'
  }, [pizza])


  // useEffect(() => {
  //   if (itemKey === 'selectedLeftSide') {
  //     if (!pizza['selectedLeftSideid'] && !show) {
  //       onToogleShow(true)
  //     }
  //   }

  //   if (itemKey === 'selectedRightSide') {
  //     if (!pizza['selectedRightSideid'] && !show) {
  //       onToogleShow(true)
  //     }
  //   }
  // }, [pizza])

  function toogleShow () {
    if (canChooseSize) {
      onToogleShow(!show);
    }
  }


  const canChooseSize = useMemo(() => {
    return true
  }, [pizza])

  // pid   immutable 
  // subId   immutable 
  // type 'del' 
  function onChoose (id, partId, pid, subId, type) {
    return () => {
      if (itemKey !== 'selectedtoppings') {
        onToogleShow(false);
      }
      onChooseDish(id, partId, pid, subId, type)
    }
  }

  const provious = useRef();

  function onChooseItem (id, partId, pid, subId, item) {
    return () => {
      if (itemKey !== 'selectedtoppings') {
        onToogleShow(false);
      }
      if (itemKey === 'sizes') { //  sizes
        if (from === 'order') { //  
          onChooseDish(id, partId, pid, subId, { action: 'reChooseSize', })
          return
        } else {
          // (!showCustomiz || isHalfPizza)  half pizza 
          // provious.current && itemKey === 'sizes' 
          if ((!showCustomiz || isHalfPizza) && provious.current) {
            if (provious.current === id) {

            } else {
              onChooseDish(id, partId, pid, subId, { action: 'reChooseSize', })
              provious.current = id;
              return
            }
          }
        }
      }
      onChooseDish(id, partId, pid, subId)
      provious.current = id;

    }
  }

  //  half side
  function onSwitch (checked) {
    props.changeCurrentSide && props.changeCurrentSide(sideType.current[0])
    props.toogleChangeHalf && props.toogleChangeHalf(checked)
  }

  function onSidePress (side) {
    return () => {
      props.changeCurrentSide && props.changeCurrentSide(side)

    }
  }

  function renderSideImg (side) {
    if (pizza.isHalf) {

    }
    switch (side) {
      case 'All over':
        return <img src={require('./all-pizza-icon.svg')} />
      case 'Left side':
        return <img src={require('./left-pizza-icon.svg')} />
      case 'Right side':
        return <img src={require('./right-pizza-icon.svg')} />
      default:
        break;
    }
  }

  function renderItem (item, index, partId, subId) {
    const showPrice = !(subId === 'selectedLeftSide' || subId === 'selectedRightSide' || (subId === 'sizes' && !finishChoose && isHalfPizza));
    const description = (subId === 'selectedLeftSide' || subId === 'selectedRightSide') && item.description;
    const price = subId === 'sizes' ? accAdd(sizePrice, item.price) : item.price;

    const showNoBorderTop = index === 0;


    return <div key={item.id}
      className={`dish_pizza_food_item_container_wrapper  ${item.default ? 'dish_pizza_item_active' : undefined}`}
      onClick={onChooseItem(item.id, index, partId, subId, item)}>
      <div className={`dish_pizza_food_item_container ${showNoBorderTop ? 'dish_pizza_food_item_container_no_border' : undefined}`}>
        <div
          className='dish_pizza_food_item_title'
          key={item.id}>
          <div className='dish_pizza_item_name'>
            <div className='dish_pizza_item_name_left'><Icon type={item.default ? 'iconxuanzhong' : 'iconweixuanze'} /></div>
            {item.name}</div>
          {showPrice && <div className='dish_pizza_item_price'><CurrencyPrice price={price} hiddenZero={subId !== 'sizes'} />
          </div>}
        </div>
        {description && <div className='dish_pizza_food_item_des tips_container'>
          <div className='dish_pizza_food_item_des_topping'>
            {
              item.defaultToppings?.map((topping, index) => {
                return <ToppingC topping={topping} key={index + '' + topping.id} />
              })
            }
          </div>
        </div>}
      </div>

    </div>
  }

  //  All over,Left side ,Right side 
  function renderToppingsContainer () {
    return <div className={'dish_pizza_side_container'}>
      {(pizza.isHalf || isHalfPizza) && <div className={'dish_pizza_side_header'}>
        {Object.keys(pizza[itemKey]).map((item, i) => {
          return <button className={item === currentSide ? 'button_active' : undefined}
            key={item + i}
            onClick={onSidePress(item)}>{item}</button>
        })}
        {renderSideImg(currentSide)}
      </div>}
      {Object.keys(pizza[itemKey]).map((item, i) => {
        if (currentSide === item) {
          return renderToppingsItem(item, i, props.index, itemKey)
        }
      })}
    </div>
  }

  function renderToppingsItem (itemName, i, partId, subId) {
    return pizza[itemKey][itemName] instanceof Array && pizza[itemKey][itemName].map((item, index) => {
      return <div className='dish_pizza_food_item_container_wrapper'
        key={item.id + '' + index}>
        <div
          className={`dish_pizza_food_item_container`}>
          <div
            className='dish_pizza_toppings_item_name'
          >
            {!!item.count && <div className='toppings_count' key='count'>{item.count}</div>}

            <div className={`toppings_name ${item.count > 0 ? 'toppings_name_selected' : undefined}`}
              key='name'
              onClick={onChoose(item.id, index, partId, subId, { action: 'add', type: currentSide })}>
              {item.default && item.count === 0 ? <LineReduce><span className='line-through'>{item.name}</span></LineReduce> : item.name}
              {item.isFree && <div key='free' className='toppings_free'>Free</div>}{item.isPremium && <div key='premium' className='toppings_premium'>Premium</div>}
            </div>
            {item.count ? <Icon type='iconicon_del' className='dish_pizza_toppings_del' onClick={onChoose(item.id, index, partId, subId, { action: 'del', type: currentSide })} /> : null}
          </div>
        </div>
      </div>
    })
  }

  //  item name
  function renderTipsHeaderOfHeader () {
    if (pizza?.selectedtoppings) {
      let isExistsSideTopping = false;
      Object.keys(pizza.selectedtoppings).map((a, j) => {
        const selectedtoppingsItem = Object.values(pizza.selectedtoppings[a]);
        if (!isExistsSideTopping && a !== 'All over') {
          selectedtoppingsItem.map((item) => {
            if (!isExistsSideTopping && item?.count > 0) {
              isExistsSideTopping = true;
              return;
            }
          });
        }
      });

      return Object.keys(pizza.selectedtoppings).map((a, j) => {
        const selectedtoppingsItem = Object.values(pizza.selectedtoppings[a]);

        if (selectedtoppingsItem.some(item => (a === 'All over' && item.def && item?.count !== 1)
          || (a === 'All over' && !item.def && item?.count > 0)
          || (a !== 'All over' && item?.count > 0)
          || (isHalfPizza && item?.count > 0))) {
          return <div className='tips_container' key={a + j}>
            {isExistsSideTopping && <div className='tips_container_title'>{a}</div>}
            {renderAddToppings(pizza, a)}
            {renderDeleteToppings(pizza, a)}
          </div>
        }
      })
    }
  }

  function renderAddToppings (pizza, a) {
    if (pizza?.selectedtoppings[a]) {
      const addToppings = pizza?.selectedtoppings[a]
        .filter(item => (isHalfPizza || item.def && item?.count > 1)
          || (!item.def && item?.count > 0) || (a !== 'All over' && item?.count > 0))
      if (addToppings && addToppings.length > 0) {
        return <LineAdd wrapper>
          {
            addToppings.map((item, index) => {
              let newItem = { ...item };
              if (newItem.def && !isHalfPizza && a === 'All over') {
                newItem.count = item.count - 1;
              }
              return <ToppingC topping={newItem} key={index + '' + item.id} />
            })
          }
        </LineAdd>
      }
    }
  }

  function renderDeleteToppings (pizza, a) {
    if (pizza?.selectedtoppings[a]) {
      const deleteToppings = pizza?.selectedtoppings[a].filter(item => item.default && !item.count)
      if (deleteToppings && deleteToppings.length > 0) {
        return <LineReduce wrapper>
          {
            deleteToppings.map((item, index) => {
              return <NoToppingC topping={item} key={index + '' + item.id} />
            })
          }
        </LineReduce>
      }
    }
  }

  const toppingPrice = getCPrice(totolPrice?.selectedtoppings, true)

  function renderTipsHeader () {
    return <div className={`dish_pizza_item_select_container ${havenoChoose ? 'dish_pizza_item_select_container_havenochoose' : undefined}`}>
      {
        pizza[itemKey + 'name'] ? <div className='dish_pizza_item_toppings_select' onClick={pizza[itemKey + 'name'] ? toogleShow : null}>
          <div className='dish_pizza_item_line_one'>
            <div> Choose Toppings
              {pizza[itemKey + 'name'] && <Icon type='iconicon_more_A1' className={`${show ? 'choose_icon_show' : 'choose_icon'}`} />}
            </div>
            {!!pizza?.selectedtoppingsCount && <p className='pizza_selected_toppings_count '>{pizza.selectedtoppingsCount}</p>}
            <p>{toppingPrice}</p>
          </div>
          {!show && <div className='dish_pizza_item_line_two'>
            {renderTipsHeaderOfHeader()}
          </div>}
        </div>
          : <div className='dish_pizza_item_unselect'>Choose Toppings</div>
      }
    </div>
  }

  // 
  const showDefault = useMemo(() => {
    if (itemKey === 'sizes' || itemKey === 'selectedLeftSide' || itemKey === 'selectedRightSide') {
      return false;
    } else {
      return pizza[props.name?.toLowerCase()] !== pizza[itemKey + 'name'];
    }
  }, [itemKey, pizza, props.name])

  // resetchoose  size 
  const havenoChoose = useMemo(() => {
    return pizza['haveno' + itemKey]
  }, [pizza, itemKey])

  function renderSelectedItem () {
    if (havenoChoose) {
      return <div className='dish_pizza_item_line_two'>
        <div className='dish_pizza_item_line_two_wrapper'> {pizza[itemKey + 'name']} have been removed.</div>
      </div>
    } else {
      if (showDefault) {
        return <div className='dish_pizza_item_line_two'>
          <LineAdd>{pizza[itemKey + 'name']}</LineAdd>
          <LineReduce><span className='line-through'>{pizza[props.name?.toLowerCase()]}</span></LineReduce>
        </div>
      } else {
        return <div className='dish_pizza_item_line_two'>
          <div className='dish_pizza_item_line_two_wrapper'> <Icon type='iconxuanzhong' />{pizza[itemKey + 'name']}</div>
        </div>
      }
    }
  }

  //    
  function renderHeaderPrice () {
    const price = itemKey === 'sizes' ? getCPrice(accAdd(sizePrice, pizza[itemKey + 'vprice'] || 0)) : getCPrice(pizza[itemKey + 'vprice'] || 0, true);
    const showPrice = !(itemKey === 'selectedLeftSide' || itemKey === 'selectedRightSide' || (itemKey === 'sizes' && !finishChoose));

    if (havenoChoose) {
      return props.name + ' resetted'
    } else {
      if (showPrice) {
        return price
      }
      else {
        return null
      }
    }
  }

  //  base sauce cheese 
  function renderHeader () {
    return <div onClick={pizza[itemKey + 'name'] ? toogleShow : null}
      className={`dish_pizza_item_select_container ${show ? 'dish_pizza_item_select_container_show' : undefined}
       ${havenoChoose ? 'dish_pizza_item_select_container_havenochoose' : undefined}`} >
      {
        pizza[itemKey + 'name'] ? <div className='dish_pizza_item_select'>
          <div className='dish_pizza_item_line_one'>
            <div>Choose {props.name}</div>
            <div> {renderHeaderPrice()}
              {pizza[itemKey + 'name'] && canChooseSize && <Icon type='iconicon_more_A1' className={`${show ? 'choose_icon_show' : 'choose_icon'}`} />}
            </div>
          </div>
          {!show && renderSelectedItem()}
        </div>
          : <div className='dish_pizza_item_unselect'>Choose {props.name} </div>
      }
    </div>
  }


  const toppingLimit = useMemo(() => {
    if (pizza.sizes && pizza.sizesid) {
      return pizza.sizes.filter(item => item.id === pizza.sizesid)[0].toppingLimit
    }
  }, [pizza])

  //  premium 
  function havePremium () {
    return pizza['selectedtoppingPlan'].filter(item => !!item.premium)?.length > 0;
  }


  //  normalTopping
  const normalBasicTopping = useMemo(() => {
    if (pizza['selectedtoppingPlan']) {
      return pizza['selectedtoppingPlan'].filter(item => !item.premium && (item.start !== 0 && item.end !== 0)).sort((a, b) => {
        return a.start - b.start
      }).concat(pizza['selectedtoppingPlan'].filter(item => !item.premium && (item.start === 0 && item.end === 0)))
    } else {
      return []
    }

  }, [pizza])

  const premiumTopping = useMemo(() => {
    if (pizza['selectedtoppingPlan']) {
      return pizza['selectedtoppingPlan'].filter(item => !!item.premium)
    } else {
      return []
    }
  }, [pizza])

  function showToppingPlan () {
    ModalAlert({
      com: renderToppingPlan(),
      className: 'selected_topping_plan',
    })
  }

  function renderNextToppingPrice () {
    const nextNormalPrice = pizza.nextNormalPrice || normalBasicTopping[0]?.price || 0
    const nextPremiumPrice = pizza.nextPremiumPrice || premiumTopping[0]?.price || 0
    return <div className='next_topping_price border-top'>
      <div className='next_topping_price_left'>
        <div>Next extra topping {getCPrice(Number(nextNormalPrice))}</div>
        {havePremium() && <div>Next extra premium topping {getCPrice(Number(nextPremiumPrice))}</div>}
        {pizza?.toppingReLimit > 0 && <div>Swap {pizza.toppingReLimit} topping(s) for free </div>}
      </div>
      <Icon type='icon5' onClick={showToppingPlan} />
    </div>
  }


  //  topping 
  function renderToppingPlan () {

    function renderItem (arr, str, normalcount) {
      return arr && arr.length > 0 && <div key={str} className='toppings_plan'>
        <h6>{str} topping:</h6>
        {
          arr.map((item, index) => {
            const { price, start, end, premium } = item;
            if (start && end) {
              return <div key={index}>{`${start} to ${end} ${getCPrice(price)}`}</div>
            } else {
              if (normalcount > 1) {
                return <div key={index}>{` ${getCPrice(price)} each ${premium ? '(on top of basic topping charge)' : ''} thereafter`}</div>
              } else {
                return <div key={index}>{` ${getCPrice(price)} each ${premium ? '(on top of basic topping charge)' : ''}`}</div>
              }
            }
          })
        }
      </div>
    }



    return <div className='selected_topping_plan_container'>
      {renderItem(normalBasicTopping, havePremium() ? 'Extra basic' : 'Extra', normalBasicTopping.length)}
      {renderItem(premiumTopping, 'Extra premium')}
      <div className='toppings_plan'>
        {pizza?.toppingReLimit > 0 && <div>Swap {pizza.toppingReLimit} topping(s) for free</div>}
        <div>Maximum {toppingLimit} Toppings.</div>
      </div>
    </div>
  }

  return (
    (((itemKey === 'selectedbase' && pizza[itemKey].length > 0)
      || (itemKey === 'selectedsauce' && pizza[itemKey].length > 0)
      || (itemKey === 'selectedcheese' && pizza[itemKey].length > 0))
      || (itemKey !== 'selectedbase' && itemKey !== 'selectedsauce' && itemKey !== 'selectedcheese')) && <section
        className='dish_pizza_item_container' key={itemKey}>
      {itemKey !== 'selectedtoppings' ? renderHeader() : renderTipsHeader()}
      <div className='dish_pizza_con_content'>
        {itemKey === 'selectedtoppings' && show && <div className='dish_pizza_item_toppings_unselect'>
          {categoryId === 5 && !isHalfPizza && <div className='dish_pizza_switch'> <span>Split in half</span>  <Switch onSwitch={onSwitch} checked={pizza.isHalf} /></div>}
          {renderNextToppingPrice()}
        </div>}


        {show && (itemKey !== 'selectedtoppings') && pizza[itemKey].map((item, i) => {
          return renderItem(item, i, props.index, itemKey)
        })}
        {show && (itemKey === 'selectedtoppings') && renderToppingsContainer()
        }
      </div>
    </section>
  )
}

export default DishPizzaItem;