import React from 'react';
import { Icon } from '../../../../../component';
import PropTypes from 'prop-types';

import './index.less';
import './indexPc.less';


const FoodTasteItem = (props) => {
  const { type, number } = props;

  function getDate () {
    switch (type) {
      case 'spicy':
        return {
          icon: 'iconicon_spicy',
          iconColor: '#F58E62',
          text: type,
        }
      case 'vegetarian':
        return {
          icon: 'iconicon_vege',
          iconColor: '#83CC9B',
          text: type,
        }
      case 'cal':
        return {
          icon: 'iconicon_cal',
          iconColor: '#62A4F5',
          text: `${number} ${type}`,
        }
      case 'nuts':
        return {
          icon: 'iconicon_peanut',
          iconColor: '#83CC9B',
          text: type,
        }
      default:
        return {}
    }
  }

  const data = getDate();

  function renderIcon () {
    if (type === 'spicy') {
      return Array.from({ length: number }).map((item, index) => {
        return <Icon type={data.icon} style={{ color: data.iconColor }} key={index} />
      })
    } else {
      return <Icon type={data.icon} style={{ color: data.iconColor }} />
    }
  }


  return (
    <section className='food_taste_item'>
      {renderIcon()}
      <span>{data.text}</span>
    </section>
  )
}

export default FoodTasteItem;

FoodTasteItem.defaultProps = {
  type: 'spicy',
}

FoodTasteItem.propTypes = {
  type: PropTypes.oneOf(['spicy', 'vegetarian', 'cal', 'nuts']),
}
