import React, { useMemo } from "react";
import './index.less'
import './indexPc.less'

const CardTypeImg = (props) => {

  const { cardType } = props;

  const img = useMemo(() => {
    const lowerCaseCardType = cardType?.toLowerCase()?.replace(/\s*/g, "");
    switch (lowerCaseCardType) {
      case 'americanexpress':
        return require('./img/AmericanExpress.svg')
      case 'debitmastercard':
        return require('./img/DebitMasterCard.svg')
      case 'diners':
        return require('./img/Diners.svg')
      case 'discover':
      case 'dinersclub':
        return require('./img/Discover.svg')
      case 'jcb':
        return require('./img/JCB.svg')
      case 'maestro':
        return require('./img/Maestro.svg')
      case 'mastercard':
        return require('./img/MasterCard.svg')
      case 'visa':
        return require('./img/Visa.svg');
      case 'revolut':
        return require('./img/revolut.svg');
      default:
        return require('../../assets/icon_card.svg');
    }
  }, [])

  return <img src={img} className='card_type_img_com' />
}

export default CardTypeImg;