import React, { useMemo } from 'react';

import './index.less';
import './indexPc.less';
import { MyMenu, AdaptHeader } from '../../component'
import MenuBg from './menuBg';

const MMenu = (props) => {

  function onLogout () {
    props.history.goBack()
  }

  function onHistory () {
    props.history.push('/historyOrder')
  }

  function onAddress () {
    props.history.push('/myAddress')
  }

  return (<section className='m_menu_page'>
    <div className="m_menu_header_container">
      <AdaptHeader title='Menu' theme='white' />
      <MenuBg className='m_menu_page_bg'/>
    </div>
    <MyMenu onLogout={onLogout} onHistory={onHistory} onAddress={onAddress} />

  </section>)

}

export default MMenu;