import React, { useState, useEffect, useRef } from 'react';
import { createRoot } from 'react-dom/client';

import Icon from '../icon';
import './index.less';
import './indexPc.less';

const Notification = (props) => {
  const { msg, title, onclick, btntext, remove } = props;

  const contentRef = useRef();
  const container = useRef();

  useEffect(() => {

    setTimeout(() => {
      animateLeave()
      contentRef.current.addEventListener("animationend", remove);
    }, 5000)
  }, [])

  //  RN 
  // useMessage(onRNMessage)



  function animateLeave () {
    contentRef.current.className = `d-alert-modal flyOut `;
    container.current.className = "com_notification_modal";
  }


  function onClickNotification (e) {
    e.stopPropagation();
    animateLeave()
    contentRef.current.addEventListener("animationend", onclick);
  }



  return (
    <section className="com_notification_modal" ref={container} onClick={remove}>
      <div className={`d-alert-modal flyIn `} ref={contentRef}>
        <div className='com_notification_modal_content' onClick={onClickNotification}>
          <div className='com_notification_modal_header'>
            <div className='com_notification_modal_header_left'>
              <Icon type={'iconimg_app_logo_A7'} />Message Notification
            </div>
            Now
          </div>
          <div className='com_notification_modal_content'>
            <h5 >{title}</h5>
            <div className='com_notification_modal_content_wrapper'>
              <p className='one-line'>{msg}</p>
              {btntext && <button>{btntext}</button>}
            </div>
          </div>
        </div>
      </div>
    </section>
  )
};


const ModalNotification = ({ title, msg, btntext, click }) => {

  let msgBox = document.createElement('div');
  document.body.appendChild(msgBox);

  // setTimeout(() => {
  //   remove();
  // }, 5000)

  function onclick (e) {

    click && click()
    remove();
  }



  function remove () {
    if (msgBox && document.body.contains(msgBox)) {
      document.body.removeChild(msgBox);
    }
  }

  createRoot(msgBox).render(
    <Notification
      title={title}
      msg={msg}
      btntext={btntext}
      onclick={onclick}
      remove={remove}
    />);
}

export default ModalNotification
