import React from 'react';
import { Modal } from '../../../component';
import './index.less';
import './indexPc.less';

const ClearBasketModel = (props) => {

  const { name, onOK, onCancel } = props;

  return <Modal title='Clear previous basket' className='clear_basket_model'>
    <div className='clear_basket_model_text'>Do you want to clear the basket from the restaurant '<strong>{name}</strong>'?</div>
    <div className='clear_basket_model_buttons'>
      <button onClick={onOK}>OK</button>
      <button onClick={onCancel}>Cancel</button>
    </div>

  </Modal>

}

export default ClearBasketModel