import { action, cookie, delay, getOs } from '../utils';
import { loginS } from '../service';

const initState = {
  isLogin: false,
  user: {},
  selectedAddress: {
    lat: '', lng: '', ad: '',
  }, // 
  deleteUserLoading: false,
  latlng: null,//
};

export default {
  namespace: 'user',
  state: initState,
  reducers: {
    save (state, { payload }) {
      return { ...state, ...payload };
    },
  },
  effects: {

    *saveAddress ({ payload: { lat, lng, ad, callBack } }, { call, put, select }) {
      try {
        yield put(action('save', {
          selectedAddress: {
            lat: lat,
            lng: lng,
            ad: ad,
          },
        }));
        callBack && callBack()
      } catch (e) {
        console.error("saveAddress", e);
      }
    },
    // 
    * saveUserInfo ({ payload: { user } }, { call, put, select }) {
      try {
        yield put(action('save', {
          user: user,
          isLogin: true,
        }));
      } catch (e) {
        console.error("saveUserInfo", e);
      }
    },

    // 
    * getCurrentLatLng ({ payload }, { call, put, select }) {
      try {

        function gainUserLatLng () {
          return new Promise((resolve, reject) => {
            navigator.geolocation.getCurrentPosition((data) => {
              if (data && data.coords && data.coords.latitude) {
                const latlng = {
                  lat: data.coords.latitude,
                  lng: data.coords.longitude,
                }
                resolve(latlng)

              }
            }, (error) => {
              reject(null)

            }, {
              enableHighAccuracy: true,
              timeout: 5000,
              maximumAge: 0
            })
          })
        }

        const res = yield call(gainUserLatLng)

        yield put(action('save', { latlng: res }));

      } catch (e) {
        console.error("saveUserInfo", e);
      }
    },

    // user 
    logout: [
      function* ({ payload }, { call, put, select }) {
        try {
          yield call(delay, 100);
          const { isLogin, } = yield select(({ user }) => user);
          if (isLogin) {
            const res = yield call(loginS.logout);
            if (res) {
              cookie.remove('SESSION')
              yield put(action('save', {
                isLogin: false,
                user: {},
                latlng: null,
              }));
              yield put(action('orderList/clearUUID'))
            }
          }
        } catch (e) {
          console.error("logout", e);
        }
      },
      { type: 'takeLatest' }
    ],

    logout401: [
      function* ({ payload }, { call, put, select }) {
        try {
          yield call(delay, 100);
          const { isLogin, } = yield select(({ user }) => user);
          if (isLogin) {
            cookie.remove('SESSION')
            yield put(action('save', {
              ...initState
            }));
            yield put(action('orderList/clearUUID'))
          }
        } catch (e) {
          console.error("logout401", e);
        }
      },
      { type: 'takeLatest' }
    ],

    *updateUserName ({ payload: { username } }, { call, put, select }) {
      try {

        const params = {
          username: username,
        }

        const { user } = yield select(({ user }) => user);

        if (user.principal.user.userName === username) {
          return
        }

        const res = yield call(loginS.updateUserName, params);

        if (res.code === 200) {
          const updatedUser = {
            ...user, ...{
              principal: {
                user: {
                  ...user.principal.user,
                  userName: username,
                }
              }
            }
          }
          yield put(action('save', { user: updatedUser }))
        }
      } catch (e) {
        console.error("updateUserName", e);
      }
    },

    deleteUser: [
      function* ({ payload: { callBack } }, { call, put, select }) {
        try {
          yield call(delay, 100);
          yield put(action('save', {
            deleteUserLoading: true
          }));
          const { isLogin, user } = yield select(({ user }) => user);
          const res = yield call(loginS.deleteUser);

          if (isLogin) {
            cookie.remove('SESSION')
            yield put(action('save', {
              ...initState
            }));
          }
          callBack && callBack()
        } catch (e) {
          console.error("deleteUser", e);
        } finally {
          yield put(action('save', {
            deleteUserLoading: false
          }));
        }
      },
      { type: 'takeLatest' }
    ],

    *saveDeviceToken ({ payload: { fcmToken } }, { call, put, select }) {
      try {

        const { user } = yield select(({ user }) => user);

        const phone = user?.principal?.user?.phone

        const params = {
          "deviceToken": fcmToken,
          "deviceType": getOs(),
          "phoneNumber": phone
        }
        const res = yield call(loginS.saveDeviceToken, params);

      } catch (e) {
        console.error("saveAddress", e);
      }
    },

    onError: [
      function* ({ payload }, { call, put, select }) {
        try {
          yield call(delay, 300);
          // ModalAlert({
          //   msg: '', onCancel: () => { }, onSure: () => {
          //     window.location.reload()
          //   }
          // })
        } catch (e) {
          console.error("logout", e);
        }
      },
      { type: 'takeLatest' }
    ],
  },
};