import { action, AppConfig, delay } from '../utils';
import { addressS } from '../service'
import { message } from '../component';

const initState = {
  allRensentAddress: [],// 
  predictions: [],// 

  edit: { // 
    id: '',
    name: '',
    address: '',
    lat: '',
    lon: '',
  },
  showModel: false,

};

export default {
  namespace: 'myAddress',
  state: initState,
  reducers: {
    save (state, { payload }) {
      return { ...state, ...payload };
    },
  },
  effects: {
    // 

    *editAddress ({ payload: { name, address, lat, lon } }, { call, put, select }) {
      try {
        const { edit } = yield select(({ myAddress }) => myAddress);
        yield put(action('save', {
          edit: {
            ...edit,
            name: name === '' ? '' : (name || edit.name),
            address: address === '' ? '' : (address || edit.address),
            lat: lat || edit.lat,
            lon: lon || edit.lon,
            id: edit.id
          }
        }))
        // 
        if (edit.addAddr !== address) {
          yield put(action('autocomplete', { input: address }))
        }
      } catch (e) {
        console.error("editAddress", e);
      }
    },


    //  
    * toogleModal ({ payload: { showModel, edit } }, { call, put, select }) {
      try {
        yield put(action('save', { showModel }));
        if (edit) {
          yield put(action('save', {
            edit: {
              ...edit
            },
          }));
        }
        if (!showModel) {
          yield put(action('save', {
            edit: initState.edit,
          }));
        }
      } catch (e) {
        console.error("toogleModal", e);
      }
    },

    // 
    * onSaveEdit ({ payload }, { call, put, select }) {
      try {
        const { edit } = yield select(({ myAddress }) => myAddress);
        if (!!edit.lat) {
          const { user } = yield select(({ user }) => user);
          const userId = user.principal.user.userId;
          if (edit.id) {
            const res = yield call(addressS.editAddr, { ...edit, userId })
          } else {
            const res = yield call(addressS.addAddr, { ...edit, userId })
          }
          // 
          yield put(action('save', {
            edit: { ...initState.edit },
            showModel: false,
          }));

        } else {
          const { predictions } = yield select(({ myAddress }) => myAddress);

          if (predictions?.length > 0) {
            message.base('Please select an address from the list to proceed.')
          } else {
            message.base('Can not find this Address.')
          }
        }
        yield put(action('getAllAddr'));
      } catch (e) {
        console.error("onSaveEdit", e);
      }
    },

    // 
    * onDelete ({ payload }, { call, put, select }) {
      try {
        if (payload?.address) {
          const { address } = payload;
          const res = yield call(addressS.deleteAddr, { id: address.id, address: address.address })
        } else {
          const { edit } = yield select(({ myAddress }) => myAddress);
          const res = yield call(addressS.deleteAddr, { id: edit.id, address: edit.address })
          // 
          yield put(action('save', {
            edit: {},
            showModel: false,
          }));
        }
        yield put(action('getAllAddr'))
      } catch (e) {
        console.error("onDelete", e);
      }
    },

    //  address 
    *clearAddress ({ payload }, { call, put, select }) {
      try {
        const { edit } = yield select(({ myAddress }) => myAddress);
        yield put(action('save', {
          edit: { ...edit, address: '' },
        }));
      } catch (e) {
        console.error("clearAddress", e);
      }
    },

    // 
    * getAllAddr ({ payload }, { call, put, select }) {
      try {
        const { isLogin } = yield select(({ user }) => user);
        if (isLogin) {
          const res = yield call(addressS.getAllAddr);
          yield put(action('save', {
            allRensentAddress: res.data || [],
          }));
        }

      } catch (e) {
        console.error("getAllAddr", e);
      }
    },

    // address  
    autocomplete: [
      function* ({ payload: { input } }, { call, put, select }) {
        if (input.length < 2) {
          return
        }
        yield call(delay, 100);
        const { latlng } = yield select(({ user }) => user);

        try {
          const params = {
            input: input,
            language: 'en',
            // components: 'country:IE',
          }
          if (latlng) {
            params.location = latlng.lat + ',' + latlng.lng;
            params.radius = AppConfig.SEARCH_PLACE_RADIUS;
          }
          const res = yield call(addressS.autocomplete, params);
          yield put(action('save', {
            predictions: res.predictions || [],
          }));
        } catch (e) {
          console.error("autocomplete", e);
        }
      },
      { type: 'takeLatest' }
    ],

    //   
    * getFullAddress ({ payload: { input, callBack } }, { call, put, select }) {
      try {
        const params = {
          input: input,
          inputtype: 'textquery',
          fields: 'formatted_address,name,rating,opening_hours,geometry',
        };

        const res = yield call(addressS.getFullAddress, params);
        if (res.status === 'OK') {
          callBack(res.candidates)
        }

      } catch (e) {
        console.error("getAllAddr", e);
      }
    },

    // 
    * queryLocalAddrsByGeo ({ payload: { lat, lng } }, { call, put, select }) {
      try {
        const latlng = lat + "," + lng + "";
        const params = {
          latlng: latlng
        }
        const resData = yield call(addressS.queryLocalAddrsByGeo, params);
        const results = resData.results[0];

      } catch (e) {
        console.error(e);
      }
    },

  },
};