import React from 'react';
import { Icon } from '../../../../component';
import PropTypes from 'prop-types';


import './index.less';
import './indexPc.less';

const ShopBasicInfoItem = (props) => {


  const { type, onClick, text2 = '', text1 = '', text3 } = props;

  function getDate () {
    switch (type) {
      case 'address':
        return {
          icon: 'iconicon_location_b',
          iconColor: '#47B36B',
          text1: text1,
          text2: text2,
        }
      case 'discount':
        return {
          icon: 'iconicon_discount_o',
          iconColor: 'var(--themeColor)',
          text1: text1,
          text2: '',
        }
      case 'time':
        return {
          icon: 'iconicon_time',
          iconColor: '#1D7EF5',
          text1: '',
          text2: text2,
          text3: text3,
        }
    }
  }

  const data = getDate();

  return <div className={`shop_basic_info_item  ${type === 'address' ? 'shop_basic_info_item_nowrap' : undefined}`}>
    <Icon type={data.icon} style={{ fill: data.iconColor }} />
    {data.text1 && <div style={{ color: data.iconColor }}> {Array.isArray(data.text1) ? data.text1.map((item, index) => {
      return <p key={index}>{item}</p>
    }) : data.text1}</div>}
    {data.text2 && <p className='text2'>{data.text2}</p>}
    {data.text3 && <div className='text3_container'>{
      data.text3.map((item, index) => {
        return <div className='text3_container_item' key={index}>
          <p >{item.type}</p>
          {
            Array.isArray(item.time) ?
              <div className='text3_container_time_container' key={index}>
                {
                  item.time?.map((a, i) => {
                    return <p className='text4' key={i}>{a}</p>
                  })
                }
              </div> : <p className='text3'>{item.time}</p>
          }
        </div>
      })
    }
    </div>
    }
  </div>
}

export default ShopBasicInfoItem;

ShopBasicInfoItem.propTypes = {
  type: PropTypes.oneOf(['address', 'discount', 'time']),
}