import { action, checkPhoneNumber, formatPhone, delay, getUniqueId, AppConfig, inRN, cookie } from '../utils';
import { loginS } from '../service';
import { message } from '../component';

const initState = {
  step: 1,
  phone: '',
  validatePhone: false,
  loading: false, // 
  code: '', // 
  errcode: false, // 
  isDeveloper: false
};

// 1 website2 android 3 ios
function getDeviceType () {
  switch (window.Platform) {
    case 'iphoneX':
      return 3;
    case 'ios':
      return 3;
    case 'android':
      return 2;
    case 'pad':
      return 3;
    default:
      return 1;
  }
}

export default {
  namespace: 'login',
  state: initState,
  reducers: {
    save (state, { payload }) {
      return { ...state, ...payload };
    },
  },
  effects: {
    // 
    * checkPhone ({ payload: { phone } }, { call, put, select }) {
      try {
        const validatePhone = checkPhoneNumber(phone);
        yield put(action('save', {
          phone: validatePhone ? formatPhone(phone) : phone,
          validatePhone: validatePhone,
        }));
      } catch (e) {
        console.error('checkPhone', e);
      }
    },

    // 
    getCode: [
      function* ({ payload }, { call, put, select }) {

        try {
          yield call(delay, 300);
          yield put(action('save', { loading: true }))
          const { phone, validatePhone, isDeveloper } = yield select(({ login }) => login);
          const { landing } = yield select(({ webConfigModal }) => webConfigModal);
          if (!validatePhone) {
            message.base('Incorrect phone number format')
            return
          }
          yield put(action('save', {
            phone: formatPhone(phone)
          }));

          if (isDeveloper) {
            const params = {
              originFrom: landing.webSiteConfigId,
              phone: String(formatPhone(phone)).replace(/\s/ig, ""),
              key: "fwjtltsh2000A_",
            }
            const res = yield call(loginS.smsStaff, params);
            if (res) {
              yield put(action('save', {
                step: 2
              }));
            } else {
              message.base('Can\'t get code')
            }
          } else {
            const params = {
              originFrom: landing.webSiteConfigId,
              phone: String(formatPhone(phone)).replace(/\s/ig, "")
            }
            const res = yield call(loginS.sendCode, params);
            if (res) {
              yield put(action('save', {
                step: 2,
              }));
            } else {
              message.base('Incorrect phone number format')
            }
          }

        } catch (e) {
          console.error('getCode', e);
        } finally {
          yield put(action('save', { loading: false }))
        }
      }, { type: 'takeLatest' }
    ],

    loginSmsCode: [
      function* ({ payload: { phone } }, { call, put, select }) {

        try {
          yield call(delay, 300);
          yield put(action('save', { loading: true }))
          const { landing } = yield select(({ webConfigModal }) => webConfigModal);



          const params = {
            originFrom: landing.webSiteConfigId,
            phone: String(formatPhone(phone)).replace(/\s/ig, "")
          }
          const res = yield call(loginS.sendCode, params);
          if (res) {

          } else {
            message.base('Incorrect phone number format')
          }
        } catch (e) {
          console.error('getCode', e);
        } finally {
          yield put(action('save', { loading: false }))
        }
      }, { type: 'takeLatest' }
    ],

    // 
    * reGetCode ({ payload }, { call, put, select }) {
      try {
        const { phone, isDeveloper } = yield select(({ login }) => login);
        const { landing } = yield select(({ webConfigModal }) => webConfigModal);
        if (!isDeveloper) {
          const params = {
            originFrom: landing.webSiteConfigId,
            phone: String(formatPhone(phone)).replace(/\s/ig, ""),
          }
          const res = yield call(loginS.sendCode, params);
        } else {
          const params = {
            originFrom: landing.webSiteConfigId,
            phone: String(formatPhone(phone)).replace(/\s/ig, ""),
            key: "fwjtltsh2000A_",
          }
          const res = yield call(loginS.smsStaff, params);
        }
      } catch (e) {
        console.error('reGetCode', e);
      }
    },
    // 
    * toLogin ({ payload: { phone, code, loginType, token, callBack } }, { call, put, select }) {
      try {
        const { loading } = yield select(({ login }) => login);
        if (!loading) {
          cookie.remove('SESSION')

          yield put(action('save', {
            loading: true
          }));
          if (loginType === 'otherLogin') {// session
            const r = yield call(loginS.getSession);
          }
          const params = {
            code: code,
            phone: phone ? String(formatPhone(phone))?.replace(/\s/ig, "") : undefined,
            loginType: loginType,
            token: token,
          };
          const res = yield call(loginS.login, params);
          if (res?.authenticated) {
            // 
            const user = {
              ...res, ...{
                details: {
                  sessionId: window.btoa(res?.details?.sessionId)
                }
              }
            }

            yield put(action('user/saveUserInfo', { user: user }));
            yield put(action('save', {
              ...initState
            }));
            yield put(action('saveUserDevice'));
            // yield put(action('home/displayActivityResult'))


            callBack && callBack(user)
          } else {
            yield put(action('save', {
              errcode: true,
            }));
            message.base('Invalid code')
          }
        }
      } catch (e) {
        console.error('toLogin', e);
      } finally {
        yield put(action('save', {
          loading: false
        }));
      }
    },

    //  
    *saveUserDevice ({ payload }, { call, put, select }) {
      try {
        if (inRN()) {
          const { landing, isNewCustomer } = yield select(({ webConfigModal }) => webConfigModal);
          yield call(loginS.saveUserDevice, {
            deviceToken: getUniqueId(),
            deviceType: getDeviceType(),
            webSiteConfigId: landing.webSiteConfigId,
            appVersion: AppConfig.APP_VERSION,
            isNewCustomer: isNewCustomer,
          });
        }

      } catch (e) {
        console.error('saveUserDevice', e);
      }
    },
    *clear ({ payload }, { call, put, select }) {
      try {
        yield put(action('save', {
          ...initState
        }));
      } catch (e) {
        console.error('clear', e);
      }
    },

    *backStep ({ payload: { step } }, { call, put, select }) {
      try {
        if (step === 1) {
          const { errcode } = yield select(({ login }) => login);
          if (errcode) { //  
            yield put(action('save', {
              code: ''
            }));
          }
        }
        yield put(action('save', {
          step: step
        }));
      } catch (e) {
        console.error('backStep', e);
      }
    },

    *goDevelop ({ payload }, { call, put, select }) {
      try {
        message.base('You have activated developer mode')
        yield put(action('save', {
          isDeveloper: true
        }));
      } catch (e) {
        console.error('goDevelop', e);
      }
    },

  },
};