import React, { useEffect, useMemo } from 'react';
import { connect, compose, withRouter } from '../../../../utils';
import AboutUsCard from '../../components/aboutUsCard';
import FoodCard from '../../components/foodCard';

import './index.less'
import './indexPc.less';


const IntroductionC = (props) => {


  const { landing, from } = props;

  return <section className={`introduction_c ${from === 'page' ? 'introduction_c_page' : undefined}`}>
    <div className='introduction_c_wapper'>
      <div className='introduction_c_title'>
        {landing?.webSiteIntroductionTitle}
      </div>
      <div className='introduction_c_content'>
        {
          landing?.webSiteIntroductions?.map((item, index) => {
            if (from === 'page') {
              const data = {
                title: item.introTitle,
                img: item.introImageUrl,
                des: item.introDescription
              }
              return <AboutUsCard data={data} reserve={index % 2 === 1} key={index} />
            } else {
              const data = {
                title: item.introTitle,
                img: item.introImageUrl,
                des: item.introDescription
              }
              if (index < 6) {
                return <FoodCard data={data} reserve={index % 2 === 1} key={index} />
              } else {
                return null
              }
            }
          })
        }
      </div>

    </div>
  </section>
}

export default compose(
  connect(({ webConfigModal: { landing, } }) => ({
    landing
  })),
  withRouter
)(IntroductionC);