import React from 'react';

import './index.less';
import './indexPc.less';
import Icon from '../icon'

const CloseIcon = (props) => {

  const { text, onClick } = props

  return !!text ? <div onClick={onClick} className='input_text_close_icon' >
    <Icon type='iconicon_close' />
  </div> : null

}

export default CloseIcon;