function addPhoneNumSpace (num, type) {
  var str = "";
  if (type === 1) {
    var index = num.indexOf('8');
    str = '00353' + num.substr(index, 2) + '' + num.substring(index + 2, num.length);
  } else if (type === 2) {
    var index2 = num.indexOf('7');
    str = '0044' + num.substr(index2, 2) + '' + num.substring(index2 + 2, num.length);
  }
  return str;
};

export function transFormTel (str) {
  if (/^00353(.+?)/.test(str)) {
    return str.replace(/^(00353)(\d{2})(\d{3})(.+?)$/, '+353 $2 $3 $4')
  }
  if (/^0044(.+?)/.test(str)) {
    return str.replace(/^(0044)(\d{2})(\d{3})(.+?)$/, '+44 $2 $3 $4')
  }

}


export function checkPhoneNumber (phone) {
  phone = formatPhone(phone);
  if (phone.length < 6) {
    return false;
  }
  return true;
};

export function formatPhone (phone, onlyFormat) {
  phone = phone.replace(/\D/g, "");
  if (phone.length <= 0)
    return '';
  if (phone.indexOf("8") === '0' && phone.length === 9)
    return addPhoneNumSpace("003538" + phone.substr(1, 8), 1);
  else if (phone.indexOf("08") === 0 && phone.length === 10)
    return addPhoneNumSpace("003538" + phone.substr(2, 8), 1);
  else if (phone.indexOf("3538") === 0 && phone.length === 12)
    return addPhoneNumSpace("003538" + phone.substr(4, 8), 1);
  else if (phone.indexOf("35308") === 0 && phone.length === 13)
    return addPhoneNumSpace("003538" + phone.substr(5, 8), 1);
  else if (phone.indexOf("003538") === 0 && phone.length === 14)
    return addPhoneNumSpace(phone, 1);
  else if (phone.indexOf("7") === 0 && phone.length === 10)
    return addPhoneNumSpace("00447" + phone.substr(1, 9), 2);
  else if (phone.indexOf("07") === 0 && phone.length === 11)
    return addPhoneNumSpace("00447" + phone.substr(2, 9), 2);
  else if (phone.indexOf("447") === 0 && phone.length === 12)
    return addPhoneNumSpace("00447" + phone.substr(3, 9), 2);
  else if (phone.indexOf("4407") === 0 && phone.length === 13)
    return addPhoneNumSpace("00447" + phone.substr(4, 9), 2);
  else if (phone.indexOf("00447") === 0 && phone.length === 14)
    return addPhoneNumSpace(phone, 2);
  else if (onlyFormat)
    return phone;
  return '';
}

export function formatMoney (price, n, currency) {
  n = n >= 0 && n <= 20 ? n : 2;
  price = parseFloat((price + "").replace(/[^\d.-]/g, "")).toFixed(n) + "";
  if (price !== "NaN") {
    var l = price.split(".")[0].split("").reverse(); //reverse()
    var r = price.split(".")[1];
    var t = "";
    for (var i = 0; i < l.length; i++) {
      t += l[i] + ((i + 1) % 3 === 0 && (i + 1) !== l.length ? "," : "");
    }
    if (n !== 0) {
      currency = currency ? currency : getCurrency();
      return currency + t.split("").reverse().join("") + "." + r;
    } else
      return t.split("").reverse().join("");
  }
}


