/**
 *
 * @param target
 * @param top boolean 
 * @returns {*}  
 */
export function getScroll (target, top) {

  if (typeof window === "undefined") {
    return 0;
  }

  const prop = top ? "pageYOffset" : "pageXOffset";
  const method = top ? "scrollTop" : "scrollLeft";
  const isWindow = target === window;

  let ret = isWindow ? target[prop] : target[method];
  // ie6,7,8 standard mode
  if (isWindow && typeof ret !== "number") {
    ret = document.documentElement[method];
  }
  return ret;
}

export function scrollTo (y, options = {}) {
  const { getContainer = () => window, callback = () => { }, duration = 450 } = options;

  const container = getContainer();
  const scrollTop = getScroll(container, true);
  const startTime = Date.now();

  const frameFunc = () => {
    const timestamp = Date.now();
    const time = timestamp - startTime;
    const nextScrollTop = easeInOutCubic(time > duration ? duration : time, scrollTop, y, duration);
    if (container === window) {
      window.scrollTo(window.pageXOffset, nextScrollTop);
    } else {
      container.scrollTop = nextScrollTop;
    }
    if (time < duration) {
      raf(frameFunc);
    } else if (typeof callback === "function") {
      callback();
    }
  };
  raf(frameFunc);
}

// scrollIntoView  antDesign  
export function scrollToAnchor (anchorName, options = {}) {
  const { getContainer = () => window, offsetTop = 0 } = options;
  if (anchorName) {
    let anchorElement = document.getElementById(anchorName);

    if (anchorElement) {
      // anchorElement.scrollIntoView({ block: 'start', behavior: 'smooth' });
      const container = getContainer();
      const scrollTop = getScroll(container, true);

      const eleOffsetTop = getOffsetTop(anchorElement, container);
      let y = scrollTop + eleOffsetTop - offsetTop;

      scrollTo(y, {
        callback: () => {
        },
        getContainer: () => getContainer()
      });
    }
  } else {
    scrollTo(0, {
      callback: () => {
      },
      getContainer: () => getContainer()
    });
  }
}

/**
 *
 * @param element  el
 * @param container  window
 * @returns {number}
 */
export function getOffsetTop (element, container) {
  if (!element) {
    return 0;
  }
  if (!element.getClientRects().length) {
    return 0;
  }
  const rect = element.getBoundingClientRect();
  if (rect.width || rect.height) {
    if (container === window) {
      container = element.ownerDocument.documentElement;
      return rect.top - container.clientTop;
    }
    return rect.top - container.getBoundingClientRect().top;
  }
}

function easeInOutCubic (t, b, c, d) {
  const cc = c - b;
  t /= d / 2;
  if (t < 1) {
    return (cc / 2) * t * t * t + b;
  }
  return (cc / 2) * ((t -= 2) * t * t + 2) + b;
}


export function getFileType (fileName) {
  // 
  let suffix = '';
  // 
  let result = '';
  try {
    const flieArr = fileName.split('.');
    suffix = flieArr[flieArr.length - 1];
  } catch (err) {
    suffix = '';
  }
  // fileName false
  if (!suffix) { return false; }
  suffix = suffix.toLocaleLowerCase();
  // 
  const imglist = ['png', 'jpg', 'jpeg', 'bmp', 'gif'];
  // 
  result = imglist.find(item => item === suffix);
  if (result) {
    return 'image';
  }
  // txt
  const txtlist = ['txt'];
  result = txtlist.find(item => item === suffix);
  if (result) {
    return 'txt';
  }
  //  excel
  const excelist = ['xls', 'xlsx'];
  result = excelist.find(item => item === suffix);
  if (result) {
    return 'excel';
  }
  //  word
  const wordlist = ['doc', 'docx'];
  result = wordlist.find(item => item === suffix);
  if (result) {
    return 'word';
  }
  //  pdf
  const pdflist = ['pdf'];
  result = pdflist.find(item => item === suffix);
  if (result) {
    return 'pdf';
  }
  //  ppt
  const pptlist = ['ppt', 'pptx'];
  result = pptlist.find(item => item === suffix);
  if (result) {
    return 'ppt';
  }
  //  
  const videolist = ['mp4', 'm2v', 'mkv', 'rmvb', 'wmv', 'avi', 'flv', 'mov', 'm4v'];
  result = videolist.find(item => item === suffix);
  if (result) {
    return 'video';
  }
  //  
  const radiolist = ['mp3', 'wav', 'wmv'];
  result = radiolist.find(item => item === suffix);
  if (result) {
    return 'radio';
  }
  //  
  return 'other';
}

export function hex2Rgba (hex, opacity) { //RGB
  const rgb = []; // rgb
  if (/^\#[0-9A-F]{3}$/i.test(hex)) { //#
    let sixHex = '#';
    hex.replace(/[0-9A-F]/ig, function (kw) {
      sixHex += kw + kw; //16
    });
    hex = sixHex; //hex
  }
  if (/^#[0-9A-F]{6}$/i.test(hex)) { //#

    hex.replace(/[0-9A-F]{2}/ig, function (kw) {
      rgb.push(eval('0x' + kw)); //
    });
    return `rgba(${rgb.join(',')},${opacity})`; //RGB
  } else {
    return 'rgba(0,0,0,0)';
  }
}

/* @param lng1 1
* @param lat1 1
* @param lng2 2
* @param lat2 2
* @return 

/*()*/
export function caculateLL (lat1, lng1, lat2, lng2) {
  var radLat1 = lat1 * Math.PI / 180.0;
  var radLat2 = lat2 * Math.PI / 180.0;
  var a = radLat1 - radLat2;
  var b = lng1 * Math.PI / 180.0 - lng2 * Math.PI / 180.0;
  var s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(a / 2), 2) + Math.cos(radLat1) * Math.cos(radLat2) * Math.pow(Math.sin(b / 2), 2)));
  s = s * 6378.137;
  s = Math.round(s * 10000) / 10;
  return s
}

/**
 *  pizza 
 * @param {immutable} pizza 
 */
export function testPizzaData (data, modelId) {

  const pizza = data.get(modelId);
  if (pizza.get('categoryId') === 5) {
    return ['base', 'sauce', 'cheese'].every((itemKey) => {
      const safe = pizza.getIn(['subPizzaDishView', 'selected' + itemKey + 'id'])
      const sizeId = pizza.getIn(['subPizzaDishView', 'sizeId'])
      const source = pizza.getIn(['subPizzaDishView', itemKey + 's', sizeId + ''])
      if (source && source.size > 0) {
        if (!safe) {
          message.base(`Please choose ${itemKey}.`)
        }
        return safe
      } else {
        return true;
      }
    })
  } else {
    return true
  }
}

