//    new Date('2022-04-27')
export function formatDate (dd) {
  if (dd) {
    const data = new Date(dd)
    const weekDay = data.getDay();

    const YYYY = data.getFullYear();
    const MM = data.getMonth() + 1;
    const DD = data.getDate();
    return `${getWeek(weekDay)}, ${DD}/${getMonth(MM)}/${YYYY}`
  } else {
    return null;
  }

}

export function formatHtml (str) {
  return str?.replace(/(<([^>]+)>)/ig, '') || '';
}


export function caculteDipulteItem (arr) {
  const newArr = [...new Set(arr)]
  const lastArr = []
  newArr.forEach(item => {
    let num = 0
    arr.forEach(i => {
      if (item == i) {
        num++
      }
    })

    lastArr.push(num === 1 ? item : `${num} x ${item} `)
  })

  return lastArr
}

