import React, { useMemo, useState, } from 'react';
import { AutoTextarea, Icon } from '../../../component';
import { useKeyboardType } from '../../../utils';

import './index.less';
import './indexPc.less';

const BasicInput = (props) => {

  const { onChange, placeholder, value, type, errtext, reg } = props


  const inputType = useKeyboardType()

  function renderInput () {
    switch (type) {
      case 'Address':
        return <p onClick={onChange} className={`${!!value ? 'input_address_wrapper_active' : undefined}`}>{value || 'Please enter your address'}</p>;
      case 'Phone':
        return <input placeholder={placeholder} onChange={onChange} value={value} {...props} type={inputType} />
      default:
        return <AutoTextarea placeholder={placeholder} onChange={onChange} value={value} {...props} />
    }
  }

  const errCom = useMemo(() => {
    if (value && reg && errtext) {
      if (reg.test(value)) {
        return null
      } else {
        return <span className='my_detail_input_empty'>{errtext}</span>
      }
    } else if (errtext) {

      return <span className='my_detail_input_empty'>{errtext}</span>
    }

  }, [value, errtext])

  return <section className={`my_detail_input ${type === 'address' ? 'my_detail_input_address' : undefined}`}>
    <span>*</span>
    {
      renderInput()
    }
    {type === 'address' && <Icon type='iconicon_more_A1' onClick={onChange} />}
    {errCom}
  </section>
}

export default BasicInput