import './index.less';
import './indexPc.less';

const message = {
  base (content, seq, type) {
    const time = seq || 3000;
    const messageList = document.getElementsByClassName('message');
    if(messageList && messageList.length > 0){
      for(let i = 0; i < messageList.length; i++){
        messageList[i].style = 'display: none';
      }
    }
    const node = document.createElement('div');
    node.className = 'message';
    node.innerHTML = `<div class="content">${content}</div>`;
    document.body.appendChild(node);
    const list = document.getElementsByClassName('message');
    setTimeout(() => {
      list && list[0] && document.body.removeChild(list[0]);
    }, time);
  },
};

export default message;
