/**
 * 
 * @param {All over:1:{count: 2default: false,id: 77,isCollection: null ...}...}  toppings  topping  modal 
 * @param {number} limit   limit  normal 
 * @returns  {all:{normal:2,premium:1,free:1} real:{...}} all  real 
 */
function getAllCount (toppings, limit = 0, categoryId = 5, defaultToppings, nextNormalCount = 0, nextPremiumCount = 0) {
  let result = Map({});
  if (categoryId === 5 && defaultToppings) {

    let newItem; // 

    newItem = toppings.get('All over');

    //  topping 
    newItem = newItem.map((item, index) => {
      let newI;
      defaultToppings.map((a, j) => {
        if (item.get('id') === a.get('id')) {
          newI = item.update('count', (v) => v - a.get('count'))
          return newI
        }
      })
      return newI || item;
    });


    // 
    newItem = newItem.filter(item => item.get('count') !== 0);


    //    default   isdefault 
    let diffTopping = Map({})
    newItem.map(item => {
      let defautKey = item.get('def') ? 'isdefault' : 'nodefault';
      let key = 'free';
      if (item.get('isPremium')) {
        key = 'premium'
      } else if (!item.get('isFree')) {
        key = 'normal'
      }
      diffTopping = diffTopping.updateIn([defautKey, key], (v) => (v || 0) + item.get('count'))
    })



    //   left right 
    toppings.map((item, index) => {
      item.map((a, j) => {
        let key = 'free';
        if (a.get('isPremium')) {
          key = 'premium'
        } else if (!a.get('isFree')) {
          key = 'normal'
        }
        diffTopping = diffTopping.updateIn(['nodefault', key], (value) => {
          if (index === 'All over') {
            return value || 0
          } else if (index === 'Left side' || index === 'Right side') {
            return accAdd(value || 0, a.get('count') * 0.5)
          } else {
            return value || 0
          }
        })
      })
    })

    let all = Map({ premium: 0, normal: 0, free: 0 })

    diffTopping = diffTopping.updateIn(['nodefault', 'normal'], (val) => accAdd(val || 0, nextNormalCount))
    diffTopping = diffTopping.updateIn(['nodefault', 'premium'], (val) => accAdd(val || 0, nextPremiumCount))

    // 
    diffTopping.map((item, key) => {
      item.map((a, j) => {
        all = all.update(j, (v) => (v || 0) + a);
      })
    })


    //   premiumpremium  >normalnormal >premiumnormal > normalpremium
    // premiumpremiumpremium
    // premiumnormalnormalnormal
    // normalPremiumpremium
    let replaceNormalCount = diffTopping.getIn(['isdefault', 'normal']) || 0; //   normal 
    let replacePremiumCount = diffTopping.getIn(['isdefault', 'premium']) || 0; //   premium 

    const normalCount = diffTopping.getIn(['nodefault', 'normal']) || 0; //   normal 
    const premiumCount = diffTopping.getIn(['nodefault', 'premium']) || 0; //   premium 

    let normalReplacePremiumCount = 0;


    let remain = limit; // 
    if (replacePremiumCount < 0) { //  premium   premiumpremium
      const replaceCount = Math.min(Math.abs(replacePremiumCount), premiumCount) // premium  

      all = all.update('premium', (v) => (v + Math.min(replaceCount, Math.abs(remain - replaceCount > 0 ? 0 : remain - replaceCount))));

      replacePremiumCount = all.get('premium') || 0;
      remain = Math.max(remain - replaceCount, 0);
      if (remain <= 0) fixAll();
    }

    if (replaceNormalCount < 0) { // normalnormal
      const replaceCount = Math.min(Math.abs(replaceNormalCount), normalCount); // normal 

      all = all.update('normal', (v) => (v + Math.min(replaceCount, Math.abs(remain - replaceCount > 0 ? 0 : remain - replaceCount))));

      replaceNormalCount = all.get('normal') || 0;
      remain = Math.max(remain - replaceCount, 0);
      if (remain <= 0) fixAll();
    }


    if (replacePremiumCount < 0 && remain > 0) { // premium  normal  
      const replaceCount = Math.min(Math.abs(replacePremiumCount), normalCount);// premium  normal  

      // 
      const replaceNoramlC = all.get('normal') + Math.min(replaceCount, Math.abs(remain - replaceCount > 0 ? replaceCount : remain - replaceCount));
      //  premium  normal  premium  normal  premium 

      const finallReplaceNormal = Math.max(replaceNoramlC - replaceCount, 0)

      all = all.update('normal', (v) => finallReplaceNormal);
      replaceNormalCount = all.get('normal') || 0;
      remain = Math.max(remain - replaceCount, 0);
      if (remain <= 0) fixAll();
    }

    if (replaceNormalCount < 0 && remain > 0) { //  normal  premium
      const replaceCount = Math.min(Math.abs(replaceNormalCount), premiumCount);// normal  premium  

      //normal  premiumpremiumnormalpremiumnormalpremium
      normalReplacePremiumCount = Math.min(replaceCount, Math.abs(remain));

      //   
      //  premium  normal  premium  normal  premium 
      all = all.update('normal', (v) => (v + Math.min(replaceCount, Math.abs(remain - replaceCount > 0 ? replaceCount : remain - replaceCount))));

      // all = all.update('normal', (v) => (v + Math.min(replaceCount, Math.abs(remain - replaceCount)))); 

      replacePremiumCount = all.get('premium') || 0;
      remain = Math.max(remain - replaceCount, 0);
      if (remain <= 0) fixAll();
    }
    if (all.get('premium')) {//premiumpremiumnormalnormal
      all = all.update('normal', (v) => (v || 0) + all.get('premium') - normalReplacePremiumCount)
    }


    fixAll();

    //  all  0
    function fixAll () {
      all = all.map(item => {
        if (item < 0) {
          return 0
        } else {
          return item;
        }
      })

    }



    result = result.update('all', () => all)

    return result
  } else {
    let all = Map({});
    toppings.map((a, j) => {
      a.map((b, k) => {
        let key = 'free';
        if (b.get('isPremium')) {
          key = 'premium'
        } else if (!b.get('isFree')) {
          key = 'normal'
        }
        if (b.get('count')) {
          all = all.updateIn([key], (value) => {
            let count;
            if (j === 'All over') { count = b.get('count') }
            else { count = b.get('count') * .5 }
            return (value || 0) + count
          })
        }
      })
    });
    all = all.update('premium', (v) => ((v || 0) + nextPremiumCount))
    all = all.update('normal', (v) => ((v || 0) + nextNormalCount))
    if (all.get('premium')) {
      all = all.update('normal', (v) => (v || 0) + all.get('premium'))
    }

    all = all.update('all', () => all)

    return all
  }
}

//  selectedtoppingsCount 
function changeAllToppingCount (pizza) {
  const selelectToppings = pizza.getIn(['subPizzaDishView', 'selectedtoppings']);


  if (selelectToppings) {
    let all = Map({})
    selelectToppings.map((a, j) => {
      a.map((b, k) => {
        let key = 'free';
        if (b.get('isPremium')) {
          key = 'premium'
        } else if (!b.get('isFree')) {
          key = 'normal'
        }
        if (b.get('count')) {
          all = all.updateIn([key], (value) => {
            let count;
            if (j === 'All over') { count = b.get('count') }
            else { count = b.get('count') * .5 }
            return (value || 0) + count
          })
        }
      })
    });
    let reslut = 0;
    all.map((item, key) => {
      reslut = reslut + item;
    });
    pizza = pizza.updateIn(['subPizzaDishView', 'selectedtoppingsCount'], () => reslut);
  }

  return pizza;
}


/**
 * 
 * @param {*} plan size  
 * @param {normal:2,premium:1} all
 * @returns {number} topping
 */
function getPrice (plan, all) {

  let allCount = all.get('all');

  let resultPirce = 0;
  //  premium 
  if (allCount.get('premium')) {
    plan.filter(item => item.get('premium'))
      .map(item => resultPirce = resultPirce + (item.get('price') || 0) * (allCount.get('premium') || 0));
  }
  //  topping    end 
  let maxEnd = 0;
  if (allCount.get('normal') > 0) {//   4.5  5-10 
    plan.map(item => {
      maxEnd = Math.max(item.get('end'), maxEnd)
      //   4.5  5-10 
      if (item.get('start') <= Math.ceil(allCount.get('normal')) && item.get('end') >= allCount.get('normal')) {
        resultPirce = resultPirce + (item.get('price') || 0)
      }
    })
  }

  //  topping  start =0 end =0      4.5  5-10 
  if (Math.ceil(allCount.get('normal')) > maxEnd && allCount.get('normal') > 0) {
    //end 
    const lastEnd = plan.filter(item => item.get('end') === maxEnd).get(0);
    if (lastEnd && (lastEnd.get('start') !== 0)) { //   start === 0  &&  end === 0  
      resultPirce = resultPirce + (lastEnd.get('price') || 0);
    }
    // 
    const last = plan.filter(item => !item.get('start')).filter(item => !item.get('premium')).get(0);
    if (last) { //   start === 0  &&  end === 0  nomal 
      resultPirce = resultPirce + (last.get('price') || 0) * ((allCount.get('normal') || 0) - maxEnd)
    }
  }

  return resultPirce;
}

function getToppingPrice (pizaa, categoryId = 5) {
  const allCount = getAllCount(
    pizaa.getIn(['subPizzaDishView', 'selectedtoppings']),
    pizaa.getIn(['subPizzaDishView', 'toppingReLimit']),
    categoryId,
    pizaa.getIn(['subPizzaDishView', 'defaultToppings']),
  );

  const selectedtoppingsPrice = getPrice(pizaa.getIn(['subPizzaDishView', 'selectedtoppingPlan']), allCount);

  let nextNormalPrice = getPrice(pizaa.getIn(['subPizzaDishView', 'selectedtoppingPlan']),
    getAllCount(
      pizaa.getIn(['subPizzaDishView', 'selectedtoppings']),
      pizaa.getIn(['subPizzaDishView', 'toppingReLimit']),
      categoryId,
      pizaa.getIn(['subPizzaDishView', 'defaultToppings']),
      1, 0
    ));
  nextNormalPrice = accSub(nextNormalPrice, selectedtoppingsPrice);
  let nextPremiumPrice = getPrice(pizaa.getIn(['subPizzaDishView', 'selectedtoppingPlan']),
    getAllCount(
      pizaa.getIn(['subPizzaDishView', 'selectedtoppings']),
      pizaa.getIn(['subPizzaDishView', 'toppingReLimit']),
      categoryId,
      pizaa.getIn(['subPizzaDishView', 'defaultToppings']),
      0, 1
    ));
  nextPremiumPrice = accSub(nextPremiumPrice, selectedtoppingsPrice);

  return { selectedtoppingsPrice, nextNormalPrice, nextPremiumPrice };
}

