//    newData   , 
function putSubtoData (data, submodelId) {
  try {
    if (submodelId) {
      const subArr = submodelId.split('d');
      const modelId = subArr[0];
      const pId = subArr[1];// 
      const subDishes = data.getIn([modelId, 'subDishListView', pId, 'subDishes']);

      let subId = undefined; // 
      subDishes.filter((item, index) => {
        if (item.get('choose')) {
          subId = index;
        }
      })

      const totolPrice = data.getIn([submodelId, 'totalPrice']);
      // 
      let newData = data.updateIn([modelId, 'subDishListView', pId, 'subDishes', subId],
        () => data.get(submodelId).update('vprice', () => totolPrice)
      )
      // 
      newData = newData.updateIn([modelId, 'totalvPrice', pId], () => data.getIn([submodelId, 'totalPrice']))

      return newData;
    } else {
      return data;
    }
  } catch (e) {
    console.error('putSubtoData', e)
  }

}

/**
 * 
 * @param {immutable} result 
 * @param {immutable} dish orderAgain check 
 */
async function mergeDishFromOrder (result, dish, rId) {
  result = result.update('count', () => dish.get('count'))
    .update('note', () => dish.get('note'))
    .update('fromPage', () => 'history') //  categoryId===8 

  switch (dish.get('categoryId')) {
    case 1:
    case 4:
    case 8:

    case 6:

      dish.get('subDishes')?.map(item => {
        result.get('subDishListView')?.map((a, pid) => {
          if (a.get('partId') === item.get('partId')) {
            let subId;
            let second = a.get('subDishes')?.map((b, index) => {
              if (b.get('id') === item.get('id')) {
                subId = index;
                return b.update('choose', () => true)
                  .update('note', () => item.get('note'))
                  .update('orderCount', (val) => (val || 0) + item.get('count'));
              } else {
                return b.update('choose', () => false);
              }
            });

            result = result.updateIn(['subDishListView', pid + '', 'choose'], value => !value) // 
              .updateIn(['subDishListView', pid + '', 'subDishes'], () => second) //   
              .updateIn(['totalvPrice', pid + ''], () => second.getIn([subId, 'vprice'])) // 
          }
        })
      })

      let chooseIds = [];
      dish.get('subPizzaDishes')?.map((item) => {
        result.get('subDishListView')?.map((a, pid) => {
          if (a.get('partId') === item.get('partId')) {
            let subId;
            let second = a.get('subDishes')?.map((b, index) => {
              const bKey = b.get('id') + '' + b.get('sizeId');
              const itemKey = item.get('id') + '' + item.get('sizeId');
              if (bKey === itemKey) {
                subId = index;
                return b.update('choose', () => true).update('note', () => item.get('note'));
              } else {
                return b.update('choose', () => false);
              }
            });


            chooseIds.push({ pid: pid, subId: subId, returnDish: item })
            result = result.updateIn(['subDishListView', pid + '', 'choose'], value => !value) // 
              .updateIn(['subDishListView', pid + '', 'subDishes'], () => second) //   
              .updateIn(['totalvPrice', pid + ''], () => second.getIn([subId, 'vprice'])) // 
          }
        })
      })

      if (dish.get('categoryId') === 6) { //  pizza

        result = await dealWithMealPizz(result, rId, chooseIds);
      }
      break;
    case 5:
      result = dealWithPizz(result, dish.get('subPizzaDish'))
      break;
  }
  return result;
}

/**
 * 
 * @param {immutable} result  pizza 
 * @param {string} rId  ID
 */
async function dealWithMealPizz (result, rId, chooseIds) {

  await Promise.all(chooseIds.map(async item => {
    await new Promise(resolve => {
      const pizzaItem = result.getIn(['subDishListView', item.pid, 'subDishes', item.subId]);

      const res = shopDetailS.getSubPizza4Meal({
        restaurantId: rId,
        partId: pizzaItem.get('partId'),
        rootDishId: pizzaItem.get('rootDishId'),
        pizzaTypeId: pizzaItem.get('pizzaTypeId'),
        sizeId: pizzaItem.get('sizeId')
      }).then(rr => {
        let fomatRR = fromJS(rr).update('fromPrice', () => pizzaItem.get('vprice'));
        fomatRR = dealWithPizz(fomatRR, item.returnDish);
        fomatRR = fomatRR.update('choose', () => true).update('note', () => pizzaItem.get('note'))
        result = result.updateIn(['subDishListView', item.pid, 'subDishes', item.subId], () => fomatRR)

        // 
        result = result.updateIn(['totalvPrice', item.pid + ''], () => fomatRR.get('totalPrice'));
        // //  result 
        let totalPrice = result.get('fromPrice') || 0;

        result.get('totalvPrice').map((val, key) => {
          totalPrice = accAdd(totalPrice, val || 0)
        });

        result = result.update('totalPrice', () => totalPrice);

        resolve()
      });
    })
  }))

  return result
}

/**
 * mergeDishFromOrder  pizza
 * @param {immutable} result 
 * @param {immutable} pizza orderAgain check 
 */
function dealWithPizz (result, pizza) {
  result = result.updateIn(['subPizzaDishView', 'sizeId'], () => pizza.get('sizeId'))
    .updateIn(['subPizzaDishView', 'sizesid'], () => pizza.get('sizeId'))
  result = initPizzaData(result);
  result = transformData(result)

  //  toppings
  if (pizza.get('toppings')) {
    result.getIn(['subPizzaDishView', 'toppings']).map((item, i) => {
      pizza.get('toppings').map((a) => {
        if (item.get('id') === a.get('id')) {
          result = result.updateIn(['subPizzaDishView', 'selectedtoppings', 'All over', i],
            () => a.update('default', () => true).update('count', () => a.get('count')))
        }
      })
    })
  }
  if (pizza.get('rToppings')?.size > 0) {
    result = result.updateIn(['subPizzaDishView', 'isHalf'], () => true)
      .updateIn(['subPizzaDishView', 'selectedtoppings', 'Right side'], () => result.getIn(['subPizzaDishView', 'toppings']).map(item => {
        return item.update('default', () => false).update('def', () => false).update('count', () => 0)
      })),
      result.getIn(['subPizzaDishView', 'toppings']).map((item, i) => {
        pizza.get('rToppings').map((a) => {
          if (item.get('id') === a.get('id')) {
            result = result.updateIn(['subPizzaDishView', 'selectedtoppings', 'Right side', i],
              () => a.update('default', () => true).update('count', () => a.get('count')))
          }
        })
      })
  }
  if (pizza.get('lToppings')?.size > 0) {
    result = result.updateIn(['subPizzaDishView', 'isHalf'], () => true)
      .updateIn(['subPizzaDishView', 'selectedtoppings', 'Left side'], () => result.getIn(['subPizzaDishView', 'toppings']).map(item => {
        return item.update('default', () => false).update('def', () => false).update('count', () => 0)
      })),
      result.getIn(['subPizzaDishView', 'toppings']).map((item, i) => {
        pizza.get('lToppings').map((a) => {
          if (item.get('id') === a.get('id')) {
            result = result.updateIn(['subPizzaDishView', 'selectedtoppings', 'Left side', i],
              () => a.update('default', () => true).update('count', () => a.get('count')))
          }
        })
      })
  }

  const { selectedtoppingsPrice, nextNormalPrice, nextPremiumPrice } = getToppingPrice(result);
  result = changeAllToppingCount(result);

  // 
  fromJS(['base', 'sauce', 'cheese',])
    .map((itemKey, j) => {
      result.getIn(['subPizzaDishView', 'selected' + itemKey,]).map((item, index) => {
        if (item.get('id') === pizza.get(itemKey + 'Id')) {
          result = result.updateIn(['totalvPrice', 'selected' + itemKey], () => item.get('price'))
            //  id ,name
            .updateIn(['subPizzaDishView', 'selected' + itemKey + 'id'], () => item.get('id'))
            .updateIn(['subPizzaDishView', 'selected' + itemKey + 'name'], () => item.get('name'))
            .updateIn(['subPizzaDishView', 'selected' + itemKey + 'vprice'], () => item.get('price'))
        }
      })
    })

  //  'sizes', 'selectedbase', 'selectedsauce', 'selectedcheese', 'selectedtoppings' 
  fromJS(['sizes', 'selectedbase', 'selectedsauce', 'selectedcheese', 'selectedtoppings']).map(item => {
    const chooseId = result.getIn(['subPizzaDishView', item + 'id']);
    const newItem = result.getIn(['subPizzaDishView', item]).map(item => {
      if (item.get('id') === chooseId) {
        return item.update('default', () => true)
      } else {
        return item.update('default', () => false)
      }
    });
    result = result.updateIn(['subPizzaDishView', item], () => newItem)

  })


  result = result.updateIn(['totalvPrice', 'selectedtoppings'], () => selectedtoppingsPrice)
    .updateIn(['subPizzaDishView', 'nextNormalPrice'], () => nextNormalPrice)
    .updateIn(['subPizzaDishView', 'nextPremiumPrice'], () => nextPremiumPrice)


  //  result 
  let totalPrice = result.get('fromPrice') || 0;
  result.get('totalvPrice').map((val, key) => {
    totalPrice = accAdd(totalPrice, val || 0)
  });
  result = result.update('totalPrice', () => totalPrice)
    .update('vprice', () => totalPrice) // 
  return result
}


//  catoryid === 8 freeSize   
function dealFreeSize (data, subDish, pid, freeSize) {


  let totalPrice = 0;
  let currentDefaultFreeSize = 0;

  const isDefaultFree = data.getIn(['subDishListView', pid, 'isDefaultFree']) || 0
  const defaultFreeSize = data.getIn(['subDishListView', pid, 'defaultFreeSize']) || 0
  const replaceSize = data.getIn(['subDishListView', pid, 'replaceSize']) || 0

  let dupiluteDefaultItem = [];

  // freeSize
  const chargedDefaultItems = subDish.map(item => {
    if (isDefaultFree === 0 && item.get('isDefault') === 1 && item.get('count') > 0 && item.get('vprice') > 0) {
      if (item.get('count') !== item.get('defaultCount')) {
        Array.from({ length: item.get('count') - item.get('defaultCount') }).map(a => {
          dupiluteDefaultItem.push(item.update('count', () => 1).update('isDefault', () => 0))
        })
        return item.update('count', () => 1)
      }
      return item
    } else {
      return null
    }

  }).filter(item => !!item)

  const freeDefaultItems = subDish.map(item => {
    if ((isDefaultFree === 1 && item.get('isDefault') === 1 && item.get('count') > 0) || (item.get('vprice') === 0 && item.get('count') > 0 && item.get('isDefault') === 1)) {
      if (item.get('count') !== item.get('defaultCount')) {
        Array.from({ length: item.get('count') - item.get('defaultCount') }).map(a => {
          dupiluteDefaultItem.push(item.update('count', () => 1).update('isDefault', () => 0))
        })
        return item.update('count', () => 1)
      }
      return item
    } else {
      return null
    }
  }).filter(item => !!item);

  let sortsecond = [];

  subDish.concat(List(dupiluteDefaultItem)).sortBy((item) => item.get('vprice')) // 
    .filter(item => item.get('choose'))
    .filter(item => {
      if (isDefaultFree === 1 && item.get('isDefault') === 1) {
        currentDefaultFreeSize = currentDefaultFreeSize + item.get('defaultCount')
      }
      return item.get('isDefault') !== 1  // freeSize
    })
    .map(item => {
      if (item.get('count') > 1) {
        Array.from({ length: item.get('count') }).map(a => {
          sortsecond.push(item.update('count', () => 1))
        })
      } else {
        sortsecond.push(item)
      }
    })

  const replaceDefalutSize = Math.min(Math.max(defaultFreeSize - currentDefaultFreeSize, 0), replaceSize);// 

  const allFreeSize = freeSize + replaceDefalutSize
  // const allFreeSize = freeSize + replaceSize

  const remainItem = List(sortsecond).takeLast(sortsecond.length - allFreeSize);

  const remainFreeItem = remainItem.filter(item => item.get('vprice') === 0).concat(freeDefaultItems)

  const chargeableItem = remainItem
    .filter(item => item.get('vprice') > 0)
    .filter(item => item.get('isDefault') !== 1)
    .concat(chargedDefaultItems)

  //   items
  chargeableItem.map(item => {
    const defaultPrice = (isDefaultFree === 1 && item.get('isDefault') === 1)
      ? 0 : item.get('vprice')
    totalPrice = totalPrice + defaultPrice * item.get('count')
  })

  const freeItem = (List(sortsecond).take(allFreeSize)).concat(remainFreeItem)

  return data.updateIn(['totalvPrice', pid + ''], (val) => totalPrice)
    .updateIn(['subDishListView', pid, 'chargeableItem'], () => chargeableItem)
    .updateIn(['subDishListView', pid, 'freeSubItem'], () => (freeItem))
    .updateIn(['subDishListView', pid, 'showPrice'], () => freeItem.size === (allFreeSize + freeDefaultItems.size))
}

//  max(15)   categoryId =4 categoryId=6  categoryId=5Pizza
const transformData = (data, max = 15) => {
  const categoryId = data.get('categoryId');
  data = data.update('count', (value) => value || 1)

  const fromPage = data.get('fromPage');

  if (categoryId === 1 || categoryId === 4 || categoryId === 8 || categoryId === 6) {
    let totalvPrice = Map({})

    data = data.update('subDishListView', (val) => {
      return val.map((item, index) => {

        let totalSize = 0;
        let name = [];
        let removename = [];
        let haveChoose = false;
        let showType = 'single';
        let defaultFreeSize = 0;
        const isMultiple = item.get('isMultiple') || 0
        const isItemMultiple = item.get('isItemMultiple') || 0
        const maxSize = item.get('maxSize') || 1;

        if (isMultiple === 0) {
          showType = 'single'
        } else {
          if (isItemMultiple === 0 && maxSize == 1) {
            showType = 'single'
          } else if (isItemMultiple === 1) {
            showType = 'itemMultiple'
          } else {
            showType = 'multiple'
          }
        }
        let second = item
          .update('subDishes', (val) => {
            if (categoryId === 8) {
              return val.map(a => {
                if (item.get('isDefaultFree') === 1 && a.get('isDefault') === 1) {
                  defaultFreeSize = defaultFreeSize + a.get('defaultCount')
                }
                const choose = (fromPage !== 'history' && a.get('isDefault') > 0) || (fromPage == 'history' && a.get('orderCount') > 0)
                if (choose) {
                  haveChoose = true

                  const chooseCount = fromPage === 'history' ? a.get('orderCount') : a.get('defaultCount')

                  totalSize = totalSize + chooseCount;
                  if (chooseCount > 1) {
                    name.push(`${a.get('defaultCount') || a.get('orderCount')}x${a.get('name')}`)
                  } else {
                    name.push(a.get('name'))
                  }
                  totalvPrice = totalvPrice.update(index + '', (val) => {
                    const defaultPrice = (item.get('isDefaultFree') === 1 && a.get('isDefault') === 1)
                      ? 0 : a.get('vprice')
                    return (val || 0) + chooseCount * defaultPrice
                  })
                } else {//   isDefault===1
                  if (a.get('isDefault') === 1) {
                    removename.push(a.get('name'))
                  }
                }
                return a.update('choose', () => choose).update('count', () => {
                  if (fromPage === 'history') {
                    return a.get('orderCount') || 0
                  } else if (a.get('isDefault') > 0) {
                    return a.get('defaultCount')
                  }
                  else {
                    return 0
                  }
                })
              })
            } else {
              return val.map(a => {
                return a.update('count', () => 0)
              })
            }
          })
          .update('totalSize', () => totalSize)
          .update('defaultFreeSize', () => defaultFreeSize)
          .update('name', () => name.join(','))
          .update('removename', () => removename.join(','))
          .update('choose', () => {
            if (categoryId === 8) {
              if (showType === 'single' && haveChoose) {
                return true
              } else if (showType !== 'single' && totalSize >= item.get('minSize')) {
                return true
              }
              return false
            } else {
              if (fromPage === 'history') {
                return true
              } else {
                return false
              }
            }
          }).update('openNext', () => {
            if (item.get('reopenId')) {
              return true
            } else if (showType === 'single' && haveChoose) {
              return true
            } else if (showType !== 'single' && totalSize === item.get('maxSize')) {
              return true
            }
            return false
          }).update('show', () => {
            if (showType === 'single' && haveChoose) {
              return false
            } else if (showType !== 'single' && totalSize === item.get('maxSize')) {
              return false
            }
            return true
          }).update('showType', () => {
            return showType
          })

        return second;
      })
    })

    if (categoryId === 8) { // 
      data = data.update('totalvPrice', () => totalvPrice);

      //  freeSize 
      data.get('subDishListView').map((dish, index) => {
        data = dealFreeSize(data, dish.get('subDishes'), index, dish.get('freeSize'))
      })

      // 
      let otherPrice = data.get('price') || 0
      data.get('totalvPrice').map((val, key) => {
        otherPrice = accAdd(otherPrice, val || 0)
      });

      data = data.update('totalPrice', () => otherPrice)
        .update('finishChoose', () => fromPage === 'history')

    }

  }

  switch (categoryId) {
    case 4:
    case 8:
      //   
      const first = data.get('subDishListView');
      if (!first) {
        return data
      }



      // 15
      let totalSubSize = 1;
      first.filter((key, val) => {
        return first.getIn([val, 'partName']) !== 'side';
      }).map((val, key) => {
        totalSubSize = val.get('subDishes').size * totalSubSize
      })
      if (totalSubSize > 15) {
        return data
      }
      if (categoryId === 8) {
        let canMerge = true
        first.map(item => {


          if (item.get('showType') !== 'single' || item.get('name') || item.get('isNoneOption')) {
            canMerge = false
          }
        })

        if (!canMerge) {
          return data
        }
      }

      if (first.size > 1) {
        // side 
        const side = first.filter((key, val) => {
          return first.getIn([val, 'partName']) === 'side';
        })
        //  side 
        let ss = first.filter((key, val) => {
          return first.getIn([val, 'partName']) !== 'side';
        }).reduce((pre, next) => {
          let subs = List([]);
          let previous = 0;
          let borderTop = false; //    border 
          pre.get('subDishes').map((a, i) => {
            next.get('subDishes').map((b, j) => {
              if (previous === i) {
                borderTop = false;
              } else {
                borderTop = true;
                previous = i;
              }
              //  
              let c = a.mergeWith((oldData, newData, key) => {
                if (key === 'name') {
                  return formatDishName(oldData + ' ' + newData);
                } else if (key === 'vprice') {
                  return accAdd(oldData, newData);
                } else if (key === 'id') {
                  return [oldData, newData];
                } else if (key === 'choose') {
                  return oldData && newData
                } else {
                  return newData
                }
              }, b);
              if (borderTop) {
                c = c.update('borderTop', () => borderTop)
              }
              subs = subs.push(c);
            })
          })
          return pre.mergeWith((oldData, newData, key) => {
            if (key === 'partId') {
              return [oldData, newData];
            } else {
              return newData
            }
          }, Map({ subDishes: subs, partId: next.get('partId') }));
        })

        if (ss.get('subDishes').size > max) {
          return data.update('totalPrice', () => data.get('fromPrice'))
        }

        if (categoryId === 8) {
          ss = ss.update('partName', () => 'Choose item')
        }
        //  first,  subDishListView  
        if (side.get(0)) {
          return data.update('subDishListView', () => List([ss, side.get(0)]))
            .update('totalPrice', () => data.get('fromPrice'))
        } else {
          return data.update('subDishListView', () => List([ss]))
            .update('totalPrice', () => data.get('fromPrice'))
        }
      } else {
        return data.update('finishChoose', () => first.size === 0)
          .update('totalPrice', () => data.get('fromPrice'))
      }
    case 5:
      if (data.get('isHalfPizza')) {
        return data.updateIn(['subPizzaDishView', 'pizzaKeys'], () => ['sizes', 'selectedLeftSide', 'selectedRightSide',])
          .updateIn(['subPizzaDishView', 'pizzaNames'], () => [{ name: 'Size', show: true }, { name: 'Left Side', show: false }, { name: 'Right Side', show: false },])
          .update('totalPrice', () => data.get('fromPrice'))
      } else {
        return data.updateIn(['subPizzaDishView', 'pizzaKeys'], () => ['sizes', 'selectedbase', 'selectedsauce', 'selectedcheese', 'selectedtoppings'])
          .updateIn(['subPizzaDishView', 'pizzaNames'], () => [{ name: 'Size', show: true }, { name: 'Base', show: false }, { name: 'Sauce', show: false }, { name: 'Cheese', show: false }, { name: 'toppings', show: false }])
          .update('totalPrice', () => data.get('fromPrice'))
          .updateIn(['subPizzaDishView', 'defaultToppings'], (value) => value?.map(item => {
            if (item.get('default')) {
              return item.update('count', () => 1)
            }
            return item
          }))
      }
    case 1:
      const first1 = data.get('subDishListView');
      if (first1.size >= 1) {
        return data.update('totalPrice', () => data.get('price'))
      } else {
        return data.update('finishChoose', () => true)
          .update('totalPrice', () => data.get('price'))
      }
    case 6:
      return data.update('totalPrice', () => data.get('fromPrice'))
    default:
      return data;
  }
}

// catoryid===8  finishChoose 
function updateFinshChooseState (data) {
  let res = true;// 
  return data.update('finishChoose', (val) => {
    if (val) {
      return true
    }
    data.get('subDishListView').map((item, index) => {
      if (!item.get('choose') || !item.get('openNext')) {
        res = false
      }
    })
    return res
  })
}

/**
 *  resetSize  pizza  selectedbase,selectedsauce,selectedcheese   
 * @param {*} choosedPizza  pizza
 * @param {*} choosedKey  selectedbase,selectedsauce,selectedcheese
 * @param {*} list  size  selectedbase,selectedsauce,selectedcheese 
 * @returns  pizza
 */
function onReChoose (choosedPizza, choosedKey, list) {
  let pid = null; //  selected list  indx
  let selectedremain = list.size > 0 && list.some((item, index) => {
    const haveselectedremain = item.get('id') === choosedPizza.getIn(['subPizzaDishView', choosedKey + 'id'])
    if (haveselectedremain) {
      pid = index + '';
    }
    return haveselectedremain
  })

  return choosedPizza.updateIn(['subPizzaDishView', choosedKey + 'vprice'], (val) => selectedremain
    ? choosedPizza.getIn(['subPizzaDishView', choosedKey, pid, 'price']) : null)
    .updateIn(['subPizzaDishView', 'haveno' + choosedKey], (val) => selectedremain ? false : true)
    .updateIn(['totalvPrice', choosedKey], (val) => selectedremain ?
      choosedPizza.getIn(['subPizzaDishView', choosedKey, pid, 'price'])
      : 0)
    .updateIn(['subPizzaDishView', choosedKey], (val) => selectedremain
      ? choosedPizza.getIn(['subPizzaDishView', choosedKey]).map((item, index) => {
        if (pid === (index + '')) {
          return item.update('default', () => true)
        } else {
          return item.update('default', () => false)
        }
      })
      : choosedPizza.getIn(['subPizzaDishView', choosedKey]).map(item => {
        return item.update('def', () => false).update('default', () => false)
      }))
}

/**
 * sizeId pizza  1. 2.
 * @param {*} firstPizza 
 * @param {*} action remain reset  size  selectedbaseidselectedsauceidselectedsaucename 
 * @returns 
 */
function initPizzaData (firstPizza, action) {
  const remain = action === 'reChooseSize';
  const reset = action === 'reset';


  const id = firstPizza.getIn(['subPizzaDishView', 'sizeId']);
  if (id || id === 0) {
    firstPizza = firstPizza
      //  size    selectedbase selectedsauce selectedcheese selectedtoppingPlan 
      .updateIn(['subPizzaDishView', 'selectedbase'], () => firstPizza.getIn(['subPizzaDishView', 'bases', id + '']))
      .updateIn(['subPizzaDishView', 'selectedsauce'], () => firstPizza.getIn(['subPizzaDishView', 'sauces', id + '']))
      .updateIn(['subPizzaDishView', 'selectedcheese'], () => firstPizza.getIn(['subPizzaDishView', 'cheeses', id + '']))
      .updateIn(['subPizzaDishView', 'selectedtoppingPlan'], () => firstPizza.getIn(['subPizzaDishView', 'toppingPlan', id + '']))
    if (!remain) { //  reset 

      firstPizza = firstPizza.updateIn(['subPizzaDishView', 'selectedbaseid'], () => null)
        .updateIn(['subPizzaDishView', 'selectedbasename'], () => null)
        .updateIn(['subPizzaDishView', 'selectedbasevprice'], () => null)
        .updateIn(['subPizzaDishView', 'selectedsauceid'], () => null)
        .updateIn(['subPizzaDishView', 'selectedsaucename'], () => null)
        .updateIn(['subPizzaDishView', 'selectedsaucevprice'], () => null)
        .updateIn(['subPizzaDishView', 'selectedcheeseid'], () => null)
        .updateIn(['subPizzaDishView', 'selectedcheesename'], () => null)
        .updateIn(['subPizzaDishView', 'selectedcheesevprice'], () => null)
        .updateIn(['subPizzaDishView', 'toppingsname'], () => null)
        .updateIn(['subPizzaDishView', 'selectedtoppingsCount'], () => 0) //  toppings 
        .updateIn(['subPizzaDishView', 'selectedtoppingsname'], () => 'toppings')// 
        .updateIn(['totalvPrice', 'selectedtoppings'], () => 0)

    } else {  // reChooseSize

    }


    const isHalfPizza = firstPizza.get('isHalfPizza');
    const showCustomiz = firstPizza.get('showCustomiz');
    if (isHalfPizza && ((showCustomiz && !remain) || reset)) { //  halfpizza 
      const leftside = firstPizza.getIn(['subPizzaDishView', 'selectedLeftSide']).map(item => {
        if (item.get('default')) {
          return item.update('default', () => false)
        } else {
          return item
        }
      })
      const rightside = firstPizza.getIn(['subPizzaDishView', 'selectedRightSide']).map(item => {
        if (item.get('default')) {
          return item.update('default', () => false)
        } else {
          return item
        }
      })

      firstPizza = firstPizza
        .updateIn(['subPizzaDishView', 'selectedLeftSidename'], () => null)
        .updateIn(['subPizzaDishView', 'selectedLeftSideid'], () => null)
        .updateIn(['subPizzaDishView', 'selectedLeftSidevprice'], () => null)
        .updateIn(['subPizzaDishView', 'selectedRightSidename'], () => null)
        .updateIn(['subPizzaDishView', 'selectedRightSideid'], () => null)
        .updateIn(['subPizzaDishView', 'selectedRightSidevprice'], () => null)
        .updateIn(['subPizzaDishView', 'selectedLeftSide'], () => leftside)
        .updateIn(['subPizzaDishView', 'selectedRightSide'], () => rightside)

      firstPizza = firstPizza
        .updateIn(['subPizzaDishView', 'selectedtoppingPlan'], () => firstPizza.getIn(['subPizzaDishView', 'toppingPlan', id + '']))
        .updateIn(['totalvPrice', 'selectedtoppings'], () => 0)
    }

    //   totalvPrice 
    fromJS(['sizes', 'selectedbase', 'selectedsauce', 'selectedcheese', 'selectedtoppings'])
      .map((itemKey, j) => {
        if (itemKey === 'selectedtoppings') {
          //  toppings   selectedtoppings All over   Left Right
          // 
          if (!remain) {
            firstPizza = firstPizza.updateIn(['subPizzaDishView', 'selectedtoppings', 'All over',], () =>
              firstPizza.getIn(['subPizzaDishView', 'toppings']).map(item => {
                if (item.get('default')) {
                  return item.update('count', () => 1)
                }
                return item.update('name', (v) => v.replace('! PREMIUM', '').replace('! FREE', ''))
              }))
              // const isHalfPizza = firstPizza.get('isHalfPizza');
              .updateIn(['subPizzaDishView', 'selectedtoppings', 'Left side'], () => List([]))
              .updateIn(['subPizzaDishView', 'selectedtoppings', 'Right side'], () => List([]))
              .updateIn(['subPizzaDishView', 'isHalf'], () => false)
              .updateIn(['subPizzaDishView', 'currentSide'], () => isHalfPizza ? 'Left side' : 'All over')

          } else {
          }
          // 
          firstPizza = changeAllToppingCount(firstPizza);

        } else {
          const list = firstPizza.getIn(['subPizzaDishView', itemKey,]);
          if ((itemKey !== 'selectedbase'
            && itemKey !== 'selectedsauce'
            && itemKey !== 'selectedcheese')
            && (!list || list && list.size === 0)) {
            message.base('Something wrong with this pizza');
            return firstPizza
          }
          // //  reset 
          if (!remain) {
            let isExsitsDefault = false;
            list.size > 0 && list.map((item, index) => {
              if (item.get('default') || (item.get('id') === id && itemKey === 'sizes')) {
                firstPizza = firstPizza.updateIn(['totalvPrice', itemKey], () => item.get('price'))
                  //  id ,name
                  .updateIn(['subPizzaDishView', itemKey + 'id'], () => item.get('id'))
                  .updateIn(['subPizzaDishView', itemKey + 'name'], () => item.get('name'))
                  .updateIn(['subPizzaDishView', itemKey + 'vprice'], () => item.get('price'))

                if (itemKey === 'sizes') {
                  firstPizza = firstPizza.update('isDelivery', () => item.get('isDelivery'))
                    .update('isCollection', () => item.get('isCollection'))
                }
                isExsitsDefault = true;
              }
            })
            //basecheesesaucepizza
            if (!isExsitsDefault) {
              let idKey = '';
              let nameKey = '';
              switch (itemKey) {
                case 'selectedbase':
                  idKey = 'baseId';
                  nameKey = 'base';
                  break;
                case 'selectedsauce':
                  idKey = 'sauceId';
                  nameKey = 'sauce';
                  break;
                case 'selectedcheese':
                  idKey = 'cheeseId';
                  nameKey = 'cheese';
                  break;
              }
              const source = firstPizza.getIn(['subPizzaDishView', nameKey + 's', id + ''])
              if (source && source.size > 0) {
                firstPizza = firstPizza.updateIn(['totalvPrice', itemKey], () => 0)
                  //  id ,name
                  .updateIn(['subPizzaDishView', itemKey + 'id'], () => firstPizza.getIn(['subPizzaDishView', idKey]))
                  .updateIn(['subPizzaDishView', itemKey + 'name'], () => firstPizza.getIn(['subPizzaDishView', nameKey]))
                  .updateIn(['subPizzaDishView', itemKey + 'vprice'], () => 0)
                  .updateIn(['subPizzaDishView', 'haveno' + itemKey], () => true)
              }
            }
          } else {
            // list  'selectedbase', 'selectedsauce', 'selectedcheese' 
            if (itemKey === 'selectedbase' || itemKey === 'selectedsauce' || itemKey === 'selectedcheese') {
              firstPizza = onReChoose(firstPizza, itemKey, list)
            }
          }
        }
      });

    const { selectedtoppingsPrice, nextNormalPrice, nextPremiumPrice } = getToppingPrice(firstPizza, 5);
    firstPizza = firstPizza
      .updateIn(['totalvPrice', 'selectedtoppings'], () => selectedtoppingsPrice)
      .updateIn(['subPizzaDishView', 'nextNormalPrice'], () => nextNormalPrice)
      .updateIn(['subPizzaDishView', 'nextPremiumPrice'], () => nextPremiumPrice)
  }
  return firstPizza
}

/**
 *  pizza  
 * @param {*} firstPizza 
 * @param {*} subId  key
 * @param {*} type  pizza reset  { action: 'reset' }
 */
function showKeys (firstPizza, subId, type) {
  const isHalfPizza = firstPizza.get('isHalfPizza');

  switch (subId) {
    case 'sizes':
      firstPizza = firstPizza.update('showKeys', () => fromJS(['sizes', 'selectedbase', 'selectedsauce', 'selectedcheese', 'selectedtoppings', 'selectedLeftSide']))
      if (isHalfPizza) {
        if (type?.action === 'reChooseSize') {
          firstPizza = firstPizza.update('showKeys', () => fromJS(['sizes', 'selectedbase', 'selectedsauce', 'selectedcheese', 'selectedtoppings', 'selectedLeftSide', 'selectedRightSide']))
        } else {
          firstPizza = firstPizza.update('customiz', () => true)
            .update('finishChoose', () => (firstPizza.get('showKeys').includes('selectedRightSide') ||
              (firstPizza.getIn(['subPizzaDishView', 'pizzaKeys']).includes('selectedsauce') && firstPizza.get('showCustomiz'))))
            .updateIn(['subPizzaDishView', 'pizzaKeys'], () => ['sizes', 'selectedLeftSide', 'selectedRightSide'])
            .updateIn(['subPizzaDishView', 'pizzaNames'], () => [{ name: 'Size', show: false }, { name: 'Left Side', show: true }, { name: 'Right Side', show: true },])
        }
      } else {
        firstPizza = firstPizza.update('showCustomiz', (val) => (type?.action === 'reset' || type?.action === 'reChooseSize') ? val : true)
          .update('finishChoose', () => true)
      }
      break;
    case 'selectedbase':
      if (firstPizza.get('finishChoose')) {
        firstPizza = firstPizza.update('showKeys', () => fromJS(['sizes', 'selectedbase', 'selectedsauce', 'selectedcheese', 'selectedtoppings']))
      } else {
        firstPizza = firstPizza.update('showKeys', () => fromJS(['sizes', 'selectedbase', 'selectedsauce']))
      }
      break;
    case 'selectedsauce':
      if (firstPizza.get('finishChoose')) {
        firstPizza = firstPizza.update('showKeys', () => fromJS(['sizes', 'selectedbase', 'selectedsauce', 'selectedcheese', 'selectedtoppings']))
      } else {
        firstPizza = firstPizza.update('showKeys', () => fromJS(['sizes', 'selectedbase', 'selectedsauce', 'selectedcheese', 'selectedtoppings']))
      }
      break;
    case 'selectedcheese':
      firstPizza = firstPizza.update('showKeys', () => fromJS(['sizes', 'selectedbase', 'selectedsauce', 'selectedcheese', 'selectedtoppings']))
        .update('finishChoose', () => true)
      break;
    // case 'selectedtoppings':
    //   break;
    case 'selectedLeftSide':
      if (!firstPizza.get('showCustomiz')) {
        firstPizza = firstPizza.update('showKeys', () => fromJS(['sizes', 'selectedLeftSide', 'selectedRightSide',]))
          .updateIn(['subPizzaDishView', 'pizzaNames'], () => [{ name: 'Size', show: false }, { name: 'Left Side', show: false }, { name: 'Right Side', show: true },])
      } else {
        if (firstPizza.get('showKeys').includes('selectedtoppings')) {
          firstPizza = firstPizza.update('showKeys', () => fromJS(['sizes', 'selectedLeftSide', 'selectedRightSide', 'selectedtoppings']))
        }
      }
      break;
    case 'selectedRightSide':
      firstPizza = firstPizza.update('showKeys', () => fromJS(['sizes', 'selectedLeftSide', 'selectedRightSide', 'selectedbase', 'selectedsauce', 'selectedcheese', 'selectedtoppings']))
        .update('showCustomiz', () => true)
        .update('finishChoose', () => true)
        .updateIn(['subPizzaDishView', 'pizzaKeys'], () => ['sizes', 'selectedLeftSide', 'selectedRightSide', 'selectedtoppings'])
        .updateIn(['subPizzaDishView', 'pizzaNames'], () => [{ name: 'Size', show: false }, { name: 'Left Side', show: false }, { name: 'Right Side', show: false }, { name: 'toppings', show: true }])
      break;
  }
  return firstPizza

}

