import React, { useMemo } from 'react';
import { Icon, ShopTel } from '../../../../component';
import { connect, compose, withRouter } from '../../../../utils';

import './index.less'
import './indexPc.less';


const TimeC = (props) => {

  const { restaurantInfo, landing } = props;

  const timeItems = useMemo(() => {

    return [
      {
        iconType: 'icona-11',
        time: restaurantInfo?.deliverTime?.join('     '),
        text: 'Delivery Time'
      },
      {
        iconType: 'icona-22',
        time: restaurantInfo?.collectionTime?.join('     '),
        text: 'Collection Time'
      },
    ].map(item => {
      return <div className='time_c_item' key={item.text}>
        <Icon type={item.iconType} />
        <h5>{item.text}</h5>
        <p>{item.time}</p>
      </div>
    })


  }, [restaurantInfo])

  if (landing?.webSiteType === 3)
    return (<section className="time_c" >
      <h4>{restaurantInfo?.name}</h4>
      <div className='time_c_line' />
      <div className='time_c_items'>
        {timeItems}
      </div>
      <ShopTel restaurantInfo={restaurantInfo} />
    </section>)
  else
    return null;
}

export default compose(
  connect(({ webConfigModal: { landing }, home: { restaurantInfo, } }) => ({
    restaurantInfo, landing
  })),
  withRouter
)(TimeC);