import React, { useRef, useEffect } from 'react';

import { scrollTo, usePrevious } from '../../utils';
import {
  Route,
  withRouter,
} from "react-router-dom";

//   

function ScrollToTop (props) {

  const provious = useRef(props.location.pathname);

  useEffect(() => {
    if (provious?.current && props.location.pathname !== provious.current) {
      scrollTo(0,0)
    }
  })

  return props.children

}
export default withRouter(ScrollToTop);