import React, { useEffect, useMemo, useRef, useState } from 'react';
import './indexPc.less';
import './index.less';
import { connect, action, useMessage, h5ToRn, inRN, isMobile, AppConfig } from '../../utils';

import PropTypes from "prop-types";
import NavIconSlide from './components/navIconSlide';
import { ScrollTop } from '../../component';
import ClearBasketModel from './clearbasketModel';
import List from './list/index';
import ShopHeader from './shopHeader';

import ShopLoadingSkeleton from './shopLoadingSkeleton';
import NavSkeleton from './navSkeleton';


import { AddressModal, SearchEmpty, MHeader } from '../../component'

const ShopList = (props) => {

  const { shops, loading, rId, selectedAddress, showAddress, rName, keyword, showClear, isFocus, landing, showFixedHeader } = props

  const lat = selectedAddress?.lat
  const lon = selectedAddress?.lng

  useEffect(() => {
    if (lat && lon) {
      const geo = { lat, lon };
      props.dispatch(action('shopList/getStoreListByGeo', { geo: geo }));
    }
  }, [selectedAddress]);

  useEffect(() => {
    props.dispatch(action('shopList/testInfo', {
      callBack: (valide) => {
        switch (valide) { // 1  2
          case 1:
            props.history.push(`/`)
            break;
          case 2:
            break;
        }
      }
    }))
    return () => props.dispatch(action('shopList/save', { isFocus: false }))
  }, [])

  //  RN 
  useMessage(onRNMessage)

  function onRNMessage (event) {
    if (event.data === AppConfig.hardwareBackPress) {
      props.dispatch(action('shopList/save', { showAddress: false }))
    }
  }

  // modal  
  function getAddress (geo) {
    props.dispatch(action('address/queryLocalAddrsByGeo', {
      lat: geo.lat, lng: geo.lon, callBack: (res) => {
        props.dispatch(action('user/saveAddress', {
          lat: geo.lat, lng: geo.lon, ad: res.formatted_address, callBack: () => {
            props.dispatch(action('shopList/save', { showAddress: false, }))
          }
        }));
      }
    }));
  }

  function onFoodShopClick (cuisine) {
    if (isMobile()) {
      props.dispatch(action('shopList/save', { isFocus: true }))
    }
    props.dispatch(action('shopList/save', { keyword: cuisine }))
    props.dispatch(action('shopList/queryByKeyword', { input: cuisine, lat: lat, lon: lon, }))

  }

  function onAddressClosePress () {
    props.dispatch(action('shopList/save', { showAddress: false }))
    if (inRN()) {
      //landing pageShopList
      if (landing?.webSiteType === 2 || landing?.webSiteType === 3) {
        h5ToRn({ type: 2, statusBar: 'light-content' });
      }
    }
  }

  // 
  function onAddressSelect (ad) {

    props.dispatch(action('shopList/save', { showAddress: false, }))
  }

  function goShopDetail (item) {
    return () => {
      if (isFocus) { //   
        props.dispatch(action('shopList/save', { isFocus: false, keyword: '', showFixedHeader: false }))
      }
      if (rId) {
        if (rId === Number.parseInt(item.id)) {
          if (item.menuUrl) {
            props.history.push(`/shopDetail/${item.menuUrl}`);
          } else {
            props.history.push(`/shopDetail?rId=${item.id}`);
          }
        } else {
          currentR.current = item
          props.dispatch(action('shopList/save', { showClear: true }));
        }
      } else {
        if (item.menuUrl) {
          props.history.push(`/selectOrderType/${item.menuUrl}`);
        } else {
          props.history.push(`/selectOrderType?rId=${item.id}`);
        }
      }
    }
  }

  const currentR = useRef()

  function onOK () {
    if (currentR.current.menuUrl) {
      props.history.push(`/selectOrderType/${currentR.current.menuUrl}`);

    } else {
      props.history.push(`/selectOrderType?rId=${currentR.current.id}`);

    }
    props.dispatch(action('orderList/clear'));
    props.dispatch(action('shopList/save', { showClear: false }));
  }

  function onCancel () {
    props.dispatch(action('shopList/save', { showClear: false }));
  }

  function renderNav () {
    if (landing.webSiteType === 1) {
      if (loading && !isFocus) {
        return <NavSkeleton />
      } else {
        return <NavIconSlide visable={!isFocus}
          onFoodShopClick={onFoodShopClick}
        />
      }
    }
  }

  return <section className="store_list_page">
    {
      landing.webSiteType === 1 &&
      <MHeader show={!isFocus} />
    }

    <ShopHeader />
    {isMobile() && showFixedHeader && !isFocus && !loading && <ShopHeader fixed />}


    {renderNav()}

    {loading && <ShopLoadingSkeleton />}

    {
      keyword.length < 2 && isFocus ? null :
        <>
          {shops?.length > 0 && !loading && <List shops={shops} goShopDetail={goShopDetail} landing={landing} />}

          {
            shops?.length === 0 && !loading && <div className="store_list_empty">
              <SearchEmpty />
              {keyword ? <>
                <h5 key='a'>Didnt find any results for {keyword}</h5>
                <p key='b'>Try another restaurant or cuisine</p>
              </> : <>
                <h5 key='c'>Couldn't find any restaurant nearby?</h5> <p key='d'>Try to search for another address or area.</p>
              </>}
            </div>
          }
        </>
    }


    {showClear && <ClearBasketModel
      name={rName}
      onOK={onOK}
      onCancel={onCancel}
    />}

    {showAddress && <AddressModal onClose={onAddressClosePress} onAddressSelect={onAddressSelect} getAddress={getAddress} />}
    <ScrollTop />
  </section>
}


ShopList.propTypes = {
  shops: PropTypes.array
};

export default connect(({ shopList: { shops, loading, showAddress, keyword, showClear, isFocus, showFixedHeader }, user: { selectedAddress },
  orderList: { rId, rName }, webConfigModal: { landing } }) => ({
    shops, loading, selectedAddress, rId, showAddress, rName, keyword, showClear, isFocus, landing, showFixedHeader
  }))(ShopList);
