import React, { useState, useEffect, useRef } from "react";
import "./indexPc.less";
import "./index.less";
import { MyMarquee } from "../../../../../../component";


export const CardTag = ({ cuisines }) => {
  const [cardDate, setCardDate] = useState([]);

  useEffect(() => {
    const data = cuisines.split(',');
    data && setCardDate(data);
  }, [])


  return (
    <MyMarquee className="card_header_tags" >
      {cardDate.map((n, key) => {
        return n !== '' && <span className="card_header_tag" key={key}>
          {String(n).replace("Fish&Chips", "Fish & Chips").replace("AsianStreetFood", "Asian Street Food")}
        </span>
      }
      )}
    </MyMarquee>
  )
};
