import React from 'react';
import { connect, compose, withRouter, AppConfig } from '../../../../utils';


import './index.less'
import './indexPc.less';




const DeliveryC = (props) => {

  const {  restaurantInfo, landing } = props;


  return (landing?.webSiteType === 3 ? <div className="delivery_c">
    <div className="map_c_delivery_left">
      <p>Local Delivery from {restaurantInfo?.currency}{ (restaurantInfo?.deliveryFee || 0).toFixed(2)}</p>
      <p>Min order {restaurantInfo?.currency}{ (restaurantInfo?.minOrder || 0).toFixed(2) }</p>
    </div>
    <div className="map_c_delivery_right">Direction</div>
  </div> : null)
}

export default compose(
  connect(({ webConfigModal: { landing },home: {  shopList, restaurantInfo } }) => ({
    landing, shopList, restaurantInfo
  })),
  withRouter
)(DeliveryC);