import React, { Suspense } from 'react'
import { createRoot } from 'react-dom/client';

import './style/global.less';
import './style/common.less';
import './style/animate.less';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import route from './route';
import dvaApp from './dva';

import { Loader, ScrollToTop } from './component';

import { redirctHost, loadJS } from './utils/loadJS';

redirctHost();
loadJS();
/**
 * switch 
 * switch + exact 
 */
const renderRouter = (item) => {

  let params = {
    key: item.path || item.name,
    exact: item.exact !== false,
    path: item.path
  };

  if (item.children) {
    params = {
      ...params,
      render: (data) => item.component(data)
    };
  } else {
    params = {
      ...params,
      component: item.component
    };
  }
  return (
    <Route
      {...params}
    >
    </Route>
  );
};

const App = () => (
  <BrowserRouter>
    <ScrollToTop>
      {/* <Suspense fallback={<Loader loading />}> */}

      <Switch>

        {
          route.map(item => {
            if (item.children) {
              const Layout = item.children.layout;
              item.component = (data) => (
                <Layout key={item.path || item.name} {...data}>
                  {
                    item.children.routes.map(children => renderRouter(children))
                  }
                </Layout>
              );
            }
            return renderRouter(item);
          })
        }
      </Switch>
      {/* </Suspense> */}

    </ScrollToTop>
  </BrowserRouter>
);

const WapperApp = dvaApp.start(<App />)

const root = createRoot(document.getElementById('app'));
root.render(<WapperApp />);

