import React, { useEffect, useRef, useMemo } from 'react';
import { Loader, Notice } from '../../component';
import ShopFoodHeader from './shopFoodHeader';
import ShopBasicInfo from './shopBasicInfo';
import FoodAllergy from './foodAllergy';
import ShopCategoryPc from './shopCategory/indexPc';
import ShopCategoryM from './shopCategory/index';
import ShopFoods from './shopFoods';
import ShopOrderList from './shopOrderList';
import ShopTotlePrice from './shopTotlePrice';
import ShopEmpty from './shopEmpty';
import { useParams } from 'react-router-dom';
import raf from "raf";


import { action, connect, getValueFromQuery, isMobile, scrollTo, toJSON } from '../../utils';


import './indexPc.less';
import './index.less';
import StoreInfoModal from './storeInfoModal';
import { changeHtmlHeaderTitle, changeHtmlHeader } from '../../utils/loadJS';


const ShopDetail = (props) => {

  let rId = getValueFromQuery('rId', props.location.search);
  let { urlName, menuUrl } = useParams();
  const { restaurantInfo, showSearch, dishMenu, dishMenuListLoading, showStoreInfo, searchKeyword, loading404, landing } = props;

  const pathname = useMemo(() => {
    if (menuUrl) {
      return menuUrl?.replace('/', '').replace('.html', '').replace('.htm', '');
    }
    return urlName?.replace('/', '').replace('.html', '').replace('.htm', '');
  }, [])

  // http://localhost:2000/shopDetail/easy-dish-1340-menu
  useEffect(() => {
    if (rId) {
      props.dispatch(action('webConfigModal/initPage', {
        callBack: () => {
          // 
          if (showSearch && searchKeyword && isMobile()) {
            return
          }
          props.dispatch(action('shopDetail/getDishMenuList', { rId: rId }));
          props.dispatch(action('shopDetail/getRestaurantInfo', { rId: rId }));
          props.dispatch(action('shopDetail/restaurantStatusCheck', { rId: rId }));
          props.dispatch(action('orderList/judgeDishBelongShop', { rId: rId }));
        }
      }));

      raf(() => {
        scrollTo(props.scrollTop || 0)
      })
    } else {
      props.dispatch(action('webConfigModal/getShopIdByName', {
        name: pathname, callBack: (sId, urlType) => {
          if (sId > 0) {
            rId = sId;
            props.dispatch(action('shopDetail/getDishMenuList', { rId: rId }));
            props.dispatch(action('shopDetail/getRestaurantInfo', { rId: rId }));
            props.dispatch(action('shopDetail/restaurantStatusCheck', { rId: rId }));
            props.dispatch(action('orderList/judgeDishBelongShop', { rId: rId }));
            if (urlType === 1) {
              props.dispatch(action('shopDetail/save', { showStoreInfo: true }))
            }

            raf(() => {
              scrollTo(props.scrollTop || 0)
            })
          } else {
            props.history.replace(`/notFoundPage?name=${props.location.pathname}`)
          }
        }
      }));
    }

    return () => {
      if (!showSearch && !searchKeyword) {
        props.dispatch(action('shopDetail/clear'));
      }
    }

  }, [])

  useEffect(() => {
    if (landing.webSiteSeo) {
      const json = toJSON(landing.webSiteSeo);
      if (landing.webSiteType === 3 && !json.title && restaurantInfo?.name) {
        const headerTitle = restaurantInfo?.name + (restaurantInfo?.couponList?.length > 0 ? ` | ${restaurantInfo?.couponList.join(',')}` : '')
        changeHtmlHeaderTitle(headerTitle);
      } else if (landing.webSiteType === 1 || landing.webSiteType === 2) {
        changeHtmlHeader(json, landing.logoUrl);
      } else if (landing.webSiteType === 3 && json.title) {
        changeHtmlHeader(json, landing.logoUrl);
      }
    }
    if (restaurantInfo.name) {
      const shopKeywordsTitle = restaurantInfo.name + ' - ' + document.title;
      const shopKeywords = restaurantInfo.cuisines + 'takeaway,easydish,online,delivery,food4u,easy dish,just-eat';
      changeHtmlHeaderTitle(shopKeywordsTitle, shopKeywords);
    }
  }, [restaurantInfo, landing.webSiteSeo]);

  const checkStatusTimer = useRef();

  useEffect(() => {
    if (process.env.NODE_ENV !== 'development') { //   log 
      checkStatusTimer.current = setInterval(() => {

        props.dispatch(action('shopDetail/restaurantStatusRefresh'));
      }, 30000)
    }
    return () => checkStatusTimer.current && clearInterval(checkStatusTimer.current)
  }, [])

  if (loading404) {
    return <Loader loading type='tran' />
  }
  return <section className='shop_detail_page'>
    <section>
      <ShopFoodHeader />
      <div className='shop_detail_header_img' style={restaurantInfo.indexPic && { backgroundImage: 'url(' + restaurantInfo.indexPic + ')' }} />
      <ShopBasicInfo />
      {
        restaurantInfo?.describe &&
        <div className='shop_notice_container'>
          <Notice text={restaurantInfo?.describe} />
        </div>
      }
      <FoodAllergy />
      {dishMenu?.length > 0 ? <section className='shop_content_wapper'>
        <ShopCategoryM />
        <ShopCategoryPc />
        <ShopFoods />
        <ShopTotlePrice />
        {showSearch && <section className='shop_detail_header_search'>
          <ShopFoods search='search' />
        </section>
        }
      </section>
        : <ShopEmpty text={`Couldn't find any dishes under this restaurant.`} loading={dishMenuListLoading} />
      }
    </section>
    <aside>
      <ShopOrderList />
    </aside>
    {showStoreInfo && <StoreInfoModal />}
  </section>
}

export default connect(({
  shopDetail: { restaurantInfo, showSearch, scrollTop, dishMenu, dishMenuListLoading, showStoreInfo, searchKeyword },
  webConfigModal: { loading404, landing },
}) => ({
  restaurantInfo, showSearch, scrollTop, dishMenu, dishMenuListLoading, showStoreInfo, searchKeyword, loading404, landing
}))(ShopDetail);