import React, { useState } from 'react';
import { Icon, ModalAlert, } from '../../../component';

import './index.less';
import './indexPc.less';

const EditAccountItem = (props) => {

  const { label, value, onSave, maxLength = 20 } = props

  const [disabled, setdisabled] = useState(true)
  const [inputValue, setInputValue] = useState(value);

  function onSaveClick () {
    setdisabled(true)
    onSave({
      label: label, value: inputValue,
    })
  }

  function onChange (e) {
    const value = e.target.value?.substring(0, maxLength);

    setInputValue(value)
  }

  return <div className='edit_account_item'>
    <label>{label}</label>
    <div className='edit_account_item_input'>
      <input value={inputValue} disabled={disabled} onChange={onChange} />
      {disabled ? <Icon type={'iconUnion'} onClick={() => setdisabled(false)} /> : <p onClick={onSaveClick}>Save</p>}
    </div>
  </div>
}


export default EditAccountItem;