import React, { useEffect } from "react";
import { AppConfig, accMul, action, connect, getValueFromQuery } from '../../utils'
import { AdaptHeader, Icon } from "../../component";
import './index.less'
import './indexPc.less'
import RevolutCheckout from '@revolut/checkout';
import { reviewPaymentS } from "../../service";
import { useParams } from 'react-router-dom';

const STATUS = {
  PENDING: 'pending',
  SUCCESS: 'success',
  FAILURE: 'failure',
  CANCEL: 'cancel',
}

const RevolutPay = (props) => {


  const { orderDetail, uuid } = props;

  const { status } = useParams();


  useEffect(() => {
    if (status) {
      switch (status) {
        case STATUS.PENDING:
          if (uuid) {
            genneratRovlutOptions()
          } else {
            props.history.replace('/')
          }
          break;
        case STATUS.SUCCESS:
          props.history.push(`/historyOrderDetail?uuid=${uuid}&from=payment`);
          props.dispatch(action('statistic/saveAdsPurchaseAndClear'));
          break;
        case STATUS.FAILURE:
          props.dispatch(action('reviewPayment/save', { showOops: true, showCard: false }))
          props.history.goBack()
          break;
        case STATUS.CANCEL:
          props.dispatch(action('reviewPayment/save', { revolutPayStatus: STATUS.CANCEL }))
          break;
        default:
          props.history.replace('/')
          break;
      }
    }

  }, [status])


  async function genneratRovlutOptions () {
    try {
      const { revolutPay } = await RevolutCheckout.payments({
        locale: 'en',// Optional, defaults to 'en'
        mode: AppConfig.REVOLUT_MODE, // Optional, defaults to 'prod'
        publicToken: AppConfig.REVOLUT_PUBLIC_KEY, // Merchant public API key
        redirectUrls: {
          success: window.location.origin + '/revolutPay/success',
          failure: window.location.origin + '/revolutPay/failure',
          cancel: window.location.origin + '/revolutPay/cancel'
        },
      })

      console.log('orderDetail', orderDetail.due)

      const paymentOptions = {
        currency: orderDetail?.currencyValue, // 3-letter currency code
        totalAmount: accMul(orderDetail?.due, 100), // in lowest denomination e.g., cents

        createOrder: async () => {
          const res = await reviewPaymentS.revolutPay({ uuid: uuid, host: window.location.origin, version: 'v2' });
          return { publicId: res.revolutId }
        },
        buttonStyle: {
          // variant: 'light-outlined',
          cashback: false,

        },
        savePaymentMethodForMerchant: true,
      }

      revolutPay.mount(document.getElementById('revolut-pay'), paymentOptions)
      revolutPay.on('payment', (event) => {
        switch (event.type) {
          case 'cancel': {
            props.history.replace('/revolutPay/cancel')
          }
            break;
          case 'error':
            props.history.replace('/revolutPay/failure')

            break
          case 'success':
            props.history.replace('/revolutPay/success')
            break;
        }
      })

    } catch (e) {
      console.error(e)
    }
  }

  return <section className="revolut_pay_page">
    <AdaptHeader title='RevolutPay' />
    <div className="revolut_pay_page_content">
      <div id='revolut-pay' />

    </div>
  </section>

}


export default connect(({ reviewPayment: { revolutPayStatus }, orderList: { uuid }, reviewPayment: { orderDetail } }) =>
  ({ revolutPayStatus, uuid, orderDetail }))(RevolutPay)