import React, { useMemo, useRef, useEffect } from 'react';

const ShopMap = (props) => {

  const { shopList } = props;

  const center = useMemo(() => {
    if (shopList?.length > 0) {
      return [shopList[0].lat, shopList[0].lon]
    } else {
      return null
    }

  }, [shopList])

  const iframeRef = useRef();


  const markers = useMemo(() => {

    return shopList.map((item, i) => {
      return {
        lat: item.lat,
        lng: item.lon,
        address: item.address,
        name: item.name,
        phone: item.phone,
      }
    })

  }, [shopList])


  function produceHereMapMarkerUrl () {
    if (markers.length > 0) {
      return `/hereMapMarker.html?center=${JSON.stringify(center)}`
    } else return null
  }

  function onLoad () {
    const params = {
      signal: 'mapMarkers',
      data: { markers: markers }
    }
    iframeRef.current.contentWindow.postMessage(JSON.stringify(params), '*')
  }


  return produceHereMapMarkerUrl() ? <iframe ref={iframeRef} src={produceHereMapMarkerUrl()} style={{ width: '100%', height: '100%' }} frameBorder="0"
    onLoad={onLoad} /> : null
}


export default ShopMap