export function redirctHost () {
  // httphttps
  if (process.env.NODE_ENV !== 'development') {
    let url = window.location.href;
    if (url.indexOf('https') < 0) {
      url = url.replace('http:', 'https:');
      window.location.replace(url);
    }

    if (AppConfig.WEB_SITE !== window.location.host) {
      url = url.replace(window.location.host, AppConfig.WEB_SITE);
      window.location.replace(url);
    }

    //   console
    console.log = () => { }
  }
}

export function loadJS () {
  // 
  init();

  //  Cookiebot
  if (!inRN()) {
    if (/www.easydish.com/.test(window.location.host) || /www.easydish.ie/.test(window.location.host)) {
      const script = document.createElement('script');
      script.type = 'text/javascript';
      script.id = "Cookiebot";
      script.setAttribute("data-cbid", "4b26794e-4f09-4672-ac1f-3fcf6b0ccb2d");
      script.setAttribute("data-blockingmode", "auto");
      script.src = 'https://consent.cookiebot.com/uc.js';
      document.head.insertBefore(script, document.getElementsByTagName("meta")[0]);

    } else if (/www.mizzoni.ie/.test(window.location.host) || /.mizzoni.ie/.test(window.location.host)) {
      const script = document.createElement('script');
      script.type = 'text/javascript';
      script.id = "Cookiebot";
      script.setAttribute("data-cbid", "49d144fb-a6f1-4f18-8783-51509e4f14be");
      script.setAttribute("data-blockingmode", "auto");
      script.src = 'https://consent.cookiebot.com/uc.js';
      document.head.insertBefore(script, document.getElementsByTagName("meta")[0]);
    }
  }
  //<!-- Facebook Pixel Code -->
  addFacebookBaseCode();

  //<!-- Global site tag (gtag.js) - Google Analytics -->
  addGoogleBaseCode();

  //<!-- Tik Tok Pixel Code -->
  addTiktokBaseCode();
}

//  header 
export function changeHtmlHeader (headerConfig, logoUrl) {
  try {
    if (headerConfig) {
      const { title, description, keywords, icon } = headerConfig;
      changeHtmlHeaderTitle(title, keywords);
      if (description) {
        document.querySelector('meta[name="description"]').setAttribute('content', description);
        addHeaderMetaOg('description', description)
      }
      (icon || logoUrl) && document.querySelector('link[rel="icon"]').setAttribute('href', icon || logoUrl);
      document.querySelector('link[rel="canonical"]').setAttribute('href', window.location.host);
    }
  } catch (error) {
    console.error(error);
  }
}

export function changeHtmlHeaderTitle (title, keywords) {
  try {
    if (title) {
      document.title = title.indexOf('Official Website') > -1 ? title : `${title} | Official Website`;
      addHeaderMetaOg('title', title);
    }
    keywords && document.querySelector('meta[name="keywords"]').setAttribute('content', keywords);
  } catch (error) {
    console.error(error);
  }
}

function addHeaderMetaOg (key, value) {
  const meta = document.createElement('meta')
  meta.setAttribute('property', `og:${key}`);
  meta.content = value;
  document.head.appendChild(meta);
}

