export function addGoogleTag (tag) {
  const googleTagEle = document.getElementById('googleTag') // googleTag 
  if (tag && !googleTagEle) {
    const text = `(function(w,d,s,l,i){w[l]=w[l]||[];w[l].push({'gtm.start':
    new Date().getTime(),event:'gtm.js'});var f=d.getElementsByTagName(s)[0],
    j=d.createElement(s),dl=l!='dataLayer'?'&l='+l:'';j.async=true;j.src=
    'https://www.googletagmanager.com/gtm.js?id='+i+dl;f.parentNode.insertBefore(j,f);
    })(window,document,'script','dataLayer','${tag}');`

    const script3 = document.createElement('script');
    script3.type = 'text/javascript';
    script3.id = 'googleTag';
    script3.text = text;
    document.head.appendChild(script3);

    const script4 = document.createElement('noscript');
    const iframe = document.createElement('iframe');
    iframe.height = "0";
    iframe.width = "0";
    iframe.style.display = 'none';
    iframe.style.visibility = 'hidden';
    iframe.src = `https://www.googletagmanager.com/ns.html?id=${tag}`;
    script4.appendChild(iframe);
    document.body.appendChild(script4);
  }

}

//<!-- Global site tag (gtag.js) - Google Analytics -->
export function addGoogleBaseCode () {
  if (/www.easydish.ie/.test(window.location.host)) {
    const script = document.createElement('script');
    script.type = 'text/javascript';
    script.async = true;
    script.src = 'https://www.googletagmanager.com/gtag/js?id=G-Z3GJM0Q80L';
    document.head.insertBefore(script, document.getElementsByTagName("meta")[0]);

    const script2 = document.createElement('script');
    script2.type = 'text/javascript';
    script2.text = "window.dataLayer = window.dataLayer || [];" +
      "function gtag(){dataLayer.push(arguments);}" +
      "gtag('js', new Date());" +
      "gtag('config', 'G-Z3GJM0Q80L');";
    document.head.insertBefore(script2, document.getElementsByTagName("meta")[0]);
  }
}

