import React from 'react';
import { Icon, LocationIcon } from '../../../../component';
import { connect, compose, action, withRouter, getReturnPath } from '../../../../utils';

import './index.less'
import './indexPc.less';

const AddressC = (props) => {

  // 
  function getAddress (geo) {
    props.dispatch(action('address/queryLocalAddrsByGeo', {
      lat: geo.lat, lng: geo.lon, callBack: (res) => {
        props.dispatch(action('user/saveAddress', {
          lat: geo.lat, lng: geo.lon, ad: res.formatted_address, callBack: () => {
            props.history.push('/shopList')
          }
        }));
      }
    }));
  }

  //  
  function goShopList (item) {
    return () => {
      props.dispatch(action('user/saveAddress', {
        lat: item.lat, lng: item.lon, ad: item.address, callBack: () => {
          props.history.push('/shopList')
        }
      })); // 
    }
  }

  function toLogin () {
    props.history.push(`/login?returnUrl=${getReturnPath(props.location)}`)
  }

  function goAddress () {
    props.dispatch(action('home/save', { showAddress: true }))
  }

  return (<section className='landinghome_address_container'>
    <div className='address_input_wrapper' onClick={goAddress}>
      <span>
        Enter your address to find local store
      </span>
      <LocationIcon className='address_icon' returnGeo={getAddress} />
      <Icon type='iconsousuo' />
    </div>
    {/*  */}
    {props.isLogin && <section className="home_rensent_address_container">
        <div className='or_select'>{props.allRensentAddress && props.allRensentAddress.length > 0?
          'Or, select a recent address':'You have no recent addresses.'}</div>
        {props.allRensentAddress?.map((item, i) => {
          if (i > 2) return null
          else if (item.lat && item.lon) return (
            <div key={item.id} className='rensent_address' onClick={goShopList(item)}>
              <Icon type='icondizhi' className='rensent_address_icon' />
              <p>{item.address}</p>
              <Icon type='iconicon_more_A1' />
            </div>
          )
        })
        }
      </section>}

    {props.isLogin && props.allRensentAddress && props.allRensentAddress.length === 0 &&
      <p className='rensent_address_no_login'> You have no recent addresses.</p>}
    {/*  */}
    {
      (!props.isLogin) && <p className='rensent_address_no_login'><span onClick={toLogin}>Login</span> for your recent addresses</p>
    }
  </section>
  )
}

export default compose(
  connect(({ address: { allRensentAddress, }, user: { isLogin }, home: { loading, showAddress } }) => ({
    allRensentAddress, isLogin, loading, showAddress
  })),
  withRouter
)(AddressC);