import React, { useMemo } from 'react';
import { AutoTextarea, CloseIcon, Icon, DishpriceIcon, MHeader } from '../../../component';
import './indexPc.less';
import './index.less';
import { connect, action, isMobile, AppConfig, getStatusBarHeight, inRN, isPc, isPad } from '../../../utils';


/**
 * 4. 
     rastaurants or cuisines  23.png
    3. logo menu
    
    
    3
 */

const ShopHeader = (props) => {

  const { selectedAddress, keyword, isFocus, rId, fixed = false } = props;

  const lat = selectedAddress?.lat
  const lon = selectedAddress?.lng
  const ad = selectedAddress?.ad

  function goAddress () {
    props.dispatch(action('shopList/save', { showAddress: true }))
  }

  function onChange (e) {
    const value = e.target.value;
    props.dispatch(action('shopList/save', { keyword: value }))
    if (value.length > AppConfig.searchCount) {
      props.dispatch(action('shopList/queryByKeyword', { input: value, lat: lat, lon: lon }))
    } else if (!isMobile() && value.length === 0) {
      getStoreListByGeo();
    }
  }


  function onFocus () {
    if (!isFocus) {
      if (isMobile()) {
        props.dispatch(action('shopList/save', { isFocus: true }))

      }
    }
  }

  function backSearch () {
    if (isFocus) {
      props.dispatch(action('shopList/save', { isFocus: false, keyword: '', showFixedHeader: false }))
    }
    getStoreListByGeo();
  }


  function getStoreListByGeo () {
    const geo = { lat, lon };
    props.dispatch(action('shopList/getStoreListByGeo', { geo: geo }));
  }

  function clearKeyWord () {
    props.dispatch(action('shopList/save', { keyword: '' }));
    getStoreListByGeo();
  }

  const className = useMemo(() => {
    if (fixed) {
      if (rId) {
        return 'shop_header_fixed'
      } else {
        return 'shop_header_fixed_without_rId'
      }
    } else {
      return undefined
    }
  }, [fixed, rId])

  return <section
    className={`store_list_header ${isFocus ? 'store_list_header_focus' : undefined} ${className}`}
    style={{ paddingTop: isFocus && getStatusBarHeight() || '.2rem' }}
  >
    {!isFocus &&
      <div className='store_list_header_top' style={{ paddingTop: fixed ? getStatusBarHeight() : 0 }}>
        <div className='store_list_header_address' onClick={goAddress} >
          <Icon type='iconicon_location_b' className='address_icon' />
          <p className='one-line'>{ad}</p>
          <Icon type='iconicon_more_A1' className='more_icon' />
        </div>
        {fixed && <DishpriceIcon />}
      </div>}

    <div className='store_list_input_wrapper' >
      {isFocus && <Icon type='iconicon_more_A1' className='back_icon'
        onClick={backSearch}
      />}
      <div className='store_list_input_header' >
        <Icon type='iconicon_search_a9' onClick={onFocus} className='store_list_input_header_search_icon' />
        <AutoTextarea className='search_input'
          onFocus={onFocus}
          value={(isFocus || isPc() || isPad()) ? keyword : ''}
          onChange={onChange}
          placeholder='Restaurants or cuisines ' />
        <CloseIcon text={(isFocus || isPc() || isPad()) && keyword} onClick={clearKeyWord} />
      </div>
    </div>


  </section>
}

export default connect(({ shopList: { keyword, isFocus }, user: { selectedAddress }, orderList: { rId } }) => ({
  selectedAddress, keyword, isFocus, rId
}))(ShopHeader);