import React, { useEffect, useMemo, useRef, } from 'react';
import { AdaptHeader, Icon, Modal, MyMarquee } from '../../../component';

import './index.less';
import './indexPc.less';

import { connect, compose, action, getOs } from '../../../utils';

import { withRouter } from 'react-router-dom';
import CouponCardItem from './couponCardItem';




const CouponCards = (props) => {
  const { couponList, selectCoupon, terms } = props;

  function closeTerms () {
    props.dispatch(action('reviewPayment/save', { terms: '' }))

  }

  function onCoupon () {
    props.dispatch(action('reviewPayment/save', { showEnterCode: true, searchCode: '', errorCouponText: '', searchCoupon: null }))
  }


  const innerScroll = useRef();

  const sortCouponList = useMemo(() => {
    if (couponList && couponList.length > 0 && selectCoupon && innerScroll.current) {
      innerScroll.current.scrollTo(0, 0)

      return [selectCoupon].concat(couponList.filter((item) => item.entityId !== selectCoupon.entityId))
    } else return []
  }, [couponList, selectCoupon])




  return <>
    <section className={`conpon_cards_wrapper ${sortCouponList.length > 0 ? "" : "conpon_cards_wrapper_none"}`}>
      <MyMarquee className='conpon_cards_container' ref={innerScroll}>
        {sortCouponList.map((item, index) => {
          return <CouponCardItem selectCoupon={selectCoupon} item={item} key={index} />
        })}
      </MyMarquee>
    </section>
    <section className='conpon_cards_wrapper'>
    <section className='review_payment_content_coupon_tip' onClick={onCoupon}>
      <Icon type='iconicon_coupon_A21' />
      {/* <p>{orderDetail.couponName ? <span>{orderDetail.couponName}</span> : 'Have a coupon or voucher?'}</p> */}
      <p>Have a coupon or voucher?</p>
      <Icon type='iconicon_more_A1' />
    </section>
    {!!terms && <Modal title={<AdaptHeader title='Terms' onLeftPress={closeTerms} />} className='coupon_terms_page' >
      <section className='coupon_terms_content' dangerouslySetInnerHTML={{ __html: terms }} />
    </Modal>}
  </section>
  </>
}

export default compose(
  withRouter, connect(({ orderList: { uuid }, reviewPayment: { terms, orderDetail, couponList, selectCoupon } }) => ({
    uuid, terms, orderDetail, couponList, selectCoupon
  })))(CouponCards)