import React, { useMemo } from 'react';
import Icon from '../icon';
import CurrencyPrice from '../currencyPrice';
import { ToppingC, NoToppingC } from '../toppingC';
import LineAdd from '../lineAdd';
import LineReduce from '../lineReduce';

import './index.less'
import './indexPc.less'


const DishView = (props) => {

  const { dishList } = props;


  function onDishClick (item, index) {
    return () => {
      props.onDishClick && props.onDishClick(item, index)
    }
  }

  function deleteDish (index) {
    return (event) => {
      event.stopPropagation();
      props.deleteDish && props.deleteDish(index)
    }
  }

  function genrateDishItem (dish, index, categoryId) {
    if (categoryId !== 8) {
      return dish.subDishes.map((a, j) => {
        if (a.choose && !a.side) {
          if (a.name && !/^NONE$/i.test(a.name)) {
            return <div key={a.id + '' + index}>
              <div>{(a?.count > 1 ? (a.count + ' x ') : '')} {a.name} {a.note && <i>({a.note})</i>}</div>
              {a.categoryId === 5 && renderPizzaDetail(a.subPizzaDishView, true)}
            </div>


          }
        }
      })
    } else return null
  }

  function renderDishDetail (subDish, categoryId) {

    if (categoryId !== 8) {
      let subDishWithoutSide = [];

      subDishWithoutSide = subDish?.filter((item, index) => {
        return item.subDishes.some(a => a.choose && !a.side && !/^NONE$/i.test(a.name))
      });
      if (subDishWithoutSide && subDishWithoutSide.length > 0) {


        return <div className='shop_order_list_detail_content'>
          {
            subDishWithoutSide?.map((item, index) => {
              const dishItemCom = genrateDishItem(item, index, categoryId)
              return dishItemCom ? <div key={index} className={`shop_order_list_detail_item ${((subDishWithoutSide.length - 1) === index) ? undefined : 'shop_order_list_detail_item_dish'}`}>
                {dishItemCom}
              </div> : null
            })
          }
        </div>
      }
    } else return null;
  }

  //  
  function generateGroupName (item, index, categoryId) {
    // if (categoryId === 8 && index === 0 && item.groupName) {
    //   return <div className='shop_order_list_detail_item_dish_title shop_order_list_detail_item_dish'>{item.groupName}</div>
    // }
    // else
    return null
  }

  //  partName
  function generateParName (dish) {
    if (dish?.isDisplayTitle === '1') {
      return <div className='shop_order_list_detail_item_8_part_name'>{dish.partName}</div>

    }
  }

  function generateSubItem (sub = [], separate) {
    if (sub?.length > 0) {
      if (separate.printSeparate !== '2') {

        return <div style={{ flex: 1 }}>
          {
            sub.map((item, index) =>
              <div key={item + index}
                className={`item_8_sub_item_spereate`} >{item}</div>)
          }
        </div>
      } else {
        return <div style={{ flex: 1 }}
          className={`item_8_sub_item  item_8_sub_item_no_border`}
        >{sub.join(separate.separateStr)}</div>
      }
    } else {
      return null
    }
  }


  /**
   * @returns 
   */
  function renderDishDetail8 (subDish, categoryId, allSeparate) {
    if (categoryId === 8) {

      // 
      const dishMap = {};
      subDish.map(item => {

        if (Object.keys(dishMap).includes(item.partId + '')) {
          dishMap[item.partId] = (dishMap[item.partId] || []).concat(item)
        } else {
          dishMap[item.partId] = [item]
        }
      })

      let groupDish = {};
      if (allSeparate.printSeparate === 2) {
        groupDish.separate = [];
        groupDish.side = [];
        Object?.values(dishMap)?.map(item => {
          if (item[0]?.partName === 'side') {
            groupDish["side"].push(item);
          } else {
            groupDish["separate"].push(item);
          }
        })
      } else {
        Object?.values(dishMap)?.map(item => {
          const key = item[0].groupId
          if (Object.keys(groupDish).includes(item[0].groupId + '')) {
            groupDish[key].push(item);
          } else {
            groupDish[key] = [item]
          }
        })
      }


      const sortGroupDish = Object.entries(groupDish).sort((a1, b1) => {

        if (a1 === undefined || b1 === undefined ||
          a1[1] === undefined || b1[1] === undefined ||
          a1[1][0] === undefined || b1[1][0] === undefined
        ) {
          return 1
        }
        const a = a1[1][0][0]?.groupSort1
        const b = b1[1][0][0]?.groupSort1
        if (a === undefined || b === undefined) {
          return 1
        }
        if (a > b) return 1;
        else if (a == b) return 0;
        else return -1;
      })


      return sortGroupDish.map(([key, group]) => {

        return <div className='shop_order_list_detail_content_8' key={key}>
          {
            renderDishDetailGroup(group, categoryId,)
          }
        </div>
      })
    }
  }

  function renderDishDetailGroup (group, categoryId,) {

    if (Object.values(group)?.length > 0) {
      group.sort((a1, b1) => {
        const a = a1[0]?.groupSort
        const b = b1[0]?.groupSort
        if (a > b) return 1;
        else if (a == b) return 0;
        else return -1;
      })
      const groupSeparate = Object.values(group)[0][0].printSeparate
      if (groupSeparate !== "2") {

        function genrateDishItem8 (dish, index, categoryId) {
          dish = dish[0]
          if (categoryId === 8) {
            let add = [];
            let reduce = [];

            // 
            const separate = {
              printSeparate: dish.printSeparatePart,
              separateStr: ', ',
            }

            dish?.subDishes?.map((a, j) => {
              if (a.choose) {
                if (a.name && !/^NONE$/i.test(a.name)) {
                  // console.log('a', a)
                  if (a.isDefault === 1) {
                    if (a.count <= 1) {

                    } else {
                      const name = `${(a?.count > 2 ? ((a.count - 1) + ' x ') : '')} ${a.name}`
                      add.push(name)
                    }
                  } else {
                    const note = a.note ? `(${a.note})` : '';
                    const name = `${(a?.count > 1 ? (a.count + ' x ') : '')} ${a.name}` + note
                    add.push(name)
                  }
                }
              }
              if (a.isDefault && a.defaultCount > 0 && a.count === 0) {
                reduce.push(a.name)
              }
            })


            if (reduce?.length > 0) {
              return <div className='shop_order_list_detail_item_8'>
                {generateParName(dish)}
                {add?.length > 0 && <LineAdd>{generateSubItem(add, separate,)}</LineAdd>}
                <LineReduce>
                  <span style={{ textDecoration: 'line-through' }}>{generateSubItem(reduce, separate,)}</span>
                </LineReduce>
              </div>
            } else if (add?.length > 0) {
              return <div className='shop_order_list_detail_item_8'>
                {generateParName(dish)}
                {generateSubItem(add, separate)}
              </div>
            } else {
              return false
            }
          } else return null;
        }

        return group?.map((item, index) => {
          const dishItemCom = genrateDishItem8(item, index, categoryId);
          return dishItemCom ? <div key={index} className='shop_order_list_detail_content_8_wrapper'>
            {generateGroupName(item[0], index, categoryId)}
            {dishItemCom}
          </div> : null
        })
      } else {
        let add = [];
        let reduce = [];

        group?.map((dish, index) => {
          dish[0]?.subDishes?.map((a, j) => {
            if (a.choose) {
              if (a.name && !/^NONE$/i.test(a.name)) {
                if (a.isDefault === 1) {
                  if (a.count <= 1) {

                  } else {
                    const name = `${(a?.count > 2 ? ((a.count - 1) + ' x ') : '')} ${a.name}`
                    add.push(name)
                  }
                } else {
                  const note = a.note ? `(${a.note})` : '';
                  const name = `${(a?.count > 1 ? (a.count + ' x ') : '')} ${a.name}` + note
                  add.push(name)
                }
              }
            }
            if (a.isDefault && a.defaultCount > 0 && a.count === 0) {
              reduce.push(a.name)
            }
          })

        });

        const separate = { printSeparate: '2', separateStr: ', ' }

        if (reduce?.length > 0) {
          return <div className='shop_order_list_detail_content_8_wrapper_1'>
            {add?.length > 0 && <LineAdd>{generateSubItem(add, separate)}</LineAdd>}
            <LineReduce>
              <span style={{ textDecoration: 'line-through' }}>{generateSubItem(reduce, separate)}</span>
            </LineReduce>
          </div>
        } else if (add?.length > 0) {
          return <div className='shop_order_list_detail_content_8_wrapper_1'>
            {generateSubItem(add, separate,)}
          </div>
        } else {
          return false
        }
      }
    } else {
      return null
    }
  }

  function renderDishDetailSide (subDish, categoryId) {
    if (subDish && subDish.length > 0 && categoryId !== 8) {
      return <div className='shop_order_list_detail_content'>
        {
          subDish?.map((item, index) => {
            if (item.subDishes && item.subDishes.length > 0) {
              return item.subDishes.map((a, j) => {
                if (a.choose && a.side) {

                  return <div className='shop_order_list_detail_item' key={a.id + '' + index}>{a.name} {a.note && <i>({a.note})</i>}</div>
                }
              })
            }
          })
        }
      </div>
    }
  }

  function renderPizzaDetail (pizza, isSub, isHalfPizza) {
    if (pizza?.selectedtoppings) {
      return <div className={!isSub ? 'shop_order_list_detail_content' : 'shop_order_list_detail_sub_content'}>
        {renderPizzaDetailOrther(pizza)}
        {renderTipsHeaderOfHeader(pizza, isHalfPizza)}
      </div>
    }
  }

  // pizza  


  function renderPizzaDetailOrther (pizza) {
    const name = ['base', 'sauce', 'cheese'].map(item => {
      const reduce = pizza[item];
      const add = pizza['selected' + item + 'name'];
      if (add && reduce !== add) {
        return <div key={item} className='shop_order_list_detail_pizza_name_item'>
          <LineAdd>{add}</LineAdd>
          <LineReduce><span className='line-through'>{reduce}</span></LineReduce>
        </div>
      } else {
        return null
      }
    }).filter(item => !!item)

    return name.length > 0 && <div className='shop_order_list_detail_pizza_name'>{name}</div>;
  }

  //  item name
  function renderTipsHeaderOfHeader (pizza, isHalfPizza = false) {
    if (pizza?.selectedtoppings) {
      let isExistsSideTopping = false;
      Object.keys(pizza.selectedtoppings).map((a, j) => {
        const selectedtoppingsItem = Object.values(pizza.selectedtoppings[a]);
        if (!isExistsSideTopping && a !== 'All over') {
          selectedtoppingsItem.map((item) => {
            if (!isExistsSideTopping && item?.count > 0) {
              isExistsSideTopping = true;
              return;
            }
          });
        }
      });

      return Object.keys(pizza.selectedtoppings).map((a, j) => {
        const selectedtoppingsItem = Object.values(pizza.selectedtoppings[a]);
        if (selectedtoppingsItem.
          some(item => (a === 'All over' && item.def && item?.count !== 1)
            || (a === 'All over' && !item.def && item?.count > 0)
            || (a !== 'All over' && item?.count > 0)
            || (isHalfPizza && item?.count > 0))) {
          return <div className='tips_container' key={a + j}>
            {isExistsSideTopping && <div className='tips_container_title'>{a}</div>}
            {renderAddToppings(pizza, a, isHalfPizza)}
            {renderDeleteToppings(pizza, a)}
          </div>
        }
      })
    }
  }

  function renderAddToppings (pizza, a, isHalfPizza = false) {
    if (pizza?.selectedtoppings[a]) {
      const addToppings = pizza?.selectedtoppings[a]
        .filter(item => isHalfPizza || (item.def && item?.count > 1)
          || (!item.def && item?.count > 0) || (a !== 'All over' && item?.count > 0))
      if (addToppings && addToppings.length > 0) {
        return <LineAdd wrapper>
          {
            addToppings.map((item, index) => {
              let newItem = { ...item };
              if (newItem.def && !isHalfPizza && a === 'All over') {
                newItem.count = item.count - 1;
              }
              return <ToppingC topping={newItem} key={index + '' + item.id} />
            })
          }
        </LineAdd>
      }
    }
  }

  function renderDeleteToppings (pizza, a) {
    if (pizza?.selectedtoppings[a]) {
      const deleteToppings = pizza?.selectedtoppings[a].filter(item => item.default && !item.count)
      if (deleteToppings && deleteToppings.length > 0) {
        return <LineReduce wrapper>
          {
            deleteToppings.map((item, index) => {
              return <NoToppingC topping={item} key={index + '' + item.id} />
            })
          }
        </LineReduce>
      }
    }
  }


  return dishList?.map((item, index) => {
    const dish = Object.values(item)[0];
    let dishName = dish.name;


    if (dish) {
      return <section className='shop_order_list_detail' key={index + ''} onClick={onDishClick(item, index)}>
        <div className='shop_order_list_detail_header' >
          <div>{dish.count} x {dish.categoryId === 5 && dish?.subPizzaDishView?.sizesname} {dishName}</div>
          <div><CurrencyPrice price={dish.totalPrice} /></div>
          {props.deleteDish && <Icon type='iconicon_del' onClick={deleteDish(index)} />}
        </div>
        {dish.note && <i className='shop_order_list_detail_note'>({dish.note})</i>}

        {renderDishDetail(dish.subDishListView, dish.categoryId,)}
        {renderDishDetail8(dish.subDishListView, dish.categoryId, {
          printSeparate: dish.printSeparate, separateStr: dish.separateStr
        })}
        {renderDishDetailSide(dish.subDishListView, dish.categoryId)}
        {renderPizzaDetail(dish.subPizzaDishView, false, dish.isHalfPizza)}
      </section>
    }
  })
}

export default DishView;