import React, { useEffect, useMemo, useRef, useState } from 'react';
import { AdaptHeader, Icon, DatePicker, ListSelect, message, SubmitModal, Loader } from '../../component';
import { moment, connect, action, getValueFromQuery, h5ToRn, useKeyboardType } from '../../utils';

import './index.less';
import './indexPc.less';
import BookingInput from './bookingInput';

const Booking = (props) => {

  const { showsubmit, shopItem, showDatePicker,
    name, email, mobile, remark, bookingConfig, submitLoading } = props;


  const { bookingNotice, bookingNoticeText, bookingInfos = [], storeTime } = bookingConfig;
  // 1. webConfig.bookingNotice 

  const unionId = getValueFromQuery('unionId', props.location.search)
  const rId = getValueFromQuery('rId', props.location.search)

  const fromPlat = getValueFromQuery('fromPlat', props.location.search);


  useEffect(() => {
    props.dispatch(action('booking/fillUserInfo'))
    props.dispatch(action('booking/getBookingConfig', { from: rId ? 'single' : 'union' }))

    return () => {
      props.dispatch(action('booking/clear'));
    }
  }, [])

  const haveSelectShop = useMemo(() => {
    return !!shopItem?.shopId
  }, [shopItem?.shopId])

  function chooseShop (item) {
    item.bookingDate = moment.format(new Date());
    item.selectBookingDayTime = item.bookingDayTime[new Date().getDay()];
    item.bookingTime = null;
    props.dispatch(action('booking/save', { shopItem: item }))
  }

  function toggleDateShow () {
    if (haveSelectShop) {
      props.dispatch(action('booking/save', { showDatePicker: !showDatePicker }))
    }
  }

  function chooseTime (item) {
    if (haveSelectShop) {
      props.dispatch(action('booking/save', { shopItem: { ...shopItem, bookingTime: item } }))
    }
  }

  function choosePeopleNum (item) {
    if (haveSelectShop) {
      props.dispatch(action('booking/save', { shopItem: { ...shopItem, bookingPeople: item } }))
    }
  }

  function onOk (e) {
    if (!shopItem) {
      message.base('Please select store first.')
      return;
    }

    const index = new Date(e).getDay();


    const selectBookingDayTime = shopItem.bookingDayTime[index === 0 ? 7 : index]

    props.dispatch(action('booking/save', {
      shopItem: {
        ...shopItem,
        bookingDate: moment.format(e),
        selectBookingDayTime: selectBookingDayTime,
        bookingTime: null,
      }
    }))
  }

  function onChangeName (e) {
    const value = e.target.value?.substring(0, 20);
    props.dispatch(action('booking/save', { name: value }))
  }

  function onChangeEmail (e) {
    const value = e.target.value?.substring(0, 80);
    props.dispatch(action('booking/save', { email: value }))
  }

  function onChangeMobile (e) {
    const value = e.target.value?.substring(0, 20)?.replace(/[^0-9^\+]/, '');
    props.dispatch(action('booking/save', { mobile: value }))
  }

  function onChangeRemark (e) {
    const value = e.target.value?.substring(0, 500)
    props.dispatch(action('booking/save', { remark: value }))
  }

  const nameRef = useRef();
  const emailRef = useRef();
  const mobileRef = useRef();

  function onBook () {
    const other = [nameRef.current, emailRef.current, mobileRef.current].every(item => item.checkValue())
    if (!other) {
      return
    }
    props.dispatch(action('booking/onBook'))

  }

  function onClose () {
    props.dispatch(action('booking/save', { showsubmit: false }))
    if (fromPlat === 'RN') {
      h5ToRn({ type: 100 });
    } else {
      props.history.goBack()
    }
  }

  const canSelectTime = useMemo(() => {// 2022-06-09 02:47:14

    const timeArr = shopItem?.selectBookingDayTime?.filter((item) => {
      return new Date(`${shopItem.bookingDate?.replace(/-/g, '/')} ${item}`).getTime() > new Date(storeTime?.replace(/-/g, '/')).getTime();
    })
    if (timeArr?.length > 0) {
      return timeArr
    } else {
      return ['No available booking time for the choosen date.']
    }
  }, [shopItem])

  const inputType = useKeyboardType();


  return (
    <section className='table_page'>
      <AdaptHeader title={'Book Table'} />
      <section className='table_content'>
        {(!!bookingNoticeText && bookingNotice === 1) && <p className='table_notice'>
          {bookingNoticeText}
        </p>}
        <section className='table_content_wrapper'>
          <section className='table_content_wrapper_left'>
            {unionId && <ListSelect
              className={'table_list_select'}
              placeholderText='Store'
              placeholderStyle={{ color: '#999' }}
              leftIcon={'iconicon_location_b'}
              chooseItem={chooseShop}
              showList={bookingInfos}
              showMask={true}
              require={true}
              showName={shopItem?.shopName}
              keyName={'shopId'}
              valueName={'shopName'}
            />}

            <div className={`table_page_picker ${!haveSelectShop ? 'table_list_select_have_select_shop' : undefined}`} onClick={toggleDateShow}>
              <Icon type='icondate_picker' />
              <p style={{ color: shopItem?.bookingDate ? undefined : '#999' }}>{shopItem?.bookingDate || 'Date*'}</p>

            </div>
            {showDatePicker && <DatePicker onOk={onOk} onClose={toggleDateShow} value={shopItem?.bookingDate} />}

            <ListSelect
              noKey={true}
              className={`table_list_select ${!haveSelectShop ? 'table_list_select_have_select_shop' : undefined}`}
              placeholderText='Time'
              placeholderStyle={{ color: '#999' }}
              leftIcon={'iconicon_time'}
              chooseItem={chooseTime}
              beforeShow={() => haveSelectShop}
              showName={shopItem?.bookingTime}
              showList={canSelectTime}
              showMask={true}
              require={true}
            />

            <ListSelect
              noKey={true}
              className={`table_list_select ${!haveSelectShop ? 'table_list_select_have_select_shop' : undefined}`}
              placeholderText='Number of People'
              placeholderStyle={{ color: '#999' }}
              chooseItem={choosePeopleNum}
              beforeShow={() => haveSelectShop}
              showName={shopItem?.bookingPeople}
              showList={shopItem?.bookingPeopleNumber}
              showMask={true}
              require={true}

            />
          </section>
          <section className='table_content_wrapper_right'>
            <BookingInput placeholder='Name*' ref={nameRef} maxLength={20}
              onChange={onChangeName} value={name}
            />
            <BookingInput placeholder='Email Address*' inputType='email' ref={emailRef}
              onChange={onChangeEmail} value={email} maxLength={80}
            />
            <BookingInput placeholder='Mobile Number*' inputType='mobile' ref={mobileRef} type={inputType}
              onChange={onChangeMobile} value={mobile} maxLength={20}
            />
            <BookingInput placeholder='Special Requests (optional)' require={false}
              onChange={onChangeRemark} value={remark} mutiline={4} maxLength={500}
            />
          </section>
        </section>
        <button className='table_book_now' onClick={onBook}>Book Now</button>
      </section>
      {showsubmit && <SubmitModal
        type='icontijiaochenggong'
        title='Thank You'
        msg='A member of the staff will be in touch to confirm the booking'
        onClose={onClose} />}
      {submitLoading && <Loader loading={submitLoading} type='tran' />}
    </section >
  )
}


export default connect(({ booking: { showDatePicker, name, email, mobile, remark, showsubmit, shopItem, bookingConfig, submitLoading } }) => ({
  showDatePicker, name, email, mobile, remark, showsubmit, bookingConfig, shopItem, submitLoading
}))(Booking);