import React, { useMemo, } from 'react';

import './index.less';
import './indexPc.less';

import { DishView, Icon } from '../../../component';
import { connect, hex2Rgba, isMobile } from '../../../utils'

const ReviewPaymentContent = (props) => {

  const { dishList, currentType, } = props;

  //  tab
  // 2 :"Delivery"
  // 3  :"Collection"
  function renderTab () {
    const color = getComputedStyle(document.body).getPropertyValue('--themeColor').trim();
    const themeColor = hex2Rgba(color, .4);
    const background = `linear-gradient(90deg,${themeColor},${themeColor}, rgba(255, 255, 255, .2))`;

    return [{ name: 'Delivery', icon: 'icona-11', type: 2 },
    { name: 'Collection', icon: 'icona-22', type: 3 }].map((item, index) => {
      if (item.type === currentType)
        return <div
          key={index}
          style={{ background: background }}
          className={'order_list_tab_item  order_list_tab_item_active'}>

          <Icon type={item.icon} />
          <h5
            key={index}
          >
            {item.name}
          </h5>
        </div>
    })
  }

  return (
    <section className='review_payment_content_container'>
      <section className='review_payment_content_tabbar' >
        {renderTab()}
      </section>

      <section className='review_payment_content_content'  >
        <DishView dishList={dishList} />
      </section>
    </section>
  )
}

export default connect()(ReviewPaymentContent);


