import React, { useEffect, useMemo, useState } from 'react';
import { withRouter } from 'react-router-dom';

import './index.less';

import ShopCategoryItem from './ShopCategoryItem/index';
import { action, connect, compose, getValueFromQuery, getScroll, inRN, getStatusBarHeight } from '../../../utils';
import ShopEmpty from '../shopEmpty';
import ShopDishDes from '../shopDishDes';

const ShopCategory = (props) => {
  const { dishMenu, selectedMenu, restaurantInfo } = props;

  //  shopFoodsM   menu 
  const typeId = Number(getValueFromQuery('typeId', props.location.search) || selectedMenu?.id);
  const menuId = Number(getValueFromQuery('menuId', props.location.search) || selectedMenu?.menuId);


  const [selectTab, setSelectTab] = useState(null);

  useEffect(() => {
    if (dishMenu && dishMenu.length > 0) {
      if (typeId && menuId) {
        dishMenu.map(a => {
          if (a?.dishTypes) {
            a.dishTypes.map(b => {
              if (b.menuId === menuId && b.id === typeId) {
                setSelectTab(a.name);
              }
            })
          }
        })
      } else {
        setSelectTab(dishMenu.filter(item => item.isDefault)[0]?.name || dishMenu[0].name || '');
      }
    }
  }, [dishMenu])

  function onCategoryItemPress (item) {
    if (restaurantInfo.id) {
      props.dispatch(action('shopDetail/save', { scrollTop: getScroll(window, true) }))
      props.history.push(`/shopFoodsM?rId=${restaurantInfo.id}&typeId=${item.id}&menuId=${item.menuId}`)
    }
  }

  function renderContent () {
    const content = dishMenu.filter(item => item.name === selectTab)[0]
    if (content?.dishTypes?.length > 0) {
      return (
        <ShopCategoryItem
          selectedMenu={selectedMenu}
          content={content}
          onCategoryItemPress={onCategoryItemPress}
        />
      )
    } else {
      return <ShopEmpty text={`Couldn't find any further details of this dish.`} />
    }
  }

  function onSelectTab (item) {
    setSelectTab(item)
  }

  const tabTop = useMemo(() => {
    if (inRN()) {
      return (getStatusBarHeight() / document.documentElement.clientWidth * 7.5 + 1).toFixed(2) + 'rem';

    } else {
      return '1rem';
    }
  }, [])

  function renderTab () {
    if (dishMenu?.length > 1) {

      return <div className='shop_category_tab' style={{ top: tabTop }}>{
        dishMenu.map(item => {
          return <h1 className={`shop_category_tab_item ${item.name === selectTab ? 'shop_category_tab_active' : undefined}`}
            key={item.name} onClick={() => onSelectTab(item.name)}
          >{item.name}</h1>
        })}</div>
    }
  }

  return <section className='shop_category_container_m'>
    {renderTab()}
    <ShopDishDes selectTab={selectTab} />
    <section className='shop_category_content_wrapper' >
      {renderContent()}
    </section>
  </section>

}

export default compose(
  withRouter,
  connect(({ shopDetail: { dishMenu, selectedMenu, restaurantInfo } }) => ({
    dishMenu, selectedMenu, restaurantInfo
  })))(ShopCategory)