import React, { useEffect } from 'react';
import './indexPc.less';
import './index.less';
import { AdaptHeader, AutoTextarea, Icon, LocationIcon, message, Modal, ModalAlert, CloseIcon } from '../../component';
import { connect, action, useMessage, AppConfig } from '../../utils';

const MyAddress = (props) => {

  const { edit, showModel, predictions } = props;

  useEffect(() => {
    props.dispatch(action('myAddress/getAllAddr'));
    props.dispatch(action('user/getCurrentLatLng'))
  }, []);

  //  RN 
  useMessage(onRNMessage)

  function onRNMessage (event) {
    if (event.data === AppConfig.hardwareBackPress) {
      closeModel()
    }
  }

  function onChangeName (e) {
    const value = e.target.value.substring(0, 50);

    props.dispatch(action('myAddress/editAddress', { name: value }))
  }

  function onChangeAddress (e) {
    const value = e.target.value?.substring(0, 255);
    props.dispatch(action('myAddress/editAddress', { address: value }))
  }

  // 
  function editAddress (item) {
    return () => {
      props.dispatch(action('myAddress/toogleModal', { showModel: true, edit: item }));
      if (!item.lat) {
        message.base('');
      }
    }
  }

  function closeModel () {
    props.dispatch(action('myAddress/toogleModal', { showModel: false }))
  }

  function onSaveEdit () {
    props.dispatch(action('myAddress/onSaveEdit'))
  }

  function onDelete (item) {
    return () => {
      ModalAlert({
        msg: 'Are you sure you want to delete this address?',
        onSure: () => {
          props.dispatch(action('myAddress/onDelete', { address: item }))
        },
        onCancel: () => { }
      })
    }

  }

  function addAddress () {
    props.dispatch(action('myAddress/toogleModal', { showModel: true, }))
  }

  function onSelectAddress (item) {
    return () => {
      props.dispatch(action('myAddress/getFullAddress', {
        input: item.description, callBack: (data) => {
          const { geometry: { location: { lat, lng } } } = data[0];
          props.dispatch(action('myAddress/editAddress', {
            address: item.description, lat: lat, lon: lng
          }))
        }
      }))
    }
  }

  //   
  function clearAddress () {
    props.dispatch(action('myAddress/clearAddress'))
  }

  return <section className="my_address_page">
    <AdaptHeader title='My address' right='+ Add' onRightPress={addAddress} />
    <section className='my_address_content'>
      {
        props.allRensentAddress?.map((item, index) => {
          return (
            <div key={item.id} className='resent_my_address_item'>
              <div className='resent_my_address_names'>
                <h5> {item.name}</h5>
                <p> {item.address}</p>
              </div>
              <Icon type='iconicon_edit' onClick={editAddress(item)} />
              <Icon type='iconicon_del' onClick={onDelete(item)} />
            </div>
          )
        })
      }
    </section>
    {showModel && <Modal
      onClose={closeModel}
      title={'Address'}
      paddingTop={false}
      className='my_address_edit_modal' showFooter>
      <section className='my_address_edit_modal_wrapper'>
        <AdaptHeader title='Address' onLeftPress={closeModel} onRightPress={onSaveEdit} right='Save' />
        <div className='my_address_edit_content'>
          {/* <AutoTextarea
            autoFocus={true}
            value={edit?.name}
            placeholder='Name for this address'
            onChange={onChangeName}
            maxLength={50} /> */}

          <div className='my_address_edit_input'>
            <AutoTextarea
              value={edit?.address}
              placeholder='Enter your address or eircode'
              onChange={onChangeAddress}
              maxLength={255} />
            <CloseIcon onClick={clearAddress} text={edit?.address} />
          </div>
          <div className='my_address_predictions_container'>
            {predictions?.map((item, index) =>
              <p key={item.description} onClick={onSelectAddress(item)}>{item.description}</p>
            )}
          </div>
        </div>
        <div className={`my_address_edit_buttons ${edit?.id ? undefined : 'my_address_buttons'}`}>
          {edit?.id && <button onClick={onDelete()}>Delete</button>}
          <button onClick={onSaveEdit}>Save</button>
        </div>
      </section>
    </Modal>}
  </section>
}

export default connect(({ myAddress: { allRensentAddress, edit, showModel, predictions } }) => ({
  allRensentAddress, edit, showModel, predictions
}))(MyAddress);
