import React from 'react';
import './index.less';
import './indexPc.less';

const Switch = (props) => {

  const { checked } = props;

  function onSwitch (e) {
    props.onSwitch && props.onSwitch(e.target.checked)
  }

  return <label className="switch_container">
    <input type="checkbox" name="" onClick={onSwitch} checked={checked} readOnly />
    <div className="checkbox"></div>
  </label>

}

export default Switch;