
//jsformate
export function format (fmt) {
  var o = {
    "M+": new Date().getMonth() + 1, //
    "d+": new Date().getDate(), //
    "h+": new Date().getHours(), //
    "m+": new Date().getMinutes(), //
    "s+": new Date().getSeconds(), //
    "q+": Math.floor((new Date().getMonth() + 3) / 3), //
    S: new Date().getMilliseconds() //
  };
  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (new Date().getFullYear() + "").substr(4 - RegExp.$1.length));
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt))
      fmt = fmt.replace(RegExp.$1, RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length));
  return fmt;
};


/**
 * 
 */
export function initDeviceInfo () {
  let _deviceInfo = ""; //
  if (navigator == null) {
    _deviceInfo = "PC";
  }
  if (navigator.userAgent != null) {
    var su = navigator.userAgent.toLowerCase(),
      mb = ["ipad", "iphone os", "midp", "rv:1.2.3.4", "ucweb", "android", "windows ce", "windows mobile"];

    // 
    for (var i in mb) {
      if (su.indexOf(mb[i]) > 0) {
        _deviceInfo = mb[i];
        break;
      }
    }
  }
  return _deviceInfo
}

/**
 * 
 */
export function getBrowserInfo () {
  var output = "other";
  // Opera 8.0+
  var isOpera = (!!window.opr && !!opr.addons) || !!window.opera || navigator.userAgent.indexOf(" OPR/") >= 0;
  if (isOpera) {
    output = "Opera";
  }
  // Firefox 1.0+
  var isFirefox = navigator.userAgent.indexOf('Firefox') > -1;
  if (isFirefox) {
    output = "Firefox";
  }
  // Safari 3.0+ "[object HTMLElementConstructor]"
  var isSafari =
    /constructor/i.test(window.HTMLElement) ||
    (function (p) {
      return p.toString() === "[object SafariRemoteNotification]";
    })(!window["safari"] || (typeof safari !== "undefined" && safari.pushNotification));
  if (isSafari) {
    output = "Safari";
  }
  // Internet Explorer 6-11
  var isIE = /*@cc_on!@*/ false || !!document.documentMode;
  if (isIE) {
    output = "IE";
  }
  // Edge 20+
  var isEdge = !isIE && !!window.StyleMedia;
  if (isEdge) {
    output = "Edge";
  }
  // Chrome 1 - 79
  var isChrome = !!window.chrome && navigator.userAgent.indexOf("Chrome") !== -1;
  if (isChrome) {
    output = "Chrome";
  }
  // Edge (based on chromium) detection
  var isEdgeChromium = isChrome && navigator.userAgent.indexOf("Edg") !== -1;
  if (isEdgeChromium) {
    output = "EdgeChromium";
  }
  return output;
};

export function detectOS () {
  var userAgent = window.navigator.userAgent,
    platform = window.navigator.platform,
    macosPlatforms = ["Macintosh", "MacIntel", "MacPPC", "Mac68K"],
    windowsPlatforms = ["Win32", "Win64", "Windows", "WinCE"],
    iosPlatforms = ["iPhone", "iPad", "iPod"],
    os = null;

  if (macosPlatforms.indexOf(platform) !== -1) {
    os = "Mac OS";
  } else if (iosPlatforms.indexOf(platform) !== -1) {
    os = "iOS";
  } else if (windowsPlatforms.indexOf(platform) !== -1) {
    os = "Windows";
  } else if (/Android/.test(userAgent)) {
    os = "Android";
  } else if (!os && /Linux/.test(platform)) {
    os = "Linux";
  }

  return os;
};

export function digits () {
  var sUserAgent = navigator.userAgent;
  var is64 = sUserAgent.indexOf("WOW64") > -1;
  if (is64) {
    return "64bit";
  } else {
    return "32bit";
  }
};