import React, { useState, } from 'react';
import Icon from '../icon';

import './index.less'
import './indexPc.less';

const ListSelect = (props) => {

  const { showName, showList, chooseItem, leftIcon, showMask, require, className, keyName = 'id', valueName = 'name', noKey = false,
    placeholderStyle, placeholderText = 'Select Store', beforeShow } = props;

  const [show, setShow] = useState(false);

  function toggleShow () {
    if (!show && beforeShow) {
      const res = beforeShow();
      if (!res) return;
    }
    setShow(!show)
  }

  function chooseshop (item) {
    return () => {
      chooseItem(item)
      setShow(false)
    }
  }

  function renderShopList () {
    if (showList?.length > 0) {
      return showList.map(item => {
        return <div onClick={chooseshop(item)} key={noKey ? item : item[keyName]}>{noKey ? item : item[valueName]}</div>
      })
    }
  }


  return <div className={`list_select_container ${className}`} onClick={toggleShow}>
    {leftIcon && <Icon type={leftIcon} />}
    <p style={(!showName && placeholderStyle) ? placeholderStyle : undefined}>{!!showName ? showName : placeholderText}
      {!showName && require && <span>*</span>}</p>
    <div className='setlect-arrow' />
    {show && <div className={showMask ? 'mask' : 'mask-tran'} />}
    {show && <div className='setlect_content'>
      {renderShopList()}
    </div>}
  </div>

}

export default ListSelect;