import React, { useEffect, useMemo } from 'react';
import { connect, compose, withRouter } from '../../../../utils';
import AboutUsCard from '../../components/aboutUsCard';

import './index.less'
import './indexPc.less';


const AboutUs = (props) => {


  const { landing, id } = props;

  const showAbout = useMemo(() => {
    if (landing?.indexAboutUsContent1 && landing?.indexAboutUsUrl1) {
      return true
    } else {
      return false
    }
  }, [landing?.indexAboutUsContent1, landing?.indexAboutUsUrl1])

  const aboutUsTitle = useMemo(() => {
    if (landing?.webSiteAboutTitle) {
      return landing.webSiteAboutTitle
    } else {
      return 'About Us'
    }

  }, [landing])

  return showAbout ? (<section className="about_us_c" id={id}>
    <div className="about_us_c_wapper">
      {landing?.indexAboutUsContent1 && landing?.indexAboutUsUrl1 &&
        <AboutUsCard data={{ title: aboutUsTitle, img: landing.indexAboutUsUrl1, des: landing.indexAboutUsContent1 }} />}
      {landing?.indexAboutUsContent2 && landing?.indexAboutUsUrl2 &&
        <AboutUsCard data={{  img: landing.indexAboutUsUrl2, des: landing.indexAboutUsContent2 }} reserve/>}
    </div>
  </section>) : null
}

export default compose(
  connect(({ webConfigModal: { landing, } }) => ({
    landing
  })),
  withRouter
)(AboutUs);