import React, { useEffect, useRef, forwardRef } from "react";
import "./indexPc.less";
import "./index.less";
import { getOs } from "../../utils";
import { scrollEle } from "../../utils/scrollEle";


const MyMarquee = forwardRef((props, ref) => {

  const { children, className, speed = 10 } = props;

  const scroll = useRef();
  const innerScroll = useRef();

  function startScroll () {
    const os = getOs();
    if (os === 'other') {
      scroll.current = new scrollEle(innerScroll.current, speed);
      scroll.current.startScroll()
    }
  }

  function stopScroll () {
    scroll.current?.stopScroll();
    scroll.current?.clear();
    scroll.current = null;
  }

  useEffect(() => {
    if (ref) {
      ref.current = innerScroll.current
    }

    return () => {
      stopScroll()
    }
  }, [])


  return (
    <div className={`my_marquee ${className}`} ref={innerScroll} onMouseEnter={startScroll} onMouseLeave={stopScroll}>
      {children}
    </div>
  )
});

export default MyMarquee
