import { action } from '../utils';
import { historyOrderS, shopDetailS } from '../service'

const initState = {
  pendingList: [],
  completedList: [],
  currentIndex: 0,
  orderDetail: {}, // 
  loading: false,
  buttonloading: false,
  errorList: null,
  showErrorList: false,
  shopId: null,
  currency: '',
  restaurantInfo: {},
  checkOrderDetail: {}, //  checkOrder   orderDetail
};

export default {
  namespace: 'historyOrder',
  state: initState,
  reducers: {
    save (state, { payload }) {
      return { ...state, ...payload };
    },
  },
  effects: {
    *init ({ payload }, { call, put, select }) {
      try {
        yield put(action('save', { loading: true }))

        const { landing } = yield select(({ webConfigModal }) => webConfigModal);
        const params0 = { pending: 0 };
        const params1 = { pending: 1 };

        if (landing?.webSiteType === 3) {
          params0.shopId = landing.shopId;
          params1.shopId = landing.shopId;
        }

        const res1 = yield call(historyOrderS.getOrders4Pend, params0);
        yield put(action('save', { pendingList: res1 || [] }))
        const res2 = yield call(historyOrderS.getOrders4Pend, params1);
        yield put(action('save', { completedList: res2 || [] }))
      } catch (e) {
        console.error('init', e)
      } finally {
        yield put(action('save', { loading: false }))
      }
    },

    *getOrderDetail ({ payload: { uuid, withoutLoaidng } }, { call, put, select }) {
      try {
        if (!withoutLoaidng) {
          yield put(action('save', { loading: true }))
        }
        const res = yield call(historyOrderS.getOrderDetail, { uuid: uuid });
        yield put(action('save', { orderDetail: res || {} }))
        if (res && res.shopId)
          yield put(action('getRestaurantInfo', { rId: res.shopId }))
      } catch (e) {
        console.error('historyOrder getOrderDetail', e)
      } finally {
        if (!withoutLoaidng) {
          yield put(action('save', { loading: false }))
        }

      }
    },

    //  notification 
    *getOrderDetailByNotice ({ payload: { m } }, { call, put, select }) {
      try {
        const res = yield call(historyOrderS.getOrderDetailByNotice, { m: m });
        yield put(action('save', { orderDetail: res || {} }))
        if (res && res.shopId)
          yield put(action('getRestaurantInfo', { rId: res.shopId }))
      } catch (e) {
        console.error('historyOrder getOrderDetail', e)
      }
    },

    // 
    *removeCustomer ({ payload: { callBack } }, { call, put, select }) {
      try {
        const { orderDetail } = yield select(({ historyOrder }) => historyOrder);

        const res = yield call(historyOrderS.removeCustomer, {
          "shopId": orderDetail.shopId,
          "orderId": orderDetail.orderId,
          "phoneNumber": orderDetail.userPhone
        });

        console.log('res', res.code)

        if (res.code == 200) {
          callBack && callBack()
        }
      } catch (e) {
        console.error('historyOrder getOrderDetail', e)
      }
    },

    //    
    * getRestaurantInfo ({ payload: { rId } }, { call, put, select }) {
      try {
        const res = yield call(shopDetailS.getRestaurantInfo, { rId: rId });
        yield put(action('save', {
          restaurantInfo: res || {},
        }));
      } catch (e) {
        console.error('getRestaurantInfo', e);
      }
    },

    *checkOrderAgain ({ payload: { uuid, shopId, callBack } }, { call, put, select, take }) {
      try {
        yield put(action('save', { buttonloading: true, loading: true }))
        const res = yield call(historyOrderS.checkOrderAgain, { uuid: uuid });
        if (res && res?.dishList?.length > 0) {
          if (res.dishList.every(item => item.isValid)) {

            yield put(action('save', { checkOrderDetail: res || {} }))
            yield put(action('confirmOrderAgain'));

            callBack && callBack(true, res.dishList); // 
          } else {
            callBack && callBack(false); // 
            yield put(action('save', { errorList: res.dishList, showErrorList: true, shopId: shopId, currency: res.currency }))
          }

        }

      } catch (e) {
        console.error('checkOrderAgain', e)
      } finally {
        yield put(action('save', { buttonloading: false, loading: false }))
      }
    },

    *confirmOrderAgain ({ payload }, { put, take, select }) {
      // 1.
      // 2.
      // 3.
      yield put(action('orderList/clear'));
      yield take('orderList/clear/@@end');
      const { checkOrderDetail } = yield select(({ historyOrder }) => historyOrder);

      yield put(action('myDetail/initStateFromOrder', { order: checkOrderDetail }));
    },

    *closeErrorList ({ payload }, { call, put, select }) {
      try {
        yield put(action('save', { errorList: null, showErrorList: false, shopId: null }))

      } catch (e) {
        console.error('closeErrorList', e)
      }
    },

    *testInfo ({ payload: { callBack } }, { call, put, select }) {
      try {
        const { isLogin } = yield select(({ user }) => user);
        if (!isLogin) { // 1  2   3 
          callBack(1);
          return
        }
        callBack(3);

      } catch (e) {
        console.error('historyOrder/testInfo', e)
      }
    },
  },
};