import React, { useMemo } from 'react';

import './index.less';
import './indexPc.less';
import { connect, action } from '../../../utils';
import { useHistory } from 'react-router';

const ReviewPaymentShopStatus = (props) => {

  const { restaurantInfo, status, currentType, rId } = props;
  const history = useHistory()

  function onCancel () {
    props.dispatch(action('reviewPayment/save', { showShopStatus: false }))
  }

  function changeType (type) {
    return () => {
      props.dispatch(action('reviewPayment/save', { showShopStatus: false }))
      if (document.body.offsetWidth < 1025) { //  pad
        history.push(`/shopOrderListM?rId=${rId}`)
      } else {
        history.push(`/shopDetail?rId=${rId}`)
      }

      props.dispatch(action('orderList/changeType', { currentType: type }))
      props.dispatch(action('orderList/judgeDishBelongCurrentType'))

    }
  }


  const sotreStatus = useMemo(() => {
    if (!status.status) {
      return {
        text: <div className='time_text_contian'><span>The store is offline now. We are unable to process your oder please try again later.</span></div>,
        buttons: <button onClick={onCancel} className='theme_button'>ok</button>
      }
    } else {
      if (!status.openStatus || (!status.collectionStatus && !status.deliveryStatus) || (!status.isCollection && !status.isDelivery)) {
        return {
          text: <div className='time_text_contian'><span>The store is closed now. Please try again later.</span></div>,
          canDelivery: false,
          canCollection: false,
          buttons: <button className='theme_button' onClick={onCancel}>ok</button>,
        }
      } else {
        if (currentType === 3) {
          if ((!status.collectionStatus || !status.isCollection)) {
            if(status.deliveryStatus && status.isDelivery){
              return {
                text: <div className='time_text_contian'>
                  <span>The <span className='theme_text'>collection</span>  service is <span className='theme_text'>closed</span>. But the <span className='green_text'>delivery</span> service is still <span className='green_text'>avaliable</span>.
                    Do you want to switch to Delivery instead?
                  </span>
                </div>,
                canDelivery: true,
                canCollection: false,
                buttons: <>
                  <button className='theme_button' onClick={changeType(2)}>Yes, switch to Delivery</button>
                  <button onClick={onCancel} >No, thanks</button>
                </>
              }
            }else{
              return {
                text: <div className='time_text_contian'>
                  <span>The <span className='theme_text'>collection</span>  service is <span className='theme_text'>closed</span>.
                  </span>
                </div>,
                canDelivery: false,
                canCollection: false,
                buttons: <>
                  <button className='theme_button' onClick={onCancel}>OK</button>
                </>
              }
            }
          }else{
            props.dispatch(action('reviewPayment/save', { showShopStatus: false }))
          }
        } else if (currentType === 2) { // devilery
          if ((!status.deliveryStatus || !status.isDelivery)) {
            if(status.collectionStatus && status.isCollection){
              return {
                text: <div className='time_text_contian'>
                  <span>The <span className='theme_text'>delivery</span>  service is <span className='theme_text'>closed</span>. But the <span className='green_text'>collection</span> service is still <span className='green_text'>avaliable</span>.
                    Do you want to switch to Collection instead?
                  </span>
                </div>,
                canDelivery: false,
                canCollection: true,
                buttons: <>
                  <button className='theme_button' onClick={changeType(3)}>Yes, switch to Collection</button>
                  <button onClick={onCancel} >No, thanks</button>
                </>
              }
            }else{
              return {
                text: <div className='time_text_contian'>
                  <span>The <span className='theme_text'>delivery</span>  service is <span className='theme_text'>closed</span>.
                  </span>
                </div>,
                canDelivery: false,
                canCollection: false,
                buttons: <>
                  <button className='theme_button' onClick={onCancel}>OK</button>
                </>
              }
            }
          }else{
            props.dispatch(action('reviewPayment/save', { showShopStatus: false }))
          }
        }

      }
    }
  }, [status, restaurantInfo])


  const timeItems = useMemo(() => {
    return [
      {
        time: sotreStatus?.canDelivery ? restaurantInfo?.deliverTime?.join('     ') : 'Closed',
        text: 'Delivery Time',
        showClose: sotreStatus?.canDelivery,
      },
      {
        time: sotreStatus?.canCollection ? restaurantInfo?.collectionTime?.join('     ') : 'Closed',
        text: 'Collection Time',
        showClose: sotreStatus?.canCollection,
      },
    ].map((item, index) => {
      return <div className={`time_desc ${index === 0 ? 'time_desc_boder' : undefined}`} key={item.text}>
        <h5>{item.text}</h5>
        <p className='time_text'>{item.showClose && item.time}</p>
        <p className='time_color'>{!item.showClose && 'Closed'}</p>
      </div>
    })
  }, [restaurantInfo])


  return <section className='review_payment_shop_status'>
    <section className='error_page_content'>
      <h5>Something went wrong.</h5>
      <img src={require('../../../assets/no_wlbsc.png')} />
      {
        status.status && <div className='error_page_time_con'>
          {timeItems}
        </div>
      }
      {sotreStatus?.text}
      {sotreStatus?.buttons}
    </section>
  </section>
}

export default connect(({ orderList: { currentType, dishListPrice, dishList, rId }, shopDetail: { restaurantInfo, status },
  user: { isLogin }, reviewPayment: { payType, buttonLoading } }) => ({
    restaurantInfo, status, currentType, isLogin, dishListPrice, dishList, payType, buttonLoading, rId
  }))(ReviewPaymentShopStatus);

