import React, { useEffect, useMemo, useState } from 'react';

import './index.less';
import './indexPc.less';

import { CurrencyPrice, message } from '../../../../../component';

const ShopOrderPrice = (props) => {

  /**
   * finishChoose 
   * totolPrice 
   * fromPrice 
   * addOrder button 
   * sub 
   * from  order 
   */
  const { finishChoose, totolPrice, fromPrice, addOrder, sub, from } = props;

  const status = useMemo(() => {
    if (sub) {
      return {
        disable: false,
        price: totolPrice,
      };
    }
    if (!finishChoose) {
      return {
        disable: true,
        price: totolPrice
      };
    }
    // if (totolPrice - fromPrice < 0) {
    //   return { disable: true, price: 0 };
    // }

    return {
      disable: false,
      price: totolPrice,
    };

  }, [finishChoose, totolPrice, fromPrice])

  function onAddorSavePress () {
    if (status.disable) {
      message.base('Please make your selections.')
    } else {
      addOrder && addOrder()
    }
  }

  return (
    <div className={`shop_order_price_container ${status.disable ? 'shop_order_price_container_disable' : undefined}`}>
      <div className='shop_order_price_btn' onClick={onAddorSavePress}>
        <span>{(sub || from === 'order') ? 'Save' : 'Add to order'}</span>
        <span><CurrencyPrice price={status.price} hiddenZero={false} /></span>
      </div>
    </div>
  )
}

export default ShopOrderPrice;