import { action, delay } from '../utils';
import { shopS } from '../service'

const initState = {
  shops: [],
  loading: true,
  showAddress: false,
  keyword: '', //   

  showClear: false,// 
  isFocus: false, // mobile 
  showFixedHeader: false,
};

export default {
  namespace: 'shopList',
  state: initState,

  reducers: {
    save (state, { payload }) {
      return { ...state, ...payload };
    },
  },

  effects: {
    //  shoplist
    * getStoreListByGeo ({ payload: { geo } }, { call, put, select }) {
      try {
        yield put(action('save', {
          loading: true,
          shops: []
        }));
        const resData = yield call(shopS.getStoreListByGeo, geo);
        yield put(action('save', {
          shops: resData
        }));
      } catch (e) {
        console.error('getStoreListByGeo', e);
      } finally {
        yield put(action('save', {
          loading: false
        }));
      }
    },

    *testInfo ({ payload: { callBack } }, { call, put, select }) {
      try {
        const { selectedAddress } = yield select(({ user }) => user);
        if (!selectedAddress?.lat || !selectedAddress?.lng) { //
          callBack(1);
          return
        }
        callBack(2);

      } catch (e) {
        console.error('shopList/testInfo', e)
      }
    },

    //  keyword  shoplist
    queryByKeyword: [
      function* ({ payload: { input, lat, lon } }, { call, put, select }) {
        yield put(action('save', {
          loading: true
        }));
        yield call(delay, 300);
        try {
          const resData = yield call(shopS.queryByKeyword, { keyword: input, lat: lat, lon: lon });
          yield put(action('save', {
            shops: resData
          }));
        } catch (e) {
          console.error('queryByKeyword', e);
        } finally {
          yield put(action('save', {
            loading: false
          }));
        }
      },
      { type: 'takeLatest' }
    ],
  },
};