import React, { useEffect, useMemo } from "react";

import './index.less';
import './indexPc.less';
import { Icon, Loader, TelCon } from '../../component'
import { action, connect, formatDate, getValueFromQuery } from "../../utils";

const TableResult = props => {
  // url   /bookingResult?id=6268a3854f34cb5167d8ed42&from=shop&fromStatus=reject
  // url   /bookingResult?id=6268a3854f34cb5167d8ed42&from=customer  //  fromStatus
  // from  'shop' 'customer'
  // fromStatus  'reject' 'confirm'

  const id = getValueFromQuery('id', props.location.search)
  const from = getValueFromQuery('from', props.location.search)
  const fromStatus = getValueFromQuery('fromStatus', props.location.search)

  const { detail, rejectReason, detailLoading } = props;


  useEffect(() => {
    props.dispatch(action('booking/getBookingDetails', { id: id }))
  }, [])

  function onChange (e) {
    const value = e.target.value;
    props.dispatch(action('booking/save', { rejectReason: value }));
  }

  function rejectBooking () {
    props.dispatch(action('booking/rejectBooking', { id: id }))
  }


  const topComponent = useMemo(() => {
    // bookingStatus 0 default ,1 accept 2 reject
    if (from === 'customer') {
      switch (detail.bookingStatus) {
        case 0:
          return {
            com: <div className="table_top_com table_top_com_blue">
              <div className="table_top_com_title table_top_com_title_1">Please wait for confirmation from the store.</div>
              <img src={require('./img/book_wait.png')} className="table_top_com_icon_1" />
              <div className="table_top_com_sub">Thank You for Booking</div>

            </div>,
            color: '#5DB8ED'
          }
        case 1:
          return {
            com: <div className="table_top_com table_top_com_green">
              <img src={require('./img/book_success.png')} className="table_top_com_icon_1" />
              <div className="table_top_com_title">Your Booking is Confirmed</div>
            </div>,
            color: '#40C1A4'
          }
        case 2:
          return {
            com: <div className="table_top_com table_top_com_red">
              <img src={require('./img/book_fail.png')} className="table_top_com_icon_1" />
              <div className="table_top_com_title">Your  Booking is Rejected</div>
            </div>,
            color: '#F37B7B'
          }
        default:
          return {}
      }
    } else if (from === 'shop') {
      if (detail?.bookingStatus === 0) {
        if (fromStatus === 'reject') {
          return {
            com: <div className="table_top_com table_top_com_red">
              <div className="table_top_com_title table_top_com_title_1">Reject Booking</div>
              <textarea
                maxLength={200}
                onChange={onChange}
                value={rejectReason}
                className="table_top_com_input"
                placeholder="Please enter the reason, for example: Restaurant too busy..." />
              <div className="table_top_com_button table_top_com_button_1" style={{ color: '#F37B7B' }}
                onClick={rejectBooking}
              >Confirm</div>
            </div>,
            color: '#F37B7B'
          }
        } else if (fromStatus === 'confirm') {
          props.dispatch(action('booking/acceptBooking', { id: id }))
          return {
            com: <div className="table_top_com_wrong ">
              Loading...
            </div>,
          }
        }
      } else if (detail?.bookingStatus === 1) {
        return {
          com: <div className="table_top_com table_top_com_green">
            <img src={require('./img/book_success.png')} className="table_top_com_icon_1" />
            <div className="table_top_com_title">Table Booking Confirmed</div>
            <div className="table_top_com_sub">The customer will be notified booking confirmation.</div>
          </div>,
          color: '#40C1A4'
        }
      } else if (detail?.bookingStatus === 2) {
        return {
          com: <div className="table_top_com table_top_com_red">
            <img src={require('./img/book_fail.png')} className="table_top_com_icon_1" />
            <div className="table_top_com_title">Table Booking is Rejected</div>
            <div className="table_top_com_sub">The customer will be notified booking rejection.</div>
          </div>,
          color: '#F37B7B'
        }
      }
      // return {
      //   com: <div className="table_top_com table_top_com_blue">
      //     <div className="table_top_com_title table_top_com_title_1">Customer is Waiting for Confirmation!</div>
      //     <div className="table_top_com_buttons_group">
      //       <div className="table_top_com_button table_top_com_button_2 " style={{ color: '#fff' }}>Reject</div>
      //       <div className="table_top_com_button table_top_com_button_3 " style={{ color: '#5DB8ED' }}>Confirm</div>
      //     </div>

      //     <div className="table_top_com_sub">Need to modify booking? You can reply to the customer directly </div>
      //   </div>,
      //   color: '#5DB8ED'
      // }
    } else {
      return {
        com: <div className="table_top_com_wrong ">
          Something wrong with your url
        </div>,
      }
    }
    return {
      com: <div className="table_top_com_wrong ">
      </div>,
    }
  }, [detail?.bookingStatus, rejectReason])





  return <section className="table_result_page">
    {topComponent.com}
    <section className="table_content">
      {detail?.rejectReason && <div className="table_card">
        <div className="table_card_text_1" style={{ color: topComponent.color }}>Reject Reason</div>
        <div className="table_card_text_2">{detail?.rejectReason}</div>
      </div>}

      <div className="table_card">
        <div className="table_card_text_1" style={{ color: topComponent.color }}>Store Info</div>
        <div className="table_card_text_2">{detail?.shopName}</div>
        <TelCon className="table_card_text_3" tel={detail?.shopPhone}>{detail?.shopPhone}</TelCon>
        <div className="table_card_text_2">{detail?.shopAddress}</div>
      </div>

      <div className="table_card">
        <div className="table_card_text_1" style={{ color: topComponent.color }}>Booking Info</div>
        <div>
          <img src={require('./img/book_date.png')} />
          <div className="table_card_time_c">
            <div className="table_card_text_4">Date</div>
            <div className="table_card_text_2">{formatDate(detail?.bookingDate?.substring(0, 10))}</div>
          </div>
        </div>
        <div>
          <img src={require('./img/book_time.png')} />
          <div className="table_card_time_c">
            <div className="table_card_text_4">Time</div>
            <div className="table_card_text_2">{detail?.bookingTime}</div>
          </div>
        </div>
        <div className="table_card_text_4">Number of People</div>
        <div className="table_card_text_2">{detail?.personNum} people</div>
        <div className="table_card_text_4">Special Requests</div>
        <div className="table_card_text_2">{detail?.bookingRequest}</div>
      </div>

      <div className="table_card">
        <div className="table_card_text_1" style={{ color: topComponent.color }}>Customer Info</div>
        <div className="table_card_text_4">Name</div>
        <div className="table_card_text_2">{detail?.bookingName}</div>
        <div className="table_card_text_4">Mobile Number</div>
        <TelCon className="table_card_text_2" tel={detail?.bookingPhone} >{detail?.bookingPhone}</TelCon>
        <div className="table_card_text_4">Email Address</div>
        <TelCon className="table_card_text_2" tel={detail?.bookingEmail} callType='mail' >{detail?.bookingEmail}</TelCon>
      </div>
    </section>
    {detailLoading && <Loader type='tran' loading={detailLoading} />}
  </section>
}

export default connect(({ booking: { detail, rejectReason, detailLoading } }) => ({
  detail, rejectReason, detailLoading
}))(TableResult);

