
import { http } from '../utils';
import { API } from '../config';


// ?restaurantId=1339&dishId=10&typeId=98&categoryId=1&menuId=5
const getDishDetail = (query) => {
  return http.get(API.getDishDetail, query)
};

const getPizzaBriefListBySizeId = (query) => {  // 
  return http.get(API.getPizzaBriefListBySizeId, query)
}

//?restaurantId=1340&menuId=9&typeId=111
const getPizzaBriefListWithTopping = (query) => {
  return http.get(API.getPizzaBriefListWithTopping, query)
};

export default {
  getDishDetail, getPizzaBriefListBySizeId, getPizzaBriefListWithTopping
}
