import React, { useMemo, useRef } from 'react';
import { withRouter } from 'react-router-dom';

import PropTypes from 'prop-types';
import Icon from '../icon'
import './index.less';
import { isMobile, getValueFromQuery, h5ToRn } from '../../utils';
import './indexPc.less';

const AdaptHeader = (props) => {

  const { size, title, right, onLeftPress, theme, onTitleClick, onRightPress, left } = props;

  const style = useRef({});

  const fromPlat = getValueFromQuery('fromPlat', props.location.search);

  useMemo(() => {
    const platform = getValueFromQuery('platform', props.location.search) || window.Platform;


    switch (platform) {
      case 'iphoneX':
        style.current = {
          paddingTop: '.88rem'
        };
        break;
      case 'ios':
        style.current = {
          paddingTop: '.5rem'
        };
        break;
      case 'android':
        style.current = {
          paddingTop: '.4rem'
        };
        break;
      case 'pad':
        style.current = {
          paddingTop: '.88rem'
        };
        break;
      default:
        style.current = {
          paddingTop: '0rem'
        };
        break;
    }
  }, [])

  function onLeftClick (e) {
    if (onLeftPress) {
      onLeftPress()
    } else {
      if (fromPlat === 'RN') {
        h5ToRn({ type: 100 });
      } else {
        props.history.goBack()
      }
    }
  }

  function onRightClick () {
    onRightPress && onRightPress();
  }

  if ((size === 'large' || size === 'middle') && isMobile()) {
    return (
      <section className={'adapt_header_large'} style={style.current}>
        <Icon
          className="adapt_header_icon_large"
          type={'iconicon_more_A1'}
          onClick={onLeftClick} />
        <section className={'adapt_header_container_large'}>
          <span
            className="adapt_header_title"
            // style={{ fontSize: size === 'middle' && '.36rem' }}
            onClick={() => onTitleClick && onTitleClick()}
          >{title}</span>
          {right && <span
            className="adapt_header_right"
            onClick={onRightClick}>{right}</span>}
        </section>
      </section>
    )
  } else {
    return (
      <section className={'adapt_header'} style={{ background: theme === 'color' ? '#fff' : 'unset', ...style.current }}>
        <section className={'adapt_header_container'}>
          <Icon
            className="adapt_header_icon"
            type={'iconicon_more_A1'}
            style={{ color: theme === 'color' ? 'var(--themeColor)' : '#fff' }}
            onClick={() => onLeftClick()} />
          {left && <span className="adapt_header_left" onClick={() => onLeftClick()}>{left}</span>}

          <div
            className="adapt_header_title"
            style={{ color: theme === 'color' ? '#222' : '#fff' }}
            onClick={() => onTitleClick && onTitleClick()}
          >{title}</div>
          <span
            className="adapt_header_right"
            onClick={() => onRightClick()}>{right}</span>
        </section>
      </section>
    )
  }
}



AdaptHeader.propTypes = {
  title: PropTypes.any,
  right: PropTypes.any,
  onLeftPress: PropTypes.func,
  onRightPress: PropTypes.func,
  onTitleClick: PropTypes.func,
  theme: PropTypes.oneOf(['white', 'color']),
  size: PropTypes.oneOf(['nomrol', 'middle', 'large']),
};

AdaptHeader.defaultProps = {
  theme: 'color',
  size: 'nomrol',
  title: '',
  left: '',
};


