function ddBotNotify (text, desp) {

  let ddd = {
    "msgtype": "markdown",
    "markdown": {
      "title": ` `,
      "text": `${text}\n\n${desp}\n\nV${AppConfig.APP_VERSION}`
    }
  };

  axios({
    method: 'post',
    url: `/robot/send?access_token=${DD_BOT_TOKEN}`,
    data: ddd
  })
}


