import React from 'react';
import { AdaptHeader } from '../../component';

import './index.less';
import './indexPc.less';

const Unsubscribe = (props) => {

  function onLeftPress () {
    props.history.push('/');
  }

  return (
    <section className='unsubscribe_page'>
      <AdaptHeader title={'Unsubscribe'} onLeftPress={onLeftPress} />

      <section className='unsubscribe_content'>
        You have been successfully removed from this subscriber list and won't receive any further emails from us.
      </section>
    </section>
  )
}

export default Unsubscribe;