import React, { useEffect, useState, } from 'react';

import './index.less';
import './indexPc.less';

import { connect } from '../../../utils';
import { CurrencyPrice } from '../../../component';


const ReviewPaymentDetailed = (props) => {

  const { orderDetail, } = props;

  return <section className='review_payment_detailed'>
    <div>
      <h5>Subtotal</h5>
      <h5 ><CurrencyPrice price={orderDetail?.price} /></h5>
    </div>
    {(!!orderDetail?.deliveryFee || orderDetail?.type === 2) && <div>
      <p>Delivery Charge</p>
      <p> <CurrencyPrice price={orderDetail?.deliveryFee} /></p>
    </div>}
    {!!orderDetail?.serviceFee && <div>
      <p>Online Service Fee</p>
      <p>  <CurrencyPrice price={orderDetail?.serviceFee} /></p>
    </div>}
    {!!orderDetail?.amountDiscount && <div>
      <p>Coupon Discount</p>
      <p>- <CurrencyPrice price={orderDetail?.amountDiscount} /></p>
    </div>}
    <div>
      <h5 className='review_payment_detailed_total'>Total</h5>
      <h5 className='review_payment_detailed_price'>
        <CurrencyPrice price={orderDetail?.due} /></h5>
    </div>
  </section>

}

export default connect(({ reviewPayment: { orderDetail },  }) => ({
  orderDetail, 
}))(ReviewPaymentDetailed)

