import React, { useState, useEffect } from "react";
import "./indexPc.less";
import "./index.less"
import { Icon } from "../../../../../../component";


export const CardCoupon = ({ couponList }) => {

  if(!couponList){
    return null;
  }

  return (
    <div className="card_header_des">
      <Icon type={'iconicon_discount_o'}/>
      <div>
        {
          couponList?.map((n, key) => {
            return <div key={key}>
              <span className="des_text">{n}</span>
            </div>
            }
          )
        }
      </div>
    </div>
  )
};
