import React, { useEffect, useMemo, useState } from 'react';
import { AdaptHeader, Loader } from '../../component';
import { action, connect, getValueFromQuery, toJSON } from '../../utils';
import OrderTypeItem from './orderTypeItem';

import './index.less';
import './indexPc.less';
import { changeHtmlHeaderTitle, changeHtmlHeader } from '../../utils/loadJS';
import { useParams } from 'react-router-dom';


const SelectOrderType = (props) => {

  const { restaurantInfo, restaurantStatus, loading, landing } = props;


  const [rId, setRId] = useState(getValueFromQuery('rId', props.location.search))


  let { menuUrl } = useParams();

  const pathname = useMemo(() => {
    if (menuUrl) {
      return menuUrl?.replace('/', '').replace('.html', '').replace('.htm', '');
    }
  }, [])

  useEffect(() => {
    if (rId) {
      props.dispatch(action('selectOrderType/getRestaurantInfo', { rId: rId }));
      props.dispatch(action('selectOrderType/restaurantStatusCheck', { rId: rId }));
    } else {
      props.dispatch(action('webConfigModal/getShopIdByName', {
        name: pathname, callBack: (sId, urlType) => {
          setRId(sId)
          props.dispatch(action('selectOrderType/getRestaurantInfo', { rId: sId }));
          props.dispatch(action('selectOrderType/restaurantStatusCheck', { rId: sId }));
        }
      }));
    }

    return () => props.dispatch(action('selectOrderType/clear', { rId: rId }))
  }, [])

  useEffect(() => {
    if (landing.webSiteSeo) {
      const json = toJSON(landing.webSiteSeo);
      if (landing.webSiteType === 3 && !json.title && restaurantInfo?.name) {
        const headerTitle = restaurantInfo?.name + (restaurantInfo?.couponList?.length > 0 ? ` | ${restaurantInfo?.couponList.join(',')}` : '')
        changeHtmlHeaderTitle(headerTitle);
      } else if (landing.webSiteType === 1 || landing.webSiteType === 2) {
        changeHtmlHeader(json, landing.logoUrl);
      } else if (landing.webSiteType === 3 && json.title) {
        changeHtmlHeader(json, landing.logoUrl);
      }
    }
    if (restaurantInfo.name) {
      const shopKeywordsTitle = restaurantInfo.name + ' - ' + document.title;
      const shopKeywords = restaurantInfo.cuisines + 'takeaway,easydish,online,delivery,food4u,easy dish,just-eat';
      changeHtmlHeaderTitle(shopKeywordsTitle, shopKeywords);
    }
  }, [restaurantInfo, landing.webSiteSeo]);

  //1 :"Takeaway"
  // 2 :"Delivery"
  // 3  :"Collection"
  // 4  :"Quick food"
  // 5 :"Eat in"
  function changeType (changeType) {
    return () => {
      if (changeType !== -1) {
        if (!loading) {
          props.dispatch(action('orderList/changeType', { currentType: changeType }));
          if (pathname) {
            props.history.push(`/shopDetail/${pathname}`);
          } else {
            props.history.push(`/shopDetail?rId=${rId}`);
          }
        }
      } else {
        props.history.push(`/booking?rId=${rId}`);
      }
    }
  }

  const timeAndOpenStatus = useMemo(() => {
    if (loading) {
      return {
        deliveryTime: ['loading...'],
        deliveryOpen: 'loading...',
        collectionTime: ['loading...'],
        collectionOpen: 'loading...',
      }
    }


    return {
      deliveryTime: restaurantStatus.isDelivery ? restaurantInfo?.deliverTime : ['Delivery service is not available'],
      deliveryOpen: restaurantStatus.isDelivery && restaurantStatus.deliveryStatus && restaurantStatus.status,
      collectionTime: restaurantStatus.isCollection ? restaurantInfo?.collectionTime : ['Collection service is not available'],
      collectionOpen: restaurantStatus.isCollection && restaurantStatus.collectionStatus && restaurantStatus.status
    }

  }, [restaurantStatus, restaurantInfo, loading])

  return (
    <section className='select_order_type_page'>
      <AdaptHeader title='How would you like to order' />
      <section className='select_order_type_content_wrapper'>
        <h5>How would you like to order</h5>
        <section className='select_order_type_content'>
          {restaurantStatus.isDelivery && <OrderTypeItem
            onClick={changeType(2)}
            time={timeAndOpenStatus.deliveryTime}
            open={timeAndOpenStatus.deliveryOpen} />}
          {restaurantStatus.isCollection && <OrderTypeItem type='Collection'
            onClick={changeType(3)}
            time={timeAndOpenStatus.collectionTime}
            open={timeAndOpenStatus.collectionOpen} />}
          {/* <OrderTypeItem type='Book Table' onClick={changeType(-1)} /> */}

        </section>
      </section>
    </section>
  )
}

export default connect(({ selectOrderType: { restaurantInfo, restaurantStatus, loading }, webConfigModal: { landing } }) => ({
  restaurantInfo, restaurantStatus, loading, landing
}))(SelectOrderType);