export function toFloat (mnumber = 0) {
  return Number(mnumber).toFixed(2);
}

export function keepInt (value) {
  if (value) {
    const pointIndex = value.toFixed(2).toString().indexOf('.');
    return value.toFixed(2).toString().slice(0, pointIndex);
  } else {
    return 0;
  }
}
