import React, { useMemo } from 'react';
import './index.less';
import './indexPc.less';

const OrderTypeItem = (props) => {

  const { type, time, open, onClick } = props;

  function getData () {
    switch (type) {
      case 'Delivery':
        return {
          bg: require('./img/btn1.png'),
          icon: require('./img/11.png'),
          color: '#F55E1D'

        }
      case 'Collection':
        return {
          bg: require('./img/btn2.png'),
          icon: require('./img/22.png'),
          color: '#F51D1D'

        }
      case 'Book Table':
        return {
          bg: require('./img/btn3.png'),
          icon: require('./img/33.png'),

        }
      default:
        break;
    }
  }

  const data = getData();

  const closeSoon = useMemo(() => {
    if (!open) {
      return false
    }
    return time.every((value, key) => {
      // return ['12:00-16:33', '18:00-20:33'].every((value, key) => {

      const current = new Date();
      const currentTime = current.getTime();

      const start = new Date(`${new Date(`${current.getFullYear()}/${current.getMonth() + 1}/${current.getDate()} ${value?.split('-')[0]}`)}`).getTime()
      const end = new Date(`${new Date(`${current.getFullYear()}/${current.getMonth() + 1}/${current.getDate()} ${value?.split('-')[1]}`)}`).getTime()

      const endtTime = new Date(`${new Date(`${current.getFullYear()}/${current.getMonth() + 1}/${start > end ? current.getDate() + 1 : current.getDate()} ${value?.split('-')[1]}`)}`).getTime()
      // const endtTime = new Date(`${new Date(`${current.getFullYear()}/${current.getMonth() + 1}/${current.getDate()} 10:20`)}`).getTime()

      const closeSoonTime = 20 * 60 * 1000;

      const res = (endtTime - currentTime) < closeSoonTime

      return res
    })
  }, [time, open])


  return (
    <div className='order_type_item' onClick={onClick}>
      <img className='bg_img' src={data.bg} />
      <div className='order_type_item_content'>
        <div className="content_top">
          <img className="content_top_icon" src={data.icon} />
          <p className='type' style={{ color: data.color }}>{type}</p>
        </div>
        <p className='time'>
          {
            time.map((value, key) => {
              return <span key={key}>{value}</span>
            })
          }
        </p>
      </div>
      {!open && <div className='order_type_close'>Closed</div>}
      {closeSoon && <div className='order_type_close'>closing soon</div>}
    </div>
  )
}

export default OrderTypeItem;

OrderTypeItem.defaultProps = {
  type: 'Delivery',
  time: [''],
  open: true,
}