import React from 'react';
import NavBlock from '../navBlock';
import { compose, withRouter, getReturnPath, connect } from '../../utils';

const LoginIcon = (props) => {
  const isLog = props.location.pathname === '/login'

  function goLogin () {
    props.history.push(`/login?returnUrl=${getReturnPath(props.location)}`)
  }

  return !props.isLogin && !isLog && <NavBlock
    type="iconAccount"
    onClick={goLogin}
  />

}


export default compose(
  withRouter,
  connect(({ user: { isLogin } }) => ({
    isLogin
  })))(LoginIcon)