import { AppConfig, action, delay } from '../utils';
import { historyOrderS, reviewPaymentS, shopDetailS } from '../service'
import { message, ModalAlert } from '../component';
import { reportPriceBug } from '../bugreport';

const initState = {

  orderDetail: {}, // 
  payType: '', // cash ,card
  showOops: false,
  couponList: [],
  selectCoupon: null,
  searchCoupon: null,
  showEnterCode: false,
  showCard: false,
  searchCode: '',
  terms: '',// 
  buttonLoading: false,
  showPayChoose: '', // 
  showShopStatus: '', //   
  deliveryFee: null,

  cardPayLoading: false,
  errCount: 0,

  revolutPayStatus: true,
};


//  
function dealPizza (frontPizza, backendPizza, type = 5) {
  if (frontPizza.subPizzaDishView) {
    const toppings = frontPizza.subPizzaDishView.selectedtoppings
    const selectedtoppings = Object.keys(toppings)
      .filter((b, k) => {
        return Object.values(toppings[b]).some((item, index) => {
          return item?.count > 0
        })
      })
      .map((a, j) => {
        return `${a} : ${Object.values(toppings[a]).filter(b => b.count > 0).map((item, index) => {
          return item.name + 'x' + item.count
        })}`
      })

    const other = {};
    ['base', 'cheese', 'sauce'].map(item => {
      other[item] = {
        id: frontPizza.subPizzaDishView['selected' + item + 'id'],
        name: frontPizza.subPizzaDishView['selected' + item + 'name'],
        price: frontPizza.subPizzaDishView['selected' + item + 'vprice'],
      }
    })

    return {
      name: frontPizza.name,
      ...other,
      selectedtoppings: selectedtoppings,
      frontPrice: frontPizza.totalPrice,
      backendPrice: backendPizza.price,
    }
  } else {
    const rr = {
      name: frontPizza.name,
      frontPizza: frontPizza,
    }
    if (type === 5) {
      rr.error = ' subPizzaDishView '
    } else {
      return rr
    }
  }
}

export default {
  namespace: 'reviewPayment',
  state: initState,
  reducers: {
    save (state, { payload }) {
      return { ...state, ...payload };
    },
  },
  effects: {
    // 
    *getOrderDetail ({ payload }, { call, put, select }) {
      try {
        yield put(action('save', { buttonLoading: true }))
        const { uuid } = yield select(({ orderList }) => orderList);

        const res = yield call(historyOrderS.getOrderDetail, { uuid: uuid });
        yield put(action('save', { orderDetail: res || {} }));
        yield put(action('checkOrderPrice', { orderDetail: res }))
      } catch (e) {
        console.error('reviewPayment,getOrderDetail', e)
      } finally {
        yield put(action('save', { buttonLoading: false }))
      }
    },

    *testPayStatus ({ payload: { uuid, callBack } }, { call, put, select }) {
      try {

        const res = yield call(historyOrderS.getOrderDetail, { uuid: uuid });
        const { errCount } = yield select(({ reviewPayment }) => reviewPayment);

        yield put(action('save', { errCount: errCount + 1 }));
        if (errCount > 10) {
          callBack && callBack({ ...res, error: true })
        } else {
          callBack && callBack(res)
        }


      } catch (e) {
        console.error('reviewPayment,testPayStatus', e)
      }
    },

    // dishList
    *checkOrderPrice ({ payload: { orderDetail } }, { call, put, select }) {
      try {
        const { dishList, dishListPrice } = yield select(({ orderList }) => orderList);
        const backEndOrder = orderDetail?.dishList;

        if (dishListPrice !== orderDetail?.price && backEndOrder) {
          const frontEndOrder = dishList.map(item => Object.values(item)[0]);
          const diff = backEndOrder.map((item, index) => {
            const frontEndItem = frontEndOrder[index];
            if (frontEndItem && item.totalPrice !== frontEndItem.totalPrice) {
              switch (item.categoryId) {
                case 5:
                  return dealPizza(frontEndItem, item)
                case 6:

                  const other = { subDishes: {} } //  partId  key

                  frontEndItem.subDishListView.map((a, i) => {
                    //   pizza 
                    item.subDishes.map((b, j) => {
                      if (a.partId === b.partId) {
                        const frontSubDish = a.subDishes.filter(s => s.choose)[0];
                        // if (frontSubDish.price !== b.price) { // 
                        other.subDishes[a.partId] = {
                          categoryId: frontSubDish.categoryId,
                          name: frontSubDish.name,
                          price: frontSubDish.price,
                        }
                        // }
                      }
                    })
                    //   pizza 
                    item.subPizzaDishes.map((b, j) => {
                      if (a.partId === b.partId) {
                        const frontSubDish = a.subDishes.filter(s => s.choose)[0];
                        // if (frontSubDish.price !== b.price) {  // 
                        other.subDishes[a.partId] = dealPizza(frontSubDish, b, item.categoryId)
                        // }
                      }
                    })
                  })
                  return {
                    name: frontEndItem.name,
                    ...other,
                    price: frontEndItem.totalPrice,
                  }

                default:
                  return {
                    name: frontEndItem.name,
                    price: frontEndItem.totalPrice,
                  }
              }
            } else {
              if (!frontEndItem) {
                reportPriceBug('', '')
              }
              return null
            }

          }).filter(i => !!i);
          const res = {
            uuid: orderDetail.uuid,
            diff: diff,
          }
          reportPriceBug('', '' + dishListPrice, '' + orderDetail.price, res)
        }

      } catch (e) {
        console.error('checkOrderPrice', e)
      }
    },

    *getRestaurantInfoAndStatus ({ payload }, { call, put, select }) {
      try {
        const { rId } = yield select(({ orderList }) => orderList);

        yield put(action('shopDetail/getRestaurantInfo', { rId: rId }))
        yield put(action('shopDetail/restaurantStatusCheck', { rId: rId }))
      } catch (e) {
        console.error('getRestaurantInfoAndStatus', e)
      }
    },

    // m  
    *getRestaurantInfo ({ payload }, { call, put, select }) {
      try {
        const { rId } = yield select(({ orderList }) => orderList);
        yield put(action('shopDetail/getRestaurantInfo', { rId: rId }))
      } catch (e) {
        console.error('getRestaurantInfoAndStatus', e)
      }
    },

    *checkRestaurantStatus ({ payload }, { call, put, select }) {
      try {
        yield put(action('save', { buttonLoading: true }))
        const { rId, currentType } = yield select(({ orderList }) => orderList);

        yield put(action('shopDetail/getRestaurantInfo', { rId: rId }))

        const status = yield call(shopDetailS.restaurantStatusCheck, { rId: rId })
        if (status) {
          yield put(action('shopDetail/save', { status: status }))
          if (!status.status || !status.openStatus
            || (currentType === 3 && (!status.collectionStatus || !status.isCollection))
            || (currentType === 2 && (!status.deliveryStatus || !status.isDelivery))) {
            yield put(action('save', { showShopStatus: true }))
          } else {
            yield put(action('save', { showPayChoose: true }))
          }
        }
      } catch (e) {
        console.error('checkRestaurantStatus', e)
      } finally {
        yield put(action('save', { buttonLoading: false }))
      }
    },

    *getCouponListByUser ({ payload }, { call, put, select }) {
      try {
        const { rId } = yield select(({ orderList }) => orderList);
        const res = yield call(reviewPaymentS.getCouponListByUser, { shopId: rId, });

        if (res && res.length > 0) {
          yield put(action('save', { couponList: res }))
        }
      } catch (e) {
        console.error('getCouponListByUser', e)
      }
    },

    *checkOrderCoupon ({ payload }, { call, put, select }) {
      try {
        yield put(action('save', { buttonLoading: true }))
        let isLoadOrder = false;
        const { rId, uuid } = yield select(({ orderList }) => orderList);
        const { selectCoupon } = yield select(({ reviewPayment }) => reviewPayment);

        if (!selectCoupon) {
          const res = yield call(reviewPaymentS.getCouponListByUser, { shopId: rId, });

          if (res && res.length > 0) {
            yield put(action('save', { selectCoupon: null, couponList: res }));
            for (let i = 0; i < res.length; i++) {
              if (res[i].valid) {
                const checkRes = yield call(reviewPaymentS.checkCouponByOrderUUID, { uuid: uuid, entityId: res[i].entityId });
                if (checkRes && checkRes.valid) {
                  yield put(action('save', { selectCoupon: res[i] }));
                  isLoadOrder = true;
                  yield put(action('getOrderDetail'));
                  break;
                }
              }
            }
          } else {
            isLoadOrder = true;
            yield put(action('getOrderDetail'));
            yield put(action('save', { couponList: null }))
          }
        } else {
          isLoadOrder = true;
          yield put(action('checkCouponByOrderUUID'));
        }
        if (!isLoadOrder) {
          yield put(action('getOrderDetail'));
        }
      } catch (e) {
        console.error('checkOrderCoupon', e)
      }
    },

    // 
    *checkCouponByOrderUUID ({ payload }, { call, put, select }) {
      try {
        const { uuid } = yield select(({ orderList }) => orderList);
        const { selectCoupon } = yield select(({ reviewPayment }) => reviewPayment);
        let isLoadOrder = false;

        const res = yield call(reviewPaymentS.checkCouponByOrderUUID, { uuid: uuid, entityId: selectCoupon.entityId });
        if (res) {
          if (res.valid) {
            isLoadOrder = true;
            yield put(action('getOrderDetail'))
          }
          else {
            yield put(action('save', { selectCoupon: null }));
            yield put(action('checkOrderCoupon'));
          }
        }
        if (!isLoadOrder) {
          yield put(action('getOrderDetail'));
        }
      } catch (e) {
        console.error('checkCouponByOrderUUID', e)
      }
    },

    // 
    *selectCoupon ({ payload: { coupon } }, { call, put, select }) {
      try {
        const { uuid } = yield select(({ orderList }) => orderList);

        const checkRes = yield call(reviewPaymentS.checkCouponByOrderUUID, { uuid: uuid, entityId: coupon.entityId });
        if (checkRes) {
          if (checkRes.valid) {
            yield put(action('save', { selectCoupon: coupon, showEnterCode: false }));
            yield put(action('getOrderDetail'))
          } else if (checkRes.errList && checkRes.errList.length > 0) {
            ModalAlert({
              title: 'Voucher not available',
              msg: checkRes.errList[0], onSure: () => {
              }
            })
          }
        } else {
          message.base('check coupon error')
        }
      } catch (e) {
        console.error('selectCoupon', e)
      }
    },

    //  save
    *pay ({ payload: { payType, cardType, callBack } }, { call, put, select, take }) {
      try {
        yield put(action('save', { buttonLoading: true }))
        const { uuid } = yield select(({ orderList }) => orderList);
        const { note } = yield select(({ myDetail }) => myDetail);

        yield put(action('orderList/updateOrder', {
          params: {
            payType: payType,
            note: note,
          }
        }));

        yield take('orderList/updateOrder/@@end');

        if (payType === 'cash') {

          yield put(action('save', { payType: payType }))
          const res = yield call(reviewPaymentS.payByCash, { uuid: uuid, host: window.location.origin })
          callBack && callBack(res);
        } else if (payType === 'card') {
          if (cardType === AppConfig.REVOLUT) {
            callBack && callBack();
          } else {
            yield put(action('save', { payType: payType, showCard: true }))
          }
        }
      } catch (e) {
        console.error('pay', e)
      } finally {
        yield put(action('save', { buttonLoading: false }))

      }
    },
    *couponSearch ({ payload }, { call, put, select }) {
      try {
        const { searchCode } = yield select(({ reviewPayment }) => reviewPayment);
        const { rId } = yield select(({ orderList }) => orderList);

        if (searchCode) {
          const res = yield call(reviewPaymentS.redeem, { code: searchCode, shopId: rId });
          if (res?.valid) {
            yield put(action('save', { searchCoupon: res }));
          } else if (res?.errList.length > 0) {
            yield put(action('save', { errorCouponText: res.errList[0] }));
          }
          // yield put(action('getCouponListByUser'))
        }
      } catch (e) {
        console.error(e)
      }
    },

    *useCoupon ({ payload }, { call, put, select }) {
      try {
        const { searchCoupon } = yield select(({ reviewPayment }) => reviewPayment);
        const { uuid } = yield select(({ orderList }) => orderList);

        if (searchCoupon) {
          const checkCouponRes = yield call(reviewPaymentS.checkCouponByOrderUUID, { uuid: uuid, entityId: searchCoupon.entityId });
          if (checkCouponRes?.valid) {
            yield put(action('save', {
              searchCoupon: null,
              selectCoupon: checkCouponRes, searchCode: '', errorCouponText: '', showEnterCode: false
            }));
            yield put(action('getOrderDetail'))
            yield put(action('getCouponListByUser'))
          } else if (checkCouponRes?.errList.length > 0) {
            yield put(action('save', { errorCouponText: checkCouponRes.errList[0] }));
          }
        }
      } catch (e) {
        console.error(e)
      }
    },

    *getTermsByCode ({ payload: { couponId } }, { call, put, select }) {
      try {
        if (couponId) {
          const res = yield call(reviewPaymentS.getTermsByCode, { couponId: couponId });
          yield put(action('save', { terms: res }))
        }
      } catch (e) {
        console.error(e)
      }
    },

    *clear ({ payload }, { call, put, select }) {
      try {
        yield put(action('save', { ...initState }))
      } catch (e) {
        console.error('reviewPayment/clear', e)
      }
    },

    // 
    *testInfo ({ payload: { callBack } }, { call, put, select }) {
      try {  // 1  2   3 
        const { dishList, currentType, currency, dishListPrice, uuid, deliveryFee, rId, saveFlag } = yield select(({ orderList }) => orderList);
        if (!rId) {
          callBack(4);
          return
        }

        const { isLogin } = yield select(({ user }) => user);
        if (!isLogin) {
          callBack(1, rId);
          return
        }
        if (!dishList || dishList.length === 0 || !currentType || !currency || dishListPrice < 0 || !uuid) {
          callBack(2, rId);
          //message.base('You have some problem about your dish')
          return
        }

        const { name, number, email, time, ad } = yield select(({ myDetail }) => myDetail);
        if (!name || !number || !email || !time) {
          callBack(2, rId);
          //message.base('You have some problem about your info')
          return
        }
        if (saveFlag === 0) { //  review payment  save
          callBack(5, rId);
          return
        }
        if (currentType === 2) {
          // if (deliveryFee === undefined) {
          //   callBack(2, rId, 1);
          //   return
          // }
          if (!ad.address || !ad.lat || !ad.lon) {
            callBack(2, rId, 2);
            //message.base('You have some problem about your address')
            return
          }
        }
        callBack(3, rId);
      } catch (e) {
        console.error('reviewPayment/testInfo', e)
      }
    },
  },
};