import React, { useState, useEffect } from "react";
import { formatMoney } from '../../../../../../utils'
import "./indexPc.less";
import "./index.less"
import { Icon } from "../../../../../../component";


export const CardLocal = ({ currency, distance, minOrder, deliveryFee, isDelivery }) => {
  return (
    <div className="card_header_local">
      <Icon type={'iconicon_location_b'}/>
      <span className="local_text">{parseFloat(distance.toFixed(2))} km</span>
      {!isDelivery ? <span className="okane"> | Delivery service is not available</span> : (
        <>
          <span className="okane">
            {deliveryFee === '0.00' || deliveryFee === 0 ? (' | Free Delivery') : ' | Delivery fee ' + formatMoney(deliveryFee, 2, currency)}
          </span>
          <span className="okane">
            {!minOrder && (minOrder === '0.00' || minOrder === 0) ? '  No minimum spend' : '  Min order ' + formatMoney(minOrder, 2, currency)}
          </span>
        </>
      )}
    </div>
  )
};
